<template>
  <div>
    <div class="page-header-box">
      <div class="header-msg-box">
        <p>异步任务配置</p>
      </div>
      <div class="header-btn-box">
        <fdcbutton v-show="queryShow" @click="showForm" icon="eye-disabled">隐藏</fdcbutton>
        <fdcbutton v-show="!queryShow" @click="showForm" icon="eye">显示</fdcbutton>
        <fdcbutton icon="android-add" @click="toAdd">新增</fdcbutton>
      </div>
    </div>

    <div class="form-box" v-show="queryShow">
      <fdcform ref="form" inline :model="formData" :label-width="80" class="search-form" @submit.native.prevent @keyup.enter.native="query()">
        <formitem label="队列编码" prop="queueCode">
          <fdcinput v-model="formData.queueCode" placeholder="请输入队列编码" class="search-input" ></fdcinput>
        </formitem>
        <fdcbutton type="primary" @click="query()" class="search-submit">查询</fdcbutton>
        <fdcbutton type="ghost" @click="reset('form')" class="search-reset">重置</fdcbutton>
      </fdcform>
    </div>

    <div class="table-box">
      <fdctable border size="small" :loading="isLoading" :data="tableData"
                :columns="tableColumns" stripe></fdctable>
      <div class="page-box">
        <Page v-show="pageShow" placement="top" :page-size="formData.pageNumber" :total="total"
              :current.sync="formData.pageNo" @on-change="changePageNo" @on-page-size-change="changePageSize"
              show-total
              show-elevator show-sizer></Page>
      </div>
    </div>
  </div>
</template>

<script>
  import SysTaskConfigAPI from "./api/SysTaskConfigAPI";
  import Event from '@/utility/Event.js';
  import {mapState} from 'vuex';

  export default {
    name: "sysTaskConfig",
    data() {
      return {
        queryShow: true,
        isLoading: false,
        pageShow: false,
        total: 0,
        formData: {
          queueCode: '',
          pageNo: 1,
          pageNumber: 10
        },
        tableData: []
      }
    },
    methods: {
      showForm() {
        this.queryShow = !this.queryShow;
      },
      toAdd() {
        Event.$emit('addPage', {
          title: '新增异步任务配置',
          router: 'addTaskConfig',
          checked: true
        })
      },
      toModify(params) {
        Event.$emit('addPage', {
          title: '修改异步任务配置',
          router: 'modifyTaskConfig',
          checked: true,
          paramId: params
        })
      },
      query() {
        this.isLoading = !this.isLoading;
        SysTaskConfigAPI.query(this.formData).then((res) => {
          this.tableData = [];
          let state = res.data.state;
          if (state === 1) {
            let dataList = res.data.data.list;
            if (dataList !== null) {
              this.pageShow = true;
              this.total = res.data.data.total;
            }
            this.tableData.push(...dataList);
          }
          this.isLoading = !this.isLoading;
        });
      },
      reset(name) {
        this.$refs[name].resetFields();
      },
      changePageNo(value) {
        this.formData.pageNo = value;
        this.query();
      },
      changePageSize(value) {
        this.formData.pageNo = 1;
        this.formData.pageNumber = value;
        this.query();
      },
      disableOrEnable(id, state) {
        SysTaskConfigAPI.disableOrEnable({'id': id, 'state': state}, this.query);
      }
    },
    computed: {
      ...mapState({
        sysEnumList: state => state.Enum.enumList,
        enumSelect: state => state.Enum.enumSelect
      }),
      tableColumns() {
        let tableCol = [
          {
            title: ' ',
            type: 'index2',
            width: 60,
            align: 'center',
            render: (h, params) => {
              return h('span', params.index + (this.formData.pageNo - 1) * this.formData.pageNumber + 1)
            }
          }, {
            title: '队列编码',
            key: 'queueCode',
            width: 120,
            align: 'center'
          },
          {
            title: '队列类型',
            key: 'queueType',
            width: 120,
            align: 'center',
            render: (h, params) => {
              return h('span', {}, this.sysEnumList['QUEUE_TYPE_' + params.row.queueType])
            }
          }, {
            title: '线程数',
            key: 'threadNum',
            width: 90,
            align: 'center'
          }, {
            title: '接口协议',
            key: 'protocol',
            width: 120,
            align: 'center'
          }, {
            title: '重试次数',
            key: 'retryNum',
            width: 100,
            align: 'center'
          }, {
            title: '最大报文字节数',
            key: 'maxByte',
            width: 150,
            align: 'center'
          }, {
            title: '查询数据量',
            key: 'queryNum',
            width: 100,
            align: 'center'
          }, {
            title: '状态',
            key: 'state',
            width: 100,
            align: 'center',
            render: (h, params) => {
              return h('span', {}, this.sysEnumList['ENABLE_STATE_' + params.row.state])
            }
          }, {
            title: '调用URL',
            key: 'targetUrl',
            width: 300,
            align: 'center'
          }, {
            title: '回调URL',
            key: 'notifyUrl',
            width: 200,
            align: 'center'
          }, {
            title: '创建人',
            key: 'creator',
            width: 80,
            align: 'center'
          }, {
            title: '创建时间',
            key: 'createdTime',
            width: 150,
            align: 'center'
          }, {
            title: '修改人',
            key: 'modifier',
            width: 80,
            align: 'center'
          }, {
            title: '修改时间',
            key: 'modifiedTime',
            width: 150,
            align: 'center'
          }, {
            title: '操作',
            key: 'action',
            width: 100,
            align: 'left',
            render: (h, params) => {
              const row = params.row;
              const type = row.state === 1 ? 'locked' : 'unlocked';
              const content = row.state === 1 ? '禁用' : '启用';
              const editColor = row.state === 1 ? '#0d96ff' : 'gray';
              return h('div', [
                h('tooltip', {
                  props: {
                    content: '修改',
                    placement: 'top'
                  }
                }, [h('span', {
                  on: {
                    click: () => {
                      if(row.state === 1){
                        this.toModify(row.id);
                      }
                    }
                  }
                }, [h('icon', {
                  props: {
                    type: 'edit',
                    size: 20,
                    color: editColor
                  },
                  style: {
                    marginRight: '5px'
                  }
                }, '修改')])
                ]),
                h('tooltip', {
                  props: {
                    content: content,
                    placement: 'top'
                  }
                }, [
                  h('span', {
                    on: {
                      click: () => {
                        if (row.state === 1) {
                          this.$Modal.confirm({
                            title: '禁用',
                            content: '<p>确定禁用？</p>',
                            onOk: () => {
                              this.disableOrEnable(row.id, 0)
                            },
                            onCancel: () => {
                            }
                          })
                        } else {
                          this.disableOrEnable(row.id, 1)
                        }
                      }
                    }
                  }, [h('icon', {
                    props: {
                      type: type,
                      size: 20
                    }
                  }, '删除')])
                ])
              ]);
            }
          }];
        return tableCol;
      }
    },
    mounted() {
      Event.$emit('addEnum', {typeList:['ENABLE_STATE', 'QUEUE_TYPE']});
      Event.$once('refreshList', obj => this.query());
    },
    components: {}
  }
</script>





