<template>
  <div>
    <div class="form-box-content">
      <div class="module-title-wrap">基本信息</div>
      <fdcform ref="formValidate" :model="formValidate" :rules="ruleValidate" :label-width="100">
        <row type="flex" justify="space-around">
          <fdccol span="7">
            <formitem label="区域编码" prop="code">
              <fdcinput v-model="formValidate.code" placeholder=""></fdcinput>
            </formitem>
          </fdccol>
          <fdccol span="7">
            <formitem label="区域名称" prop="name">
              <fdcinput v-model="formValidate.name" placeholder=""></fdcinput>
            </formitem>
          </fdccol>
          <fdccol span="7">
            <formitem label="区域简码" prop="shortCode">
              <fdcinput v-model="formValidate.shortCode" placeholder=""></fdcinput>
            </formitem>
          </fdccol>
        </row>
        <row type="flex" justify="space-around">
          <fdccol span="7">
            <formitem label="省" prop="provinceId">
              <fdcselect v-model="formValidate.provinceId" placeholder="" @on-change="selectCity">
                <fdcoption v-for="item in provinceList" :value="item.id" :key="item.id">
                  {{ item.name }}
                </fdcoption>
              </fdcselect>
            </formitem>
          </fdccol>
          <fdccol span="7">
            <formitem label="市" prop="cityId">
              <fdcselect v-model="formValidate.cityId" placeholder="" @on-change="selectDistrict"
                         :disabled="formValidate.provinceId === ''">
                <fdcoption v-for="item in cityList" :value="item.id" :key="item.id">
                  {{ item.name }}
                </fdcoption>
              </fdcselect>
            </formitem>
          </fdccol>
          <fdccol span="7">
            <formitem label="区" prop="districtId">
              <fdcselect v-model="formValidate.districtId" placeholder="" :disabled="formValidate.cityId === ''">
                <fdcoption v-for="item in districtList" :value="item.id" :key="item.id">
                  {{ item.name }}
                </fdcoption>
              </fdcselect>
            </formitem>
          </fdccol>
        </row>
        <row type="flex" justify="space-around">
          <fdccol span="23">
            <formitem label="区域类型" prop="type">
              <Radiogroup v-model="formValidate.type">
                <Radio v-for="item in enumSelect['AREA_TYPE']" :label="item.enumCode" :key="item.enumCode">
                  {{ item.enumValue }}
                </Radio>
              </Radiogroup>
            </formitem>
          </fdccol>
        </row>
        <row type="flex" justify="space-around">
          <fdccol span="7">
            <formitem label="所属网点" prop="depotName">
              <fdcselect v-model="formValidate.depotId" placeholder="" :disabled="formValidate.cityId === ''">
                <fdcoption v-for="item in depotList" :value="item.id" :key="item.id">
                  {{ item.name }}
                </fdcoption>
              </fdcselect>
            </formitem>
          </fdccol>
          <!--显示用-->
          <fdccol span="7"></fdccol>
          <fdccol span="7"></fdccol>
        </row>
        <row type="flex" justify="space-around">
          <fdccol span="23">
            <formitem label="匹配顺序" prop="matchOrder">
              <Radiogroup v-model="formValidate.matchOrder">
                <Radio v-for="item in enumSelect['MATCH_ORDER']" :label="item.enumCode" :key="item.enumCode">
                  {{ item.enumValue }}
                </Radio>
              </Radiogroup>
            </formitem>
          </fdccol>
        </row>
        <row type="flex" justify="space-around">
          <fdccol span="7">
            <formitem label="优先级" prop="priority">
              <fdcinput v-model="formValidate.priority" placeholder=""></fdcinput>
            </formitem>
          </fdccol>
          <fdccol span="7"></fdccol>
          <fdccol span="7"></fdccol>
        </row>

        <!--分割线-->
        <div class="module-title-wrap">地址库</div>
        <row type="flex" justify="space-around">
          <fdccol span="23">
            <formitem label="匹配方式" prop="addressMatchMode">
              <Radiogroup v-model="formValidate.addressMatchMode">
                <Radio v-for="item in enumSelect['ADDRESS_MATCH_MODE']" :label="item.enumCode" :key="item.enumCode">
                  {{ item.enumValue }}
                </Radio>
              </Radiogroup>
            </formitem>
          </fdccol>
        </row>
        <row type="flex" justify="space-around">
          <fdccol span="15">
            <formitem label="地址库关键字" prop="addressKeywords">
              <fdcinput v-model="formValidate.addressKeywords" type="textarea" placeholder=""></fdcinput>
            </formitem>
          </fdccol>
          <fdccol span="7"></fdccol>
        </row>

        <div class="module-title-wrap">地图</div>
        <row type="flex" justify="space-around">
          <fdccol span="23">
            <formitem label="范围类型" prop="MAP_RANGE_TYPE">
              <Radiogroup v-model="formValidate.mapRangeType">
                <Radio v-for="item in enumSelect['MAP_RANGE_TYPE']" :label="item.enumCode" :key="item.enumCode">
                  {{ item.enumValue }}
                </Radio>
              </Radiogroup>
            </formitem>
          </fdccol>
        </row>
        <row type="flex" justify="space-around">
          <fdccol span="23">
            <formitem>
              <label>关键词：<input v-model="keyword"></label>
              <label>地区：<input v-model="location"></label>
              <baidu-map class="map" ak="yF73Le6GB25SEhuHpnf9aFc62mG4oXo7" :center="center"
                         :zoom="15" @moving="syncCenterAndZoom"
                         @moveend="syncCenterAndZoom"
                         @zoomend="syncCenterAndZoom">
                <bm-local-search :keyword="keyword" :auto-viewport="true" :panel="false" :location="location"
                                 :selectFirstResult="true"/>
                <bm-navigation anchor="BMAP_ANCHOR_TOP_RIGHT"/>
                <bm-geolocation anchor="BMAP_ANCHOR_BOTTOM_RIGHT" :showAddressBar="true" :autoLocation="true"/>
                <bm-polygon :path="polygonPath" stroke-color="blue" :stroke-opacity="0.5" :stroke-weight="5"
                            :editing="true"
                            @lineupdate="updatePolygonPath"/>
                <bm-circle :center="circlePath.center" :radius="circlePath.radius" stroke-color="red" :stroke-opacity="0.5" :stroke-weight="5"
                           @lineupdate="updateCirclePath" :editing="true"></bm-circle>

              </baidu-map>
              <fdcbutton type="primary" v-show="formValidate.mapRangeType === 2" @click="addPolygonPoint">添加多边形</fdcbutton>
              <fdcbutton type="primary" v-show="formValidate.mapRangeType !== 2" @click="addCircle">添加圆形</fdcbutton>
              <fdcbutton type="primary" @click="clearPath">清除坐标点</fdcbutton>
              <fdcbutton type="primary" @click="savePath">保存坐标点</fdcbutton>
            </formitem>
          </fdccol>
        </row>
        <row type="flex" justify="space-around">
          <fdccol span="15">
            <formitem label="地图区域坐标" prop="mapRegionalCoordinates">
              <fdcinput v-model="formValidate.mapRegionalCoordinates" type="textarea" placeholder=""></fdcinput>
            </formitem>
          </fdccol>
          <fdccol span="7"></fdccol>
        </row>
        <row type="flex" justify="space-around">
          <fdccol span="7">
            <formitem>
              <fdcbutton type="primary" @click="handlerSubmit('formValidate')">提交</fdcbutton>
              <fdcbutton type="ghost" @click="cancel('addArea')" class="buttonClass">取消</fdcbutton>
            </formitem>
          </fdccol>
        </row>
      </fdcform>
    </div>
  </div>
</template>
<script>
  import AreaAPI from './api/AreaAPI'
  import GeoAPI from '../../basicData/geoInfo/api/GEOAPI'
  import LocationAPI from '../../basicData/location/api/LocationAPI'
  import {mapState} from 'vuex'
  import Event from '@/utility/Event.js'
  import BaiduMap from 'vue-baidu-map/components/Map/Map.vue'
  import BmPolygon from 'vue-baidu-map/components/overlays/Polygon'
  import BmNavigation from 'vue-baidu-map/components/controls/Navigation'
  import BmGeolocation from 'vue-baidu-map/components/controls/Geolocation'
  import BmLocalSearch from 'vue-baidu-map/components/search/LocalSearch'
  import BmCircle from 'vue-baidu-map/components/overlays/Circle'

  export default {
    name: 'addArea',
    data: function () {
      return {
        ruleValidate: {
          name: [{
            required: true,
            type: 'string',
            message: '请输入区域名称',
            trigger: 'blur'
          }],
          code: [{
            required: true,
            type: 'string',
            message: '请输入区域编号',
            trigger: 'blur'
          }],
          shortCode: [{
            required: true,
            type: 'string',
            message: '请输入区域简码',
            trigger: 'blur'
          }],
        },
        formValidate: {
          name: '',
          code: '',
          shortCode: '',
          provinceId: '',
          cityId: '',
          districtId: '',
          type: '',
          depotId: '',
          matchOrder: '',
          priority: '',
          addressMatchMode: '',
          mapRangeType: 2,
          addressKeywords: '',
          mapRegionalCoordinates: ''

        },
        provinceList: '',
        cityList: '',
        districtList: '',
        depotList: '',
        center: {
          lng: 114.431362,
          lat: 30.469663
        },
        polygonPath: [],
        circlePath: {
          center: {
            lng: 0,
            lat: 0
          },
          radius: 500
        },
        location: '武汉',
        keyword: '光谷智慧园26栋'
      }
    },
    components: {
      BaiduMap, BmPolygon, BmNavigation, BmGeolocation, BmLocalSearch, BmCircle
    },
    computed: {
      ...mapState({
        enumSelect: state => state.Enum.enumSelect
      })
    },
    methods: {
      insert() {
        AreaAPI.insert(this.formValidate).then((res) => {
          if (res.data.state === 1) {
            this.$Notice.success({
              title: '操作成功',
              desc: '新增成功'
            })
            Event.$emit('closePage', 'addArea')
            Event.$emit('refreshAreaList', null)
          } else {
            this.$Notice.error({
              title: '操作失败',
              desc: res.data.msg
            })
          }
        })
      },
      handlerSubmit(name) {
        this.$refs[name].validate((valid) => {
          if (valid) {
            this.insert()
          }
        })
      },
      cancel(router) {
        Event.$emit('closePage', router)
      },
      getProvinceList() {
        GeoAPI.getProvinceList().then((res) => {
          if (res.data) {
            this.provinceList = res.data;
          }
        })
      },
      selectCity(parentId) {
        this.getGeoInfoListByParentId(parentId, 2);
      },
      selectDistrict(parentId) {
        this.getGeoInfoListByParentId(parentId, 3);
        this.getDepotListByCityId();
      },
      getGeoInfoListByParentId(parentId, type) {
        GeoAPI.getGeoInfoListByParentId({parentId: parentId}).then((res) => {
          if (res.data) {
            if (type === 2) {
              this.cityList = res.data;
            } else if (type === 3) {
              this.districtList = res.data;
            }
          }
        })
      },
      getDepotListByCityId() {
        LocationAPI.getLocationListByCityId({cityId: this.formValidate.cityId}).then((res) => {
          if (res.data) {
            this.depotList = res.data;
          }
        });
      },
      //地图
      updatePolygonPath(e) {
        this.polygonPath = e.target.getPath()
      },
      updateCirclePath(e){
        this.circlePath.center = e.target.getCenter()
        this.circlePath.radius = e.target.getRadius()
      },
      //初始化多边形
      addPolygonPoint() {
        //只需要添加一个多边形
        if (this.polygonPath.length >= 3) return;
        this.polygonPath.push({lng: this.center.lng - 0.01, lat: this.center.lat - 0.01})
        this.polygonPath.push({lng: this.center.lng + 0.01, lat: this.center.lat})
        this.polygonPath.push({lng: this.center.lng, lat: this.center.lat + 0.01})
      },
      //初始化圆形
      addCircle(){
        this.circlePath.center = this.center
        this.circlePath.radius = 500
      },
      clearPath() {
        //清除多边形
        this.polygonPath = []
        //清除圆形
        // this.circlePath.center = []
        this.circlePath.radius = 0
        //清除坐标点
        this.formValidate.mapRegionalCoordinates = ''
      },
      //保存坐标点,如果是多边形，则是则是顶点的坐标，如果是圆形则是圆心和半径
      savePath() {
        this.formValidate.mapRegionalCoordinates  = ""
        if(this.formValidate.mapRangeType === 2){
          this.polygonPath.forEach(point => {
            this.formValidate.mapRegionalCoordinates += "(" + point.lng + "," + point.lat + ");"
          })
        }else if(this.formValidate.mapRangeType === 1){
            this.formValidate.mapRegionalCoordinates = "(" + this.circlePath.center.lng + "," + this.circlePath.center.lat + ");" + this.circlePath.radius
        }
      },
      //移动地图获取地图中心
      syncCenterAndZoom(e) {
        this.center = e.target.getCenter()
        this.zoom = e.target.getZoom()
      }
    },
    mounted() {
      const condition = ['AREA_TYPE', 'MATCH_ORDER', 'ADDRESS_MATCH_MODE', 'MAP_RANGE_TYPE']
      Event.$emit('addEnum', {typeList: condition})
      this.getProvinceList()
    }
  }
</script>
<style scoped>
  .map {
    width: 1000px;
    height: 500px;
  }
</style>



