<template>
  <div>
    <div class="page-header-box">
      <div class="header-msg-box">
        <p>作业点管理</p>
      </div>
      <div class="header-btn-box">
        <fdcbutton v-show="queryShow" @click="hideForm" icon="eye-disabled">隐藏</fdcbutton>
        <fdcbutton v-show="!queryShow" @click="showForm" icon="eye">显示</fdcbutton>
        <fdcbutton icon="android-add" @click="toAdd">新增</fdcbutton>
      </div>
    </div>

    <div class="form-box" v-show="queryShow">
      <fdcform ref="form" inline :model="formData" :label-width="80" class="search-form" @keyup.enter.native="query()">
        <formitem label="编码" prop="code">
          <fdcinput v-model="formData.code" placeholder="请输入编码" class="search-input"
                    ></fdcinput>
        </formitem>
        <formitem label="名称" prop="name">
          <fdcinput v-model="formData.name" placeholder="请输入名称" class="search-input"
                    ></fdcinput>
        </formitem>
        <formitem label="类型" prop="type">
          <fdcselect v-model="formData.type" placeholder="请选择作业点类型" class="search-input" clearable @on-change="query()">
            <fdcoption v-for="item in enumSelect['LOCATION_TYPE']" :value="item.enumCode" :key="item.enumCode">
              {{ item.enumValue }}
            </fdcoption>
          </fdcselect>
        </formitem>
        <fdcbutton type="primary" @click="query()" class="search-submit">查询</fdcbutton>
        <fdcbutton type="ghost" @click="reset('form')" class="search-reset">重置</fdcbutton>
      </fdcform>
    </div>
    <div class="table-box">
      <fdctable border size="small" :loading="isLoading" :data="tableData" :columns="tableColumns"
                stripe></fdctable>
      <div class="page-box">
        <Page v-show="pageShow" placement="top" :page-size="formData.pageNumber" :total="total"
              :current.sync="formData.pageNo" @on-change="changePageNo" @on-page-size-change="changePageSize" show-total
              show-elevator show-sizer></Page>
      </div>
    </div>
  </div>
</template>

<script>
  import Event from '@/utility/Event';
  import LocationAPI from './api/LocationAPI';
  import {mapActions, mapState} from 'vuex';
  import EnumSelect from '@/components/enumSelect.vue'

  export default {
    name: "location",
    data() {
      return {
        queryShow: true,
        isLoading: false,
        pageShow: false,
        tableLoading: false,
        total: 0,
        formData: {
          code: '',
          name: '',
          level: '',
          pageNo: 1,
          pageNumber: 10
        },
        tableData: [],
        ruleValidate: {
          code: [{
            required: true,
            type: 'string',
            message: '编码必填',
            trigger: 'blur'
          }],
          name: [{
            required: true,
            type: 'string',
            message: '名称必填',
            trigger: 'blur'
          }],
          level: [{
            required: true,
            type: 'number',
            message: '级别必填',
            trigger: 'change'
          }]
        }
      }
    },
    methods: {
      ...mapActions([
        'generateEnumList'
      ]),
      hideForm() {
        this.queryShow = !this.queryShow;
      },
      showForm() {
        this.queryShow = !this.queryShow;
      },
      query() {
        this.isLoading = !this.isLoading;
        LocationAPI.query(this.formData).then((res) => {
          this.tableData = [];
          let state = res.data.state;
          if (state === 1) {
            let dataList = res.data.data.list;
            if (dataList !== null && dataList.length > 0) {
              this.pageShow = true;
              this.total = res.data.data.total;
              this.tableData.push(...dataList);
            }
          }
          this.isLoading = !this.isLoading;
        });
      },
      reset(name) {
        this.$refs[name].resetFields();
      },
      toAdd() {
        Event.$emit('addPage', {
          title: '新增作业点',
          router: 'addLocation',
          checked: true
        })
      },
      toModify(params) {
        Event.$emit('addPage', {
          title: '修改作业点',
          router: 'modifyLocation',
          checked: true,
          paramId: params
        })
      },
      changePageNo(value) {
        this.formData.pageNo = value;
        this.query();
      },
      changePageSize(value) {
        this.formData.pageNo = 1;
        this.formData.pageNumber = value;
        this.query();
      },
      disableOrEnable(id, state) {
        LocationAPI.disableOrEnable({'id': id, 'state': state}, this.query);
      }
    },
    computed: {
      ...mapState({
        sysEnumList: state => state.Enum.enumList,
        enumSelect: state => state.Enum.enumSelect
      }),
      tableColumns() {
        let tableCol = [{
          title: ' ',
          key: 'number',
          width: 60,
          align: 'center',
          render: (h, params) => {
            return h('span', params.index + (this.formData.pageNo - 1) * this.formData.pageNumber + 1)
          }
        }, {
          title: '编码',
          key: 'code',
          align: 'center'
        }, {
          title: '名称',
          key: 'name',
          align: 'center'
        }, {
          title: '类型',
          key: 'type',
          align: 'center',
          render: (h, params) => {
            return h('span', {}, this.sysEnumList['LOCATION_TYPE_' + params.row.type])
          }
        }, {
          title: '城市',
          key: 'cityName',
          align: 'center'
        }, {
          title: '联系人',
          key: 'contact',
          align: 'center'
        }, {
          title: '联系电话',
          key: 'telephone',
          align: 'center'
        }, {
          title: '地址',
          key: 'address',
          align: 'center'
        }, {
          title: '状态',
          key: 'isDeleted',
          align: 'center',
          render: (h, params) => {
            return h('span', {}, this.sysEnumList['DISABLE_STATE_' + params.row.isDeleted])
          }
        }, {
          title: '操作',
          key: 'action',
          width: 100,
          align: 'center',
          render: (h, params) => {
            const row = params.row;
            const type = row.isDeleted === 0 ? 'locked' : 'unlocked';
            const content = row.isDeleted === 0 ? '禁用' : '启用';
            const editColor = row.isDeleted === 0 ? '#0d96ff' : 'gray';
            return h('div', [
              h('tooltip', {
                  props: {
                    content: '修改',
                    placement: 'top'
                  }
                }, [h('span', {
                  on: {
                    click: () => {
                      if (row.isDeleted === 0) {
                        this.toModify(params.row.id);
                      }
                    }
                  }
                }, [h('icon', {
                  props: {
                    type: 'edit',
                    size: 20,
                    color: editColor
                  },
                  style: {
                    marginRight: '8px'
                  }
                }, '修改')])]
              ),
              h('tooltip', {
                props: {
                  content: content,
                  placement: 'top'
                }
              }, [
                h('span', {
                  on: {
                    click: () => {
                      if (row.isDeleted === 0) {
                        this.$Modal.confirm({
                          title: '禁用',
                          content: '<p>确定禁用？</p>',
                          onOk: () => {
                            this.disableOrEnable(row.id, 1)
                          },
                          onCancel: () => {
                          }
                        })
                      } else {
                        this.disableOrEnable(row.id, 0)
                      }
                    }
                  }
                }, [h('icon', {
                  props: {
                    type: type,
                    size: 20
                  }
                }, '禁用')])
              ])
            ]);
          }
        }];
        return tableCol;
      }
    },
    mounted() {
      Event.$emit('addEnum', {typeList: ['LOCATION_TYPE', 'DISABLE_STATE']});
      Event.$on('refreshLocationList', obj => this.query());
    },
    components: {
      enumSelect: EnumSelect
    }
  }
</script>





