<template>
  <div>
    <div class="tab-form">
      <tabs :value="tabName">
        <tabpane label="个人信息" name="userInfo">
          <div>
            <row>
              <fdccol span='10'>
                <div class="tab-form-avatar">
                  <img src="https://i.loli.net/2017/08/21/599a521472424.jpg"/>
                  <Upload ref="upload" :show-upload-list="false" :on-success="handleSuccess"
                          :format="['jpg','jpeg','png']"
                          :max-size="2048" :on-format-error="handleFormatError" :on-exceeded-size="handleMaxSize"
                          :before-upload="handleBeforeUpload" type="drag" :action="url"
                          class="user-upload">
                    <div class="upload-icon">
                      <Icon type="camera" size="20"></Icon>
                    </div>
                  </Upload>
                </div>
              </fdccol>

              <fdccol span='14'>
                <fdcform ref="formData" :model="formData" :label-width="100" :rules="ruleValidate">
                  <row>
                    <fdccol span='16'>
                      <formitem label="登陆名" prop="loginName">
                        <fdcinput v-model="formData.loginName" placeholder="登录名">
                        </fdcinput>
                      </formitem>
                    </fdccol>
                  </row>
                  <row>
                    <fdccol span='16'>
                      <formitem label="姓名" prop="name">
                        <fdcinput v-model="formData.name" placeholder="姓名">
                        </fdcinput>
                      </formitem>
                    </fdccol>
                  </row>
                  <row>
                    <fdccol span='16'>
                      <formitem label="邮箱" prop="email">
                        <fdcinput v-model="formData.email" placeholder="邮箱">
                        </fdcinput>
                      </formitem>
                    </fdccol>
                  </row>
                  <row>
                    <fdccol span='16'>
                      <formitem label="联系方式" prop="contactInfo">
                        <fdcinput v-model="formData.contactInfo" placeholder="联系方式">
                        </fdcinput>
                      </formitem>
                    </fdccol>
                  </row>
                  <row>
                    <fdccol span='16'>
                      <formitem label="所属部门" prop="department">
                        <fdcinput v-model="formData.department" placeholder="所属部门">
                        </fdcinput>
                      </formitem>
                    </fdccol>
                  </row>
                  <row>
                    <fdccol span='16'>
                      <formitem label="身份证" prop="idCard">
                        <fdcinput v-model="formData.idCard" placeholder="身份证">
                        </fdcinput>
                      </formitem>
                    </fdccol>
                  </row>
                  <row type="flex" justify="space-around">
                    <fdccol span='24'>
                      <formitem>
                        <fdcbutton type="primary" @click="handleSubmit('formData')">提交</fdcbutton>
                        <fdcbutton type="ghost" @click="close" class="buttonClass">取消</fdcbutton>
                      </formitem>
                    </fdccol>
                  </row>
                </fdcform>
              </fdccol>
            </row>
          </div>
        </tabpane>

        <tabpane label="修改密码" name="modifyPassword">
          <div>
            <fdcform ref="formData2" :model="formData2" :rules="ruleValidate2" :label-width="80">
              <row type="flex" justify="space-around">
                <fdccol span="12">
                  <formitem label="旧密码" prop="oldPassword">
                    <fdcinput v-model="formData2.oldPassword" type="password" placeholder="Enter old password">
                    </fdcinput>
                  </formitem>
                </fdccol>
              </row>
              <row type="flex" justify="space-around">
                <fdccol span="12">
                  <formitem label="新密码" prop="newPassword">
                    <fdcinput v-model="formData2.newPassword" type="password" placeholder="Enter new password">
                    </fdcinput>
                  </formitem>
                </fdccol>
              </row>
              <row type="flex" justify="space-around">
                <fdccol span="12">
                  <formitem label="确认密码" prop="confirmPassword">
                    <fdcinput v-model="formData2.confirmPassword" type="password" placeholder="Confirm password">
                    </fdcinput>
                  </formitem>
                </fdccol>
              </row>
              <row type="flex" justify="space-around">
                <fdccol span='12'>
                  <formitem>
                    <fdcbutton type="primary" @click="handleSubmit2('formData2')">提交</fdcbutton>
                    <fdcbutton type="ghost" @click="close" class="buttonClass">取消</fdcbutton>
                  </formitem>
                </fdccol>
              </row>
            </fdcform>
          </div>
        </tabpane>
      </tabs>
    </div>
  </div>
</template>

<script>
  import tags from '@/components/tags.vue'
  import UserAPI from './api/UserAPI'
  import Event from '@/utility/Event.js'
  import {mapState} from 'vuex'

  export default {
    name: 'userInfo',
    data() {
      return {
        tabName: 'userInfo',
        url: '',
        formData: {
          id: '',
          loginName: '',
          name: '',
          email: '',
          contactInfo: '',
          department: '',
          idCard: ''
        },
        ruleValidate: {
          loginName: [{
            required: true,
            message: '请输入用户名',
            trigger: 'blur'
          }, {
            type: 'string',
            message: '请输入合法用户名',
            trigger: 'blur'
          }],
          name: [{
            required: true,
            message: '请输入用户名',
            trigger: 'blur'
          }],
          email: [{
            type: 'email',
            message: '请输入正确的邮箱',
            trigger: 'blur'
          }],
          contactInfo: [{
            type: 'string',
            trigger: 'blur'
          }],
          department: [{
            type: 'string',
            trigger: 'blur'
          }],
          idCard: [{
            required: true,
            pattern: /^[0-9]{15,18}$/,
            message: '请输入正确的身份证号码'
          }]
        },
        formData2: {
          oldPassword: '',
          newPassword: '',
          confirmPassword: ''
        },
        ruleValidate2: {
          oldPassword: [{
            required: true,
            message: '请输入旧密码',
            trigger: 'blur'
          }],
          newPassword: [{
            required: true,
            message: '请输入新密码',
            trigger: 'blur'
          }, {
            pattern: /^[a-zA-Z]\w{5,17}$/,
            message: '请以字母开头，长度在6~18之间，只能包含字母、数字和下划线',
            trigger: 'blur'
          }],
          confirmPassword: [{
            required: true,
            message: '请再次输入新密码',
            trigger: 'blur'
          }],
        }
      }
    },
    computed: {
      ...mapState({
        userName: state => state.User.userName,
        rsa_exponent : state => state.User.rsa_exponent,
        rsa_modulus : state => state.User.rsa_modulus,
        rsa_passkey : state => state.User.rsa_passkey
      })
    },
    components: {
      tags: tags
    },
    methods: {
      close() {
        Event.$emit('closePage', 'userInfo')
      },
      handleSubmit(name) {
        this.$refs[name].validate((valid) => {
          if (valid) {
            this.modify();
          }
        })
      },
      handleSubmit2(name) {
        this.$refs[name].validate((valid) => {
          if (valid) {
            this.modifyPassword();
          }
        })
      },
      modify() {
        const items = this.formData
        UserAPI.modify(items).then((res) => {
          if (res.data.state === 1) {
            this.$Notice.success({
              title: '操作成功',
              desc: '修改用户成功'
            });
            Event.$emit('closePage', 'userInfo')
          } else {
            this.$Notice.error({
              title: '操作失败',
              desc: res.data.msg
            });
          }
        })
      },
      modifyPassword() {
        if (this.formData2.newPassword === this.formData2.oldPassword) {
          this.$Notice.error({
            title: '新密码不能与旧密码相同',
          });
          return;
        }
        if (this.formData2.confirmPassword !== this.formData2.newPassword) {
          this.$Notice.error({
            title: '确认密码与新密码输入不一致',
          });
          return;
        }
        debugger
        let key = parseInt(Math.random()*100000);
        let oldPassword = this.formData2.oldPassword.split("").reverse().join("");
        oldPassword = this.encryptRSA(oldPassword);
        let newPassword = this.formData2.newPassword.split("").reverse().join("");
        newPassword = this.encryptRSA(newPassword);
        let params = {
          'oldPassword':this.rsa_passkey+""+key+""+oldPassword,
          'newPassword':this.rsa_passkey+""+key+""+newPassword,
          'key':key
        };
        UserAPI.modifyPassword(params).then((res) => {
          if (res.data.state === 1) {
            this.$Notice.success({
              title: '操作成功',
              desc: '密码修改成功'
            });
            Event.$emit('closePage', 'userInfo');
          } else {
            this.$Notice.error({
              title: '操作失败',
              desc: res.data.msg
            })
          }
        });
      },
      encryptRSA (password) {
        var key = window.RSAUtils.getKeyPair(this.rsa_exponent, '', this.rsa_modulus);
        return window.RSAUtils.encryptedString(key, password);
      },
      query() {
        UserAPI.getByName(this.userName).then((res) => {
          if (res.data.state === 1) {
            this.formData = res.data.data
          } else {
            this.$Notice.error({
              title: '获取个人信息失败',
              desc: res.data.msg
            })
          }
        })
      },
      handleSuccess(res, file) {
        file.url = ''
        file.name = '7eb99afb9d5f317c912f08b5212fd69a'
      },
      handleFormatError(file) {
        this.$Notice.warning({
          title: 'The file format is incorrect',
          desc: 'File format of ' + file.name + ' is incorrect, please select jpg or png.'
        });
      },
      handleMaxSize(file) {
        this.$Notice.warning({
          title: 'Exceeding file size limit',
          desc: 'File  ' + file.name + ' is too large, no more than 2M.'
        })
      },
      handleBeforeUpload() {

      }
    },
    mounted() {
      this.query();
    },
    created() {
      // this.url = UserAPI.uploadFiles()

    }
  }
</script>



