<template>
  <div>
    <div class="page-header-box">
      <div class="header-msg-box">
        <p>承运商管理</p>
      </div>
      <div class="header-btn-box">
        <fdcbutton v-show="formshow" @click="hideForm" icon="eye-disabled">隐藏</fdcbutton>
        <fdcbutton v-show="!formshow" @click="showForm" icon="eye">显示</fdcbutton>
        <fdcbutton icon="android-add" @click="addCarrier">新增</fdcbutton>
      </div>
    </div>
    <div class="form-box" v-show="formshow">
      <fdcform ref="formData"  :model="formData"  :label-width="80" class="search-form" inline>
            <formitem label="名称" prop="name">
              <fdcinput v-model="formData.name" @enter="handleSubmit('formData')" placeholder="" class="search-input">
              </fdcinput>
            </formitem>
            <formitem label="代码" prop="code">
              <fdcinput v-model="formData.code" @enter="handleSubmit('formData')" placeholder="" class="search-input">
              </fdcinput>
              <fdcbutton type="primary" class = "search-submit" @click="handleSubmit('formData')">查询</fdcbutton>
              <fdcbutton type="ghost"  class = "search-reset" @click="handleReset('formData')">重置</fdcbutton>
            </formitem>
      </fdcform>
    </div>
    <div class="table-box">
      <fdctable border size="small" :loading="isLoading" :data="tableData"
                :columns="tableColumns" @on-current-change="selectData" stripe></fdctable>
      <div class="page-box">
        <Page v-show="pageShow" placement="top" :total="total" :page-size="formData.pageSize" :current.sync="formData.pageNo" @on-change="changePageNo" @on-page-size-change="changePageSize" show-total show-elevator show-sizer></Page>
      </div>
    </div>
  </div>
</template>

<script>

import Event from '@/utility/Event.js'
import CarrierAPI from './api/CarrierAPI'
import { mapActions, mapState } from 'vuex'
export default {
  name: 'carrier',
  data: function () {
    return {
      formshow: true,
      isLoading: false,
      pageShow: false,
      total: 0,
      formData: {
        parentName: '',
        name: '',
        pageNo: 1,
        pageSize: 10
      },
      tableData: [],
      tableColumns: [
        {
          title: ' ',
          key: 'number',
          type: 'index2',
          align: 'center',
          width: '5%',
          render: (h, params) => {
            return h('span', params.index + (this.formData.pageNo - 1) * this.formData.pageSize + 1)
          }
        },
        {
          title: '代码',
          key: 'code',
          align: 'center'
        },
        {
          title: '名称',
          key: 'name',
          align: 'center'
        },
        {
          title: '类型',
          key: 'type',
          align: 'center',
          render: (h, params) => {
            return h('span', {}, this.sysEnumList['CARRIER_TYPE_' + params.row.type])
          }
        },
        {
          title: '联系人',
          key: 'contact',
          align: 'center'
        },
        {
          title: '联系电话',
          key: 'telephone',
          align: 'center'
        },
        {
          title: '邮箱',
          key: 'email',
          align: 'center'
        },
        {
          title: '备注',
          key: 'remark',
          align: 'center'
        },
        {
          title: '操作',
          key: 'action',
          width: 120,
          align: 'left',
          render: (h, params) => {
            return h('div', [
              h('tooltip', {
                props: {
                  content: '修改',
                  placement: 'top'
                }
              }, [h('span', {
                on: {
                  click: () => {
                    this.modifyCarrier(params.row.id)
                  }
                }
              }, [h('icon', {
                props: {
                  type: 'edit',
                  size: 20,
                  color: '#0d96ff'
                },
                style: {
                  marginRight: '8px'
                }
              }, '修改')])]),
              h('tooltip', {
                props: {
                  content: '删除',
                  placement: 'top'
                }
              }, [h('span', {
                on: {
                  click: () => {
                    this.$Modal.confirm({
                      title: '删除',
                      content: '<p>确定删除？</p>',
                      onOk: () => {
                        this.removeCarrier(params.row.id)
                      },
                      onCancel: () => {
                      }
                    })
                  }
                }
              }, [h('icon', {
                props: {
                  type: 'trash-a',
                  size: 20,
                  color: '#f11a09'
                },
                style: {
                  marginRight: '8px'
                }
              }, '删除')])])
            ])
          }
        }
      ]
    }
  },
  mounted () {
    Event.$emit('addEnum', {typeList: ['CARRIER_TYPE']})
    Event.$once('refreshCarrierList', obj => this.query())
  },
  computed: {
    ...mapState({
      sysEnumList: state => state.Enum.enumList
    })
  },
  methods: {
    ...mapActions([
      'generateEnumList'
    ]),
    clickHandler: function (code) {
      Event.$emit('addPage', {
        title: '修改国际化配置',
        router: 'modifyI18n',
        param: code,
        checked: true
      })
    },
    hideForm () {
      this.formshow = !this.formshow
    },
    showForm () {
      this.formshow = !this.formshow
    },
    query () {
      this.isLoading = !this.isLoading
      CarrierAPI.query(this.formData).then((res) => {
        console.log(res)
        this.tableData = []
        let state = res.data.state
        if (state === 1) {
          console.log(res.data.data.list)
          let dataList = res.data.data.list
          if (dataList !== null) {
            this.pageShow = true
            this.total = res.data.data.total
          }
          this.tableData.push(...dataList)
        }
        this.isLoading = !this.isLoading
      })
    },
    handleSubmit (name) {
      this.$refs[name].validate((valid) => {
        if (valid) {
          this.query()
        }
      })
    },
    handleReset (name) {
      this.$refs[name].resetFields()
    },
    changePageNo (value) {
      this.formData.pageNo = value
      this.query()
    },
    changePageSize (value) {
      this.formData.pageNo = 1
      this.formData.pageSize = value
      this.query()
    },
    selectData () {

    },
    addCarrier () {
      Event.$emit('addPage', {
        title: '新增承运商',
        router: 'addCarrier',
        checked: true
      })
    },
    modifyCarrier (params) {
      Event.$emit('addPage', {
        title: '修改承运商',
        router: 'modifyCarrier',
        checked: true,
        paramId: params
      })
    },
    removeCarrier (params) {
      CarrierAPI.delete({id: params}, this.query())
    }
  },
  components: {
  }
}
</script>



