<template>
  <div>
    <div class="form-box-content">
      <div class="module-title-wrap">基本信息</div>
      <fdcform ref="formData" :model="formValidate2" :rules="ruleValidate" :label-width="80">
        <row type="flex" justify="space-around">
          <fdccol span="12">
            <formitem label="属性名称" prop="name">
              <fdcinput v-model="formValidate2.name" placeholder="Enter name">
              </fdcinput>
            </formitem>
          </fdccol>
        </row>
        <row type="flex" justify="space-around">
          <fdccol span="12">
            <formitem label="属性标识" prop="code">
              <fdcinput v-model="formValidate2.code" placeholder="Enter code">
              </fdcinput>
            </formitem>
          </fdccol>
        </row>
        <row type="flex" justify="space-around">
          <fdccol span="12">
            <formitem label="属性值" prop="parameterValue">
              <fdcinput v-model="formValidate2.parameterValue" type="textarea" placeholder="Enter parameter value">
              </fdcinput>
            </formitem>
          </fdccol>
        </row>
        <row type="flex" justify="space-around">
          <fdccol span="12">
            <formitem label="备注">
              <fdcinput v-model="formValidate2.remark" type="textarea" placeholder="Enter mark">
              </fdcinput>
            </formitem>
          </fdccol>
        </row>
        <row type="flex" justify="space-around">
          <fdccol span = '12'>
            <formitem>
              <fdcbutton type="primary" @click="handleSubmit('formData')">提交</fdcbutton>
              <fdcbutton type="ghost" @click="close" class="buttonClass">取消</fdcbutton>
            </formitem>
          </fdccol>
        </row>
      </fdcform>
    </div>
  </div>
</template>

<script>
import SysParameterAPI from './api/SysParameterAPI'
import Event from '@/utility/Event.js'

export default {
  name: 'addSysParameter',
  data () {
    return {
      typetemp: '',
      ruleValidate: {
        name: [{
          required: true,
          type: 'string',
          message: '属性名必填',
          trigger: 'blur'
        }],
        code: [{
          required:true,
          type:'string',
          message:'属性code必填',
          trigger:'blur'
        }],
        parameterValue:[{
          required:true,
          message:'属性值必填',
          trigger:'blur'
        }]
      },
      formValidate2: {
        name: '',
        code: '',
        parameterValue: '',
        remark: ''
      }
    }
  },
  computed: {
//      ...mapState({
//        city:state => state.addUser.city
//      })
  },
  components:{
  },
  methods: {
    handleSubmit(name) {
      let that = this;
      this.$refs[name].validate((valid) => {
        if (valid) {
          that.insertParams();
        }
      })
    },
    handleReset(name) {
      this.$refs[name].resetFields();
    },
    insertParams (){
      SysParameterAPI.insert(this.formValidate2).then((res)=>{
        if(res.data.state === 1){
          this.$Notice.success({
            title: '操作成功',
            desc:'新增系统参数成功'
          });
          Event.$emit('closePage', 'addSysParameter')
          Event.$emit('refreshList',null);
        } else{
          this.$Notice.error({
            title: '操作失败',
            desc:res.data.msg
          });
        }
      });
    },
    cancel(){
      Event.$emit('closeTag',{router: "addParameter"});
    },
    close () {
      Event.$emit('closePage', 'addSysParameter')
    }
  },
  mounted() {
  }
}
</script>




