<template>
  <div class="top-menu-box">
    <fdcmenu mode="horizontal" ref="topMenu" :active-name = "defaultModule" @on-select="showModuleMenus">
      <fdcmenuitem v-for="item in moduleList" :name="item.enumCode" :key="item.enumCode">
        <icon type="ios-paper"></icon>
        {{item.enumValue}}
      </fdcmenuitem>
    </fdcmenu>
  </div>
</template>

<script>
import { mapState } from 'vuex'
import EnumAPI from '../../page/system/sysMagnagement/enum/api/EnumAPI'
export default {
  name: "module",
  data () {
    return {
      moduleList: [],
      params: {
        moduleId: 1,
        token: this.token
      }
    }
  },
  computed: {
    ...mapState({
      defaultModule: state => state.defaultModule,
      token: state => state.User.token
    })
  },
  updated () {
    // 用于更新module的activeName
    this.$nextTick(() => {
      if (this.$refs.topMenu) {
        this.$refs.topMenu.updateActiveName()
      }
    })
  },
  mounted: function () {
    this.getAllModules()
  },
  methods: {
    getAllModules: function () {
      const query = {}
      query.enumTypeList = ['MODULE_TYPE']
      EnumAPI.findEnumByType(query).then((res) => {
        if (res.data.state == 1) {
          this.moduleList.push(...res.data.data)
        }
      })
    },
    showModuleMenus(name) {
      this.$store.commit('changeModule', name)
    }
  }
}
</script>



