import HttpUtil from '@/common/HttpUtil'

const AreaAPI = {
  insert (params) {
    return HttpUtil.postJSON('/api/preSortingConfig/area/insert', params)
  },
  getById (params) {
    return HttpUtil.post('/api/preSortingConfig/area/get', params)
  },
  modify (params) {
    return HttpUtil.post('/api/preSortingConfig/area/modify', params)
  },
  pushData2Es (params) {
    return HttpUtil.post('/api/preSortingConfig/area/pushData2Es', params)
  },
  query (params) {
    return HttpUtil.post('/api/preSortingConfig/area/query', params)
  },
  delete (params, callback) {
    return HttpUtil.post('/api/preSortingConfig/area/del', params, true, callback)
  }
};

export default AreaAPI



