import HttpUtil from '@/common/HttpUtil'
const CarrierAPI = {
  insert (params) {
    return HttpUtil.postJSON('/api/basic/carrier/insert', params)
  },
  getById (id) {
    return HttpUtil.post('/api/basic/carrier/getById', {id: id})
  },
  modify (params) {
    return HttpUtil.post('/api/basic/carrier/modify', params)
  },
  query (params) {
    return HttpUtil.post('/api/basic/carrier/query', params)
  },
  delete (param) {
    return HttpUtil.post('/api/basic/carrier/del', {carrierId: param.id})
  }

}
export default CarrierAPI



