<template>
  <div>
    <div class="form-box-content" style="padding-top: 15px;padding-bottom: 15px">
      <fdcform ref="form" :model="formData" :rules="paramValidate" :label-width="120">
        <row type="flex" justify="space-around">
          <fdccol span="12" >
            <formitem :label="'统一编码：'" prop="unityCode">
              <fdcinput v-model="formData.unityCode" placeholder="请输入分组统一编码"></fdcinput>
            </formitem>
          </fdccol>
        </row>
        <row type="flex" justify="space-around">
          <fdccol span="12" >
            <formitem :label="'名称：'" prop="name">
              <fdcinput v-model="formData.name" placeholder="请输入分组统一编码"></fdcinput>
            </formitem>
          </fdccol>
        </row>
        <row type="flex" justify="space-around">
          <fdccol span="12" >
            <formitem>
              <fdcbutton type="primary" @click="handleSubmit('form')">提交</fdcbutton>
              <fdcbutton type="ghost" @click="handleReset('form')" class="buttonClass">重置</fdcbutton>
              <fdcbutton type="ghost" @click="close('apiGroupAdd')" class="buttonClass">取消</fdcbutton>
            </formitem>
          </fdccol>
        </row>
      </fdcform>
    </div>
  </div>
</template>

<script>
import tags from '@/components/tags.vue'
import Event from '@/utility/Event.js'
import GroupAPI from './api/GroupAPI'
import {mapState, mapAction} from 'vuex'
export default {
  name: 'apiGroupAdd',
  data () {
    return {
      paramValidate: {
        unityCode: [{required: true, type: 'string', message: '统一编码必填', trigger: 'blur'}],
        name: [{required: true, type: 'string', message: '分组名称必填', trigger: 'blur'}]
      },
      formData: {
        unityCode: '',
        name: ''
      }
    }
  },
  methods: {
    handleSubmit (name) {
      this.add()
    },
    handleReset (name) {
      this.$refs[name].resetFields()
    },
    add () {
      GroupAPI.add(this.formData).then((res) => {
        if (res.data.state == 1) {
          this.$Notice.success({
            title: '操作成功',
            desc: '新增分组成功'
          })
          this.close('apiGroupAdd')
        } else {
          this.$Notice.error({
            title: '操作失败',
            desc: res.data.msg
          })
        }
      })
    },
    close (router) {
      Event.$emit('closePage', router)
      Event.$once('refreshGroupList', null)
    }
  },
  components: {tags}
}
</script>



