<template>
  <div>
    <div class="page-header-box">
      <div class="header-msg-box">
        <p>国际化配置</p>
      </div>
      <div class="header-btn-box">
        <fdcbutton v-show="formshow" @click="hideForm" size="small" icon="eye-disabled">隐藏</fdcbutton>
        <fdcbutton v-show="!formshow" @click="showForm" icon="eye">显示</fdcbutton>
        <fdcbutton icon="android-add" @click="addI18n">新增</fdcbutton>
      </div>
    </div>
    <div class="form-box" v-show="formshow">
      <fdcform ref="formData" :model="formData"  :label-width="80" inline  class="search-form">
            <formitem label="代码" prop="code">
              <fdcinput v-model="formData.code" @enter="handleSubmit('formData')" placeholder="请输入国际化代码" class="search-input">
              </fdcinput>
              <fdcbutton type="primary" @click="handleSubmit('formData')" class="search-submit">查询</fdcbutton>
              <fdcbutton type="ghost" @click="handleReset('formData')" class="search-reset">重置</fdcbutton>
            </formitem>
      </fdcform>
    </div>
    <div class="table-box">
      <fdctable border size="small" :loading="isLoading" :data="tableData"
                :columns="tableColumns" @on-current-change="selectData" stripe></fdctable>
      <div class="page-box">
        <Page v-show="pageShow" placement="top" :total="total" :page-size="formData.pageSize" :current.sync="formData.pageNo" @on-change="changePageNo" @on-page-size-change="changePageSize" show-total show-elevator show-sizer></Page>
      </div>
    </div>
  </div>
</template>
<script>
import Event from '@/utility/Event.js'
import I18nAPI from './api/I18nAPI'
export default {
  name: 'i18n',
  data: function () {
    return {
      formshow: true,
      isLoading: false,
      pageShow: false,
      total: 0,
      formData: {
        code: '',
        pageNo: 1,
        pageSize: 10
      },
      tableData: [],
      tableColumns: [
        {
          title: ' ',
          key: 'number',
          type: 'index2',
          align: 'center',
          width: '5%',
          render: (h, params) => {
            return h('span', params.index + (this.formData.pageNo - 1) * this.formData.pageSize + 1)
          }
        },
        {
          title: '代码',
          key: 'code',
          align: 'center'
        }
      ],
      actionColumn: {
        title: '操作',
        key: 'action',
        width: 120,
        align: 'left',
        render: (h, params) => {
          return h('div', [
            h('tooltip', {
              props: {
                content: '详情',
                placement: 'top'
              }
            }, [h('span', {
              on: {
                click: () => {
                  this.showI18n(params.row.code)
                }
              }
            }, [h('icon', {
              props: {
                type: 'clipboard',
                size: 20,
                color: '#ffb80d'
              },
              style: {
                marginRight: '5px'
              }
            }, '查看')])]),
            h('tooltip', {
              props: {
                content: '修改',
                placement: 'top'
              }
            }, [h('span', {
              on: {
                click: () => {
                  this.modifyI18n(params.row.code)
                }
              }
            }, [h('icon', {
              props: {
                type: 'edit',
                size: 20,
                color: '#0d96ff'
              },
              style: {
                marginRight: '5px'
              }
            }, '修改')])]),
            h('tooltip', {
              props: {
                content: '删除',
                placement: 'top'
              }
            }, [h('span', {
              on: {
                click: () => {
                  this.$Modal.confirm({
                    title: '删除',
                    content: '<p>确定删除？</p>',
                    onOk: () => {
                      this.remove(params.row.code)
                    },
                    onCancel: () => {
                    }
                  })
                }
              }
            }, [h('icon', {
              props: {
                type: 'trash-a',
                size: 20,
                color: '#f11a09'
              },
              style: {
                marginRight: '8px'
              }
            }, '删除')])])
          ])
        }
      }
    }
  },
  computed: {},
  methods: {
    generateTableColumn: function () {
      I18nAPI.getI18nLanguageList().then((res) => {
        if (res.data) {
          for (let i = 0; i < res.data.length; i++) {
            const col = {
              title: res.data[i].name,
              key: res.data[i].code,
              align: 'center'
            }
            this.tableColumns.push(col)
          }
          this.tableColumns.push(this.actionColumn)
        }
      })
    },
    hideForm () {
      this.formshow = !this.formshow
    },
    showForm () {
      this.formshow = !this.formshow
    },
    handleSubmit (name) {
      this.$refs[name].validate((valid) => {
        if (valid) {
          this.query()
        }
      })
    },
    handleReset (name) {
      this.$refs[name].resetFields()
    },
    query () {
      this.isLoading = !this.isLoading
      let queryCondition = {
        'pageNo': this.formData.pageNo,
        'pageNumber': this.formData.pageSize,
        'code': this.formData.code
      }
      I18nAPI.query(queryCondition).then((res) => {
        this.tableData = []
        let state = res.data.state
        if (state === 1) {
          let dataList = res.data.data.list
          if (dataList !== null) {
            this.pageShow = true
            this.total = res.data.data.total
          }
          this.tableData.push(...dataList)
        }
        this.isLoading = !this.isLoading
      })
    },
    changePageNo (value) {
      this.pageNo = value
      this.query()
    },
    changePageSize (value) {
      this.formData.pageNo = 1
      this.formData.pageSize = value
      this.query()
    },
    selectData () {

    },
    remove (code) {
      I18nAPI.delete({code: code}, this.query)
    },
    addI18n () {
      // this.modal1 =
      Event.$emit('addPage', {
        title: '新增国际化',
        router: 'addI18n',
        checked: true
      })
    },
    modifyI18n (code) {
      Event.$emit('addPage', {
        title: '修改国际化配置',
        router: 'modifyI18n',
        param: code,
        checked: true
      })
    },
    showI18n (code) {
      Event.$emit('addPage', {
        title: '国际化配置详情',
        router: 'showI18n',
        param: code,
        checked: true
      })
    }
  },
  components: {
  },
  mounted: function () {
    this.generateTableColumn()
    Event.$once('refreshI18nList', obj => this.query())
  }
}
</script>



