<style scoped>
  .table-box {
    position: relative;
    box-sizing: border-box ;
    height:100%
  }
</style>
<template>
  <div>
    <div class="page-header-box">
      <div class="header-msg-box">
        <p>消费者管理</p>
      </div>
      <div class="header-btn-box">
        <fdcbutton v-show="queryShow" @click="hideForm" icon="eye-disabled">隐藏</fdcbutton>
        <fdcbutton v-show="!queryShow" @click="showForm" icon="eye">显示</fdcbutton>
        <fdcbutton icon="android-add" @click="add">新增</fdcbutton>
      </div>
    </div>
    <div class="form-box" v-show="queryShow">
      <fdcform ref="form" inline :model="formData" :label-width="80" class="search-form" @submit.native.prevent>
            <formitem :label="'统一编码'" prop="unityCode">
              <fdcinput v-model="formData.unityCode" placeholder="消费者统一编码" class="search-input"></fdcinput>
            </formitem>
            <formitem :label="'名称：'" prop="developer">
              <fdcinput v-model="formData.name" placeholder="消费者名称" class="search-input"></fdcinput>
            </formitem>
            <formitem :label="'联系人：'" prop="developer">
              <fdcinput v-model="formData.linkman" placeholder="消费者联系人" class="search-input"></fdcinput>
              <fdcbutton type="primary" @click="handleSubmit('form')" class="search-submit">查询</fdcbutton>
              <fdcbutton type="ghost" @click="handleReset('form')" class="search-reset">重置</fdcbutton>
            </formitem>
      </fdcform>
    </div>
    <div class="table-box">
      <fdctable border size="small" :loading="tableLoading" width="200px" :data="tableData" :columns="tableColumns" stripe></fdctable>
      <div class="page-box">
        <Page v-show="pageShow" placement="top" :page-size="formData.pageSize" :total="total" :current.sync="formData.pageNo" @on-change="changePageNo" @on-page-size-change="changePageSize" show-total show-elevator show-sizer></Page>
      </div>
    </div>
    <modal v-model="grantShow" :width="800" title="给API消费者分配分组" @on-ok="grant" @on-cancel="closeGrant">
      <div class="form-box">
        <fdcform ref="grantForm" inline :model="grantFormData" :label-width="100">
          <row span="24">
            <fdccol span="12">
              <formitem :label="'角色名称：'" prop="groupName">
                <fdcinput v-model="grantFormData.groupName" placeholder="请输入角色名称" class="search-input"></fdcinput>
              </formitem>
            </fdccol>
            <fdccol span="12">
              <fdcbutton type="primary" @click="handleSubmit('grantForm')" style="margin-left: 20px" class="search-submit">查询</fdcbutton>
              <fdcbutton type="ghost" @click="handleReset('grantForm')" style="margin-left: 8px" class="search-reset">重置</fdcbutton>
            </fdccol>
          </row>
        </fdcform>
      </div>
      <div class="table-box">
        <fdctable border :loading="grantLoading" width="200px" height="300px" :data="grantData" :columns="grantDataColumns"
                  stripe @on-selection-change="selectChange"></fdctable>
        <div class="page-box">
          <Page v-show="grantPageShow" placement="top" :page-size="grantFormData.pageSize" :total="grantTotal"
                :current.sync="grantFormData.pageNo" @on-change="grantChangePageNo" @on-page-size-change="grantChangePageSize" show-total show-elevator show-sizer></Page>
        </div>
      </div>
    </modal>
  </div>
</template>

<script>
import Event from '@/utility/Event.js'
import ConsumerAPI from './api/ConsumerAPI'
import {mapState, mapAction} from 'vuex'
export default {
  name: 'apiConsumerManage',
  data () {
    return {
      queryShow: true,
      pageShow: false,
      tableLoading: false,
      total: 0,
      formData: {
        unityCode: '',
        name: '',
        linkman: '',
        pageNo: 1,
        pageSize: 10
      },
      tableData: [],
      grantShow: false,
      grantPageShow: false,
      grantLoading: false,
      grantTotal: 0,
      grantFormData: {
        id: '',
        consumerId: '',
        groupName: '',
        pageNo: 1,
        pageSize: 10
      },
      grantData: [],
      selectedData: [],
      toGrantData: []
    }
  },
  computed: {
    tableColumns () {
      let tabCol = [
        {title: '编号', key: 'number', width: 80, align: 'center'},
        {title: '统一编码', key: 'unityCode', align: 'center'},
        {title: '名称', key: 'name', align: 'center'},
        {title: '联系人', key: 'linkman', align: 'center'},
        {title: '联系人邮箱', key: 'linkmanEmail', align: 'center'},
        {title: '联系人电话', key: 'linkmanMobile', align: 'center'},
        {title: '状态', key: 'state', align: 'center'},
        {title: '创建时间', key: 'createdTime', align: 'center'},
        {title: '创建者', key: 'creator', align: 'center'},
        {title: '操作',
          key: 'operate',
          align: 'left',
          render: (h, params) => {
            const row = params.row
            return h('div', [
              h('tooltip', {
                props: {
                  content: '查看',
                  placement: 'top'
                }
              },
              [h('span', {
                on: {
                  click: () => {
                    this.view(row.id)
                  }
                }
              }, [h('icon', {
                props: {
                  type: 'clipboard',
                  size: 20,
                  color: '#ffb80d'
                },
                style: {
                  marginRight: '5px'
                }
              }, '查看')])]),
              h('tooltip', {
                props: {
                  content: '修改',
                  placement: 'top'
                }
              },
              [h('span', {
                on: {
                  click: () => {
                    this.modify(row.id)
                  }
                }
              }, [h('icon', {
                props: {
                  type: 'edit',
                  size: 20,
                  color: '#0d96ff'
                },
                style: {
                  marginRight: '5px'
                }
              }, '修改')])]),
              h('tooltip', {
                props: {
                  content: '给API消费者分配分组',
                  placement: 'top'
                }
              }, [h('span', {
                on: {
                  click: () => {
                    this.togrant(row.id)
                  }
                }
              },
              [h('icon', {
                props: { type: 'person-add', size: 20, color: '#0d96ff' },
                style: { marginRight: '5px' }
              }, '给API消费者分配分组')])]),
              h('tooltip', {
                props: {
                  content: row.state == 1 ? '禁用' : '启用',
                  placement: 'top'
                }
              },
              [h('span', {
                on: {
                  click: () => {
                    if (row.state == 1) {
                      this.remove(row.id)
                    } else {
                      this.normal(row.id)
                    }
                  }
                }
              }, [h('icon', {
                props: {
                  type: row.state == 1 ? 'locked' : 'unlocked',
                  size: 20,
                  color: row.state == 1 ? '#8a8a8a' : '#0d96ff',
                }
              }, row.state == 1 ? '禁用' : '启用')])])
            ])
          }
        }
      ]
      return tabCol
    },
    grantDataColumns () {
      let tableCol = [
        {type: 'selection', width: 60, align: 'center'},
        {title: '分组名称', key: 'name', align: 'center'},
        {title: '分组编码', key: 'unityCode', align: 'center'},
        {title: '状态', key: 'state', align: 'center'},
        {title: '创建时间', key: 'createdTime', align: 'center'}
      ]
      return tableCol
    }
  },
  methods: {
    hideForm () {
      this.queryShow = !this.queryShow
    },
    showForm () {
      this.queryShow = !this.queryShow
    },
    // 查询
    handleSubmit (name) {
      if (name == 'form') {
        this.query()
      } else {
        this.queryUngrant()
      }
    },
    handleReset (name) {
      this.$refs[name].resetFields()
    },
    changePageNo (value) {
      this.formData.pageNo = value
      this.query()
    },
    changePageSize (value) {
      this.formData.pageNo = 1
      this.formData.pageSize = value
      this.query()
    },
    query () {
      this.tableLoading = !this.tableLoading
      let query = { 'pageNo': this.formData.pageNo,
        'pageNumber': this.formData.pageSize,
        'unityCode': this.formData.unityCode,
        'name': this.formData.name,
        'linkman': this.formData.linkman
      }
      ConsumerAPI.query(query).then((res) => {
        this.tableData = []
        if (res.data.state == 1) {
          let dataList = res.data.data.list
          if (dataList != null) {
            this.pageShow = true
            this.total = res.data.data.total
          }
          for (let i = 0; i < dataList.length; i++) {
            let consumer = dataList[i]
            this.tableData.push({
              id: consumer.id,
              number: (i + 1),
              unityCode: consumer.unityCode,
              name: consumer.name,
              channel: consumer.channel,
              linkman: consumer.linkman,
              linkmanEmail: consumer.linkmanEmail,
              linkmanMobile: consumer.linkmanMobile,
              ipWhiteList: consumer.ipWhiteList,
              ipBlackList: consumer.ipBlackList,
              state: consumer.state,
              creator: consumer.creator,
              createdTime: consumer.createdTime
            })
          }
        }
        this.tableLoading = !this.tableLoading
      })
    },
    grant () {
      this.getGrantData()
      if (this.selectedData.length > 0) {
        let groupIdlist = []
        this.selectedData.forEach((data) => {
          groupIdlist.push(data.id)
        })
        let permissions = {
          'id': this.grantFormData.consumerId,
          'groupIdList': groupIdlist
        }
        ConsumerAPI.grant(this.grantFormData.consumerId, permissions).then((res) => {
          if (res.data.state == 1) {
            this.$Notice.success({
              title: '操作成功',
              desc: '给API消费者分配分组成功'
            })
            this.query()
          } else {
            this.$Notice.error({
              title: '操作失败',
              desc: res.data.msg
            })
          }
        })
      }
    },
    queryUngrant () {
      this.grantLoading = !this.grantLoading
      let query = { 'pageNo': this.grantFormData.pageNo,
        'pageNumber': this.grantFormData.pageSize,
        'consumerId': this.grantFormData.consumerId,
        'groupName': this.grantFormData.groupName
      }
      ConsumerAPI.queryUngrantGroup(query).then((res) => {
        this.grantData = []
        if (res.data.state == 1) {
          let dataList = res.data.data.list
          if (dataList != null) {
            this.grantPageShow = true
            this.grantTotal = res.data.data.total
          }
          for (let i = 0; i < dataList.length; i++) {
            let group = dataList[i]
            this.grantData.push({
              id: group.id,
              number: (i + 1),
              name: group.name,
              unityCode: group.unityCode,
              state: group.state,
              createdTime: group.createdTime
            })
          }
        }
        this.grantLoading = !this.grantLoading
      })
    },
    add () {
      Event.$emit('addPage', {
        title: '添加消费者',
        router: 'apiConsumerAdd',
        check: true
      })
    },
    view (id) {
      Event.$emit('addPage', {
        title: '消费者明细',
        router: 'apiConsumerView',
        checked: true,
        id: id
      })
    },
    modify (id) {
      Event.$emit('addPage', {
        title: '更新消费者',
        router: 'apiConsumerModify',
        checked: true,
        id: id
      })
    },
    togrant (consumerId) {
      this.grantShow = true
      this.selectedData = []
      this.toGrantData = []
      this.grantFormData.consumerId = consumerId
      this.queryUngrant()
    },
    selectChange (selection) {
      let pageSelectData = {
        page: this.grantFormData.pageNo,
        grantData: selection
      }
      this.toGrantData[this.grantFormData.pageNo - 1] = pageSelectData
    },
    getGrantData () {
      this.selectedData = []
      this.toGrantData.forEach((togrant) => {
        this.selectedData = this.selectedData.concat(togrant.grantData)
      })
    },
    closeGrant () {
      this.grantShow = false
      this.selectedData = []
      this.toGrantData = []
    },
    grantChangePageNo (value) {
      this.grantFormData.pageNo = value
      this.queryUngrant()
    },
    grantChangePageSize (value) {
      this.grantFormData.pageNo = 1
      this.grantFormData.pageSize = value
    },
    normal (id) {
      ConsumerAPI.normal(id).then((res) => {
        if (res.data.state == 1) {
          this.$Notice.success({
            title: '操作成功',
            desc: '启用成功'
          })
          this.query()
        } else {
          this.$Notice.error({
            title: '操作失败',
            desc: res.data.msg
          })
        }
      })
    },
    remove (id) {
      ConsumerAPI.remove(id).then((res) => {
        if (res.data.state == 1) {
          this.$Notice.success({
            title: '操作成功',
            desc: '禁用成功'
          })
          this.query()
        } else {
          this.$Notice.error({
            title: '操作失败',
            desc: res.data.msg
          })
        }
      })
    }
  },
  mounted () {
    this.query()
    Event.$once('refreshConsumerList', obj => this.query())
    this.tableHeight = window.innerHeight - 310
  },
  components: {}
}
</script>



