import HttpUtil from '@/common/HttpUtil'
const EnumAPI = {
  query (params) {
    return HttpUtil.post('/api/system/sysEnum/query', params)
  },
  insert (params) {
    return HttpUtil.postJSON('/api/system/sysEnum/insert', params)
  },
  modify (params) {
    return HttpUtil.postJSON('/api/system/sysEnum/modify', params)
  },
  delete (params, callback) {
    return HttpUtil.post('/api/system/sysEnum/del', {'enumType': params}, true, callback)
  },
  active (params) {
    return HttpUtil.post('/api/system/sysEnum/active', params)
  },
  findEnumByType (params) {
    return HttpUtil.post('/api/system/sysEnum/findEnumByType', params)
  },
  findEnumByTypeAndCode (params) {
    return HttpUtil.post('/api/system/sysEnum/findEnumByTypeAndCode', params)
  },
  getByType (params) {
    return HttpUtil.post('/api/system/sysEnum/getByType', {'enumType': params})
  }
}
export default EnumAPI



