import HttpUtil from '@/common/HttpUtil';

const SysTaskConfigAPI = {
  query(params) {
    return HttpUtil.post('/api/system/taskConfig/query', params, false);
  },
  disableOrEnable (params, callback) {
    return HttpUtil.post('/api/system/taskConfig/disableOrEnable', params, true, callback)
  },
  insert(params, callback) {
    return HttpUtil.post('/api/system/taskConfig/insert', params, false, callback);
  },
  getById(id){
    return HttpUtil.post('/api/system/taskConfig/get',{id:id})
  },
  modify(params, callback) {
    return HttpUtil.post('/api/system/taskConfig/update', params, false, callback);
  },
}

export default SysTaskConfigAPI




