<template>
  <div>
    <div class="form-box-content">
      <div class="module-title-wrap">基本信息</div>
      <fdcform ref="formData" :model="formData" :label-width="100">
        <row type="flex" justify="space-around">
          <fdccol span="11" >
          <formitem :label="'统一编码：'" prop="unityCode" style="padding-left: 5px;">
            <fdcinput v-model="formData.unityCode" placeholder="分组统一编码" readonly>
            </fdcinput>
          </formitem>
        </fdccol>
        <fdccol span="11">
          <formitem :label="'分组名称：'" prop="name" style="padding-left: 5px;">
            <fdcinput v-model="formData.name" placeholder="分组名称"  readonly>
            </fdcinput>
          </formitem>
        </fdccol>
      </row>
      </fdcform>
      <div class="table-box">
        <div class="module-title-wrap">接口列表</div>
        <fdctable border :loading="tableLoading" width="200px" :data="tableData" :columns="tableColumns" stripe></fdctable>
        <div class="page-box">
          <Page v-show="pageShow" placement="top" :page-size="formData.pageSize" :total="total"
                :current.sync="formData.pageNo" @on-change="changePageNo" @on-page-size-change="changePageSize" show-total show-elevator show-sizer></Page>
        </div>
      </div>
      <div style="padding-left: 30px;padding-bottom: 10px">
        <fdcbutton type="ghost" @click="close('apiGroupView')" style="margin-left: 8px">关闭</fdcbutton>
      </div>
    </div>
  </div>
</template>
<script>
import Event from '@/utility/Event.js'
import GroupAPI from './api/GroupAPI'
import {mapState, mapAction} from 'vuex'
export default {
  name: 'apiGroupView',
  data () {
    return {
      modal: false,
      queryShow: true,
      pageShow: false,
      tableLoading: false,
      total: 0,
      formData: {
        id: this.$route.query.id,
        unityCode: '',
        name: '',
        groupId: this.$route.query.id,
        pageNo: 1,
        pageSize: 10
      },
      tableData: []
    }
  },
  computed: {
    tableColumns () {
      let tableCol = [
        {title: '编号', key: 'number', width: 80, align: 'center'},
        {title: '接口名称', key: 'fullName', width: 500, align: 'center'},
        {title: '每天最大调用次数', key: 'dmaxCount', align: 'center'},
        {title: '每小时最大调用次数', key: 'hmaxCount', align: 'center'},
        {title: '起始时间', key: 'beginTime', align: 'center'},
        {title: '开发者', key: 'developer', align: 'center'},
        {title: '开发者邮箱', key: 'developerEmail', align: 'center'},
        {title: '开发者电话', key: 'developerMobile', align: 'center'}
      ]
      return tableCol
    }
  },
  methods: {
    changePageNo (value) {
      this.formData.pageNo = value
      this.query()
    },
    changePageSize (value) {
      this.formData.pageNo = 1
      this.formData.pageSize = value
      this.showPermissions()
    },
    view () {
      GroupAPI.view(this.$route.query.groupId).then((res) => {
        if (res.data.state == 1) {
          this.formData = res.data.data
        }
      })
    },
    showPermissions () {
      this.tableLoading = !this.tableLoading
      let queryP = {
        'pageNo': this.formData.pageNo,
        'pageNumber': this.formData.pageSize,
        'groupId': this.$route.query.groupId
      }
      GroupAPI.queryPermissions(queryP).then((res) => {
        this.tableData = []
        if (res.data.state == 1) {
          let dataList = res.data.data.list
          if (dataList != null) {
            this.pageShow = true
            this.total = res.data.data.total
          }
          for (let i = 0; i < dataList.length; i++) {
            let permission = dataList[i]
            this.tableData.push({
              id: permission.service.id,
              number: (i + 1),
              fullName: permission.service.fullName,
              developer: permission.service.developer,
              developerEmail: permission.service.developerEmail,
              developerMobile: permission.service.developerMobile,
              dmaxCount: permission.dmaxCount,
              hmaxCount: permission.hmaxCount,
              beginTime: permission.beginTime,
              endTime: permission.endTime
            })
          }
        }
        this.tableLoading = !this.tableLoading
      })
    },
    close (router) {
      Event.$emit('closePage', router)
      Event.$once('refreshGroupList', null)
    }
  },

  mounted () {
    this.view()
    this.showPermissions()
  },
  components: {}
}
</script>



