export default {
  methods:{
    has(permissionCode){
      if(!permissionCode){
        return true;
      }
      let permissionCodeList = JSON.parse(sessionStorage.getItem('permissionCodeList'));
      if(!permissionCodeList){
        return false;
      }else{
        return permissionCodeList.indexOf(permissionCode)!=-1;
      }
    },
    _import(fullName){
      return required()
    }
  }
}



