<template>
  <div>
    <div class="page-header-box">
      <div class="header-msg-box">
        <p>异步任务管理</p>
      </div>
      <div class="header-btn-box">
        <fdcbutton v-show="queryShow" @click="showForm" icon="eye-disabled">隐藏</fdcbutton>
        <fdcbutton v-show="!queryShow" @click="showForm" icon="eye">显示</fdcbutton>
        <fdcbutton icon="arrow-swap" @click="handle">调用</fdcbutton>
      </div>
    </div>

    <div class="form-box" v-show="queryShow">
      <fdcform ref="form" inline :model="formData" :label-width="80" class="search-form" :rules="ruleValidate" @keyup.enter.native="handleSubmit('form')">
        <formitem label="队列编码" prop="queueCode">
          <fdcinput v-model="formData.queueCode" placeholder="请输入队列编码" class="search-input" ></fdcinput>
        </formitem>
        <formitem label="相关单号" prop="billCode">
          <fdcinput v-model="formData.billCode" placeholder="请输入相关单号" class="search-input" ></fdcinput>
        </formitem>
        <formitem label="状态" prop="state">
          <fdcselect v-model="formData.state" placeholder="选择角色级别" class="search-input" clearable @on-change="handleSubmit('form')">
            <fdcoption v-for="item in enumSelect['TASK_STATE']" :value="item.enumCode" :key="item.enumCode">
              {{ item.enumValue }}
            </fdcoption>
          </fdcselect>
        </formitem>
        <fdcbutton type="primary" @click="handleSubmit('form')" class="search-submit">查询</fdcbutton>
        <fdcbutton type="ghost" @click="reset('form')" class="search-reset">重置</fdcbutton>
      </fdcform>
    </div>

    <div class="table-box">
      <fdctable border size="small" :loading="isLoading" :data="tableData"
                :columns="tableColumns"
                stripe></fdctable>
      <div class="page-box">
        <Page v-show="pageShow" placement="top" :page-size="formData.pageNumber" :total="total"
              :current.sync="formData.pageNo" @on-change="changePageNo" @on-page-size-change="changePageSize"
              show-total
              show-elevator show-sizer></Page>
      </div>
    </div>
  </div>
</template>

<script>
  import SysTaskAPI from "./api/SysTaskAPI";
  import Event from '@/utility/Event.js';
  import {mapState} from 'vuex';

  export default {
    name: "sysTask",
    data() {
      return {
        queryShow: true,
        isLoading: false,
        pageShow: false,
        total: 0,
        formData: {
          queueCode: '',
          billCode: '',
          state: '',
          pageNo: 1,
          pageNumber: 10
        },
        tableData: [],
        ruleValidate: {
          queueCode: [{
            type: 'string',
            required: true,
            message: ' ',
            trigger: 'blur'
          }]
        }
      }
    },
    methods: {
      showForm() {
        this.queryShow = !this.queryShow;
      },
      handleSubmit (name) {
        this.$refs[name].validate((valid) => {
          if (valid) {
            this.query();
          }
        })
      },
      query() {
        this.isLoading = !this.isLoading;
        SysTaskAPI.query(this.formData).then((res) => {
          this.tableData = [];
          let state = res.data.state;
          if (state === 1) {
            let dataList = res.data.data.list;
            if (dataList !== null) {
              this.pageShow = true;
              this.total = res.data.data.total;
            }
            this.tableData.push(...dataList);
          }
          this.isLoading = !this.isLoading;
        });
      },
      reset(name) {
        this.$refs[name].resetFields();
      },
      changePageNo(value) {
        this.formData.pageNo = value;
        this.query();
      },
      changePageSize(value) {
        this.formData.pageNo = 1;
        this.formData.pageNumber = value;
        this.query();
      },
      //调用
      handle() {

      }
    },
    computed: {
      ...mapState({
        sysEnumList: state => state.Enum.enumList,
        enumSelect: state => state.Enum.enumSelect
      }),
      tableColumns() {
        let tableCol = [
          {
            type: 'selection',
            width: 60,
            align: 'center'
          }, {
            title: ' ',
            type: 'index2',
            width: 60,
            align: 'center',
            render: (h, params) => {
              return h('span', params.index + (this.formData.pageNo - 1) * this.formData.pageNumber + 1)
            }
          }, {
            title: '队列编码',
            key: 'queueCode',
            align: 'center'
          }, {
            title: '相关单号',
            key: 'billCode',
            align: 'center'
          }, {
            title: '状态',
            key: 'state',
            width: 100,
            align: 'center',
            render: (h, params) => {
              return h('span', {}, this.sysEnumList['TASK_STATE_' + params.row.state])
            }
          }, {
            title: '调用次数',
            key: 'callCount',
            width: 100,
            align: 'center'
          }, {
            title: '报文',
            key: 'requestContent',
            align: 'center',
            ellipsis: true
          }, {
            title: '错误日志',
            key: 'response',
            align: 'center'
          }, {
            title: '耗时',
            key: 'costTime',
            width: 100,
            align: 'center'
          }, {
            title: '创建时间',
            key: 'createdTime',
            align: 'center'
          }, {
            title: '修改时间',
            key: 'modifiedTime',
            align: 'center'
          }, {
            title: '操作',
            key: 'action',
            width: 100,
            align: 'left',
            render: (h, params) => {
              return h('div', [
                h('tooltip', {
                  props: {
                    content: '调用',
                    placement: 'top'
                  }
                }, [h('span', {
                  on: {
                    click: () => {
                      this.handle(params.row.id);
                    }
                  }
                }, [h('icon', {
                  props: {
                    type: 'arrow-swap',
                    size: 20,
                    color: '#fab41e'
                  },
                  style: {
                    marginRight: '8px'
                  }
                }, '修改')])])
              ]);
            }
          }];
        return tableCol;
      }
    },
    mounted() {
      Event.$emit('addEnum', {typeList:['TASK_STATE']});
      Event.$once('refreshList', obj => this.query());
    },
    components: {}
  }
</script>





