import Vue from 'vue'
import router from '../router'
import axios from 'axios'
var qs = require('qs')
var vm = new Vue()

const Utils = {
  get (url, params,notice,callback) {
    return new Promise((resolve, reject) => {
      axios.get(url, {
        params: params,
        withCredentials: true
      }).then(function (response) {
        processStatusAndCode(response,resolve,notice,callback);
      }).catch(function (error) {
        processGolbalError(error,notice);
      })
    })
  },
  post (url, params,notice,callback) {
    return new Promise((resolve, reject) => {
      axios.post(url, qs.stringify(params), {
        withCredentials: true,
        headers: {
          'X-Requested-With': 'XMLHttpRequest',
          'Content-Type': 'application/x-www-form-urlencoded; charset=UTF-8'
        }
      }).then(function (response) {
        processStatusAndCode(response,resolve,notice,callback);
      }).catch(function (error) {
        processGolbalError(error,notice);
      })
    })
  },
  postJSON (url, params,notice,callback) {
    return new Promise((resolve, reject) => {
      axios.post(url, params,{
        headers: {
          'Content-Type': 'application/json; charset=UTF-8'
        }
      }).then(function (response) {
        processStatusAndCode(response,resolve,notice,callback);
      }).catch(function (error) {
        processGolbalError(error);
      })
    })
  }
}

function processStatusAndCode(response,resolve,notice,callback){
  if(response.status === 200 || response.status === 304){
    if (response.data.code == 'ACCESS_DENIED' ||  response.data.code == 'NOT_LOGGED_ON' || response.data.code == 'TOKEN_ILLEGAL') {
      router.push({ path: '/login' });
    } else {
      if(response.data.state==1 && notice){
        vm.$Notice.success({
          title: '操作成功',
          desc:response.data.msg
        });
      }else if(notice){
        vm.$Notice.error({
          title: '操作失败',
          desc:response.data.msg
        });
      }
      if(response.data.state==1 && callback){
        callback();
      }
      resolve(response);
    }
  }
}

function processGolbalError(error){
    vm.$Notice.error({
      title: 'error',
      duration:0,
      desc:error.message
    });
}

export default Utils



