<style scoped>
</style>
<template>
  <div>
    <div class="form-box-content">
      <div class="module-title-wrap">基本信息</div>
      <fdcform ref="formData" :model="formData"  :label-width="80" :rules="ruleValidate" >
        <row type="flex" justify="space-around">
          <fdccol span = '7'>
            <formitem label="登陆名" prop="loginName">
              <fdcinput v-model="formData.loginName" disabled placeholder="登录名">
              </fdcinput>
            </formitem>
          </fdccol>
          <fdccol span = '7'>
            <formitem label="姓名" prop="name">
              <fdcinput v-model="formData.name" placeholder="姓名">
              </fdcinput>
            </formitem>
          </fdccol>
          <fdccol span = '7'>
            <formitem label="邮箱" prop="email">
              <fdcinput v-model="formData.email" placeholder="邮箱">
              </fdcinput>
            </formitem>
          </fdccol>
        </row>
        <row type="flex" justify="space-around">
          <fdccol span = '7'>
            <formitem label="联系方式" prop="contactInfo">
              <fdcinput v-model="formData.contactInfo" placeholder="联系方式">
              </fdcinput>
            </formitem>
          </fdccol>
          <fdccol span = '7'>
            <formitem label="所属部门" prop="department">
              <fdcinput v-model="formData.department" placeholder="所属部门">
              </fdcinput>
            </formitem>
          </fdccol>
          <fdccol span = '7'>
            <formitem label="身份证" prop="idCard">
              <fdcinput v-model="formData.idCard" placeholder="身份证">
              </fdcinput>
            </formitem>
          </fdccol>
        </row>
        <row type="flex" justify="space-around">
          <fdccol span = '7'>
            <formitem label="所属公司" v-show = "false" prop="companyId">
              <fdcinput v-model="formData.companyId" placeholder="所属公司">
              </fdcinput>
            </formitem>
            <formitem label="所属公司" >
              <fdcinput v-model="formData.companyName" placeholder="所属公司">
                <fdcbutton slot="append" icon="ios-search"  :disabled="companyButton"  @click="handleCompanyModal"></fdcbutton>
              </fdcinput>
            </formitem>
          </fdccol>
          <fdccol span="15"></fdccol>
        </row>
        <row type="flex" justify="space-around">
          <fdccol span = '7'>
            <formitem>
              <fdcbutton type="primary" @click="handleSubmit('formData')">提交</fdcbutton>
              <fdcbutton type="ghost" @click="close('modifyUser')" class="buttonClass">取消</fdcbutton>
            </formitem>
          </fdccol>
        </row>
      </fdcform>
    </div>
    <modal v-model="companyShow" :width="800" title="请选择公司" @on-ok="ok" @on-cancel="cancel">
      <company ></company>
    </modal>
  </div>
</template>

<script>
import UserAPI from './api/UserAPI'
import Event from '@/utility/Event.js'
import company from '@/components/modal/company.vue'

export default {
  name: 'modifyUser',
  data () {
    return {
      companyButton: true,
      companyShow: false,
      company: {
        id: '',
        name: ''
      },
      formData: {
        id: '',
        loginName: '',
        name: '',
        email: '',
        contactInfo: '',
        userType: '',
        department: '',
        idCard: '',
        companyId: '',
        companyName: ''
      },
      ruleValidate: {

        loginName: [{
          required: true,
          message: '登录名不允许为空',
          trigger: 'blur'
        },{
          max:20,
          message: '登录名超长',
          trigger: 'blur'
        },{
          pattern: /^[a-z,A-Z,0-9]+$/,
          message: '登录名格式不正确',
          trigger: 'blur'
        }],
        name: [{
          required: true,
          message: '姓名不允许为空',
          trigger: 'blur'
        },{
          max:16,
          message: '姓名超长',
          trigger: 'blur'
        }],
        email: [{
          type: 'email',
          message: '请输入正确的邮箱',
          trigger: 'blur'
        },{
          max:50,
          message: '邮箱超长',
          trigger: 'blur'
        }],
        contactInfo: [{
          type: 'string',
          trigger: 'blur'
        },{
          max:66,
          message: '联系方式超长',
          trigger: 'blur'
        }],
        department: [{
          type: 'string',
          trigger: 'blur'
        },{
          max:10,
          message: '部门超长',
          trigger: 'blur'
        }],
        idCard: [{
          pattern: /^[0-9,'X']{15,18}$/,
          message: '请输入正确的身份证号码'
        }],
        companyName: [{
          required: true,
          message: '公司必选',
          trigger: 'blur'
        }]
      }
    }
  },
  computed: {
  },
  components: {
    company: company
  },
  methods: {
    ok () {
      this.formData.companyId = this.company.id
      this.formData.companyName = this.company.name
    },
    cancel () {
      this.companyShow = false
    },
    close (router) {
      Event.$emit('closePage', router)
    },
    handleSubmit (name) {
      this.$refs[name].validate((valid) => {
        if (valid) {
          this.modify()
        }
      })
    },
    modify () {
      UserAPI.modify(this.formData).then((res) => {
        if (res.data.state === 1) {
          this.$Notice.success({
            title: '操作成功',
            desc: '修改用户成功'
          })
          Event.$emit('closePage', 'modifyUser')
          Event.$emit('refreshList', null)
        } else {
          this.$Notice.error({
            title: '操作失败',
            desc: res.data.msg
          })
        }
      })
    },
    handleReset (name) {
      this.$refs[name].resetFields()
    },
    changePage () {
    },
    selectCompany (rowData) {
      if (rowData !== null) {
        this.company = rowData
      }
    },
    query () {
      UserAPI.getById(this.$route.query.paramId).then((res) => {
        if (res.data) {
          if (res.data.localLevel) {
            this.companyButton = false
          }
          this.formData = res.data
          this.formData.userType = res.data.userType
        } else {
          this.$Notice.error({
            title: '操作失败',
            desc: res.data.msg
          })
        }
      })
    },
    handleCompanyModal() {
      this.companyShow = true
      Event.$emit('queryCompany', null)
    }
  },
  created () {
    this.query()
    Event.$on('selectCompany', obj => this.selectCompany(obj))
  }
}
</script>



