<template>
  <div>
    <div class="form-box-content">
      <div class="module-title-wrap">基本信息</div>
      <fdcform ref="formValidate" :model="formValidate" :rules="ruleValidate" :label-width="100">
        <row type="flex" justify="space-around">
          <fdccol span="7">
            <formitem label="编码" prop="code">
              <fdcinput v-model="formValidate.code" placeholder=""></fdcinput>
            </formitem>
          </fdccol>
          <fdccol span="7">
            <formitem label="名称" prop="name">
              <fdcinput v-model="formValidate.name" placeholder=""></fdcinput>
            </formitem>
          </fdccol>
          <fdccol span="7">
            <formitem label="类型" prop="type">
              <fdcselect v-model="formValidate.type">
                <fdcoption v-for="item in enumSelect['LOCATION_TYPE']" :value="item.enumCode" :key="item.enumCode">
                  {{ item.enumValue }}
                </fdcoption>
              </fdcselect>
            </formitem>
          </fdccol>
        </row>
        <row type="flex" justify="space-around">
          <fdccol span="7">
            <formitem label="城市" v-show="false" prop="cityId">
              <fdcinput v-model="formValidate.cityId" placeholder="城市">
              </fdcinput>
            </formitem>
            <formitem label="城市" prop="cityName">
              <fdcinput v-model="formValidate.cityName" readonly placeholder="城市">
                <fdcbutton slot="append" icon="ios-search" @click="cityShow = true"></fdcbutton>
              </fdcinput>
            </formitem>
          </fdccol>
          <fdccol span="7">
            <formitem label="联系人" prop="contact">
              <fdcinput v-model="formValidate.contact" placeholder=""></fdcinput>
            </formitem>
          </fdccol>
          <fdccol span="7">
            <formitem label="联系电话" prop="telephone">
              <fdcinput v-model="formValidate.telephone" placeholder=""></fdcinput>
            </formitem>
          </fdccol>
        </row>
        <row type="flex" justify="space-around">
          <fdccol span="15">
            <formitem label="地址" prop="address">
              <fdcinput v-model="formValidate.address" type="textarea" placeholder=""></fdcinput>
            </formitem>
          </fdccol>
          <fdccol span="7"></fdccol>
        </row>
        <row type="flex" justify="space-around">
          <fdccol span="7">
            <formitem>
              <fdcbutton type="primary" @click="handlerSubmit('formValidate')">提交</fdcbutton>
              <fdcbutton type="ghost" @click="close()" class="buttonClass">取消</fdcbutton>
            </formitem>
          </fdccol>
        </row>
      </fdcform>
    </div>
    <modal v-model="cityShow" :width="800" title="请选择城市" @on-ok="ok" @on-cancel="cancel">
      <select-city></select-city>
    </modal>
  </div>
</template>
<script>
  import LocationAPI from './api/LocationAPI'
  import {mapState} from 'vuex'
  import SelectCity from "../../../../components/modal/selectCity"
  import Event from '@/utility/Event.js'

  export default {
    name: 'modifyLocation',
    data: function () {
      return {
        formValidate: {
          code: '',
          name: '',
          type: '',
          cityId: '',
          cityName: '',
          contact: '',
          telephone: '',
          address: ''
        },
        ruleValidate: {
          code: [{
            required: true,
            type: 'string',
            message: '请输入编号',
            trigger: 'blur'
          },{
            max: 20,
            message: '编号超长',
            trigger: 'blur'
          }],
          name: [{
            required: true,
            type: 'string',
            message: '请输入名称',
            trigger: 'blur'
          },{
            max: 20,
            message: '名称超长',
            trigger: 'blur'
          }],
          type: [{
            required: true,
            type: 'number',
            message: '请选择类型',
            trigger: 'change'
          }],
          cityName: [{
            required: true,
            type: 'string',
            message: '请选择城市',
            trigger: 'blur'
          }],
          telephone: [
            {
              max: 50,
              message: '联系电话超长',
              trigger: 'blur'
            },
            {
              pattern: /^[0-9,'\-']+$/,
              message: '联系电话格式不正确',
              trigger: 'blur'
            }
          ],
          address: [
            {
              max: 100,
              message: '地址超长',
              trigger: 'blur'
            },
          ],

        },
        cityShow: false,
        city: {
          id: '',
          name: ''
        },
        locationId: this.$route.query.paramId
      }
    },
    computed: {
      ...mapState({
        enumSelect: state => state.Enum.enumSelect
      })
    },
    methods: {
      getData() {
        LocationAPI.getById({id:this.locationId}).then((res) => {
          if (res.data) {
            this.formValidate = res.data;
          } else {
            this.$Notice.error({
              title: '操作失败',
              desc: res.data.msg
            })
          }
        })
      },
      modify() {
        LocationAPI.modify(this.formValidate).then((res) => {
          if (res.data.state == 1) {
            this.$Notice.success({
              title: '操作成功',
              desc: '修改成功'
            });
            Event.$emit('closePage', 'modifyLocation');
            Event.$emit('refreshLocationList', null);
          } else {
            this.$Notice.error({
              title: '操作失败',
              desc: res.data.msg
            });
          }
        })
      },
      handlerSubmit(name) {
        this.$refs[name].validate((valid) => {
          if (valid) {
            this.modify();
          }
        })
      },
      cancel() {
        this.cityShow = false;
        Event.$emit('clearSelect', null);
      },
      close() {
        Event.$emit('closePage', 'modifyLocation')
      },
      ok() {
        this.formValidate.cityId = this.city.id;
        this.formValidate.cityName = this.city.name;
      },
      selectCity(rowData) {
        this.city = rowData;
      }
    },
    mounted() {
      this.getData();
      const condition = ['LOCATION_TYPE'];
      Event.$emit('addEnum', {typeList: condition});
      Event.$on('selectCity', obj => this.selectCity(obj));
    },
    components: {SelectCity}
  }
</script>



