import Vue from 'vue';
import I18nAPI from '../../../src/page/system/sysMagnagement/i18n/api/I18nAPI'

export default {
  state: {
    lang:'zh_CN',
    i18nMap: {
      'sys_login_rember_account':'记住账户',
      'sys_login':'登录',
      'sys_login_forget':'忘记密码',
      'sys_login_rember_password':'记住密码',
      'sys_login_userName':'请输入用户名',
      'sys_login_password':'请输入密码'
    }
  },
  actions: {
    switchLang(context, lang) {
      context.commit("switchLang", lang);
    },
    fetchI18n(context,codes){
      let params = {languageCode:context.state.lang,codes:codes};
      I18nAPI.getI18ns(params).then(res=>{
        if(res.data.state==1){
          let callbackData = res.data.data;
          let commitObj = {};
          for(let i=0;i<callbackData.length;i++){
            commitObj[callbackData[i].code] = callbackData[i].value;
          }
          context.commit("setI18nValue",commitObj);
        }
      });
    }
  },
  mutations: {
    switchLang(state, lang) {
      state.i18nMap = {};
      state.lang = lang;
      Vue.config.lang = lang;
    },
    setI18nValue(state,obj){
      state.i18nMap = Object.assign({},state.i18nMap,obj);
    }
  }
}



