<style scoped>
  .table-box {
    position: relative;
    box-sizing: border-box;
    height: 100%
  }
</style>
<template>
  <div>
    <div class="page-header-box">
      <div class="header-msg-box">
        <p>用户绑定作业点</p>
      </div>
      <div class="header-btn-box">
        <fdcbutton icon="plus-round" @click="toBindLocations">绑定作业点</fdcbutton>
        <fdcbutton icon="close-round" @click="toUnBindLocations">批量解绑</fdcbutton>
        <fdcbutton icon="ios-undo" @click="close">关闭</fdcbutton>
      </div>
    </div>
    <div class="form-box">
      <fdcform ref="form" inline :model="formData" :label-width="80" @keyup.enter.native="handleSubmit('form')">
        <formitem label="编码" prop="code">
          <fdcinput v-model="formData.code" placeholder="请输入编码"
                    class="search-input" ></fdcinput>
        </formitem>
        <formitem label="名称" prop="name">
          <fdcinput v-model="formData.name"  placeholder="请输入名称"
                    class="search-input"></fdcinput>
        </formitem>
        <formitem label="类型" prop="type">
          <fdcselect v-model="formData.type" placeholder="请选择作业点类型" class="search-input" clearable
                     @on-change="handleSubmit('form')">
            <fdcoption v-for="item in enumSelect['LOCATION_TYPE']" :value="item.enumCode" :key="item.enumCode">
              {{ item.enumValue }}
            </fdcoption>
          </fdcselect>
        </formitem>
        <fdcbutton type="primary" @click="handleSubmit('form')" class="search-submit">查询</fdcbutton>
        <fdcbutton type="ghost" @click="handleReset('form')" class="search-reset">重置</fdcbutton>
      </fdcform>
    </div>
    <div class="table-box">
      <fdctable ref="table" border size="small" :loading="isLoading" :data="tableData"
                :columns="tableColumns" stripe @on-selection-change="selectChange"></fdctable>
      <div class="page-box">
        <Page v-show="pageShow" placement="top" :page-size="formData.pageNumber" :total="total"
              :current.sync="formData.pageNo"
              @on-change="changePageNo" @on-page-size-change="changePageSize" show-total show-elevator
              show-sizer></Page>
      </div>
    </div>

    <modal v-model="bindLocationsShow" :width="1200" title="请绑定作业点" @on-ok="bindLocations" @on-cancel="cancel">
      <div class="form-box">
        <fdcform ref="form2" inline :model="formData2" :label-width="80">
          <formitem label="编码" prop="code">
            <fdcinput v-model="formData2.code" @keyup.enter.native="handleSubmit2('form2')" placeholder="请输入编码"
                      class="search-input"></fdcinput>
          </formitem>
          <formitem label="登录名" prop="name">
            <fdcinput v-model="formData2.name" @keyup.enter.native="handleSubmit2('form2')" placeholder="请输入名称"
                      class="search-input"></fdcinput>
          </formitem>
          <formitem label="类型" prop="type">
            <fdcselect v-model="formData2.type" placeholder="请选择作业点类型" class="search-input" clearable
                       @on-change="handleSubmit('form')">
              <fdcoption v-for="item in enumSelect['LOCATION_TYPE']" :value="item.enumCode" :key="item.enumCode">
                {{ item.enumValue }}
              </fdcoption>
            </fdcselect>
          </formitem>
          <fdcbutton type="primary" @click="handleSubmit2('form2')" class="search-submit">查询</fdcbutton>
          <fdcbutton type="ghost" @click="handleReset('form2')" class="search-reset">重置</fdcbutton>
        </fdcform>
      </div>
      <div class="table-box">
        <fdctable ref="table2" border size="small" :loading="isLoading" :data="tableData2"
                  :columns="tableColumns2" stripe @on-selection-change="selectChange"></fdctable>
        <div class="page-box">
          <Page v-show="pageShow2" placement="top" :page-size="formData2.pageNumber" :total="total2"
                :current.sync="formData2.pageNo"
                @on-change="changePageNo2" @on-page-size-change="changePageSize2" show-total show-elevator
                show-sizer></Page>
        </div>
      </div>
    </modal>
  </div>
</template>

<script>
  import Event from '@/utility/Event.js'
  import {mapActions, mapState} from 'vuex'
  import UserAPI from "./api/UserAPI";

  export default {
    name: 'userLocation',
    data() {
      return {
        total: 0,
        total2: 0,
        formData: {
          code: '',
          name: '',
          type: '',
          pageNumber: 10,
          pageNo: 1
        },
        formData2: {
          code: '',
          name: '',
          type: '',
          pageNumber: 10,
          pageNo: 1
        },
        isLoading: false,
        pageShow: false,
        pageShow2: false,
        tableData: [],
        tableData2: [],
        bindLocationsShow: false,
        selectDataStore: [], //分页批量选择，建一个数据仓库
        dataStoreTo: [], //建立一个中间数据仓库
        defaultLocationId: '',
        userId: this.$route.query.paramId
      }
    },
    mounted() {
      Event.$emit('addEnum', {typeList: ['LOCATION_TYPE']});
      this.queryBindLocations();
    },
    watch: {
      userIds() {
        if (this.$route.query.paramId) {
          this.userId = this.$route.query.paramId;
          this.queryBindLocations();
        }
      }
    },
    computed: {
      ...mapState({
        sysEnumList: state => state.Enum.enumList,
        enumSelect: state => state.Enum.enumSelect
      }),
      userIds() {
        return this.$route.query.paramId
      },
      tableColumns() {
        let tableCol = [
          {
            type: 'selection',
            width: 60,
            align: 'center'
          },
          {
            title: ' ',
            key: 'number',
            width: 60,
            align: 'center',
            render: (h, params) => {
              return h('span', params.index + (this.formData.pageNo - 1) * this.formData.pageNumber + 1)
            }
          },
          {
            title: '编码',
            key: 'code',
            align: 'center',
            ellipsis: true
          },
          {
            title: '名称',
            key: 'name',
            align: 'center',
            ellipsis: true
          },
          {
            title: '类型',
            key: 'type',
            align: 'center',
            render: (h, params) => {
              return h('span', {}, this.sysEnumList['LOCATION_TYPE_' + params.row.type])
            }
          },
          {
            title: '操作',
            key: 'action',
            align: 'left',
            render: (h, params) => {
              const color = params.row.id === this.defaultLocationId ? '#faad14' : 'gray';
              const content = params.row.id === this.defaultLocationId ? '默认' : '设为默认';
              return h('div', [
                h('tooltip', {
                  props: {
                    content: content,
                    placement: 'top'
                  }
                }, [h('span', {
                  on: {
                    click: () => {
                      if (params.row.id === this.defaultLocationId) {
                        return;
                      }
                      this.$Modal.confirm({
                        title: '设置默认作业点',
                        content: '<p>确定设置？</p>',
                        onOk: () => {
                          this.setDefaultLocation(params.row.id);
                        },
                        onCancel: () => {
                        }
                      });
                    }
                  }
                }, [h('icon', {
                  props: {
                    type: 'star',
                    size: 22,
                    color: color
                  },
                  style: {
                    marginRight: '10px'
                  }
                }, '默认')])
                ]),
                h('tooltip', {
                  props: {
                    content: '解绑',
                    placement: 'top'
                  }
                }, [h('span', {
                  on: {
                    click: () => {
                      this.$Modal.confirm({
                        title: '解绑',
                        content: '<p>确定解绑？</p>',
                        onOk: () => {
                          this.unBind(params.row.id);
                        },
                        onCancel: () => {
                        }
                      });
                    }
                  }
                }, [h('icon', {
                  props: {
                    type: 'close-round',
                    size: 20
                  },
                  style: {
                    marginRight: '5px'
                  }
                }, '解绑')])
                ])
              ])
            }
          }];
        return tableCol;
      },
      tableColumns2() {
        let tableCol = [
          {
            type: 'selection',
            width: 60,
            align: 'center'
          },
          {
            title: ' ',
            key: 'number',
            width: 60,
            align: 'center',
            render: (h, params) => {
              return h('span', params.index + (this.formData2.pageNo - 1) * this.formData2.pageNumber + 1)
            }
          },
          {
            title: '编码',
            key: 'code',
            align: 'center',
            ellipsis: true
          },
          {
            title: '名称',
            key: 'name',
            align: 'center',
            ellipsis: true
          },
          {
            title: '类型',
            key: 'type',
            align: 'center',
            render: (h, params) => {
              return h('span', {}, this.sysEnumList['LOCATION_TYPE_' + params.row.type])
            }
          },
        ];
        return tableCol;
      },
    },
    methods: {
      ...mapActions([
        'generateEnumList'
      ]),
      // 绑定用户按钮
      toBindLocations() {
        this.bindLocationsShow = true;
        this.queryUnBindLocations();

        //已选择数据要清空
        this.selectDataStore = [];
        this.dataStoreTo = [];
      },
      // 查询绑定的作业点
      handleSubmit(name) {
        this.$refs[name].validate((valid) => {
          if (valid) {
            this.queryBindLocations();
          }
        })
      },
      //查询未绑定的作业点
      handleSubmit2(name) {
        this.$refs[name].validate((valid) => {
          if (valid) {
            this.queryUnBindLocations();
          }
        })
      },
      queryBindLocations() {
        this.getDefaultLocation();
        this.isLoading = !this.isLoading;
        let condition = {
          'userId': this.userId,
          ...this.formData
        };
        UserAPI.queryBindLocations(condition).then((res) => {
          this.tableData = [];
          let state = res.data.state;
          if (state === 1) {
            let dataList = res.data.data.list;
            if (dataList !== null && dataList.length > 0) {
              this.pageShow = true;
              this.total = res.data.data.total;
            }
            this.tableData.push(...dataList)
          }
          this.isLoading = !this.isLoading
        })
      },
      queryUnBindLocations() {
        this.isLoading = !this.isLoading;
        let condition = {
          'userId': this.userId,
          ...this.formData2
        };
        UserAPI.queryUnBindLocations(condition).then((res) => {
          this.tableData2 = [];
          let state = res.data.state;
          if (state === 1) {
            let dataList = res.data.data.list;
            if (dataList !== null && dataList.length > 0) {
              this.pageShow2 = true;
              this.total2 = res.data.data.total
            }
            this.tableData2.push(...dataList)
          }
          this.isLoading = !this.isLoading
        })
      },
      //绑定作业点
      bindLocations() {
        this.getSelectedData();
        debugger
        if (this.selectDataStore.length > 0) {
          let locationIdList = [];
          for (let i = 0; i < this.selectDataStore.length; i++) {
            locationIdList.push(this.selectDataStore[i].id);
          }
          let condition = {
            'userId': this.userId,
            'locationIdList': locationIdList
          };
          UserAPI.bindLocations(condition).then((res) => {
            if (res.data.state == 1) {
              this.$Notice.success({
                title: '绑定成功',
              });
              this.queryBindLocations();
            } else {
              this.$Notice.error({
                title: '操作失败',
                desc: res.data.msg
              });
            }
          });

          this.bindLocationsShow = false;
          //已选择数据要清空
          this.selectDataStore = [];
          this.dataStoreTo = [];
        }
      },
      toUnBindLocations() {
        this.getSelectedData();
        if (this.selectDataStore.length > 0) {
          this.$Modal.confirm({
            title: '解绑',
            content: '<p>确定解绑？</p>',
            onOk: () => {
              this.unBindLocations();
            }
          });
        }
      },
      //解绑用户
      unBindLocations() {
        if (this.selectDataStore.length > 0) {
          let locationIdList = [];
          for (let i = 0; i < this.selectDataStore.length; i++) {
            locationIdList.push(this.selectDataStore[i].id);
          }
          let condition = {
            'userId': this.userId,
            'locationIdList': locationIdList
          };
          if (this.total === locationIdList.length) {
            this.$Notice.warning({
              title: '解绑所有作业点将会失去作业点数据权限',
            });
            return;
          }
          UserAPI.unBindLocations(condition).then((res) => {
            if (res.data.state == 1) {
              this.$Notice.success({
                title: '解绑成功',
              });
              this.queryBindLocations();
            } else {
              this.$Notice.error({
                title: '操作失败',
                desc: res.data.msg
              });
            }
          });

          this.bindLocationsShow = false;
          //已选择数据要清空
          this.selectDataStore = [];
          this.dataStoreTo = [];
          this.queryBindLocations();
        }
      },
      unBind(locationId) {
        let locationIdList = [];
        locationIdList.push(locationId);
        let condition = {
          'userId': this.userId,
          'locationIdList': locationIdList
        };
        if (this.total === 1) {
          this.$Notice.warning({
            title: '解绑所有作业点将会失去作业点数据权限',
          });
          return;
        }
        UserAPI.unBindLocations(condition).then((res) => {
          if (res.data.state == 1) {
            this.$Notice.success({
              title: '解绑成功',
            });
            this.queryBindLocations();
          } else {
            this.$Notice.error({
              title: '操作失败',
              desc: res.data.msg
            });
          }
        });
      },
      cancel() {
        this.bindLocationsShow = false;
        //已选择数据要清空
        this.selectDataStore = [];
        this.dataStoreTo = [];
      }
      ,
      handleReset(name) {
        this.$refs[name].resetFields()
      }
      ,
      changePageNo(value) {
        this.formData.pageNo = value
        this.queryBindLocations();
      }
      ,
      changePageSize(value) {
        this.formData.pageNo = 1;
        this.formData.pageNumber = value;
        this.queryBindLocations();
      },
      changePageNo2(value) {
        this.formData2.pageNo = value;
        this.queryUnBindLocations();
      }
      ,
      changePageSize2(value) {
        this.formData2.pageNo = 1;
        this.formData2.pageNumber = value;
        this.queryUnBindLocations();
      },
      //获取选择的数据
      selectChange(selection) {
        let pageDataStore = {
          page: this.formData.pageNo,   //当前操作页数
          dataArr: selection   //当前操作页用户选择的数据
        };
        this.dataStoreTo[this.formData.pageNo - 1] = pageDataStore    //丢给中间数据仓库(记录下位置，用以解决数据覆盖问题)
      },
      //获取所有选择的数据
      getSelectedData() {
        this.selectDataStore = [];
        this.dataStoreTo.forEach((item, index) => {
          this.selectDataStore = this.selectDataStore.concat(this.dataStoreTo[index].dataArr); //拿到用户所选全部数据this.selectDataStore
        })
      },
      //关闭角色绑定用户页面
      close() {
        Event.$emit('closePage', 'userLocation');
      },
      //获取默认作业点
      getDefaultLocation() {
        UserAPI.getDefaultLocation(this.userId).then((res) => {
          if (res.data.state == 1) {
            this.defaultLocationId = res.data.data;
          }
        });
      },
      //设置默认作业点
      setDefaultLocation(locationId) {
        let condition = {
          userId: this.userId,
          locationId: locationId
        };
        UserAPI.setDefaultLocation(condition).then((res) => {
          if (res.data.state == 1) {
            this.$Notice.success({
              title: '操作成功',
            });
            this.queryBindLocations();
          }
        });
      }
    }
    ,
    components: {}
  }
</script>



