<style scoped>
</style>
<template>
  <div>
    <div class="form-box-content">
      <div class="module-title-wrap">运单详情</div>
      <fdcform ref="formData" :model="formData"  :label-width="100" >
        <row type="flex" justify="space-around">
          <fdccol span = '5'>
            <formitem label="运单号" prop="code">
              <fdcinput v-model="formData.code" readonly>
              </fdcinput>
            </formitem>
          </fdccol>
          <fdccol span = '5'>
            <formitem label="客户单号" prop="customerCode">
              <fdcinput v-model="formData.customerCode" readonly>
              </fdcinput>
            </formitem>
          </fdccol>
          <fdccol span = '5'>
            <formitem label="3PL运单号" prop="tplCode">
              <fdcinput v-model="formData.tplCode" readonly >
              </fdcinput>
            </formitem>
          </fdccol>
          <fdccol span = '5'>
            <formitem label="委托方" prop="customerName">
              <fdcinput v-model="formData.customerName" readonly>
              </fdcinput>
            </formitem>
          </fdccol>
        </row>
        <row type="flex" justify="space-around">
          <fdccol span = '5'>
            <formitem label="下单时间" prop="orderTime">
              <fdcinput v-model="formData.orderTime" readonly>
              </fdcinput>
            </formitem>
          </fdccol>
          <fdccol span = '5'>
            <formitem label="付款方式" prop="payMode">
              <fdcinput v-model="formData.payMode" readonly>
              </fdcinput>
            </formitem>
          </fdccol>
          <fdccol span = '5'>
            <formitem label="应收金额" prop="amountReceivable">
              <fdcinput v-model="formData.amountReceivable" readonly>
              </fdcinput>
            </formitem>
          </fdccol>
          <fdccol span = '5'>
            <formitem label="运单类型" prop="type">
              <fdcinput v-model="formData.type" readonly>
              </fdcinput>
            </formitem>
          </fdccol>
        </row>
        <div class="module-split-wrap"></div>
        <row type="flex" justify="space-around">
          <fdccol span = '5'>
            <formitem label="收货人" prop="receiver">
              <fdcinput v-model="formData.receiver" readonly>
              </fdcinput>
            </formitem>
          </fdccol>
          <fdccol span = '5'>
            <formitem label="联系电话" prop="receiverTelephone">
              <fdcinput v-model="formData.receiverTelephone" readonly>
              </fdcinput>
            </formitem>
          </fdccol>
          <fdccol span = '11'>
            <formitem label="送货时间" prop="deliveryTime">
              <fdcinput v-model="formData.deliveryTime" readonly>
              </fdcinput>
            </formitem>
          </fdccol>
        </row>
        <row type="flex" justify="space-around">
          <fdccol span = '5'>
            <formitem label="收货省" prop="toProvince">
              <fdcinput v-model="formData.toProvince" readonly>
              </fdcinput>
            </formitem>
          </fdccol>
          <fdccol span = '5'>
            <formitem label="收货市" prop="toCity">
              <fdcinput v-model="formData.toCity" readonly>
              </fdcinput>
            </formitem>
          </fdccol>
          <fdccol span = '5'>
            <formitem label="收货区" prop="toDistrict">
              <fdcinput v-model="formData.toDistrict" readonly>
              </fdcinput>
            </formitem>
          </fdccol>
          <fdccol span = '5'>
            <!--<formitem label="区域" prop="toAreaName">-->
              <!--<fdcinput v-model="formData.toAreaName" >-->
              <!--</fdcinput>-->
            <!--</formitem>-->
          </fdccol>
        </row>
        <row type="flex" justify="space-around">
          <fdccol span = '23'>
            <formitem label="收货地址" prop="receiverAddress">
              <fdcinput v-model="formData.receiverAddress" readonly>
              </fdcinput>
            </formitem>
          </fdccol>
        </row>
        <div class="module-split-wrap"></div>
        <row type="flex" justify="space-around">
          <fdccol span = '5'>
            <formitem label="配送商" prop="carrierName">
              <fdcinput v-model="formData.carrierName" readonly>
              </fdcinput>
            </formitem>
          </fdccol>
          <fdccol span = '5'>
            <formitem label="配送方式" prop="deliveryMode">
              <fdcinput v-model="formData.deliveryMode" readonly>
              </fdcinput>
            </formitem>
          </fdccol>
          <fdccol span = '5'>
            <formitem label="发货仓库" prop="warehouseName">
              <fdcinput v-model="formData.warehouseName" readonly>
              </fdcinput>
            </formitem>
          </fdccol>
          <fdccol span = '5'>
            <!--<formitem label="上门提货" prop="pickupState">-->
              <!--<fdcinput v-model="formData.pickupState" >-->
              <!--</fdcinput>-->
            <!--</formitem>-->
          </fdccol>
        </row>
        <row type="flex" justify="space-around">
          <fdccol span = '5'>
            <formitem label="网点" prop="siteName">
              <fdcinput v-model="formData.siteName" readonly>
              </fdcinput>
            </formitem>
          </fdccol>
          <fdccol span = '5'>
            <formitem label="状态" prop="state">
              <fdcinput v-model="formData.state" readonly>
              </fdcinput>
            </formitem>
          </fdccol>
          <fdccol span = '5'>
            <formitem label="回单时间" prop="returnTime">
              <fdcinput v-model="formData.returnTime" readonly>
              </fdcinput>
            </formitem>
          </fdccol>
          <fdccol span="5"></fdccol>
        </row>
        <row type="flex" justify="space-around">
          <fdccol span = '23'>
            <formitem label="备注" prop="remark">
              <fdcinput v-model="formData.remark" readonly >
              </fdcinput>
            </formitem>
          </fdccol>
        </row>
        <div class="module-split-wrap"></div>
        <div >
          <tabs :value="tabName" type="card">
            <tabpane label="包裹" name="doPackage">
              <div class="table-box">
                <fdctable ref="table" border size="small" :data="tableData" :columns="tableColumns" stripe></fdctable>
                <div class="page-box">
                  <Page placement="top" :page-size="tableCondition.pageNumber" :total="total" :current.sync="tableCondition.pageNo"  @on-change="changePageNo" @on-page-size-change="changePageSize" show-total show-elevator show-sizer></Page>
                </div>
              </div>
            </tabpane>
            <tabpane label="货物" name="doGoods">
            </tabpane>
            <tabpane label="支付" name="doPayment">
            </tabpane>
            <tabpane label="运输" name="doShipment">
            </tabpane>
          </tabs>
        </div>
      </fdcform>
    </div>
  </div>
</template>

<script>
  import DoOrderAPI from './api/DoOrderAPI'
  import DoPackageAPI from '../doPackage/api/DoPackageAPI'
  import Event from '@/utility/Event.js'
  import location from '@/components/modal/location.vue'
  import {mapState} from 'vuex'

  export default {
    name: 'doOrderDetail',
    data () {
      return {
        locationShow: false,
        selectValue: [],
        total: 0,
        tabName: 'doPackage',
        location: {
          id: '',
          name: ''
        },
        formData: {
          code: '',
          customerCode:'',
          tplCode: '',
          customerName: '',
          orderTime: '',
          payMode: '',
          amountReceivable:'',
          receiverTelephone: '',
          deliveryTime: '',
          toProvince:'',
          toCity: '',
          toDistrict:'',
          toAreaName:'',
          receiverAddress:''
        },
        tableColumns:[
          {
            title: ' ',
            type: 'index2',
            width: 60,
            align: 'center',
            render: (h, params) => {
              return h('span', params.index + (this.formData.pageNo - 1) * this.formData.pageNumber + 1)
            }
          },
          {
            title: '包裹号',
            key: 'code',
            align: 'center'
          },
          {
            title: '当前位置',
            key: 'currentLocationName',
            align: 'center'
          },
          {
            title: '重量(kg)',
            key: 'weight',
            align: 'center'
          },
          {
            title: '体积(cm³)',
            key: 'code',
            align: 'center'
          },
          {
            title: '称重人',
            key: 'weigher',
            align: 'center'
          },
          {
            title: '称重时间',
            key: 'weightTime',
            align: 'center'
          },
          {
            title: '出库时间',
            key: 'createdTime',
            align: 'center'
          },
        ],
        tableData: [],
        tableCondition:{
          pageNumber: 10,
          pageNo:1
        }
      }
    },
    computed: {
      ...mapState({
        enumSelect: state => state.Enum.enumSelect
      })
    },
    components: {
      location: location
    },
    methods: {
      close (router) {
        Event.$emit('closePage', router)
      },
      changePageNo(value) {
        this.formData.pageNo = value
        this.query()
      },
      changePageSize(value) {
        this.formData.pageNo = 1
        this.formData.pageNumber = value
        this.query()
      },
      query () {
        DoOrderAPI.getById(this.$route.query.paramId).then((res) => {
          if (res.data) {
            this.formData = res.data
            this.formData.type = this.sysEnumList['ORDER_TYPE_' + res.data.type]
            this.formData.state = this.sysEnumList['ORDER_STATE_' + res.data.state]
            this.formData.payMode = this.sysEnumList['PAY_MODE_' + res.data.payMode]
            this.formData.deliveryMode = this.sysEnumList['DELIVERY_MODE_' + res.data.deliveryMode]
          } else {
            this.$Notice.error({
              title: '操作失败',
              desc: res.data.msg
            })
          }
        })
        DoPackageAPI.query(this.tableCondition).then((res) => {
          this.tableData = []
          let state = res.data.state
          if (state === 1 && res.data.data) {
            let dataList = res.data.data.list
            if (dataList !== null) {
              this.total = res.data.data.total
            }
            this.tableData.push(...dataList)
          }
        })
      }
    },
    computed: {
      ...mapState({
        sysEnumList: state => state.Enum.enumList
      })
    },
    mounted () {
      Event.$emit('addEnum', {typeList:['ORDER_STATE','ORDER_TYPE','DELIVERY_MODE','PAY_MODE']})
      this.query()
    }
  }
</script>



