<style scoped>
  .main-content-box-collapsed {
    left : 78px;
  }
</style>
<style>
  @import "./assets/css/common.css";
</style>
<template>
  <div id="app">
    <Layout v-if="this.$route.name!='login'">
      <side-menu></side-menu>
    </Layout>
    <Layout v-if="this.$route.name!='login'" >
      <header-menu></header-menu>
    </Layout>
    <Layout v-if="this.$route.name!='login'" class="main-content-box" :class="contentClass">
      <tags></tags>
      <keep-alive :include="cachePage">
        <router-view></router-view>
      </keep-alive>
      <modal v-model="showLocation" :width="1200" title="切换作业点" @on-ok="ok" @on-cancel="cancel">
        <change-location></change-location>
      </modal>
    </Layout>
    <router-view v-else></router-view>
  </div>
</template>

<script>
import Event from '@/utility/Event.js'
import { mapState, mapActions } from 'vuex'
import SideMenu from '@/components/sideMenu'
import headerMenu from '@/components/header/headerMenu.vue'
import EnumAPI from './page/system/sysMagnagement/enum/api/EnumAPI'
import UT from '@/utility/utility.js'
import tags from '@/components/tags.vue'
import ChangeLocation from "./page/basic/basicData/location/changeLocation"
import LocationAPI from './page/basic/basicData/location/api/LocationAPI'

export default {
  name: 'App',
  data () {
    return {
      showLocation:false,
      locationInfo: ''
    }
  },
  components: {ChangeLocation, SideMenu, headerMenu, tags},
  watch: {
    showChangeLocation () {
     this.showLocation = this.$store.state.showChangeLocation
    }
  },
  computed: {
    ...mapState({
      tagList: state => state.tagList,
      isCollapsed: state => state.isCollapsed,
      sysEnumList: state => state.Enum.enumList,
      enumSelect: state => state.Enum.enumSelect,
      showChangeLocation:state => state.showChangeLocation,
      token: state => state.User.token
    }),
    contentClass () {
      return [
        this.isCollapsed ? 'main-content-box-collapsed' : ''
      ]
    },
    cachePage () {
      let page = []
      if(this.tagList){
        this.tagList.map((item) => {
          page.push(item && item.router);
        })
      }
      return page
    }
  },
  mounted () {
    Event.$on('selectChangeLocation', obj => this.selectChangeLocation(obj))
    Event.$on('addEnum', obj => this.addEnum(obj))
    Event.$on('modifyEnum', obj => this.modifyEnum(obj))
    this.init()
    if (window.history.pushState) {
      window.onpopstate = () => this.init()
    }
  },
  methods: {
    ...mapActions([
      'fetchInfoFromSession',
      'generateEnumSelect',
      'generateEnumList',
      'changeLocation',
      'changLocationFromPage',
      'initAll'
    ]),
    init () {
      this.fetchInfoFromSession();
      if (this.$route.name !== 'index') {
        Event.$emit('addPage', {
          name: this.$route.meta.title,
          router: this.$route.name
        })
      }
    },
    addEnum (obj) {
      // let enumType = UT.checkEnum(obj.typeList, this.enumSelect)
      // if (enumType.length !== 0) {
        const query = {}
        query.enumTypeList = obj.typeList
        EnumAPI.findEnumByType(query).then(response => {
          if (response.data.state === 1) {
            const data = response.data.data
            let enummap = {}
            let enumSelect = {}
            for (let i = 0; i < data.length; i++) {
              let $enum = data[i]
              let $key = $enum.enumType + '_' + $enum.enumCode
              enummap[$key] = $enum.enumValue
              if(obj.expCodes && obj.expCodes.includes($key)) {
                continue
              }
              if (enumSelect[$enum.enumType] === undefined ) {
                let enumSelectValue = []
                enumSelectValue.push($enum)
                enumSelect[$enum.enumType] = enumSelectValue
              } else {
                let enumSelectValue = enumSelect[$enum.enumType]
                enumSelectValue.push($enum)
                enumSelect[$enum.enumType] = enumSelectValue
              }
            }
            this.generateEnumSelect(Object.assign({}, this.enumSelect, enumSelect))
            this.generateEnumList(Object.assign({}, this.sysEnumList, enummap))
          }
        })
      // }
    },
    modifyEnum (obj) {
      const query = {}
      query.enumTypeList = obj
      if (UT.checkEnumExists(obj, this.enumSelect)) {
        EnumAPI.findEnumByType(query).then(response => {
          if (response.data.state === 1) {
            const data = response.data.data
            let enummap = {}
            let enumSelect = {}
            for (let i = 0; i < data.length; i++) {
              let $enum = data[i]
              let $key = $enum.enumType + '_' + $enum.enumCode
              enummap[$key] = $enum.enumValue
              if (enumSelect[$enum.enumType] === undefined ) {
                let enumSelectValue = []
                enumSelectValue.push($enum)
                enumSelect[$enum.enumType] = enumSelectValue
              } else {
                let enumSelectValue = enumSelect[$enum.enumType]
                enumSelectValue.push($enum)
                enumSelect[$enum.enumType] = enumSelectValue
              }
            }
            this.generateEnumSelect(Object.assign({}, this.enumSelect, enumSelect))
            this.generateEnumList(Object.assign({}, this.sysEnumList, enummap))
          }
        })
      }
    },
    ok () {
      this.changeLocation(false);
      LocationAPI.changeLocation({token: this.token, locationId: this.locationInfo.id}).then((res) => {
        if (res.data.state === 1) {
          this.$Notice.success({
            title: '切换成功',
          });
          //更新相关数据，关闭所有tag
          this.changLocationFromPage(this.locationInfo.name);
          this.initAll();
        } else {
          this.$Notice.error({
            title: '操作失败',
            desc: res.data.msg
          });
        }
      });
    },
    cancel () {
     this.changeLocation(false)
    },
    selectChangeLocation(rowData){
      this.locationInfo = rowData;
    }
  },
  created () {
    this.changeLocation(false)
    this.$store.commit('setOpenedList');
  }
}
</script>



