import Vue from 'vue'

var UT = {
  trim(string){
    return string.replace(/(^\s*)|(\s*$)/g, "");
  },
  //数组克隆
  copyArray(arr) {
    let result = result || [];
    for (let i = 0; i < arr.length; i++) {
      if (arr[i] instanceof Array) {
        result[i] = [];
        copyArray(arr[i], result[i]);
      } else {
        result[i] = arr[i];
      }
    }
    return result;
  },
  //检查某个路由是否合法
  validRouter(router){
    let allRouters = JSON.parse(sessionStorage.getItem('allRouters'));
    for(var i=0;i<allRouters.length;i++){
      if(allRouters[i].path.indexOf(router)>-1){
        return true;
      }
    }
    return false;
  },
  //检查某个路由是否已存在
  checkRouter(name, data) {
    let result = false;
    for (let i = 0; i < data.length; i++) {
      if (data[i].router == name) {
        result = true;
        return result;
      }
    }
    return result;
  },
  // 检查某个枚举是否已存在,存在剔除，不存在返回
  checkEnum (enumTypeList, data) {
    let newList = this.copyArray(enumTypeList)
    let keys = Object.keys(data)
    for (let i = 0; i < enumTypeList.length; i++) {
      if (keys.includes(enumTypeList[i])) {
        newList.splice(newList.indexOf(enumTypeList[i]), 1)
      }
    }
    return newList
  },
  checkEnumExists (enumType, data) {
    let keys = Object.keys(data)
    if (keys.includes(enumType)) {
      return true
    }
    return false
  },
  switchLang (value) {
    Vue.config.lang = value
  }
}

export default UT;



