<template>
  <div>
    <div class="page-header-box">
      <div class="header-msg-box">
        <p>员工管理</p>
      </div>
      <div class="header-btn-box">
        <fdcbutton v-show="queryShow" @click="hideForm" icon="eye-disabled">隐藏</fdcbutton>
        <fdcbutton v-show="!queryShow" @click="showForm" icon="eye">显示</fdcbutton>
        <fdcbutton icon="android-add" @click="add">入职</fdcbutton>
      </div>
    </div>
    <div class="form-box" v-show="queryShow">
      <fdcform ref="form" inline :model="formData" :label-width="80" class="search-form" @keyup.enter.native="handleSubmit('form')" >
        <formitem label="姓名" prop="name">
          <fdcinput v-model="formData.name" placeholder="请输入员工姓名" class="search-input"></fdcinput>
        </formitem>
        <formitem label="工号" prop="code">
          <fdcinput v-model="formData.code" placeholder="请输入工号" class="search-input"></fdcinput>
          <fdcbutton type="primary" @click="handleSubmit('form')" class="search-submit">查询</fdcbutton>
          <fdcbutton type="ghost" @click="handleReset('form')" class="search-reset">重置</fdcbutton>
        </formitem>
      </fdcform>
    </div>
    <div class="table-box">
      <fdctable ref="table" border size="small" :loading="isLoading" :data="tableData" :columns="tableColumns" stripe></fdctable>
      <div class="page-box">
        <Page placement="top" :page-size="formData.pageNumber" :total="total" :current.sync="formData.pageNo"  @on-change="changePageNo" @on-page-size-change="changePageSize" show-total show-elevator show-sizer></Page>
      </div>
    </div>


  </div>
</template>

<script>
  import EmployeeAPI from './api/EmployeeAPI'
  import Event from '@/utility/Event.js'
  import { mapState } from 'vuex'

  export default {
    name: 'employee',
    data () {
      return {
        total: 0,
        queryShow: true,
        showModal: false,
        formData: {
          name: '',
          pageNumber: 10,
          pageNo: 1
        },
        isLoading: false,
        pageShow: false,
        tableColumns: [
          {
            title: ' ',
            type: 'index2',
            width: 60,
            align: 'center',
            render: (h, params) => {
              return h('span', params.index + (this.formData.pageNo - 1) * this.formData.pageNumber + 1)
            }
          },
          {
            title: '姓名',
            key: 'name',
            align: 'center'
          },
          {
            title: '工号',
            key: 'code',
            align: 'center'
          },
          {
            title: '登录名',
            key: 'loginName',
            align: 'center'
          },
          {
            title: '员工类型',
            key: 'type',
            align: 'center',
            width: 120,
            render: (h, params) => {
              return h('span', {}, this.sysEnumList['EMPLOYEE_TYPE_' + params.row.type])
            }
          },
          {
            title: '所属作业点',
            key: 'locationName',
            align: 'center',
            width: 160
          },
          {
            title: '联系方式',
            key: 'telephone',
            align: 'center'
          },
          {
            title: '入职日期',
            key: 'hiredate',
            align: 'center'
          },
          {
            title: '状态',
            key: 'isDeleted',
            align: 'center',
            render: (h, params) => {
              return h('span', {}, this.sysEnumList['EMPLOYEE_STATE_' + params.row.isDeleted])
            }
          },
          {
            title: '所属公司',
            key: 'companyName',
            align: 'center'
          },
          {
            title: '操作',
            key: 'action',
            align: 'left',
            render: (h, params) => {
              const row = params.row;
              const type = row.isDeleted === 0 ? 'locked' : 'unlocked'
              const color = row.isDeleted === 0 ? 'red' : '#0d96ff'
              const content = row.isDeleted === 0 ? '离职' : null
              const show = row.isDeleted === 0 ? false : true
              return h('div', [
                h('tooltip', {
                  props: {
                    content: '修改',
                    placement: 'top'
                  }
                }, [h('span', {
                  on: {
                    click: () => {
                      if (row.isDeleted === 0) {
                        this.modify(row.id)
                      }else{
                        this.$Notice.error({
                          title: '员工已离职不允许修改'
                        })
                      }
                    }
                  }
                }, [h('icon', {
                  props: {
                    type: 'edit',
                    size: 20,
                    color: '#0d96ff'
                  },
                  style: {
                    marginRight: '5px'
                  }
                }, '修改')])
                ]),
                h('tooltip', {
                  props: {
                    disabled:show,
                    content: content,
                    placement: 'top'
                  }
                }, [
                  h('span', {
                    on: {
                      click: () => {
                        if (row.isDeleted === 0) {
                          this.$Modal.confirm({
                            title: '离职',
                            content: '<p>确定离职？</p>',
                            onOk: () => {
                              this.quit(row.id, 1)
                            },
                            onCancel: () => {}
                          })
                        }
                      }
                    }
                  }, [h('icon', {
                    props: {
                      type: type,
                      size: 20,
                      color: color
                    }
                  }, '离职')])
                ])
              ])
            }
          }
        ],
        tableData: []
      }
    },
    mounted () {
      Event.$emit('addEnum', {typeList:['EMPLOYEE_STATE','EMPLOYEE_TYPE']})
      Event.$on('refreshList', obj => this.query())
    },
    computed: {
      ...mapState({
        sysEnumList: state => state.Enum.enumList
      })
    },
    methods: {
      hideForm () {
        this.queryShow = !this.queryShow
      },
      showForm () {
        this.queryShow = !this.queryShow
      },
      // 查询
      handleSubmit (name) {
        this.$refs[name].validate((valid) => {
          if (valid) {
            this.query();
          }
        })
      },
      query () {
        this.isLoading = !this.isLoading
        EmployeeAPI.query(this.formData).then((res) => {
          this.tableData = []
          let state = res.data.state
          if (state === 1 && res.data.data) {
            let dataList = res.data.data.list
            if (dataList !== null) {
              this.total = res.data.data.total
            }
            this.tableData.push(...dataList)
          }
          this.isLoading = !this.isLoading
        })
      },
      quit(id) {
        //离职
        EmployeeAPI.quit(id,this.query)
      },
      handleReset (name) {
        this.$refs[name].resetFields()
      },
      changePageNo (value) {
        this.formData.pageNo = value
        this.query()
      },
      changePageSize (value) {
        this.formData.pageNo = 1
        this.formData.pageNumber = value
        this.query()
      },

      add () {
        Event.$emit('addPage', {
          title: '新增员工',
          router: 'addEmployee',
          checked: true
        })
      },
      modify (params) {
        Event.$emit('addPage', {
          title: '修改员工',
          router: 'modifyEmployee',
          checked: true,
          paramId: params
        })
      }
    },
    components: {
    }
  }
</script>



