<template>
  <div>
    <div class="form-box-content">
      <div class="module-title-wrap">基本信息</div>
      <fdcform ref="formValidate" :model="formValidate" :rules="ruleValidate" :label-width="100">
        <row type="flex" justify="space-around">
          <fdccol span="7">
            <formitem label="省" prop="provinceName">
              <fdcinput v-model="formValidate.provinceName" placeholder=""></fdcinput>
            </formitem>
          </fdccol>
          <!--显示用-->
          <fdccol span="7"></fdccol>
          <fdccol span="7"></fdccol>
        </row>
        <row type="flex" justify="space-around">
          <fdccol span="7">
            <formitem label="市" prop="cityName">
              <fdcinput v-model="formValidate.cityName" placeholder=""></fdcinput>
            </formitem>
          </fdccol>
          <!--显示用-->
          <fdccol span="7"></fdccol>
          <fdccol span="7"></fdccol>
        </row>
        <row type="flex" justify="space-around">
          <fdccol span="7">
            <formitem label="区" prop="districtName">
              <fdcinput v-model="formValidate.districtName" placeholder=""></fdcinput>
            </formitem>
          </fdccol>
          <!--显示用-->
          <fdccol span="7"></fdccol>
          <fdccol span="7"></fdccol>
        </row>
        <row type="flex" justify="space-around">
          <fdccol span="23">
            <formitem label="匹配方式" prop="addressMatchMode">
              <Radiogroup v-model="formValidate.addressMatchMode">
                <Radio v-for="item in enumSelect['ADDRESS_MATCH_MODE']" :label="item.enumCode" :key="item.enumCode">
                  {{ item.enumValue }}
                </Radio>
              </Radiogroup>
            </formitem>
          </fdccol>
        </row>
        <row type="flex" justify="space-around">
          <fdccol span="15">
            <formitem label="详细地址" prop="addressKeywords">
              <fdcinput v-model="formValidate.addressKeywords" type="textarea" placeholder="" @on-change="clearPath"></fdcinput>
            </formitem>
          </fdccol>
          <fdccol span="7"></fdccol>
        </row>
        <row type="flex" justify="space-around">
          <fdccol span="23">
            <formitem>
              <!--<label>关键词：<input v-model="keyword"></label>-->
              <!--<label>地区：<input v-model="location"></label>-->
              <baidu-map class="map" ak="yF73Le6GB25SEhuHpnf9aFc62mG4oXo7" :center="center" :zoom="15"
                         :scroll-wheel-zoom="true"
                         @moving="syncCenterAndZoom"
                         @moveend="syncCenterAndZoom"
                         @zoomend="syncCenterAndZoom">
                <bm-local-search :keyword="formValidate.addressKeywords" :auto-viewport="true" :panel="false" :location="formValidate.cityName"
                                 :selectFirstResult="true"/>
                <bm-polygon v-if="formValidate2.mapRangeType === 2" :path="polygonPath" stroke-color="blue"
                            :stroke-opacity="0.5" :stroke-weight="5"
                            :editing="false"/>
                <bm-circle v-if="formValidate2.mapRangeType !== 2" :center="circlePath.center" :radius="radius"
                           stroke-color="red"
                           :stroke-opacity="0.5" :stroke-weight="5"
                           :editing="false"/>
              </baidu-map>
            </formitem>
          </fdccol>
        </row>
        <row type="flex" justify="space-around">
          <fdccol span="7">
            <formitem>
              <fdcbutton type="primary" @click="handlerSubmit('formValidate')">提交</fdcbutton>
              <fdcbutton type="ghost" @click="cancel('mapParseTest')" class="buttonClass">取消</fdcbutton>
            </formitem>
          </fdccol>
        </row>
        <div class="module-title-wrap">查询结果</div>
        <row type="flex" justify="space-around">
          <fdccol span="7">
            <formitem label="区域" prop="areaName">
              <fdcinput v-model="formValidate2.areaName" placeholder="" readonly></fdcinput>
            </formitem>
          </fdccol>
          <fdccol span="7">
            <formitem label="网点" prop="depotName">
              <fdcinput v-model="formValidate2.depotName" placeholder="" readonly></fdcinput>
            </formitem>
          </fdccol>
          <fdccol span="7">
            <formitem label="简码" prop="shortCode">
              <fdcinput v-model="formValidate2.shortCode" placeholder="" readonly></fdcinput>
            </formitem>
          </fdccol>
        </row>
        <row type="flex" justify="space-around">
          <fdccol span="7">
            <formitem label="用时" prop="costTime">
              <fdcinput v-model="formValidate2.costTime" placeholder="" readonly></fdcinput>
            </formitem>
          </fdccol>
          <!--显示用-->
          <fdccol span="7"></fdccol>
          <fdccol span="7"></fdccol>
        </row>
      </fdcform>
    </div>
  </div>
</template>

<script>
  import BaiduMap from 'vue-baidu-map/components/Map/Map.vue'
  import BmLocalSearch from 'vue-baidu-map/components/search/LocalSearch'
  import BmPolygon from 'vue-baidu-map/components/overlays/Polygon'
  import BmCircle from 'vue-baidu-map/components/overlays/Circle'
  import AreaParseTestAPI from './api/AreaParseTestAPI'
  import {mapState} from 'vuex'
  import Event from '@/utility/Event.js'

  export default {
    name: "areaParseTest",
    data() {
      return {
        ruleValidate: {
          provinceName: [{
            required: true,
            type: 'string',
            message: '请输入省',
            trigger: 'blur'
          }],
          cityName: [{
            required: true,
            type: 'string',
            message: '请输入市',
            trigger: 'blur'
          }],
          districtName: [{
            required: true,
            type: 'string',
            message: '请输入区',
            trigger: 'blur'
          }],
          addressKeywords: [{
            required: true,
            type: 'string',
            message: '请输入详细地址',
            trigger: 'blur'
          }],
        },
        formValidate: {
          provinceName: '',
          cityName: '',
          districtName: '',
          addressMatchMode: 2,
          addressKeywords: ''
        },
        formValidate2: {
          areaName: '',
          depotName: '',
          shortCode: '',
          costTime: '',
          mapRangeType: ''
        },
        center: {
          lng: 114.431362,
          lat: 30.469663
        },
        location: '武汉',
        keyword: '光谷智慧园26栋',
        polygonPath: [],
        circlePath: {
          center: {
            lng: 0,
            lat: 0
          }
        },
        radius: 500,
      }
    },
    components: {
      BaiduMap, BmLocalSearch, BmPolygon, BmCircle
    },
    computed: {
      ...mapState({
        enumSelect: state => state.Enum.enumSelect
      })
    },
    methods: {
      //移动地图获取地图中心
      syncCenterAndZoom(e) {
        this.center = e.target.getCenter();
        this.zoom = e.target.getZoom();
      },
      test() {
        AreaParseTestAPI.test(this.formValidate).then((res) => {
          if (res.data.state === 1) {
            this.formValidate2 = res.data.data;
            //画出区域范围
            this.setMapRange(res.data.data.mapRangeType, res.data.data.mapRegionalCoordinates);
            this.$Notice.success({
              title: '解析成功',
            })
          } else {
            this.formValidate2 = [];
            this.$Notice.error({
              title: '解析失败',
              desc: res.data.msg
            })
          }
        })
      },
      //画出地图范围
      setMapRange(type, data) {
        //获取地图中心点
        console.log(type + "|" + data);
        let poiStr = data.split(";")
        let poi = poiStr[0].replace(/\(/g, "").replace(/\)/g, "").split(",")
        this.center.lng = poi[0];
        this.center.lat = poi[1];
        if (type === 1) {  //圆形
          this.circlePath.center.lng = poi[0];
          this.circlePath.center.lat = poi[1];
          this.radius = poiStr[1];
        } else if (type === 2) {  //多边形
          poiStr.forEach(p => {
            if (p !== "") {  //有个空，这个需要排除掉
              let p2 = p.replace(/\(/g, "").replace(/\)/g, "").split(",")
              let point = {lng: p2[0], lat: p2[1]}
              this.polygonPath.push(point)
            }
          });
        }
      },
      handlerSubmit(name) {
        this.$refs[name].validate((valid) => {
          if (valid) {
            this.test()
          }
        })
      },
      cancel(router) {
        Event.$emit('closePage', router)
      },
      clearPath(){
        this.polygonPath = [];
        this.radius = 0;
      }
    },
    mounted() {
      const condition = ['ADDRESS_MATCH_MODE']
      Event.$emit('addEnum', {typeList: condition})
    }
  }
</script>

<style scoped>
  .map {
    width: 1000px;
    height: 500px;
  }
</style>




