import HttpUtil from '@/common/HttpUtil'

const DoPackageAPI = {
  query (params) {
    return HttpUtil.post('/api/deliveryOrder/doPackage/query', params)
  },
  modify (params) {
    return HttpUtil.post('/api/deliveryOrder/doOrder/modify', params)
  },
  getById (id) {
    return HttpUtil.post('/api/deliveryOrder/doOrder/getById', {id: id})
  }
}
export default DoPackageAPI



