<style scoped>
</style>
<template>
  <div>
    <div class="form-box-content">
      <div class="module-title-wrap">基本信息</div>
      <fdcform ref="formValidate" :model="formValidate"  :label-width="80">
        <row type="flex" justify="space-around">
          <fdccol span="9">
            <formitem label="枚举类型" prop="enumType"
                      :rules="[{required: true,type: 'string',message: '枚举类型必填',trigger: 'blur'},
                      {max:30,message: '枚举类型超长',trigger: 'blur'},
                      {pattern: /^[A-Z,'\_']+$/,message: '枚举类型格式不正确',trigger: 'blur'}]">
              <fdcinput v-model="formValidate.enumType" placeholder="请输入枚举类型" >
              </fdcinput>
            </formitem>
            <formitem label="修改前枚举类型" prop="oldEnumType" v-if="false">
              <fdcinput v-model="formValidate.oldEnumType">
              </fdcinput>
            </formitem>
          </fdccol>
          <fdccol span="13">
          </fdccol>
        </row>
        <div v-for="(item, index) in formValidate.values">
          <row type="flex" justify="space-around">
            <fdccol span="9">
              <formitem :key="'type'+index"
                        :label='枚举类型' :prop="'values.'+index+'.enumType'" v-if="false">
                <fdcinput v-model="item.enumType" type="text">
                </fdcinput>
              </formitem>
              <formitem :key="'code'+index"
                        :label="'枚举代码'" :prop="'values.'+index+'.enumCode'"
                        :rules="[{required: true, pattern: /^[0-9]+$/, message:'枚举代码必须为数字类型', trigger: 'blur'},
                        {max:3,message: '枚举代码超长',trigger: 'blur'}]">
                <fdcinput v-model ="item.enumCode" placeholder="请输入枚举代码">
                </fdcinput>
              </formitem>
            </fdccol>
            <fdccol span="9">
              <formitem :key="'value'+index" :label="'枚举值'" :prop="'values.'+index+'.enumValue'"
                        :rules="[{required: true, message:'枚举值'+'不能为空', trigger: 'blur'},
                        {max:33,message: '枚举值超长',trigger: 'blur'}]">
                <fdcinput v-model="item.enumValue" placeholder="请输入枚举值" >
                </fdcinput>
              </formitem>
            </fdccol>
            <fdccol span="3">
              <fdcbutton type="ghost" @click="handleRemove(index)" class="buttonClass">删除</fdcbutton>
            </fdccol>
          </row>
        </div>
        <row type="flex" justify="space-around">
          <fdccol span = '23'>
            <formitem>
              <fdcbutton type="dashed"  @click="handleAdd">增加</fdcbutton>
            </formitem>
          </fdccol>
        </row>
        <row>
          <fdccol span = '8' offset="8">
            <formitem>
              <fdcbutton type="primary" @click="handleSubmit('formValidate')">提交</fdcbutton>
              <fdcbutton type="ghost" @click="cancel('modifyEnum')" style="margin-left: 8px">取消</fdcbutton>
            </formitem>
          </fdccol>
        </row>
      </fdcform>
    </div>
  </div>
</template>

<script>
import EnumAPI from './api/EnumAPI'
import Event from '@/utility/Event.js'
import {mapState} from 'vuex'

export default {
  name: 'modifyEnum',
  data () {
    return {
      typetemp: '',
      ruleValidate: {
      },
      index: 1,
      formValidate: {
        enumType: '',
        oldEnumType: '',
        values: [
          {
            index: 1,
            id: '',
            enumType: '',
            enumCode: '',
            enumValue: ''
          }
        ]
      }
    }
  },
  computed: {
    ...mapState({
      enumSelect: state => state.Enum.enumSelect
    })
  },
  components: {
  },
  methods: {
    handleSubmit (name) {
      this.$refs[name].validate((valid) => {
        if (valid) {
          this.modify()
        }
      })
    },
    ok() {

    },
    cancel (router) {
      Event.$emit('closePage', router)
    },
    modify () {
      if (this.formValidate.values.length === 0) {
        this.$Notice.error({
          title: '操作失败',
          desc: '至少需要保留一条枚举'
        })
      }
      EnumAPI.modify(this.formValidate).then((res) => {
        if (res.data.state === 1) {
          this.$Notice.success({
            title: '操作成功',
            desc: '修改枚举成功'
          });
          Event.$emit('modifyEnum', this.formValidate.enumType)
          Event.$emit('closePage', 'modifyEnum')
          Event.$emit('refreshList', null)
        } else {
          this.$Notice.error({
            title: '操作失败',
            desc: res.data.msg
          });
        }
      });
    },
    remove (index) {
      this.data.splice(index, 1);
    },
    handleAdd () {
      this.index++;
      this.formValidate.values.push({
        index: this.index,
        enumType: this.typetemp,
        enumCode: 1,
        enumValue: '',
        status: 1
      });
    },
    handleRemove (index) {
      this.formValidate.values.splice(index, 1)
    },
    query () {
      EnumAPI.getByType(this.$route.query.paramId).then((res) => {
        if (res.data) {
          this.formValidate = res.data
          for (let i = 0; i < this.formValidate.values.length; i++) {
            this.formValidate.values[i].index = i
            this.formValidate.values[i].enumCode=this.formValidate.values[i].enumCode.toString()
          }
        } else {
          this.$Notice.error({
            title: '操作失败',
            desc: res.data.msg
          })
        }
      })
    }
  },
  mounted () {
    this.query()
  },
  watch: {
    'formValidate.enumType': function (n, o) {
      this.typetemp = n
      let items = this.formValidate.values
      for (let i = 0; i < items.length;i++ ) {
        items[i].enumType = n
      }
    }
  }
}
</script>



