<template>
  <div>
    <div class="page-header-box">
      <div class="header-msg-box">
        <p>系统日志监控</p>
      </div>
      <div class="header-btn-box">
        <fdcbutton v-show="formshow" @click="hideForm" icon="eye-disabled">隐藏</fdcbutton>
        <fdcbutton v-show="!formshow" @click="showForm" icon="eye">显示</fdcbutton>
      </div>
    </div>
    <div class="form-box" v-show="formshow">
      <fdcform ref="formData" inline :model="formData"  :label-width="80" class="search-form">
            <formitem label="创建时间" prop="createdTimeStart">
                <Datepicker type="date" format="yyyy-MM-dd" :clearable='false' @on-change="formData.createdTimeStart=$event" v-model="formData.createdTimeStart" placeholder="Select date" class="search-input"></Datepicker>
            </formitem>
            <formitem label="日志级别" prop="logLevel">
              <enumSelect @onChange="selectLogLevel"  enum-type="LOG_LEVEL" class="search-input" clearable></enumSelect>
            </formitem>
            <formitem label="类名" prop="className">
              <fdcinput v-model="formData.className" placeholder="请输入类名" class="search-input">
              </fdcinput>
            </formitem>
            <formitem label="业务单号" prop="billCode">
              <fdcinput v-model="formData.billCode" placeholder="请输入业务单号" class="search-input">
              </fdcinput>
              <fdcbutton type="primary" class = "search-submit" @click="handleSubmit('formData')">提交</fdcbutton>
              <fdcbutton type="ghost" class = "search-reset" @click="handleReset('formData')">重置</fdcbutton>
            </formitem>
      </fdcform>
    </div>
    <div class="table-box">
      <fdctable border size="small" :loading="isLoading" :data="tableData" :columns="tableColumns" stripe></fdctable>
      <div class="page-box">
        <Page v-show="pageShow" placement="top" :total="total" :page-size="formData.pageNumber" :current.sync="formData.pageNo" @on-change="changePage" show-total show-elevator show-sizer></Page>
      </div>
    </div>
  </div>
</template>

<script>
import Event from '@/utility/Event.js'
import SysLogAPI from './api/SysLogAPI'
import {mapState} from 'vuex'
import EnumSelect from '@/components/enumSelect.vue'

export default {
  name: 'sysLog',
  data () {
    return {
      clearable: false,
      formshow: true,
      isLoading: false,
      pageShow: false,
      total: 0,
      formData: {
        createdTimeStart: '',
        className: '',
        billCode: '',
        logLevel: '',
        pageNo: 1,
        pageNumber: 10,
        createdTimeStart:''
      },
      tableData: [],
      tableColumns: [
      {
        title: ' ',
        key: 'number',
        type: 'index',
        align: 'center',
        width:'4%'
      },
      {
        title: '系统名',
        key: 'systemName',
        align: 'center'
      },
      {
        title: '类名',
        key: 'className',
        align: 'center',
        ellipsis:true
      },
      {
        title: '方法名',
        key: 'methodName',
        align: 'center'
      },
       {
        title: '日志级别',
        key: 'logLevel',
         align: 'center',
         render: (h, params) => {
           return h('span', {}, this.sysLogLevelList['LOG_LEVEL_' + params.row.logLevel])
         },
         width:'6%'
      },
       {
        title: '业务单号',
        key: 'billCode',
         align: 'center'
      },
       {
        title: '来源IP',
        key: 'srcIp',
         align: 'center'
      },
       {
        title: '入参',
        key: 'paramIn',
         align: 'center',
         ellipsis:true,
         render: (h, params) => {
           return h('div', [h('span', {
             attrs: {
               title: params.row.paramIn.toString()
             }
           },params.row.paramIn.toString())])
         }
      },
      {
        title: '出参',
        key: 'paramOut',
        align: 'center',
        ellipsis:true,
        render: (h, params) => {
          return h('div', [h('span', {
            attrs: {
              title: params.row.paramOut.toString()
            }
          },params.row.paramOut.toString())])
        }
      },
      {
        title: '日志内容',
        key: 'content',
        align: 'center',
        ellipsis:true
      },
      {
        title: '处理时间',
        key: 'costTime',
        align: 'center'
      },
      {
        title: '创建时间',
        key: 'createdTime',
        align: 'center',
        width:'12%'
      }
      ]
    }
  },
  computed: {
    ...mapState({
      sysLogLevelList: state => state.Enum.enumList
    })
  },
  methods: {
    hideForm () {
      this.formshow = !this.formshow;
    },
    showForm () {
      this.formshow = !this.formshow;
    },
    // 查询
    handleSubmit (name) {
      this.$refs[name].validate((valid) => {
        if (valid) {
          this.query();
        }
      })
    },
    changePage (value) {
      this.formData.pageNo = value
      this.query()
    },
    changePageSize (value) {
      this.formData.pageNo = 1
      this.formData.pageNumber = value
      this.query()
    },
    selectLogLevel(value){
      this.formData.logLevel = value;
    },
    query() {
      this.isLoading = !this.isLoading;
      SysLogAPI.query(this.formData).then((res)=>{
        this.tableData = [];
        let state = res.data.state;
        if (state === 1) {
          let dataList = res.data.data.list;
          if (dataList !== null) {
            this.pageShow = true;
            this.total = res.data.data.total;
          }
          this.tableData.push(...dataList)
        }
        this.isLoading = !this.isLoading;
      });
    },
    handleReset (name) {
      this.$refs[name].resetFields();
    },
    remove (index) {
      this.data.splice(index, 1);
    }
  },
  mounted () {
    Event.$once("refreshList",obj=>this.query());
    Event.$emit('addEnum', {typeList:['LOG_LEVEL']});

    // this.tableHeight = window.innerHeight - 310
    /*var currentDate = new Date(),
      stamp = currentDate.setDate(currentDate.getDate() - 7),//获取前7天的时间戳
      date=new Date(stamp);*/
    this.formData.createdTimeStart = new Date();
  },
  components: {
    enumSelect:EnumSelect
  }
}
</script>



