<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>
  html,body,#app,.login-box{
    height: 100%;
  }
  #app{
    position: relative;
  }
  .login-box{
    background: #f3f6fc url("assets/bg.png") 100% 62.5% ;
  }
  .uiBase-input-icon{
    width: 78px;
    height: 30px;
  }
  .uiBase-icon-ios-clock-outline:before{
    position: absolute;
    top: 1px;
    right: 1px;
    width: 100%;
    height: 100%;
    content: '';
    background: url("assets/code.png");
  }
</style>
<style scoped>
  .login{
    width: 438px;
    height: 580px;
    position: absolute;
    left: 50%;
    top: 50%;
    margin-left: -219px;
    margin-top: -290px;
    padding: 80px 50px 0;
    border-radius: 10px;
    background-color: #fff;
    text-align: center;
  }
  .login-logo{
    padding-bottom: 60px;
  }
  .login-logo .version{
    margin-left: 14px;
    color: #3399ff;
    font-size: 18px;
  }
  .blue-link{
    color: #3399ff;
    font-size: 12px;
    text-align: left;
  }
</style>
<template>
  <div class="login-box">
    <div class="login">
      <div class="login-logo">
        <img src="./assets/logo.png" alt="">
        <span class="version">V1.0</span>
      </div>
      <fdcform ref="loginForm" :model="formInline" :rules="ruleInline" @submit.native.prevent
               @keyup.enter.native="handleSubmit('loginForm')">
        <formitem prop="username">
          <fdcinput type="text" v-model="formInline.username" :placeholder="i18nMap['sys_login_userName']">
            <Icon type="person" slot="prepend"></Icon>
          </fdcinput>
        </formitem>

        <formitem prop="password">
          <fdcinput type="password" v-model="formInline.password" :placeholder="i18nMap['sys_login_password']">
            <Icon type="unlocked" slot="prepend"></Icon>
          </fdcinput>
        </formitem>
        <formitem prop="language">
          <fdcselect v-model="formInline.language">
            <fdcoption v-for="option in languages" :key="option.value" :value="option.value">{{option.text}}</fdcoption>
          </fdcselect>
        </formitem>

        <formitem prop="code">
          <fdcinput v-model="formInline.code" v-show="showCode" icon="ios-clock-outline" placeholder="请输入验证码..."></fdcinput>
        </formitem>

        <formitem>
          <checkboxgroup>
            <checkbox v-model="formInline.remberAccount" :label="i18nMap['sys_login_rember_account']"></checkbox>
            <checkbox v-model="formInline.remberPassword" :label="i18nMap['sys_login_rember_password']"></checkbox>
          </checkboxgroup>
        </formitem>

        <formitem style="background-color: #1890ff">
          <fdcbutton long type="primary" @click="handleSubmit('loginForm')">{{i18nMap['sys_login']}}</fdcbutton>
        </formitem>
      </fdcform>
      <div class="blue-link">{{i18nMap['sys_login_forget']}}</div>
    </div>
  </div>
</template>

<script>
  import I18nAPI from '../system/sysMagnagement/i18n/api/I18nAPI'
  import '../../common/security'
  import i18n from '../../../store/modules/i18n'
  import {setCookie,getCookie } from '../../common/cookie'
  import {AUTH_TOKEN} from '../../common/constants'
  import {mapState,mapActions} from 'vuex'

  export default {
    name: 'login',
    data() {
      return {
        showCode:false,
        languages:[{value:'zh_CN',text:'简体中文'}],
        i18nCodes:['sys_login_rember_account','sys_login','sys_login_forget','sys_login_rember_password','sys_login_userName','sys_login_password'],
        formInline: {
          language:'zh_CN',
          username: '',
          password: '',
          code : '',
          remberAccount:false,
          remberPassword:false
        }
      }
    },
    computed: {
      ...mapState({
        i18nMap : state => state.I18n.i18nMap,
        rsa_exponent : state => state.User.rsa_exponent,
        rsa_modulus : state => state.User.rsa_modulus,
        rsa_passkey : state => state.User.rsa_passkey,
        //permissionCodeList : state => state.User.permission,
        //token : state => state.User.token
      }),
      ruleInline(){
        let ruleInline = {
          username: [{
            required: true,
            message:this.i18nMap['sys_login_userName'],
            trigger: 'blur'
          }],
          password: [{
            required: true,
            message: this.i18nMap['sys_login_password'],
            trigger: 'blur'
          }],
          code: [{
            message: this.i18nMap['sys_login_code'],
            trigger: 'blur'
          }]
        };
        return ruleInline;
      }
    },
    mounted:function (){
      let userName = localStorage.getItem('jd.login.username');
      let password = localStorage.getItem('jd.login.password');
      if(userName){
        this.formInline.username = userName;
        this.formInline.remberAccount = true;
      }
      if(password){
        this.formInline.password = password;
        this.formInline.remberPassword = true;
      }
      /*I18nAPI.getI18nLanguage().then(res => {
        if(res.data.state ==1){
          let callbackData = res.data.data;
          for (let i = 0; i < callbackData.length; i++) {
            let language = callbackData[i];
            this.languages.push({text:language.name,value:language.code});
          }
        }
      });*/

      //this.fetchI18n(this.i18nCodes);
      this.getRSAPublicKey(this.rsa_passkey);

    },
    watch:{
      'formInline.language':{
        handler:function (val,oldVal) {
          this.switchLang(val);
          this.fetchI18n(this.i18nCodes);
        }
      }
    },
    methods:{
      ...mapActions([
        'fetchI18n',
        'switchLang',
        'getRSAPublicKey',
        'login',
        'generateRouters',
        'generateMenu'
      ]),
      handleSubmit(name) {
        this.$refs[name].validate((valid) => {
          if (valid) {
              let key = parseInt(Math.random()*100000);
              let password = this.formInline.password.split("").reverse().join("");
              password = this.encryptRSA(password);
              let userLogin = {
                  'username':this.formInline.username,
                  'password':this.rsa_passkey+""+key+""+password,
                  'language':this.formInline.language,
                  'key':key
              };
              if(this.formInline.remberAccount){
                  this.remberAccount();
              }
              if(this.formInline.remberPassword){
                  this.remberPassword();
              }
              this.login(userLogin).then(res => {
                let that = this;
                if (res.data.state === 1) {
                  //生成动态路由
                   this.generateRouters(res.data.data.permissionCodeList).then(() => {
                     that.generateMenu();
                   });
                   this.$router.push({ path: '/index'});
                }else{
                  this.$Message.error(res.data.msg);
                }
              });
          }
        })
      },
      encryptRSA (password) {
        var key = window.RSAUtils.getKeyPair(this.rsa_exponent, '', this.rsa_modulus);
        return window.RSAUtils.encryptedString(key, password);
      },
      remberAccount(){
        localStorage.setItem("jd.login.username",this.formInline.username);
      },
      remberPassword(){
        localStorage.setItem("jd.login.password",this.formInline.password);
      }
    }
  }
</script>



