<template>
  <div class="location-con" v-if="typeof(locationMsg) !== 'undefined' && locationMsg !== 'undefined'">
    <span style="font-size: 12px">
      <icon type="location" size="22"></icon>
      {{locationMsg}}
    </span>
  </div>
</template>
<script>
import {mapState, mapActions} from 'vuex'

export default {
  name: "headerLocation",
  data() {
    return {}
  },
  computed: {
    ...mapState({
      locationMsg: state => state.User.location,
    }),
  },
  methods: {

  }
}
</script>

<style scoped>

</style>



