<template>
  <div>
    <fdcselect v-model="selectValue" :disabled="disabled" :clearable="clearable"  @on-change="_onChange">
      <fdcoption v-for="item in enumModel" :value="item.enumCode" :key="item.enumCode">{{ item.enumValue }}</fdcoption>
    </fdcselect>
  </div>
</template>

<script>
import EnumAPI from '../page/system/sysMagnagement/enum/api/EnumAPI'
export default {
  name: 'EnumSelect',
  data () {
    return {
      enumModel: [],
      selectValue: ''
    }
  },
  props: {
    // 设置为默认选中
    enumCode: {
      type: String,
      default: ''
    },
    // 获取对应的枚举类型
    enumType: {
      type: String,
      required: true
    },
    size: {
      type: String,
      default: 'middle'
    },
    disabled: {
      type: Boolean,
      default: false
    },
    clearable: {
      type: Boolean,
      default: false
    }
  },
  mounted () {
    let that = this
    EnumAPI.findEnumByType({enumTypeList: [this.enumType]}).then(response => {
      if (response.data.state === 1) {
        let list = response.data.data
        that.selectValue = parseInt(that.enumCode)
        that.enumModel.push(...list)
      }
    });
  },
  computed: {
  },
  methods: {
    _onChange () {
      this.$emit('onChange', this.selectValue)
    }
  },
  watch: {

  }

}
</script>



