import HttpUtil from '@/common/HttpUtil';

const LocationAPI = {
  query(params) {
    return HttpUtil.post('/api/basic/location/query', params, false);
  },
  insert(params, callback) {
    return HttpUtil.post('/api/basic/location/insert', params, false, callback);
  },
  getById(params) {
    return HttpUtil.post('/api/basic/location/get', params)
  },
  modify(params, callback) {
    return HttpUtil.post('/api/basic/location/modify', params, false, callback);
  },
  disableOrEnable (params, callback) {
    return HttpUtil.post('/api/basic/location/disableOrEnable', params, true, callback)
  },
  queryBindLocations(params){
    return HttpUtil.post('/api/basic/location/queryBindLocations', params, false)
  },
  changeLocation(params) {
    return HttpUtil.post('/api/basic/location/changeLocation', params, false);
  },
  getLocationListByCityId(params){
    return HttpUtil.post('/api/basic/location/getLocationListByCityId', params)
  }
};

export default LocationAPI



