import HttpUtil from '@/common/HttpUtil'
const PermissionAPI = {
  insert (params) {
    return HttpUtil.postJSON('/api/system/permission/insert', params)
  },
  getById (id) {
    return HttpUtil.post('/api/system/permission/getById', {id: id})
  },
  modify (params) {
    return HttpUtil.post('/api/system/permission/update', params)
  },
  query (params) {
    return HttpUtil.post('/api/system/permission/query', params)
  },
  findListByModule (params) {
    return HttpUtil.post('/api/system/permission/findListByModule', {module: params})
  },
  delete (param) {
    return HttpUtil.post('/api/system/permission/del', {permissionId: param.id})
  }

}
export default PermissionAPI



