<!-- Add "scoped" attribute to limit CSS to this component only -->
<template>
  <div>
    <div class="page-header-box">
      <div class="header-msg-box">
        <p>用户管理</p>
      </div>
      <div class="header-btn-box">
        <fdcbutton v-show="queryShow" @click="hideForm" icon="eye-disabled">隐藏</fdcbutton>
        <fdcbutton v-show="!queryShow" @click="showForm" icon="eye">显示</fdcbutton>
        <fdcbutton icon="android-add" @click="add">新增</fdcbutton>
      </div>
    </div>
    <div class="form-box" v-show="queryShow">
      <fdcform ref="form" inline :model="formData" :label-width="80" class="search-form" @keyup.enter.native="handleSubmit('form')" >
        <formitem label="姓名" prop="name">
          <fdcinput v-model="formData.name" placeholder="请输入用户姓名" class="search-input"></fdcinput>
        </formitem>
        <formitem label="登录名" prop="loginName">
          <fdcinput v-model="formData.loginName" placeholder="请输入登录名" class="search-input"></fdcinput>
          <fdcbutton type="primary" @click="handleSubmit('form')" class="search-submit">查询</fdcbutton>
          <fdcbutton type="ghost" @click="handleReset('form')" class="search-reset">重置</fdcbutton>
        </formitem>
      </fdcform>
    </div>
    <div class="table-box">
      <fdctable ref="table" border size="small" :loading="isLoading" :data="tableData" :columns="tableColumns" stripe></fdctable>
      <div class="page-box">
        <Page placement="top" :page-size="formData.pageNumber" :total="total" :current.sync="formData.pageNo"  @on-change="changePageNo" @on-page-size-change="changePageSize" show-total show-elevator show-sizer></Page>
      </div>
    </div>
  </div>
</template>

<script>
import UserAPI from './api/UserAPI'
import Event from '@/utility/Event.js'
import { mapActions, mapState } from 'vuex'

export default {
  name: 'user',
  data () {
    return {
      total: 0,
      queryShow: true,
      formData: {
        name: '',
        pageNumber: 10,
        pageNo: 1
      },
      isLoading: false,
      pageShow: false,
      tableColumns: [
        {
          title: ' ',
          type: 'index2',
          width: 60,
          align: 'center',
          render: (h, params) => {
            return h('span', params.index + (this.formData.pageNo - 1) * this.formData.pageNumber + 1)
          }
        },
        {
          title: '登录名',
          key: 'loginName',
          align: 'center',
          width: 160
        },
        {
          title: '姓名',
          key: 'name',
          align: 'center',
          width: 160
        },
        {
          title: '身份证',
          key: 'idCard',
          align: 'center',
          ellipsis: true,
          width: 160
        },
        {
          title: '邮箱',
          key: 'email',
          align: 'center',
          width: 160
        },
        {
          title: '联系方式',
          key: 'contactInfo',
          align: 'center',
          width: 160
        },
        {
          title: '最后登陆时间',
          key: 'lastLoginTime',
          align: 'center',
          width: 160
        },
        {
          title: '所属部门',
          key: 'department',
          align: 'center',
          width: 160
        },
        {
          title: '所属公司',
          key: 'companyName',
          align: 'center',
          width: 160
        },
        {
          title: '状态',
          key: 'isDeleted',
          width: 100,
          align: 'center',
          render: (h, params) => {
            return h('span', {}, this.sysEnumList['YES_NO_' + params.row.isDeleted])
          }
        },
        {
          title: '操作',
          key: 'action',
          width: 100,
          align: 'left',
          render: (h, params) => {
            const row = params.row;
            const type = row.isDeleted === 0 ? 'locked' : 'unlocked'
            const color = row.isDeleted === 0 ? 'red' : '#0d96ff'
            const content = row.isDeleted === 0 ? '禁用' : '启用'
            return h('div', [
              h('tooltip', {
                props: {
                  content: '绑定作业点',
                  placement: 'top'
                }
              }, [h('span', {
                on: {
                  click: () => {
                    this.bindLocation(row.id)
                  }
                }
              }, [h('icon', {
                props: {
                  type: 'location',
                  size: 22,
                  color: '#0d96ff'
                },
                style: {
                  marginRight: '5px'
                }
              }, '绑定作业点')])
              ]),
              h('tooltip', {
                props: {
                  content: '修改',
                  placement: 'top'
                }
              }, [h('span', {
                on: {
                  click: () => {
                    this.modify(row.id)
                  }
                }
              }, [h('icon', {
                props: {
                  type: 'edit',
                  size: 20,
                  color: '#0d96ff'
                },
                style: {
                  marginRight: '5px'
                }
              }, '修改')])
              ]),
              h('tooltip', {
                props: {
                  content: content,
                  placement: 'top'
                }
              }, [
                h('span', {
                  on: {
                    click: () => {
                      if (row.isDeleted === 0) {
                        this.$Modal.confirm({
                          title: '禁用',
                          content: '<p>确定禁用？</p>',
                          onOk: () => {
                            this.disableOrEnable(row.id, 1)
                          },
                          onCancel: () => {}
                        })
                      } else {
                        this.disableOrEnable(row.id, 0)
                      }
                    }
                  }
                }, [h('icon', {
                  props: {
                    type: type,
                    size: 20,
                    color: color
                  }
                }, '删除')])
              ])
            ])
          }
        }
      ],
      tableData: []
    }
  },
  mounted () {
    Event.$emit('addEnum', {typeList:['YES_NO']})
    Event.$on('refreshList', obj => this.query())
  },
  computed: {
    ...mapState({
      sysEnumList: state => state.Enum.enumList
    })
  },
  methods: {
    hideForm () {
      this.queryShow = !this.queryShow
    },
    showForm () {
      this.queryShow = !this.queryShow
    },
    // 查询
    handleSubmit (name) {
      this.$refs[name].validate((valid) => {
        if (valid) {
          this.query();
        }
      })
    },
    query () {
      this.isLoading = !this.isLoading
      UserAPI.query(this.formData).then((res) => {
        this.tableData = []
        let state = res.data.state
        if (state === 1) {
          let dataList = res.data.data.list
          if (dataList !== null) {
            this.pageShow = true
            this.total = res.data.data.total
          }
          this.tableData.push(...dataList)
        }
        this.isLoading = !this.isLoading
      })
    },
    handleReset (name) {
      this.$refs[name].resetFields()
    },
    disableOrEnable (id, isDeleted) {
      const user = {}
      user.id = id
      user.isDeleted = isDeleted
      UserAPI.disableOrEnable(user, this.query)
    },
    changePageNo (value) {
      this.formData.pageNo = value
      this.query()
    },
    changePageSize (value) {
      this.formData.pageNo = 1
      this.formData.pageNumber = value
      this.query()
    },
    showModal () {
      this.modal = !this.modal
    },
    add () {
      Event.$emit('addPage', {
        title: '新增用户',
        router: 'addUser',
        checked: true
      })
    },
    modify (params) {
      Event.$emit('addPage', {
        title: '修改用户',
        router: 'modifyUser',
        checked: true,
        paramId: params
      })
    },
    bindLocation (params) {
      Event.$emit('addPage', {
        title: '绑定作业点',
        router: 'userLocation',
        checked: true,
        paramId: params
      })
    },

  },
  components: {
  }
}
</script>



