<template>
  <div>
    <div class="page-header-box">
      <div class="header-msg-box">
        <p>系统参数管理</p>
      </div>
      <div class="header-btn-box">
        <fdcbutton v-show="formshow" @click="hideForm" icon="eye-disabled">隐藏</fdcbutton>
        <fdcbutton v-show="!formshow" @click="showForm" icon="eye">显示</fdcbutton>
        <fdcbutton icon="android-add" @click="addSysParameter">新增</fdcbutton>
      </div>
    </div>
    <div class="form-box" v-show="formshow">
      <fdcform ref="formData" inline :model="formData"  :label-width="80" class="search-form">
            <formitem label="属性名称" prop="name">
              <fdcinput v-model="formData.name" placeholder="请输入属性名称" class="search-input">
                <!--<fdcbutton slot="append" icon="ios-search"></fdcbutton>-->
              </fdcinput>
            </formitem>

            <formitem label="属性标识" prop="code">
              <fdcinput v-model="formData.code" placeholder="请输入属性标识" class="search-input">
                <!--<fdcbutton slot="append" icon="ios-search"></fdcbutton>-->
              </fdcinput>
              <fdcbutton type="primary" class = "search-submit" @click="handleSubmit('formData')">查询</fdcbutton>
              <fdcbutton type="ghost"  class = "search-reset" @click="handleReset('formData')">重置</fdcbutton>
            </formitem>
      </fdcform>
    </div>
    <div class="table-box">
      <fdctable border size="small" :loading="isLoading" :data="tableData" :columns="tableColumns" stripe></fdctable>
      <div class="page-box">
        <Page v-show="pageShow" placement="top" :total="total" :page-size="formData.pageNumber" :current.sync="formData.pageNo" @on-change="changePage" @on-page-size-change="changePageSize" show-total show-elevator show-sizer></Page>
      </div>
    </div>
  </div>
</template>

<script>
import Event from '@/utility/Event.js'
import SysParameterAPI from './api/SysParameterAPI'
import {mapState} from 'vuex'

export default {
  name: 'sysParameter',
  data () {
    return {
      formshow: true,
      isLoading: false,
      pageShow: false,
      total: 0,
      formData: {
        name: '',
        pageNo: 1,
        pageNumber: 10
      },
      tableData: [],
      tableColumns: [
      {
        title: ' ',
        key: 'number',
        type: 'index',
        align: 'center',
        width:'5%'
      },
      {
        title: '属性名称',
        key: 'name',
        align: 'center'
      },
      {
        title: '属性标识',
        key: 'code',
        align: 'center'
      },
      {
        title: '属性值',
        key: 'parameterValue',
        align: 'center'
      },
       {
        title: '备注',
        key: 'remark',
         align: 'center'
      },
       {
        title: '创建时间',
        key: 'createdTime',
         align: 'center'
      },
       {
        title: '创建人',
        key: 'creator',
         align: 'center'
      },
       {
        title: '修改时间',
        key: 'modifiedTime',
         align: 'center'
      },
      {
        title: '修改人',
        key: 'modifier',
        align: 'center'
      },
      {
        title: '操作',
        key: 'action',
        align: 'left',
        width:'5%',
        render: (h, params) => {
          return h('div', [
            h('tooltip', {
                props: {
                  content: '修改',
                  placement: 'top'
                }
              },
            [h('span', {
              on: {
                click: () => {
                  this.modifyParameter(params.row.id)
                }
              }
            }, [h('icon', {
              props: {
                type: 'edit',
                size: 20,
                color: '#0d96ff'
              },
              style: {
                marginRight: '5px'
              }
            }, '修改')])])
          ])
        }
      }
      ]
    }
  },
  computed: {
    ...mapState({
      tagList:state=>state.tagList
    })
  },
  methods: {
    hideForm () {
      this.formshow = !this.formshow;
    },
    showForm () {
      this.formshow = !this.formshow;
    },
    // 查询
    handleSubmit (name) {
      this.$refs[name].validate((valid) => {
        if (valid) {
          this.query();
        }
      })
    },
    changePage (value) {
      this.formData.pageNo = value
      this.query()
    },
    changePageSize (value) {
      this.formData.pageNo = 1
      this.formData.pageNumber = value
      this.query()
    },
    query() {
      this.isLoading = !this.isLoading;

      SysParameterAPI.query(this.formData).then((res)=>{
        this.tableData = [];
        let state = res.data.state;
        if (state === 1) {
          let dataList = res.data.data.list;
          if (dataList !== null) {
            this.pageShow = true;
            this.total = res.data.data.total;
          }
          this.tableData.push(...dataList)
        }
        this.isLoading = !this.isLoading;
      });
    },
    handleReset (name) {
      this.$refs[name].resetFields();
    },
    remove (index) {
      this.data.splice(index, 1);
    },
    addSysParameter () {
      // this.modal1 =
      Event.$emit('addPage',{
        title: "新增系统参数",
        router: "addSysParameter",
        checked: true
      });
    },
    modifyParameter(params){
      Event.$emit('addPage',{
        title: "修改系统参数",
        router: "modifySysParameter",
        checked: true,
        paramId:params
      });
    }
  },
  mounted () {
    Event.$once('refreshList', obj => this.query())
  },
  components: {
  }
}
</script>



