<style scoped>
  .top-tags-box-collapsed {
    padding:5px 10px 5px 78px;
  }
</style>
<template>
  <div class="top-tags-box" :class="tagsClass">
    <div class="top-tags-list" >
      <tag checkable v-for="(item,index) in tagList" :color="$route.name==item.router?'blue':''"
           :closable='item.router=="index"?false:true' type="dot" :name="item.router" :key="index" @on-close="closeTag(item.router)"
           @on-change="selectTag(item.router)">{{item.title}}
      </tag>
    </div>
  </div>
</template>

<script>
import UT from '../utility/utility.js'
import {mapState} from 'vuex'
import Event from '@/utility/Event.js'

export default {
  name: 'tags',
  data () {
    return {
    }
  },
  computed: {
    ...mapState({
      tagList : state => state.tagList,
      isCollapsed: state => state.isCollapsed,
      lastPageObj: state => state.lastPageObj
    }),
    tagsClass () {
      return [
        this.isCollapsed ? 'top-tags-box-collapsed' : ''
      ]
    }
  },
  methods: {
    // 标签切换
    selectTag (router) {
      this.$router.push({
        path: '/' + router
      })
    },
    // 关闭标签
    closeTag (router) {
      //关闭后下一个要激活的页面
      let flag = false
      this.tagList.map((item, index) => {
        if (item.router === this.lastPageObj[0].router) {
          if (router === this.$route.name) {
            flag = true
            this.$router.push({
              path: '/' + this.lastPageObj[0].router
            })
          }
        }
      });
      if(!flag) {
        //关闭当前页面，前面的router已经被关闭
        if (router === this.$route.name) {
          let lastPage = UT.copyArray(this.lastPageObj)
          lastPage.splice(0,1,{
            title: '首页',
            router: 'index'
          })
          if(this.tagList.length === 2) {
            this.$store.commit('setLastPageObj',lastPage)
          }else{
            let len = this.tagList.length;
            for (let i = 1; i < len; i++) {
              if (this.tagList[i].router === router) {
                if (i < (len - 1)) {
                  lastPage.splice(0,1,this.tagList[i + 1])
                } else {
                  lastPage.splice(0,1,this.tagList[i - 1])
                }
                this.$store.commit('setLastPageObj',lastPage)
                break;
              }
            }
          }
          this.$router.push({
            path: '/' + this.lastPageObj[0].router
          })
        }
      }
      this.$store.commit('removeTag', router)
    },
    // 添加标签
    addTag (obj) {
      if(UT.validRouter(obj.router)){
        let temp = UT.copyArray(this.$store.state.tagList)
        if (!UT.checkRouter(obj.router, temp)) {
          temp.push(obj)
          this.$store.commit('changeTag', temp)
        }
        this.$router.push({
          path: '/' + obj.router,
          query: obj
        })
      }else{
        this.$router.push({
          path: '/page404'
        })
      }

    }
  },
  created () {

  },
  mounted () {
    Event.$on('addPage', obj => this.addTag(obj))
    Event.$on('closePage', obj => this.closeTag(obj))
  },
  activated () {
    Event.$once('addPage', obj => this.addTag(obj))
    Event.$once('closePage', obj => this.closeTag(obj))
  }
}
</script>



