import HttpUtil from '@/common/HttpUtil'
const CompanyAPI = {
  query (params) {
    return HttpUtil.post('/api/system/company/query', params, false);
  },
  insert (params, callback) {
    return HttpUtil.post('/api/system/company/insert', params, true, callback);
  },
  modify (params, callback) {
    return HttpUtil.post('/api/system/company/modify', params, true, callback);
  },
  delete (params, callback) {
    return HttpUtil.post('/api/system/company/del', params, true, callback);
  },
  active (params, callback) {
    return HttpUtil.post('/api/system/company/active', params, true, callback);
  },
  getById (params) {
    return HttpUtil.post('/api/system/company/getById', {companyId: params})
  }
}
export default CompanyAPI



