<template>
  <div>
    <div class="form-box-content" style="padding-top: 15px;padding-bottom: 15px">
      <div class="module-title-wrap">基本信息</div>
      <fdcform ref="form" :model="formData" :rules="paramValidate" :label-width="120">
        <row type="flex" justify="space-around">
          <fdccol span="12" >
            <formitem :label="'统一编码：'" prop="unityCode">
              <fdcinput v-model="formData.unityCode" placeholder="请输入分组统一编码" readonly></fdcinput>
            </formitem>
          </fdccol>
        </row>
        <row type="flex" justify="space-around">
          <fdccol span="12" >
            <formitem :label="'名称：'" prop="name">
              <fdcinput v-model="formData.name" placeholder="请输入分组名称"></fdcinput>
            </formitem>
          </fdccol>
        </row>
        <row type="flex" justify="space-around">
          <fdccol span="12" >
            <formitem>
              <fdcbutton type="primary" @click="handleSubmit('form')" >提交</fdcbutton>
              <fdcbutton type="ghost" @click="close('apiGroupModify')">取消</fdcbutton>
            </formitem>
          </fdccol>
        </row>
      </fdcform>
      <div class="table-box">
        <div class="module-title-wrap">接口列表</div>
        <fdctable border :loading="tableLoading" width="200px" :data="tableData" :columns="tableColumns" stripe></fdctable>
        <div class="page-box">
          <Page v-show="pageShow" placement="top" :page-size="formData.pageSize" :total="total"
                :current.sync="formData.pageNo" @on-change="changePageNo" @on-page-size-change="changePageSize" show-total show-elevator show-sizer></Page>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import Event from '@/utility/Event.js'
import GroupAPI from './api/GroupAPI'
import { mapState, mapAction } from 'vuex'
export default {
  name: 'apiGroupModify',
  data () {
    return {
      modal: false,
      queryShow: true,
      pageShow: false,
      tableLoading: false,
      total: 0,
      paramValidate: {
        unityCode: [
          { required: true, type: 'string', message: '统一编码必填' }
        ],
        name: [{ required: true, type: 'string', message: '名称密填' }]
      },
      formData: {
        id: this.$route.query.id,
        unityCode: '',
        name: '',
        groupId: this.$route.query.id,
        pageNo: 1,
        pageSize: 10
      },
      tableData: []
    }
  },
  computed: {
    tableColumns () {
      let tableCol = [
        {title: '编号', key: 'number', width: 80, align: 'center'},
        {title: '接口名称', key: 'fullName', width: 500, align: 'center'},
        {title: '日最大调用量', key: 'dmaxCount', align: 'center'},
        {title: '时最大调用量', key: 'hmaxCount', align: 'center'},
        {title: '起始时间', key: 'beginTime', align: 'center'},
        {title: '截止时间', key: 'endTime', align: 'center'},
        {title: '操作',
          key: 'operate',
          align: 'left',
          render: (h, params) => {
            const row = params.row
            return h('div', [
              h('tooltip', {
                props: {
                  content: '撤销权限',
                  placement: 'top'
                }
              },
              [h('span', {
                on: {
                  click: () => {
                    this.revoke(row.id)
                  }
                }
              }, [h('icon', {
                props: {
                  type: 'trash-a',
                  size: 20,
                  color: 'red'
                }
              }, '撤销权限')])]
              )])
          }
        }
      ]
      return tableCol
    }
  },
  methods: {
    handleSubmit (name) {
      this.modify()
    },
    handleReset (name) {
      this.$refs[name].resetFields()
    },
    modify () {
      let group = {
        id: this.$route.query.groupId,
        unityCode: this.formData.unityCode,
        name: this.formData.name
      }
      GroupAPI.modify(group).then((res) => {
        if (res.data.state == 1) {
          this.$Notice.success({
            title: '操作成功',
            desc: '修改分组成功'
          })
          this.close('apiGroupModify')
        } else {
          this.$Notice.error({
            title: '操作失败',
            desc: res.data.msg
          })
        }
      })
    },
    view () {
      GroupAPI.view(this.$route.query.groupId).then(res => {
        if (res.data.state == 1) {
          this.formData = res.data.data
        }
      })
    },
    showPermissions () {
      this.tableLoading = !this.tableLoading
      let queryP = {
        'pageNo': this.formData.pageNo,
        'pageNumber': this.formData.pageSize,
        'groupId': this.$route.query.groupId
      }
      GroupAPI.queryPermissions(queryP).then((res) => {
        this.tableData = []
        if (res.data.state == 1) {
          let dataList = res.data.data.list
          if (dataList != null) {
            this.pageShow = true
            this.total = res.data.data.total
          }
          for (let i = 0; i < dataList.length; i++) {
            let permission = dataList[i]
            this.tableData.push({
              id: permission.id,
              number: (i + 1),
              fullName: permission.service.fullName,
              methodName: permission.methodName,
              methodCode: permission.methodCode,
              dmaxCount: permission.dmaxCount,
              hmaxCount: permission.hmaxCount,
              beginTime: permission.beginTime,
              endTime: permission.endTime
            })
          }
        }
        this.tableLoading = !this.tableLoading
      })
    },
    revoke (permissionId) {
      let permission = {
        'id': permissionId
      }
      GroupAPI.revoke(this.$route.query.groupId, permission).then((res) => {
        if (res.data.state == 1) {
          this.$Notice.success({
            title: '操作成功',
            desc: '服务删除成功'
          })
          this.showPermissions()
        } else {
          this.$Notice.error({
            title: '操作失败',
            desc: res.data.msg
          })
        }
      })
    },
    close (router) {
      Event.$emit('closePage', router)
      Event.$once('refreshGroupList', null)
    }
  },
  mounted () {
    this.view()
    this.showPermissions()
  },
  components: {  }
}
</script>



