<template>
  <div>
    <div class="form-box-content">
      <div class="module-title-wrap">缓存管理</div>
      <fdcform ref="cacheModel" :model="cacheModel"  :label-width="100">
        <row type="flex" justify="space-around">
          <fdccol span="12">
            <formitem label="Key名称" prop="key">
              <fdcinput v-model="cacheModel.cacheKey" placeholder="Key"></fdcinput>
            </formitem>
          </fdccol>
        </row>
        <row type="flex" justify="space-around">
          <fdccol span="12">
              <formitem label="VALUE值" prop="key">
                <fdcinput v-model="cacheModel.cacheValue" type="textarea" :rows="10" placeholder="VALUE值"></fdcinput>
              </formitem>
            </fdccol>
        </row>
        <row type="flex" justify="space-around">
          <fdccol span="12">
              <formitem label="缓存失效时间" prop="value">
                <fdcinput v-model="cacheModel.timeOut" placeholder="缓存失效时间"></fdcinput>
              </formitem>
          </fdccol>
        </row>

        <row type="flex" justify="space-around">
          <fdccol span="12">
            <formitem label=" " prop="value" >
              <fdcbutton type="primary" @click="query">查询</fdcbutton>
              <fdcbutton @click="reload">更新</fdcbutton>
              <fdcbutton @click = 'del'>删除</fdcbutton>
            </formitem>
          </fdccol>
        </row>
      </fdcform>

    </div>
  </div>
</template>

<script>
  import CacheAPI from './api/CacheAPI';
  export default {
    name:'cacheManager',
    data(){
      return{

        cacheModel:{
          timeOut:'',
          cacheKey:'',
          cacheValue:''
        }
      }
    },
    components:{
    },
    methods:{
      query() {
        CacheAPI.query(this.cacheModel.cacheKey).then((res)=>{

          if (res.data) {
            this.cacheModel = res.data;
          }else{
            this.$Notice.error({
              title: '操作失败',
              desc:'没有查询到对应缓存'
            });
          }
        });
      },
      reload(){
        CacheAPI.update(this.cacheModel).then((res)=>{
          if(res.data.state === 1){
            this.$Notice.success({
              title: '更新成功',
              desc:'更新缓存成功'
            });
          } else{
            this.$Notice.error({
              title: '操作失败',
              desc:res.data.msg
            });
          }
        })
      },
      del(){
        CacheAPI.delete(this.cacheModel.cacheKey).then((res)=>{
          if(res.data.state === 1){
            this.$Notice.success({
              title: '清除成功',
              desc:'清除缓存成功'
            });
          } else{
            this.$Notice.error({
              title: '操作失败',
              desc:res.data.msg
            });
          }
        })
      }
    }
  }
</script>




