import HttpUtil from '@/common/HttpUtil'

const EmployeeAPI = {
  query (params) {
    return HttpUtil.post('/api/basic/employee/query', params)
  },
  entry (params) {
    return HttpUtil.post('/api/basic/employee/entry', params)
  },
  modify (params) {
    return HttpUtil.post('/api/basic/employee/modify', params)
  },
  quit (id, callback) {
    return HttpUtil.post('/api/basic/employee/quit', {id: id}, true, callback)
  },
  getById (id) {
    return HttpUtil.post('/api/basic/employee/getById', {id: id})
  }
}
export default EmployeeAPI



