<template>
  <div>
    <div class="page-header-box">
      <div class="header-msg-box">
        <p>配送区域管理</p>
      </div>
      <div class="header-btn-box">
        <fdcbutton v-show="formShow" @click="hideForm" icon="eye-disabled">隐藏</fdcbutton>
        <fdcbutton v-show="!formShow" @click="showForm" icon="eye">显示</fdcbutton>
        <fdcbutton icon="android-add" @click="addArea">新增</fdcbutton>
        <fdcbutton icon="android-sync" @click="pushData2Es">推送ES</fdcbutton>
      </div>
    </div>
    <div class="form-box" v-show="formShow">
      <fdcform ref="formData"  :model="formData"  :label-width="80" class="search-form" inline @keyup.enter.native="query()">
        <formitem label="名称" prop="name">
          <fdcinput v-model="formData.name" @enter="handleSubmit('formData')" placeholder="" class="search-input">
          </fdcinput>
        </formitem>
        <formitem label="代码" prop="code">
          <fdcinput v-model="formData.code" @enter="handleSubmit('formData')" placeholder="" class="search-input">
          </fdcinput>
          <fdcbutton type="primary" class = "search-submit" @click="handleSubmit('formData')">查询</fdcbutton>
          <fdcbutton type="ghost"  class = "search-reset" @click="handleReset('formData')">重置</fdcbutton>
        </formitem>
      </fdcform>
    </div>
    <div class="table-box">
      <fdctable border size="small" :loading="isLoading" :data="tableData"
                :columns="tableColumns" @on-current-change="selectData" stripe></fdctable>
      <div class="page-box">
        <Page v-show="pageShow" placement="top" :total="total" :page-size="formData.pageSize" :current.sync="formData.pageNo" @on-change="changePageNo" @on-page-size-change="changePageSize" show-total show-elevator show-sizer></Page>
      </div>
    </div>
  </div>
</template>

<script>

  import Event from '@/utility/Event.js'
  import AreaApi from './api/AreaAPI'
  import { mapActions, mapState } from 'vuex'
  export default {
    name: 'area',
    data: function () {
      return {
        formShow: true,
        isLoading: false,
        pageShow: false,
        total: 0,
        formData: {
          name: '',
          code: '',
          pageNo: 1,
          pageSize: 10
        },
        tableData: [],
        tableColumns: [
          {
            title: ' ',
            key: 'number',
            type: 'index2',
            align: 'center',
            width: 60,
            render: (h, params) => {
              return h('span', params.index + (this.formData.pageNo - 1) * this.formData.pageSize + 1)
            }
          },
          {
            title: '代码',
            key: 'code',
            align: 'center',
            width: 80
          },
          {
            title: '名称',
            key: 'name',
            align: 'center',
            width: 100
          },
          {
            title: '简码',
            key: 'shortCode',
            align: 'center',
            width: 80
          },
          {
            title: '省',
            key: 'provinceName',
            align: 'center',
            width: 80
          },
          {
            title: '市',
            key: 'cityName',
            align: 'center',
            width: 80
          },
          {
            title: '区',
            key: 'districtName',
            align: 'center',
            width: 80
          },
          {
            title: '区域类型',
            key: 'type',
            align: 'center',
            width: 100,
            render: (h, params) => {
              return h('span', {}, this.sysEnumList['AREA_TYPE_' + params.row.type])
            }
          },
          {
            title: '网点',
            key: 'depotName',
            align: 'center',
            width: 80
          },
          {
            title: '匹配顺序',
            key: 'matchOrder',
            align: 'center',
            width: 100,
            render: (h, params) => {
              return h('span', {}, this.sysEnumList['MATCH_ORDER_' + params.row.matchOrder])
            }
          },
          {
            title: '优先级',
            key: 'priority',
            align: 'center',
            width: 80
          },
          {
            title: '地址库匹配方式',
            key: 'addressMatchMode',
            align: 'center',
            width: 150,
            render: (h, params) => {
              return h('span', {}, this.sysEnumList['ADDRESS_MATCH_MODE_' + params.row.addressMatchMode])
            }
          },
          {
            title: '地址库关键字',
            key: 'addressKeywords',
            align: 'center',
            width: 200
          },
          {
            title: '地图范围类型',
            key: 'mapRangeType',
            align: 'center',
            width: 150,
            render: (h, params) => {
              return h('span', {}, this.sysEnumList['MAP_RANGE_TYPE_' + params.row.mapRangeType])
            }
          },
          {
            title: '地图区域坐标',
            key: 'mapRegionalCoordinates',
            align: 'center',
            width: 150
          },
          // {
          //   title: '备注',
          //   key: 'remark',
          //   align: 'center',
          //   width: 150
          // },
          {
            title: '操作',
            key: 'action',
            width: 120,
            align: 'left',
            render: (h, params) => {
              return h('div', [
                h('tooltip', {
                  props: {
                    content: '修改',
                    placement: 'top'
                  }
                }, [h('span', {
                  on: {
                    click: () => {
                      this.modifyArea(params.row.id)
                    }
                  }
                }, [h('icon', {
                  props: {
                    type: 'edit',
                    size: 20,
                    color: '#0d96ff'
                  },
                  style: {
                    marginRight: '8px'
                  }
                }, '修改')])]),
                h('tooltip', {
                  props: {
                    content: '删除',
                    placement: 'top'
                  }
                }, [h('span', {
                  on: {
                    click: () => {
                      this.$Modal.confirm({
                        title: '删除',
                        content: '<p>确定删除？</p>',
                        onOk: () => {
                          this.removeArea(params.row.id)
                        },
                        onCancel: () => {
                        }
                      })
                    }
                  }
                }, [h('icon', {
                  props: {
                    type: 'trash-a',
                    size: 20,
                    color: '#f11a09'
                  },
                  style: {
                    marginRight: '8px'
                  }
                }, '删除')])])
              ])
            }
          }
        ]
      }
    },
    mounted () {
      Event.$emit('addEnum', {typeList: ['AREA_TYPE','ADDRESS_MATCH_MODE','MAP_RANGE_TYPE', 'MATCH_ORDER']})
      Event.$on('refreshAreaList', obj => this.query())
    },
    computed: {
      ...mapState({
        sysEnumList: state => state.Enum.enumList
      })
    },
    methods: {
      ...mapActions([
        'generateEnumList'
      ]),
      clickHandler: function (code) {
        Event.$emit('addPage', {
          title: '修改国际化配置',
          router: 'modifyI18n',
          param: code,
          checked: true
        })
      },
      hideForm () {
        this.formShow = !this.formShow
      },
      showForm () {
        this.formShow = !this.formShow
      },
      query () {
        this.isLoading = !this.isLoading
        AreaApi.query(this.formData).then((res) => {
          console.log(res)
          this.tableData = []
          let state = res.data.state
          if (state === 1) {
            console.log(res.data.data.list)
            let dataList = res.data.data.list
            if (dataList !== null) {
              this.pageShow = true
              this.total = res.data.data.total
            }
            this.tableData.push(...dataList)
          }
          this.isLoading = !this.isLoading
        })
      },
      handleSubmit (name) {
        this.$refs[name].validate((valid) => {
          if (valid) {
            this.query()
          }
        })
      },
      handleReset (name) {
        this.$refs[name].resetFields()
      },
      changePageNo (value) {
        this.formData.pageNo = value
        this.query()
      },
      changePageSize (value) {
        this.formData.pageNo = 1
        this.formData.pageSize = value
        this.query()
      },
      selectData () {

      },
      addArea () {
        Event.$emit('addPage', {
          title: '新增配送区域',
          router: 'addArea',
          checked: true
        })
      },
      modifyArea (params) {
        Event.$emit('addPage', {
          title: '修改配送区域',
          router: 'modifyArea',
          checked: true,
          paramId: params
        })
      },
      pushData2Es () {
        AreaApi.pushData2Es().then((res) => {
          if (res.data.state === 1) {
            this.$Notice.success({
              title: '操作成功',
            })
          } else {
            this.$Notice.error({
              title: '操作失败',
              desc: res.data.msg
            })
          }
        })
      },
      removeArea (params) {
        AreaApi.delete({areaId: params}, this.query())
      }
    },
    components: {
    }
  }
</script>



