import HttpUtil from '@/common/HttpUtil'

const UserAPI = {
  getUserMenu (param) {
    let postUrl = '/api/system/permission/queryUserMenu'
    return HttpUtil.post(postUrl, param)
  },
  query (params) {
    return HttpUtil.post('/api/system/user/query', params)
  },
  insert (params) {
    return HttpUtil.post('/api/system/user/insert', params)
  },
  modify (params) {
    return HttpUtil.post('/api/system/user/modify', params)
  },
  delete (params) {
    return HttpUtil.post('/api/system/user/del', params)
  },
  disableOrEnable (params, callback) {
    return HttpUtil.post('/api/system/user/disableOrEnable', params, true, callback)
  },
  getById (id) {
    return HttpUtil.post('/api/system/user/getById', {id: id})
  },
  getByName (username) {
    return HttpUtil.post('/api/system/user/getByName', {userName: username})
  },
  getMscPrompt(params){
    return HttpUtil.post('/api/msc/uc/prompt', params,false)
  },
  pullMsg(params){
    return HttpUtil.post('/api/msc/pull', params,false)
  },
  readMsg (params) {
    return HttpUtil.post('/api/msc/updateToRead', params,false)
  },
  queryUser (params) {
    return HttpUtil.post('/api/system/user/queryUser', params, false)
  },
  modifyPassword(params) {
    return HttpUtil.post('/api/system/user/modifyPassword', params)
  },
  queryBindLocations(params){
    return HttpUtil.post('/api/system/user/queryBindLocations', params, false)
  },
  queryUnBindLocations(params){
    return HttpUtil.post('/api/system/user/queryUnBindUsers', params, false)
  },
  bindLocations(params){
    return HttpUtil.post('/api/system/user/bindLocations', params)
  },
  unBindLocations(params){
    return HttpUtil.post('/api/system/user/unBindLocations', params)
  },
  getDefaultLocation (userId) {
    return HttpUtil.post('/api/system/user/getDefaultLocation', {userId: userId})
  },
  setDefaultLocation (params) {
    return HttpUtil.post('/api/system/user/setDefaultLocation', params)
  }
}
export default UserAPI



