import HttpUtil from '@/common/HttpUtil'
const ServiceAPI = {
  query (params) {
    return HttpUtil.post('/api/api/service/display/list/page', params, false)
  },
  queryMethods (params) {
    return HttpUtil.post('/api/api/service/display/list/method',params, false)
  },
  add (params) {
    return HttpUtil.post('/api/api/service/add', params)
  },
  view (params) {
    return HttpUtil.post('/api/api/service/display/view/' + params)
  },
  modify (params) {
    return HttpUtil.post('/api/api/service/modify', params)
  },
  normal (params) {
    return HttpUtil.post('/api/api/service/normal/' + params)
  },
  remove (params) {
    return HttpUtil.post('/api/api/service/remove/' + params)
  }
}
export default ServiceAPI



