import HttpUtil from '@/common/HttpUtil'
const GeoAPI = {
  insert (params) {
    return HttpUtil.postJSON('/api/basic/geoInf/insert', params)
  },
  getById (id) {
    return HttpUtil.post('/api/basic/geoInf/getById', {id: id})
  },
  getProvinceList () {
    return HttpUtil.post('api/basic/geoInf/getListByType', {type: 1})
  },
  getCityList () {
    return HttpUtil.post('api/basic/geoInf/getListByType', {type: 2})
  },
  getGeoInfoListByParentId (params) {
    return HttpUtil.post('api/basic/geoInf/getGeoInfoListByParentId', params)
  },
  modify (params) {
    console.log('modifyApi', params)
    return HttpUtil.postJSON('/api/basic/geoInf/modify', params)
  },
  query (params) {
    return HttpUtil.post('/api/basic/geoInf/query', params)
  },
  delete (param) {
    return HttpUtil.post('/api/basic/geoInf/del', {geoInfId: param.id})
  }

}
export default GeoAPI



