import Vue from 'vue'
import App from './App'
import router from './router'
import Fdc from '@jd/fdc/dist/fdc'
import store from '../store'
import VueI18n from 'vue-i18n';
import mixin from './utility/mixin.js'
import zh from '@jd/fdc/dist/locale/zh-CN'
import en from '@jd/fdc/dist/locale/en-US'
import id from '@jd/fdc/dist/locale/id-ID'
import zh_CN from '../static/lang/zh-CN'
import en_US from '../static/lang/en-US'
import in_ID from '../static/lang/id-ID'

require('@jd/fdc/dist/styles/fdc.css')

Vue.use(Fdc)
Vue.use(VueI18n)
Vue.config.lang = 'zh-CN'

const mergeZH = Object.assign(zh,zh_CN)
const mergeEN = Object.assign(en,en_US)
const mergeIn = Object.assign(id,in_ID)
Vue.locale('zh-CN',mergeZH)
Vue.locale('en-US',mergeEN)
Vue.locale('id-ID',mergeIn)
Vue.config.productionTip = false
Vue.mixin(mixin);


new Vue({
  el: '#app',
  router,
  store,
  components: { App },
  template: '<App/>'
})



