<template>
    <div class="message-con">
      <badge v-bind:count="unReadMsgCount">
        <dropdown>
          <icon type="email" size="22"></icon>
          <dropdownmenu  style="width:250px" slot="list">
            <dropdownitem v-for="msg in unReadMsg" :key="msg.id">
              <icon :type="messageType(msg.type)"></icon>
              <a @click="messageDetail(msg)" href="#">&nbsp;&nbsp;{{msg.title}}</a>
            </dropdownitem>
            <dropdownitem divided><icon type="more"></icon><a href="#">&nbsp;&nbsp;更多</a></dropdownitem>
          </dropdownmenu>
        </dropdown>
      </badge>
      <modal v-model="showDetail" width="400">
        <p slot="header">
          <icon type="email"></icon>
          <span style="text-align:left;font-size:12px">{{messageTitle}}</span>
        </p>
        <div style="text-align:left">
          <p>{{messageContent}}</p>
        </div>
        <div slot="footer">
          <fdcbutton type="info" long >确   认</fdcbutton>
        </div>
      </modal>
    </div>
</template>

<script>
import UserAPI from '../../page/basic/userAuthority/user/api/UserAPI'
export default {
  name: "hederMessage",
  data() {
    return {
      unReadMsgCount: 0,
      unReadMsg: [],
      showDetail: false,
      messageContent: '',
      messageTitle: ''
    }
  },
  method: {
    messageType (type) {
      if (type == 'SYS_MESSAGE') {
        return 'android-textsms'
      } else if (type == 'BUS_MESSAGE') {
        return 'android-notifications'
      } else if (type == 'PROCLAMATION') {
        return 'android-volume-up'
      }
    },
    messageDetail (msg) {
      this.messageTitle = msg.title
      this.messageContent = msg.content ? msg.content : msg.title
      this.showDetail = true
      UserAPI.readMsg({messageId: msg.id})
    }
  },
  mounted: function () {
    let that = this
    UserAPI.getMscPrompt({t_i_m_e: new Date().getTime()}).then(res => {
      let response = res.data
      if (response.state == 1 && response.data.count && parseInt(response.data.count) > 0) {
        that.unReadMsgCount = response.data.count
        that.unReadMsg = response.data.latest
      }
    })
    this.timer = setInterval(function () {
      UserAPI.pullMsg({t_i_m_e: new Date().getTime()}).then(res => {
        let response = res.data
        if (response.state == 1) {
          that.$Notice.success({
            title: response.data.noticeTitle,
            duration: 10, // 10秒钟自动关闭
            render: h => {
              return h('span', [h('a', response.data.message.title)])
            }
          })
        }
      })
    }, 30000)// 每30s刷新一次
  },
  beforeDestroy: function () {
    if (this.timer) {
      clearInterval(this.timer) // 在Vue实例销毁前，清除定时器
    }
  }
}
</script>

<style scoped>

</style>



