<style scoped>
</style>
<template>
  <div>
    <div class="form-box-content">
      <div class="module-title-wrap">基本信息</div>
      <fdcform ref="formValidate"  :model="formValidate" :rules="ruleValidate" :label-width="120">
        <row type="flex" justify="space-around">
          <fdccol span="10">
            <fdcinput  v-model="formValidate.oldCode" v-show="false" v-once></fdcinput>
            <formitem label="代码" prop="code"
                      :rules="{required: true,type: 'string',message: '枚举类型必填',trigger: 'blur'}">
              <fdcinput v-model="formValidate.code"  placeholder=""></fdcinput>
            </formitem>
          </fdccol>
          <fdccol span="10"></fdccol>
        </row>
          <div v-for="(item, index) in formValidate.details">
            <row type="flex" justify="space-around">
              <fdccol span="10">
              <fdcinput v-model="item.id"  v-show="false" placeholder="">
              </fdcinput>
              <fdcinput v-model="item.language"  v-show="false" placeholder="">
              </fdcinput>
              <formitem :label="item.name" :key="index" :prop="'details.'+index+'.value'"
                        :rules="[{required: true, type:'string', message:'国际化值必填', trigger: 'blur'}]">
                <fdcinput v-model="item.value" type="textarea" placeholder="">
                </fdcinput>
              </formitem>
            </fdccol>
            <fdccol span="10">
              <formitem label="备注" prop="remark">
                <fdcinput  v-model="item.remark" type="textarea" placeholder="Enter value">
                </fdcinput>
              </formitem>
            </fdccol>
            </row>
          </div>
        <row type="flex" justify="space-around">
          <fdccol span = '6'>
            <formitem>
              <fdcbutton type="primary" @click="handleSubmit('formValidate')">提交</fdcbutton>
              <fdcbutton type="ghost" @click="cancel('modifyI18n')" class="buttonClass">取消</fdcbutton>
            </formitem>
          </fdccol>
        </row>
      </fdcform>
    </div>
  </div>
</template>
<script>
import I18nAPI from './api/I18nAPI'
import Event from '@/utility/Event.js'
export default {
  name: 'modifyI18n',
  data: function () {
    return {
      ruleValidate: {
      },
      formValidate: {
        code: '',
        oldCode: '',
        changeCode: false,
        details: []
      }
    }
  },
  methods: {
    changeCode: function () {
    },
    generateForm: function () {
      I18nAPI.getByCode(this.$route.query.param).then((res) => {
        if (res.data) {
          this.formValidate.code = res.data.data.code
          this.formValidate.oldCode = res.data.data.code
          this.getI18nLanguageList(res.data.data.details)
        } else {
          this.$Notice.error({
            title: '操作失败',
            desc: res.data.msg
          })
        }
      })
    },
    getI18nLanguageList: function (details) {
      I18nAPI.getI18nLanguageList().then((res) => {
        if (res.data) {
          for (let i = 0; i < res.data.length; i++) {
            let name = res.data[i].name
            let code = res.data[i].code
            var formData
            var flag = false
            for (let j = 0; j < details.length; j++) {
              if (details[j].language === code) {
                flag = true
                formData = {
                  id: details[j].id,
                  language: code,
                  value: details[j].value,
                  name: name,
                  remark: details[j].remark
                }
                break
              }
            }
            if (flag === false) {
              formData = {
                language: code,
                value: '',
                name: name,
                remark: ''
              }
            }
            this.formValidate.details.push(formData)
          }
        }
      })
    },
    handleSubmit (name) {
      this.$refs[name].validate((valid) => {
        if (valid) {
          this.updateParam()
        }
      })
    },
    updateParam: function () {
      const values = this.formValidate.details
      this.formValidate.details = []
      for (let i = 0; i < values.length; i++) {
        const detail = {
          id: values[i].id,
          language: values[i].language,
          value: values[i].value,
          remark: values[i].remark
        }
        this.formValidate.details.push(detail)
      }
      if (this.formValidate.code != this.formValidate.oldCode) {
        this.formValidate.changeCode = true
      }
      I18nAPI.modify(this.formValidate).then((res) => {
        if (res.data.state === 1) {
          this.$Notice.success({
            title: '操作成功',
            desc: '修改国际化配置成功'
          })
          Event.$emit('closePage', 'modifyI18n')
          Event.$emit('refreshI18nList', null)
        } else {
          this.$Notice.error({
            title: '操作失败',
            desc: res.data.msg
          })
        }
      })
    },
    cancel: function (router) {
      Event.$emit('closePage', router)
    }
  },
  components: {},
  mounted: function () {
    this.generateForm()
  }
}
</script>



