<template>
  <div>
    <div class="form-box-content">
      <div class="module-title-wrap">基本信息</div>
      <fdcform ref="formValidate" :model="formValidate" :rules="ruleValidate" :label-width="100">
        <row type="flex" justify="space-around">
          <fdccol span="7">
            <formitem label="编码" prop="code">
              <fdcinput v-model="formValidate.code" placeholder=""></fdcinput>
            </formitem>
          </fdccol>
          <fdccol span="7">
            <formitem label="名称" prop="name">
              <fdcinput v-model="formValidate.name" placeholder=""></fdcinput>
            </formitem>
          </fdccol>
          <fdccol span="7">
            <formitem label="类型" prop="module">
              <fdcselect v-model="formValidate.type">
                <fdcoption v-for="item in enumSelect['CARRIER_TYPE']" :value="item.enumCode" :key="item.enumCode">
                  {{ item.enumValue }}
                </fdcoption>
              </fdcselect>
            </formitem>
          </fdccol>
        </row>
        <row type="flex" justify="space-around">
          <fdccol span="7">
            <formitem label="联系人" prop="contact">
              <fdcinput v-model="formValidate.contact" placeholder=""></fdcinput>
            </formitem>
          </fdccol>
          <fdccol span="7">
            <formitem label="联系电话" prop="telephone">
              <fdcinput v-model="formValidate.telephone" placeholder=""></fdcinput>
            </formitem>
          </fdccol>
          <fdccol span="7">
            <formitem label="邮箱" prop="email">
              <fdcinput v-model="formValidate.email" placeholder=""></fdcinput>
            </formitem>
          </fdccol>
        </row>
        <row type="flex" justify="space-around">
          <fdccol span="15">
            <formitem label="备注" prop="remark">
              <fdcinput v-model="formValidate.remark" type="textarea" placeholder=""></fdcinput>
            </formitem>
          </fdccol>
          <fdccol span="7"></fdccol>
        </row>
        <row type="flex" justify="space-around">
          <fdccol span="7">
            <formitem>
              <fdcbutton type="primary" @click="handlerSubmit('formValidate')">提交</fdcbutton>
              <fdcbutton type="ghost" @click="cancel('modifyCarrier')" class="buttonClass">取消</fdcbutton>
            </formitem>
          </fdccol>
        </row>
      </fdcform>
    </div>
  </div>
</template>
<script>
import CarrierAPI from './api/CarrierAPI'
import {mapState} from 'vuex'
import Event from '@/utility/Event.js'
export default {
  name: 'modifyCarrier',
  data: function () {
    return {
      ruleValidate: {
        name: [{
          required: true,
          type: 'string',
          message: '请输入承运商名称',
          trigger: 'blur'
        }],
        code: [{
          required: true,
          type: 'string',
          message: '请输入承运商编号',
          trigger: 'blur'
        }]
      },
      formValidate: {
        id: '',
        code: '',
        companyId: '',
        name: '',
        type: '',
        contact: '',
        telephone: '',
        email: '',
        remark: ''
      }
    }
  },
  computed: {
    ...mapState({
      enumSelect: state => state.Enum.enumSelect
    })
  },
  methods: {
    generateForm: function () {
      CarrierAPI.getById(this.$route.query.paramId).then((res) => {
        if (res.data) {
          console.log(res.data)
          this.formValidate = res.data
        } else {
          this.$Notice.error({
            title: '操作失败',
            desc: res.data.msg
          })
        }
      })
    },
    updateCarrier: function () {
      CarrierAPI.modify(this.formValidate).then((res) => {
        if (res.data.state == 1) {
          this.$Notice.success({
            title: '操作成功',
            desc: '修改承运商成功'
          })
          Event.$emit('closePage', 'modifyCarrier')
          Event.$emit('refreshCarrierList', null)
        } else {
          this.$Notice.error({
            title: '操作失败',
            desc: res.data.msg
          })
        }
      })
    },
    handlerSubmit: function (name) {
      this.$refs[name].validate((valid) => {
        if (valid) {
          this.updateCarrier()
        }
      })
    },
    cancel: function (router) {
      Event.$emit('closePage', router)
    }
  },
  mounted: function () {
    const condition = ['CARRIER_TYPE']
    Event.$emit('addEnum', {typeList: condition})
    this.generateForm()
  }
}
</script>



