<template>
  <baidu-map class="map" ak="yF73Le6GB25SEhuHpnf9aFc62mG4oXo7" :center="center" :zoom="zoom" :scroll-wheel-zoom="true">
    <bm-marker :position="start" :icon="startIcon"></bm-marker>
    <bm-marker :position="end" :icon="endIcon"></bm-marker>
    <bm-marker :position="deliveryStaff" :icon="deliveryStaffIcon"></bm-marker>
    <bm-polyline :path="polylinePath" stroke-color="blue" :stroke-opacity="0.5" :stroke-weight="5">
    </bm-polyline>
  </baidu-map>
</template>

<script>
  import BaiduMap from 'vue-baidu-map/components/Map/Map.vue'
  import BmPolyline from 'vue-baidu-map/components/overlays/Polyline'
  import BmMarker from 'vue-baidu-map/components/overlays/Marker'
  import dsImg from '@/assets/img/ds.png'

  export default {
    name: "mapDemo",
    data() {
      return {
        center: {lng: 114.430, lat: 30.468},
        zoom: 15,
        start: {lng: 114.431146, lat: 30.467979},
        end: {lng: 114.44009, lat: 30.456743},
        deliveryStaff: {lng: 114.431071, lat: 30.462377},
        polylinePath: [
          {lng: 114.431146, lat: 30.467979},
          {lng: 114.430676, lat: 30.467576},
          {lng: 114.430784, lat: 30.4663},
          {lng: 114.430855, lat: 30.464774},
          {lng: 114.430891, lat: 30.464712},
          {lng: 114.431071, lat: 30.462377},
          {lng: 114.43143, lat: 30.459264},
          {lng: 114.43373, lat: 30.459327},
          {lng: 114.437144, lat: 30.459607},
          {lng: 114.437395, lat: 30.458424},
          {lng: 114.437934, lat: 30.456432},
          {lng: 114.44009, lat: 30.456743}
        ],
        startIcon: {
          url: 'https://api.map.baidu.com/images/dest_markers.png',
          size: {width: 36, height: 36},
          opts: {
            //定位
            anchor: {width: 12, height: 36},
            //图片偏移量，用来获取一张png里面的多个图标中的一个图标
            imageOffset: {width: 0, height: 2}
          }
        },
        endIcon: {
          url: 'https://api.map.baidu.com/images/dest_markers.png',
          size: {width: 36, height: 36},
          opts: {
            anchor: {width: 12, height: 36},
            imageOffset: {width: -1, height: -34}
          }
        },
        deliveryStaffIcon: {
          url: dsImg,
          size: {width: 50, height: 50},
          opts: {
            anchor: {width: 16, height: 16}
          }
        },
      }
    },
    methods: {

    },
    components: {
      BaiduMap,BmPolyline, BmMarker
    }
  }
</script>
<style scoped>
  .map {
    width: 100%;
    height: 600px;
  }
</style>



