<style scoped>
</style>
<template>
  <div>
    <div class="form-box-content">
      <div class="module-title-wrap">基本信息</div>
      <fdcform ref="formData" :model="formData"  :label-width="120" :rules="ruleValidate">
        <row type="flex" justify="space-around">
          <fdccol span="7">
            <formitem label="队列编码" prop="queueCode">
              <fdcinput v-model="formData.queueCode" placeholder="队列编码">
              </fdcinput>
            </formitem>
          </fdccol>
          <fdccol span = '7'>
            <formitem label="队列类型" prop="queueType">
              <fdcselect v-model="formData.queueType" placeholder="选择队列类型">
                <fdcoption v-for="item in enumSelect['QUEUE_TYPE']" :value="item.enumCode" :key="item.enumCode">
                  {{ item.enumValue }}
                </fdcoption>
              </fdcselect>
            </formitem>
          </fdccol>
          <fdccol span = '7'>
            <formitem label="线程数" prop="threadNum">
              <fdcinput v-model="formData.threadNum" placeholder="线程数">
              </fdcinput>
            </formitem>
          </fdccol>
        </row>
        <row type="flex" justify="space-around">
          <fdccol span="7">
            <formitem label="接口协议" prop="protocol">
              <fdcinput v-model="formData.protocol" placeholder="接口协议">
              </fdcinput>
            </formitem>
          </fdccol>
          <fdccol span = '7'>
            <formitem label="重试次数" prop="retryNum">
              <fdcinput v-model="formData.retryNum" placeholder="重试次数">
              </fdcinput>
            </formitem>
          </fdccol>
          <fdccol span = '7'>
            <formitem label="最大报文字节数" prop="maxByte">
              <fdcinput v-model="formData.maxByte" placeholder="最大报文字节数">
              </fdcinput>
            </formitem>
          </fdccol>
        </row>
        <row type="flex" justify="space-around">
          <fdccol span="7">
            <formitem label="查询数据量" prop="queryNum">
              <fdcinput v-model="formData.queryNum" placeholder="查询数据量">
              </fdcinput>
            </formitem>
          </fdccol>
          <fdccol span = '7'>
            <formitem label="调用URL" prop="targetUrl">
              <fdcinput v-model="formData.targetUrl" placeholder="调用URL">
              </fdcinput>
            </formitem>
          </fdccol>
          <fdccol span = '7'>
            <formitem label="回调URL" prop="notifyUrl">
              <fdcinput v-model="formData.notifyUrl" placeholder="回调URL">
              </fdcinput>
            </formitem>
          </fdccol>
        </row>

        <row type="flex" justify="space-around">
          <fdccol span = '7'>
            <formitem>
              <fdcbutton type="primary" @click="handleSubmit('formData')">提交</fdcbutton>
              <fdcbutton type="ghost" @click="close" class="buttonClass">取消</fdcbutton>
            </formitem>
          </fdccol>
        </row>
      </fdcform>
    </div>
  </div>
</template>

<script>
  import Event from '@/utility/Event.js'
  import company from '@/components/modal/company.vue'
  import {mapState} from 'vuex'
  import SysTaskConfigAPI from "./api/SysTaskConfigAPI";

  export default {
    name: 'addTaskConfig',
    data () {
      return {
        formData: {
          queueCode: '',
          queueType: '',
          threadNum: '',
          protocol: '',
          retryNum: '',
          maxByte: '',
          queryNum: '',
          targetUrl: '',
          notifyUrl: ''
        },
        ruleValidate: {
          queueCode: [{
            type: 'string',
            required: true,
            message: '请输入队列编码',
            trigger: 'blur'
          }],
          queueType: [{
            required: true,
            type: 'number',
            message: '请选择队列类型',
            trigger: 'change'
          }],
          threadNum: [{
            required: true,
            message: '请输入线程数',
            pattern: /^\d+$/,
            trigger: 'blur'
          }],
          protocol: [{
            required: true,
            type: 'string',
            message: '请输入接口协议',
            trigger: 'blur'
          }],
          retryNum: [{
            required: true,
            pattern: /^\d+$/,
            message: '请输入重试次数',
            trigger: 'blur'
          }],
          maxByte: [{
            required: true,
            pattern: /^\d+$/,
            message: '请输入最大报文字节数',
            trigger: 'blur'
          }],
          queryNum: [{
            required: true,
            pattern: /^\d+$/,
            message: '请输入查询数据量',
            trigger: 'blur'
          }],
          targetUrl: [{
            required: true,
            type: 'url',
            message: '请输入调用URL'
          }],
          notifyUrl: [{
            required: true,
            type: 'url',
            message: '请输入调用URL'
          }]
        }
      }
    },
    computed: {
      ...mapState({
        enumSelect: state => state.Enum.enumSelect
      })
    },
    components: {
      company: company
    },
    methods: {
      close () {
        Event.$emit('closePage', 'addTaskConfig')
      },
      handleSubmit (name) {
        this.$refs[name].validate((valid) => {
          if (valid) {
            this.insert()
          }
        })
      },
      insert () {
        SysTaskConfigAPI.insert(this.formData).then((res) => {
          if (res.data.state === 1) {
            this.$Notice.success({
              title: '操作成功',
              desc: '新增成功'
            })
            Event.$emit('closePage', 'addTaskConfig');
            Event.$emit('refreshList', obj => this.query());
          } else {
            this.$Notice.error({
              title: '操作失败',
              desc: res.data.msg
            })
          }
        })
      }
    },
    mounted () {
      Event.$emit('addEnum', {typeList:['QUEUE_TYPE']});
    }
  }
</script>



