<template>
  <div>
    <div class="page-header-box">
      <div class="header-msg-box">
        <p>角色管理</p>
      </div>
      <div class="header-btn-box">
        <fdcbutton v-show="queryShow" @click="hideForm" icon="eye-disabled">隐藏</fdcbutton>
        <fdcbutton v-show="!queryShow" @click="showForm" icon="eye">显示</fdcbutton>
        <fdcbutton icon="android-add" @click="toAdd" v-show="addBtnShow">新增</fdcbutton>
      </div>
    </div>

    <div class="form-box" v-show="queryShow">
      <fdcform ref="form" inline :model="formData" :label-width="80" class="search-form" @keyup.enter.native="query()">
        <formitem label="编码" prop="code">
          <fdcinput v-model="formData.code" placeholder="请输入编码" class="search-input"></fdcinput>
        </formitem>
        <formitem label="名称" prop="name">
          <fdcinput v-model="formData.name" placeholder="请输入名称" class="search-input"></fdcinput>
        </formitem>
        <formitem label="级别" prop="level">
          <fdcselect v-model="formData.level" placeholder="选择角色级别" class="search-input" clearable @on-change="query()">
            <fdcoption v-for="item in enumSelect['ROLE_LEVEL']" :value="item.enumCode" :key="item.enumCode">
              {{ item.enumValue }}
            </fdcoption>
          </fdcselect>
        </formitem>
          <fdcbutton type="primary" @click="query()" class="search-submit">查询</fdcbutton>
          <fdcbutton type="ghost" @click="reset('form')" class="search-reset">重置</fdcbutton>
      </fdcform>
    </div>
    <div class="table-box">
      <fdctable border size="small" :loading="isLoading" :data="tableData" :columns="tableColumns"
                stripe></fdctable>
      <div class="page-box">
        <Page v-show="pageShow" placement="top" :page-size="formData.pageNumber" :total="total"
              :current.sync="formData.pageNo" @on-change="changePageNo" @on-page-size-change="changePageSize" show-total
              show-elevator show-sizer></Page>
      </div>
    </div>

    <!--修改页面弹窗-->
    <Modal :width="900" v-model="showEditModal">
      <div class="form-box-content">
        <div class="module-title-wrap">角色信息</div>
        <fdcform ref="formValidate" :model="formValidate" :rules="ruleValidate" :label-width="80" inline>
          <row span="24">
            <fdccol span="8" offset="4">
              <formitem label="编码" prop="code">
                <fdcinput v-model="formValidate.code" placeholder="Enter code" style="width:400px">
                </fdcinput>
              </formitem>
            </fdccol>
          </row>
          <row span="24">
            <fdccol span="8" offset="4">
              <formitem label="名称" prop="name">
                <fdcinput v-model="formValidate.name" placeholder="Enter name" style="width:400px">
                </fdcinput>
              </formitem>
            </fdccol>
          </row>
          <row span="24">
            <fdccol span="8" offset="4">
              <formitem label="级别" prop="level">
                <fdcselect v-model="formValidate.level" placeholder="选择角色级别" style="width:400px" :disabled="isDisabled">
                  <fdcoption v-for="item in enumSelect['ROLE_LEVEL']" :value="item.enumCode" :key="item.enumCode">
                    {{ item.enumValue }}
                  </fdcoption>
                </fdcselect>
              </formitem>
            </fdccol>
          </row>
        </fdcform>
      </div>
      <!--自定义页脚和按钮-->
      <div slot="footer">
        <fdcbutton type="primary" @click="add('formValidate')" v-show="addSaveBtnShow">提交</fdcbutton>
        <fdcbutton type="primary" @click="modify('formValidate')" v-show="editSaveBtnShow">提交</fdcbutton>
        <fdcbutton type="ghost" @click="cancel('formValidate')" style="margin-left: 8px">取消</fdcbutton>
      </div>
    </Modal>
  </div>
</template>

<script>
  import Event from '@/utility/Event';
  import SysRoleAPI from './api/SysRoleAPI';
  import {mapActions, mapState} from 'vuex';
  import EnumSelect from '@/components/enumSelect.vue'

  export default {
    name: "sysRole",
    data() {
      return {
        queryShow: true,
        isLoading: false,
        pageShow: false,
        tableLoading: false,
        total: 0,
        formData: {
          code: '',
          name: '',
          level: '',
          pageNo: 1,
          pageNumber: 10
        },
        tableData: [],
        showEditModal: false,
        ruleValidate: {
          code: [{
            required: true,
            type: 'string',
            message: '编码必填',
            trigger: 'blur'
          }],
          name: [{
            required: true,
            type: 'string',
            message: '名称必填',
            trigger: 'blur'
          }],
          level: [{
            required: true,
            type: 'number',
            message: '级别必填',
            trigger: 'change'
          }]
        },
        formValidate: {
          id: '',
          name: '',
          code: '',
          level: ''
        },
        addSaveBtnShow: false,
        editSaveBtnShow: false,
        isDisabled: false,
        addBtnShow: true
      }
    },
    methods: {
      ...mapActions([
        'generateEnumList'
      ]),
      hideForm() {
        this.queryShow = !this.queryShow;
      },
      showForm() {
        this.queryShow = !this.queryShow;
      },
      query() {
        this.isLoading = !this.isLoading;
        SysRoleAPI.query(this.formData).then((res) => {
          this.tableData = [];
          let state = res.data.state;
          if (state === 1) {
            let dataList = res.data.data.list;
            if (dataList !== null) {
              this.pageShow = true;
              this.total = res.data.data.total;
              this.tableData.push(...dataList);
            }
          }
          this.isLoading = !this.isLoading;
        });
      },
      reset(name) {
        this.$refs[name].resetFields();
      },
      toAdd() {
        this.showEditModal = true;
        this.addSaveBtnShow = true;
        this.editSaveBtnShow = false;
        this.isDisabled = false;
        this.formValidate.id = '';
        this.formValidate.code = '';
        this.formValidate.name = '';
        this.formValidate.level = '';
      },
      add(name) {
        this.$refs[name].validate((valid) => {
          if (valid) {
            SysRoleAPI.insert(this.formValidate).then((res) => {
              if (res.data.state === 1) {
                this.$Notice.success({
                  title: '操作成功',
                  desc: '新增角色成功'
                });
                this.showEditModal = false;
                this.formValidate.level = '';
                this.query();
              } else {
                this.$Notice.error({
                  title: '操作失败',
                  desc: res.data.msg
                });
              }
            });
          }
        });
      },
      toModify(role) {
        this.showEditModal = true;
        this.addSaveBtnShow = false;
        this.editSaveBtnShow = true;
        this.isDisabled = true;
        this.formValidate.id = role.id;
        this.formValidate.code = role.code;
        this.formValidate.name = role.name;
        this.formValidate.level = role.level;
      },
      modify(name) {
        this.$refs[name].validate((valid) => {
          if (valid) {
            SysRoleAPI.modify(this.formValidate).then((res) => {
              if (res.data.state === 1) {
                this.$Notice.success({
                  title: '操作成功',
                  desc: '修改角色成功'
                });
                this.showEditModal = false;
                this.formValidate.level = '';
                  this.query();
              } else {
                this.$Notice.error({
                  title: '操作失败',
                  desc: res.data.msg
                });
              }
            });
          }
        })
      },
      //角色绑定用户
      bindUser (roleId) {
        Event.$emit('addPage', {
          title: "角色绑定用户",
          router: "sysRoleUser",
          checked: true,
          roleId: roleId,
        });
      },
      cancel (name) {
        this.showEditModal = false;
        this.$refs[name].resetFields();
      },
      showDetail(roleId) {
        Event.$emit('addPage', {
          title: "查看详情",
          router: "sysRolePermission",
          checked: true,
          roleId: roleId,
          editFlag: false,
        });
      },
      permission(roleId) {
        Event.$emit('addPage', {
          title: "角色分配权限",
          router: "sysRolePermission",
          checked: true,
          roleId: roleId
        });
      },
      changePageNo(value) {
        this.formData.pageNo = value;
        this.query();
      },
      changePageSize(value) {
        this.formData.pageNo = 1;
        this.formData.pageNumber = value;
        this.query();
      },
      remove(roleId) {
        SysRoleAPI.delete({roleId: roleId}).then((res) => {
          if (res.data.state == 1) {
            this.$Notice.success({
              title: '操作成功',
              desc: '删除成功'
            });
            this.query();
          } else {
            this.$Notice.error({
              title: '操作失败',
              desc: res.data.msg
            });
          }
        });
      },
      selectRoleLevel(value) {
        this.formData.level = value;
      }
    },
    computed: {
      ...mapState({
        sysEnumList: state => state.Enum.enumList,
        enumSelect: state => state.Enum.enumSelect,
        localLevel: state => state.User.localLevel
      }),
      tableColumns() {
        let tableCol = [{
          title: ' ',
          key: 'number',
          width: 60,
          align: 'center',
          render: (h, params) => {
            return h('span', params.index + (this.formData.pageNo - 1) * this.formData.pageNumber + 1)
          }
        }, {
          title: '编码',
          key: 'code',
          align: 'center'
        }, {
          title: '名称',
          key: 'name',
          align: 'center'
        }, {
          title: '级别',
          key: 'level',
          align: 'center',
          render: (h, params) => {
            return h('span', {}, this.sysEnumList['ROLE_LEVEL_' + params.row.level])
          }
        }, {
          title: '操作',
          key: 'action',
          width: 150,
          align: 'left',
          render: (h, params) => {
            let actionList = [];
            let permission = h('tooltip', {
              props: {
                content: '分配权限',
                placement: 'top'
              }
            }, [h('span', {
              on: {
                click: () => {
                  this.permission(params.row.id);
                }
              }
            }, [h('icon', {
              props: {
                type: 'key',
                size: 20,
                color: '#0d96ff'
              },
              style: {
                marginRight: '8px'
              }
            }, '分配权限')])]);

            let bindUsers = h('tooltip', {
              props: {
                content: '角色绑定用户',
                placement: 'top'
              }
            }, [h('span', {
              on: {
                click: () => {
                  this.bindUser(params.row.id);
                }
              }
            }, [h('icon', {
              props: {
                type: 'person-add',
                size: 20,
                color:'#0d96ff'
              },
              style: {
                marginRight: '8px'
              }
            }, '角色绑定用户')])]);
            let modify = h('tooltip', {
              props: {
                content: '修改',
                placement: 'top'
              }
            }, [h('span', {
              on: {
                click: () => {
                  this.toModify(params.row);
                }
              }
            }, [h('icon', {
              props: {
                type: 'edit',
                size: 20,
                color: '#0d96ff'
              },
              style: {
                marginRight: '8px'
              }
            }, '修改')])]);

            let del = h('tooltip', {
              props: {
                content: '删除',
                placement: 'top'
              }
            }, [h('span', {
              on: {
                click: () => {
                  this.$Modal.confirm({
                    title: '删除',
                    content: '<p>确定删除？</p>',
                    onOk: () => {
                      this.remove(params.row.id);
                    },
                    onCancel: () => {
                      // this.$Message.info('Clicked cancel');
                    }
                  });
                }
              }
            }, [h('icon', {
              props: {
                type: 'trash-a',
                size: 20,
                color: 'red'
              },
              style: {
                marginRight: '8px'
              }
            }, '删除')])]);
            actionList.push(permission);
            actionList.push(bindUsers);
            if(params.row.level > 2){
              actionList.push(modify);
              actionList.push(del);
            }
            return h('div', actionList);
          }
        }
        ];
        return tableCol;
      }
    },
    mounted() {
      let expCodes = [];
      if (this.localLevel === 1){
        this.addBtnShow = false;
      }
      for (let i = 1; i < this.localLevel; i++) {
        expCodes.push('ROLE_LEVEL_' + i);
      }
      Event.$emit('addEnum', {typeList: ['ROLE_LEVEL'], expCodes: expCodes});
      Event.$once('refreshList', obj => this.query());
    },
    components: {
      enumSelect: EnumSelect
    }
  }
</script>





