<template>
  <div>
    <div class="form-box-content">
      <div class="module-title-wrap">基本信息</div>
      <fdcform ref="formData" :model="formData" :label-width="100">
        <row type="flex" justify="space-around">
          <fdccol span="11" >
            <formitem :label="'服务类全路径：'" prop="fullName">
              <fdcinput v-model="formData.fullName" placeholder="服务类全路径" readonly>
              </fdcinput>
            </formitem>
          </fdccol>
          <fdccol span="11">
            <formitem :label="'开发者名称：'" prop="developer">
              <fdcinput v-model="formData.developer" placeholder="开发者名称" readonly>
              </fdcinput>
            </formitem>
          </fdccol>
        </row>
        <row type="flex" justify="space-around">
          <fdccol span="11" >
            <formitem :label="'开发者邮箱：'" prop="developerEmail">
              <fdcinput v-model="formData.developerEmail" placeholder="开发者邮箱" readonly>
              </fdcinput>
            </formitem>
          </fdccol>
          <fdccol span="11">
            <formitem :label="'开发者手机：'" prop="developerMobile">
              <fdcinput v-model="formData.developerMobile" placeholder="开发者名称" readonly>
              </fdcinput>
            </formitem>
          </fdccol>
        </row>
        <row type="flex" justify="space-around">
          <fdccol span="11">
            <formitem :label="'创建者：'" prop="createdTime" >
              <fdcinput v-model="formData.creator" placeholder="创建者" readonly>
              </fdcinput>
            </formitem>
          </fdccol>
          <fdccol span="11">
            <formitem :label="'创建时间：'" prop="createdTime">
              <fdcinput v-model="formData.createdTime" placeholder="开发者名称" readonly>
              </fdcinput>
            </formitem>
          </fdccol>
        </row>
      </fdcform>
      <div class="table-box">
      <div class="module-title-wrap">方法列表</div>
      <fdctable border :loading="tableLoading" width="200px" :data="tableData" :columns="tableColumns" stripe></fdctable>
      <div style="padding-left: 30px;padding-top: 15px;padding-bottom: 5px">
        <fdcbutton type="ghost" @click="close('serviceView')" style="margin-left: 8px">关闭</fdcbutton>
      </div>
      </div>
    </div>
  </div>
</template>
<script>
import Event from '@/utility/Event.js'
import ServiceAPI from './api/ServiceAPI'
export default {
  name: 'serviceView',
  data () {
    return {
      modal: false,
      queryShow: true,
      pageShow: false,
      tableLoading: false,
      total: 0,
      formData: {
        id: this.$route.query.id,
        fullName: '',
        developer: '',
        developerEmail: '',
        developerMobile: '',
        pageNo: 1,
        pageSize: 10
      },
      tableData: []
    }
  },
  computed: {
    tableColumns () {
      let tableCol = [
        {title: '编号', key: 'number', width: 80, align: 'center'},
        {title: '返参类型', key: 'returnType', align: 'center'},
        {title: '方法名称', key: 'methodName', width: 200, align: 'center'},
        {title: '入参类型', key: 'parameterType', align: 'center'},
        {title: '是否合规', key: 'verify', align: 'center'}
      ]
      return tableCol
    }
  },
  methods: {
    view () {
      ServiceAPI.view(this.$route.query.id).then((res) => {
        if (res.data.state == 1) {
          this.formData = res.data.data
        }
      })
    },
    showMethods () {
      this.tableLoading = !this.tableLoading
      let query = {
        'pageNo': this.formData.pageNo,
        'pageNumber': this.formData.pageSize,
        'id': this.formData.id
      }
      ServiceAPI.queryMethods(query).then((res) => {
        this.tableData = []
        if (res.data.state == 1) {
          let dataList = res.data.data
          if (dataList != null) {
            this.pageShow = true
            this.total = res.data.data.total
          }
          for (let i = 0; i < dataList.length; i++) {
            let method = dataList[i]
            this.tableData.push({
              number: (i + 1),
              methodName: method.name,
              methodCode: method.code,
              parameterType: method.parameterTypeName,
              returnType: method.returnTypeName,
              verify: method.verify
            })
          }
        }
        this.tableLoading = !this.tableLoading
      })
    },
    close (router) {
      Event.$emit('closePage', router)
      Event.$once('refreshServiceList', null)
    }
  },
  mounted () {
    this.view()
    this.showMethods()
  },
  components: {}
}
</script>



