<style scoped>
</style>
<template>
  <div>
    <div class="form-box-content">
      <div class="module-title-wrap">基本信息</div>
      <fdcform ref="formData" :model="formData"  :label-width="100" :rules="ruleValidate" >
        <row type="flex" justify="space-around">
          <fdccol span = '7'>
            <formitem label="工号" prop="code">
              <fdcinput v-model="formData.code" placeholder="工号" >
              </fdcinput>
            </formitem>
          </fdccol>
          <fdccol span = '7'>
            <formitem label="姓名" prop="name">
              <fdcinput v-model="formData.name" placeholder="姓名" >
              </fdcinput>
            </formitem>
          </fdccol>
          <fdccol span = '7'>
            <formitem label="登陆名" prop="loginName">
              <fdcinput v-model="formData.loginName" placeholder="登录名" >
              </fdcinput>
            </formitem>
          </fdccol>
        </row>
        <row type="flex" justify="space-around">
          <fdccol span = '7'>
            <formitem label="员工类型" prop="type">
              <fdcselect v-model="formData.type" placeholder="选择员工类型" class="search-input">
                <fdcoption v-for="item in enumSelect['EMPLOYEE_TYPE']" :value="item.enumCode" :key="item.enumCode">
                  {{ item.enumValue }}
                </fdcoption>
              </fdcselect>
            </formitem>
          </fdccol>
          <fdccol span = '7'>
            <formitem label="所属作业点" v-show = "false" prop="locationId">
              <fdcinput v-model="formData.locationId" placeholder="所属作业点" >
              </fdcinput>
            </formitem>
            <formitem label="所属作业点" prop="locationName">
              <fdcinput v-model="formData.locationName" readonly placeholder="所属作业点" >
                <fdcbutton slot="append" icon="ios-search" @click="handleLocationModal"></fdcbutton>
              </fdcinput>
            </formitem>
          </fdccol>
          <fdccol span = '7'>
            <formitem label="联系方式" prop="telephone">
              <fdcinput v-model="formData.telephone" placeholder="联系方式" >
              </fdcinput>
            </formitem>
          </fdccol>
        </row>
        <row type="flex" justify="space-around">
          <fdccol span = '7'>
            <formitem label="备注" prop="remark">
              <fdcinput v-model="formData.remark" placeholder="备注">
              </fdcinput>
            </formitem>
          </fdccol>
          <fdccol span="15"></fdccol>
        </row>
        <row type="flex" justify="space-around">
          <fdccol span = '7'>
            <formitem>
              <fdcbutton type="primary" @click="handleSubmit('formData')">提交</fdcbutton>
              <fdcbutton type="ghost" @click="close('addEmployee')" class="buttonClass">取消</fdcbutton>
            </formitem>
          </fdccol>
        </row>
      </fdcform>
    </div>
    <modal v-model="locationShow" :width="800" title="请选择作业点" @on-ok="ok" @on-cancel="cancel">
      <location ></location>
    </modal>
  </div>
</template>

<script>
  import EmployeeAPI from './api/EmployeeAPI'
  import Event from '@/utility/Event.js'
  import location from '@/components/modal/location.vue'
  import {mapState} from 'vuex'

  export default {
    name: 'addEmployee',
    data () {
      return {
        locationShow: false,
        selectValue: [],
        location: {
          id: '',
          name: ''
        },
        formData: {
          loginName: '',
          code:'',
          name: '',
          type: '',
          telephone: '',
          remark: '',
          locationId: '',
          locationName: ''
        },
        ruleValidate: {
          code: [{
            required: true,
            message: '工号不允许为空',
            trigger: 'blur'
          },{
            max:17,
            message: '工号超长',
            trigger: 'blur'
          }],
          loginName: [{
            required: true,
            message: '登录名不允许为空',
            trigger: 'blur'
          },{
            max:20,
            message: '登录名超长',
            trigger: 'blur'
          },{
            pattern: /^[a-z,A-Z,0-9]+$/,
            message: '登录名格式不正确',
            trigger: 'blur'
          }],
          name: [{
            required: true,
            message: '姓名不允许为空',
            trigger: 'blur'
          },{
            max:16,
            message: '姓名超长',
            trigger: 'blur'
          }],
          type: [{
            required: true,
            type: 'number',
            message: '员工类型不允许为空',
            trigger: 'change'
          }],
          locationName: [{
            required: true,
            message: '作业点不允许为空',
            trigger: 'change'
          }],
          telephone: [{
            max:20,
            message: '联系方式超长',
            trigger: 'blur'
          },{
            pattern: /^[0-9,'\-']+$/,
            message: '联系方式格式不正确',
            trigger: 'blur'
          }],
          remark: [{
            type: 'string',
            trigger: 'blur'
          },{
            max:66,
            message: '备注超长',
            trigger: 'blur'
          }]
        }
      }
    },
    computed: {
      ...mapState({
        enumSelect: state => state.Enum.enumSelect
      })
    },
    components: {
      location: location
    },
    methods: {
      ok () {
        this.formData.locationId = this.location.id
        this.formData.locationName = this.location.name
        Event.$emit('clearSelect', null)
      },
      cancel () {
        this.locationShow = false
        Event.$emit('clearSelect', null)
      },
      close (router) {
        Event.$emit('closePage', router)
      },
      handleSubmit (name) {
        this.$refs[name].validate((valid) => {
          if (valid) {
            this.entry()
          }
        })
      },
      handleLocationModal() {
        this.locationShow = true
        Event.$emit('queryLocation', null)
      },
      entry () {
        EmployeeAPI.entry(this.formData).then((res) => {
          if (res.data.state === 1) {
            this.$Notice.success({
              title: '操作成功',
              desc: '新增成功'
            })
            Event.$emit('closePage', 'addEmployee')
            Event.$emit('refreshList', null)
          } else {
            this.$Notice.error({
              title: '操作失败',
              desc: res.data.msg
            })
          }
        })
      },
      handleReset (name) {
        this.$refs[name].resetFields()
      },
      changePage () {
      },
      selectLocation (rowData) {
        this.location = rowData
      }
    },
    mounted () {
      Event.$on('selectLocation', obj => this.selectLocation(obj))
    }
  }
</script>



