<template>
  <div>
    <div class="form-box-content" style="padding-top: 15px;padding-bottom: 15px">
      <fdcform ref="form" :model="formData" :rules="paramValidate" :label-width="120">
        <row type="flex" justify="space-around">
          <fdccol span="12" >
            <formitem :label="'服务类全路径：'" prop="fullName">
              <fdcinput v-model="formData.fullName" placeholder="请输入服务类全路径" class="search-input"></fdcinput>
            </formitem>
          </fdccol>
        </row>
        <row type="flex" justify="space-around">
          <fdccol span="12" >
            <formitem :label="'开发者名称：'" prop="developer">
              <fdcinput v-model="formData.developer" placeholder="请输入开发者名称" class="search-input"></fdcinput>
            </formitem>
          </fdccol>
        </row>
        <row type="flex" justify="space-around">
          <fdccol span="12" >
            <formitem :label="'开发者手机：'" prop="developerMobile">
              <fdcinput v-model="formData.developerMobile" placeholder="请输入开发者手机" class="search-input"></fdcinput>
            </formitem>
          </fdccol>
        </row>
        <row type="flex" justify="space-around">
          <fdccol span="12" >
            <formitem :label="'开发者邮箱：'" prop="developerEmail">
              <fdcinput v-model="formData.developerEmail" placeholder="请输入开发者邮箱" class="search-input"></fdcinput>
            </formitem>
          </fdccol>
        </row>
        <row type="flex" justify="space-around">
          <fdccol span="12" >
            <formitem>
              <fdcbutton type="primary" @click="handleSubmit('form')">提交</fdcbutton>
              <fdcbutton type="ghost" @click="close('serviceModify')" class="buttonClass">取消</fdcbutton>
            </formitem>
          </fdccol>
        </row>
      </fdcform>
    </div>
  </div>
</template>

<script>
import Event from '@/utility/Event.js'
import ServiceAPI from './api/ServiceAPI'
import {mapState, mapAction} from 'vuex'

export default {
  name: 'serviceModify',
  data () {
    return {
      paramValidate: {
        fullName: [{
          required: true,
          type: 'string',
          message: '服务类全路径必填',
          trigger: 'blur'
        }],
        developer: [{
          required: true,
          type: 'string',
          message: '开发者名称必填',
          trigger: 'blur'
        }],
        developerEmail: [{
          required: true,
          type: 'string',
          message: '开发者邮箱必填',
          trigger: 'blur'
        }],
        developerMobile: [{
          required: true,
          type: 'string',
          message: '开发者手机必填',
          trigger: 'blur'
        }]
      },
      formData: {
        id: this.$route.query.id,
        fullName: '',
        developer: '',
        developerEmail: '',
        developerMobile: ''
      }
    }
  },
  methods: {
    handleSubmit (name) {
      this.modify()
    },
    handleReset (name) {
      this.$refs[name].resetFields()
    },
    modify () {
      let serviceVO = {
        id: this.$route.query.id,
        fullName: this.formData.fullName,
        developer: this.formData.developer,
        developerEmail: this.formData.developerEmail,
        developerMobile: this.formData.developerMobile
      }
      ServiceAPI.modify(serviceVO).then((res) => {
        if (res.data.state == 1) {
          this.$Notice.success({
            title: '操作成功',
            desc: '修改服务成功'
          })
          this.close()
        } else {
          this.$Notice.error({
            title: '操作失败',
            desc: res.data.msg
          })
        }
      })
    },
    view () {
      ServiceAPI.view(this.$route.query.id).then((res) => {
        if (res.data.state == 1) {
          this.formData = res.data.data
        }
      })
    },
    close (router) {
      Event.$emit('closePage', router)
      Event.$once('refreshServiceList', null)
    }
  },
  mounted () {
    this.view()
  },
  components: {}
}
</script>



