import LoginAPI from '@/page/login/api/LoginAPI'
import { setCookie, delCookie } from '@/common/cookie'
import {AUTH_TOKEN} from '@/common/constants'
import UserAPI from '../../../src/page/basic/userAuthority/user/api/UserAPI'
import {generateMenuMap, filterAsyncRouter} from '../../../src/common/helper.js'
import { asyncRouters, constantRouters, router } from '../../../src/router/index'

export default {
  state: {
    rsa_passkey: parseInt(Math.random() * 100000),
    rsa_exponent: '',
    rsa_modulus: '',
    token: '',
    userName: '',
    moduleType: '',
    permissionRouters: [],
    permissionCodeList: [],
    menuList: [],
    menuMap: {},
    location: '',
    localLevel: ''
  },

  mutations: {
    SET_EXPONENT (state, exponent) {
      state.rsa_exponent = exponent
    },
    SET_MODULUS (state, modulus) {
      state.rsa_modulus = modulus
    },
    SET_TOKEN (state, token) {
      state.token = token
    },
    SET_USERNAME (state, userName) {
      state.userName = userName
    },
    SET_LOCAL_LEVEL(state, localLevel){
      state.localLevel = localLevel
    },
    SET_LOCATION(state, location){
      state.location = location
    },
    SET_MODULETYPE (state, moduleType) {
      state.moduleType = moduleType
    },
    SET_PASSKEY (state, passkey) {
      state.rsa_passkey = passkey
    },
    SET_PERMISSION_ROUTER (state, permissionRouters) {
      state.permissionRouters = permissionRouters
    },
    SET_MENU_LIST (state, menuList) {
      state.menuList = menuList
    },
    SET_MENU_MAP (state, menuMap) {
      state.menuMap = menuMap
    },
    SET_PERMISSION_CODE_LIST (state, permissionCodeList) {
      state.permissionCodeList = permissionCodeList
    }
  },

  actions: {
    // 生成动态路由，生成动态菜单
    generateRouters ({ commit }, permissionCodeList) {
      return new Promise(resolve => {
        let accessedRouters = filterAsyncRouter(asyncRouters, permissionCodeList)
        router.addRoutes(accessedRouters)// 动态添加路由
        let allRouters = constantRouters.concat(accessedRouters)
        commit('SET_PERMISSION_ROUTER', allRouters)
        sessionStorage.setItem('permissionCodeList', JSON.stringify(permissionCodeList));
        sessionStorage.setItem('allRouters',JSON.stringify(allRouters));
        resolve(allRouters)
      })
    },
    generateMenu ({commit, state}) {
      const param = {
        token: state.token,
        moduleType: state.moduleType
      }
      UserAPI.getUserMenu(param).then(response => {
        if (response.data.state === 1) {
          let menuList = response.data.data
          let menuMap = {'page404':'/page404','page500':'/page500','demo':'/demo'}
          commit('SET_MENU_LIST', menuList)
          sessionStorage.setItem('menuList', JSON.stringify(menuList))
          generateMenuMap(menuList, menuMap)
          commit('SET_MENU_MAP', menuMap)
          sessionStorage.setItem('menuMap', JSON.stringify(menuMap))
        }
      })
    },
    // 解决页面刷新问题
    fetchInfoFromSession ({commit}) {
      let $token = sessionStorage.getItem('token')
      if ($token && $token != '') {
        let $userName = sessionStorage.getItem('userName')
        let $localLevel = sessionStorage.getItem('localLevel')
        let $location = sessionStorage.getItem('location')
        let $moduleType = sessionStorage.getItem('moduleType')
        let $menuList = JSON.parse(sessionStorage.getItem('menuList'))
        let $menuMap = JSON.parse(sessionStorage.getItem('menuMap'))
        let permissionCodeList = JSON.parse(sessionStorage.getItem('permissionCodeList'))
        let accessedRouters = filterAsyncRouter(asyncRouters, permissionCodeList)
        // 动态添加路由
        router.onReady(function () {
          router.addRoutes(accessedRouters)
        })
        let allRouters = constantRouters.concat(accessedRouters)

        commit('SET_TOKEN', $token)
        commit('SET_USERNAME', $userName)
        commit('SET_LOCAL_LEVEL', $localLevel)
        commit('SET_LOCATION', $location)
        commit('SET_MODULETYPE', $moduleType)
        commit('SET_PERMISSION_ROUTER', allRouters)
        commit('SET_MENU_LIST', $menuList)
        commit('SET_MENU_MAP', $menuMap)
        commit('SET_PERMISSION_CODE_LIST', permissionCodeList)
      }
    },
    // 获取公私钥
    getRSAPublicKey ({commit}, passkey) {
      return new Promise((resolve, reject) => {
        LoginAPI.getRSAPublicKey({key: passkey}).then(response => {
          const callBackData = response.data
          if (callBackData.state == 1) {
            commit('SET_EXPONENT', callBackData.data.exponent)
            commit('SET_MODULUS', callBackData.data.modulus)
            commit('SET_PASSKEY', callBackData.data.key)
          }
        })
      })
    },
    // 登录
    login ({ commit }, loginInfo) {
      return new Promise((resolve, reject) => {
        LoginAPI.login(loginInfo).then(response => {
          const callBackData = response.data
          if (callBackData.state == 1) {
            const token = callBackData.data.token
            const moduleType = callBackData.data.moduleType
            setCookie(AUTH_TOKEN, token, 1000 * 60)
            commit('SET_TOKEN', token)
            commit('SET_USERNAME', callBackData.data.userName)
            commit('SET_LOCAL_LEVEL', callBackData.data.localLevel)
            commit('SET_LOCATION', callBackData.data.location)
            commit('SET_MODULETYPE', moduleType)
            sessionStorage.setItem('token', token)
            sessionStorage.setItem('userName', callBackData.data.userName)
            sessionStorage.setItem('moduleType', moduleType)
            sessionStorage.setItem('localLevel', callBackData.data.localLevel)
            sessionStorage.setItem('location', callBackData.data.location)
          }
          resolve(response)
        }).catch(error => {
          reject(error)
        })
      })
    },
    // 注销
    LogOut ({ commit}, token) {
      return new Promise((resolve, reject) => {
        LoginAPI.logout(token).then(() => {
          commit('SET_TOKEN', '')
          /*commit('changeTag',[{ title: '首页', router: 'index' }]);*/
          sessionStorage.clear()
          delCookie(AUTH_TOKEN)
          resolve()
        }).catch(error => {
          reject(error)
        })
      })
    },
    //切换作业点，显示新的作业点
    changLocationFromPage({commit}, location) {
      sessionStorage.setItem('location', location);
      commit('SET_LOCATION', location);
    }
  }
}



