import EnumAPI from '../../../src/page/system/sysMagnagement/enum/api/EnumAPI'
export default {
  state: {
    enumList: {},
    enumSelect: {},
    sysEnum: {}
  },
  actions: {
    generateEnumList (context, params) {
      context.commit('GENERATE_ENUM', params)
    },
    generateEnum (context, params) {
      EnumAPI.findEnumByTypeAndCode(params).then(response => {
        if (response.data.state === 1) {
          context.commit('SYS_ENUMERATE', response.data.data);
        }
      })
    },
    generateEnumSelect (context, params) {
      context.commit('ENUM_SELECT', params)
    }
  },
  mutations: {
    GENERATE_ENUM (state, enumList) {
      state.enumList = enumList
    },
    SYS_ENUMERATE (state, sysEnum) {
      state.sysEnum = sysEnum
    },
    ENUM_SELECT (state, enumSelect) {
      state.enumSelect = enumSelect
    }
  }
}



