import HttpUtil from '@/common/HttpUtil'
const I18nAPI = {
  getI18ns (params) {
    let postUrl = '/api/system/i18n/queryAll'
    return HttpUtil.post(postUrl, params, false)
  },
  getI18n (params) {
    let postUrl = '/api/system/i18n/queryByCode'
    return HttpUtil.post(postUrl, params, false)
  },
  getI18nLanguage (params) {
    let postUrl = '/api/system/sso/language'
    return HttpUtil.post(postUrl, params, false)
  },
  getI18nLanguageList () {
    let postUrl = '/api/system/i18n/getLocalI18nLanguageList'
    return HttpUtil.post(postUrl, false)
  },
  insert (params) {
    return HttpUtil.postJSON('/api/system/i18n/insert', params)
  },
  modify (params) {
    return HttpUtil.postJSON('/api/system/i18n/update', params)
  },
  query (params) {
    return HttpUtil.post('/api/system/i18n/query', params)
  },
  delete (param) {
    return HttpUtil.post('/api/system/i18n/del', {code: param.code})
  },
  getByCode (param) {
    return HttpUtil.post('/api/system/i18n/get', {code: param})
  }
}
export default I18nAPI



