import HttpUtil from '@/common/HttpUtil'
const ConsumerAPI = {
  query (params) {
    return HttpUtil.post('/api/api/consumer/display/list/page', params, false)
  },
  queryPermissions (params) {
    return HttpUtil.post('/api/api/consumer/display/list/permission', params, false)
  },
  queryUngrantGroup (params) {
    return HttpUtil.post('/api/api/consumer/display/list/ungrant/group', params, false)
  },
  add (params) {
    return HttpUtil.post('/api/api/consumer/add', params)
  },
  view (params) {
    return HttpUtil.post('/api/api/consumer/display/view/' + params)
  },
  modify (params) {
    return HttpUtil.post('/api/api/consumer/modify', params)
  },
  normal (params) {
    return HttpUtil.post('/api/api/consumer/normal/' + params)
  },
  remove (params) {
    return HttpUtil.post('/api/api/consumer/remove/' + params)
  },
  grant (consumerId, permissions) {
    return HttpUtil.post('/api/api/consumer/grant/permissions/to/' + consumerId, permissions)
  },
  revoke (consumerId, permissions) {
    return HttpUtil.post('/api/api/consumer/revoke/permissions/from/' + consumerId, permissions)
  }
}
export default ConsumerAPI



