<style scoped>
  .table-box {
    position: relative;
    box-sizing: border-box;
    height: 100%
  }
</style>
<template>
  <div>
    <div class="form-box">
      <fdcform ref="form" inline :model="formData" :label-width="80" @keyup.enter.native="handleSubmit('form')">
        <formitem label="编码" prop="code">
          <fdcinput v-model="formData.code" placeholder="请输入编码"
                    class="search-input" ></fdcinput>
        </formitem>
        <formitem label="名称" prop="name">
          <fdcinput v-model="formData.name" placeholder="请输入名称"
                    class="search-input"></fdcinput>
        </formitem>
        <formitem label="类型" prop="type">
          <fdcselect v-model="formData.type" placeholder="请选择作业点类型" class="search-input" clearable
                     @on-change="handleSubmit('form')">
            <fdcoption v-for="item in enumSelect['LOCATION_TYPE']" :value="item.enumCode" :key="item.enumCode">
              {{ item.enumValue }}
            </fdcoption>
          </fdcselect>
        </formitem>
        <fdcbutton type="primary" @click="handleSubmit('form')" class="search-submit">查询</fdcbutton>
        <fdcbutton type="ghost" @click="handleReset('form')" class="search-reset">重置</fdcbutton>
      </fdcform>
    </div>
    <div class="table-box">
      <fdctable ref="table" border highlight-row size="small" :loading="isLoading" :data="tableData"
                :columns="tableColumns" stripe @on-current-change="select"></fdctable>
      <div class="page-box">
        <Page v-show="pageShow" placement="top" :page-size="formData.pageNumber" :total="total"
              :current.sync="formData.pageNo"
              @on-change="changePageNo" @on-page-size-change="changePageSize" show-total show-elevator
              show-sizer></Page>
      </div>
    </div>
  </div>
</template>

<script>
  import Event from '@/utility/Event.js'
  import {mapActions, mapState} from 'vuex'
  import LocationAPI from "./api/LocationAPI";

  export default {
    name: 'changeLocation',
    data() {
      return {
        total: 0,
        formData: {
          code: '',
          name: '',
          type: '',
          pageNumber: 10,
          pageNo: 1
        },
        isLoading: false,
        pageShow: false,
        tableData: []
      }
    },
    mounted() {
      Event.$emit('addEnum', {typeList: ['LOCATION_TYPE']});
      let table = this.$refs['table'];
      Event.$on('clearSelect', () => this.handleClearCurrentRow(table));
      this.queryBindLocations();
    },
    computed: {
      ...mapState({
        sysEnumList: state => state.Enum.enumList,
        enumSelect: state => state.Enum.enumSelect
      }),
      tableColumns() {
        let tableCol = [
          {
            title: ' ',
            key: 'number',
            width: 60,
            align: 'center',
            render: (h, params) => {
              return h('span', params.index + (this.formData.pageNo - 1) * this.formData.pageNumber + 1)
            }
          },
          {
            title: '编码',
            key: 'code',
            align: 'center',
            ellipsis: true
          },
          {
            title: '名称',
            key: 'name',
            align: 'center',
            ellipsis: true
          },
          {
            title: '类型',
            key: 'type',
            align: 'center',
            render: (h, params) => {
              return h('span', {}, this.sysEnumList['LOCATION_TYPE_' + params.row.type])
            }
          }];
        return tableCol;
      }
    },
    methods: {
      ...mapActions([
        'generateEnumList'
      ]),
      select (currentRow, oldCurrentRow) {
        this.rowData = currentRow;
        Event.$emit('selectChangeLocation', this.rowData);
      },
      // 查询绑定的作业点
      handleSubmit(name) {
        this.$refs[name].validate((valid) => {
          if (valid) {
            this.queryBindLocations();
          }
        })
      },
      queryBindLocations() {
        this.isLoading = !this.isLoading;
        LocationAPI.queryBindLocations(this.formData).then((res) => {
          this.tableData = [];
          let state = res.data.state;
          if (state === 1) {
            let dataList = res.data.data.list;
            if (dataList !== null && dataList.length > 0) {
              this.pageShow = true;
              this.total = res.data.data.total;
            }
            this.tableData.push(...dataList)
          }
          this.isLoading = !this.isLoading
        })
      },
      handleReset(name) {
        this.$refs[name].resetFields()
      }
      ,
      changePageNo(value) {
        this.formData.pageNo = value
        this.queryBindLocations();
      }
      ,
      changePageSize(value) {
        this.formData.pageNo = 1;
        this.formData.pageNumber = value;
        this.queryBindLocations();
      },
      handleClearCurrentRow (table) {
        table.clearCurrentRow()
      }
    }
    ,
    components: {}
  }
</script>



