<template>
  <div>
    <div class="form-box-content" style="padding-top: 15px;padding-bottom: 15px">
      <div class="module-title-wrap">基本信息</div>
      <fdcform ref="form" :model="formData" :rules="paramValidate" :label-width="120">
        <row type="flex" justify="space-around">
          <fdccol span="12">
            <formitem :label="'统一编码：'" prop="unityCode">
              <fdcinput v-model="formData.unityCode" placeholder="消费者统一编码" readonly>
              </fdcinput>
            </formitem>
          </fdccol>
        </row>
        <row type="flex" justify="space-around">
          <fdccol span="12">
            <formitem :label="'名称：'" prop="name">
              <fdcinput v-model="formData.name" placeholder="消费者名称">
              </fdcinput>
            </formitem>
          </fdccol>
        </row>
        <row type="flex" justify="space-around">
          <fdccol span="12">
            <formitem :label="'IP白名单：'" prop="ipWhiteList">
              <fdcinput v-model="formData.ipWhiteList" placeholder="IP白名单">
              </fdcinput>
            </formitem>
          </fdccol>
        </row>
        <row type="flex" justify="space-around">
          <fdccol span="12">
            <formitem :label="'IP黑名单：'" prop="ipBlackList">
              <fdcinput v-model="formData.ipBlackList" placeholder="IP黑名单">
              </fdcinput>
            </formitem>
          </fdccol>
        </row>
        <row type="flex" justify="space-around">
          <fdccol span="12">
            <formitem :label="'联系人：'" prop="linkman">
              <fdcinput v-model="formData.linkman" placeholder="消费者联系人">
              </fdcinput>
            </formitem>
          </fdccol>
        </row>
        <row type="flex" justify="space-around">
          <fdccol span="12">
            <formitem :label="'联系人邮箱：'" prop="linkmanEmail" >
              <fdcinput v-model="formData.linkmanEmail" placeholder="消费者联系人">
              </fdcinput>
            </formitem>
          </fdccol>
        </row>
        <row type="flex" justify="space-around">
          <fdccol span="12">
            <formitem :label="'联系人电话：'" prop="linkmanMobile">
              <fdcinput v-model="formData.linkmanMobile" placeholder="消费者联系人电话">
              </fdcinput>
            </formitem>
          </fdccol>
        </row>
        <row type="flex" justify="space-around">
          <fdccol span="12">
            <formitem>
              <fdcbutton type="primary" @click="handleSubmit('form')">提交</fdcbutton>
              <fdcbutton type="ghost" @click="close('apiConsumerModify')" class="buttonClass">取消</fdcbutton>
            </formitem>
          </fdccol>
        </row>
      </fdcform>
      <div class="table-box">
        <div class="module-title-wrap">接口列表</div>
        <fdctable border :loading="tableLoading" width="200px" :data="tableData" :columns="tableColumns" stripe></fdctable>
        <div class="page-box">
          <Page v-show="pageShow" placement="top" :page-size="formData.pageSize" :total="total"
                :current.sync="formData.pageNo" @on-change="changePageNo" @on-page-size-change="changePageSize" show-total show-elevator show-sizer></Page>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import Event from '@/utility/Event.js'
import ConsumerAPI from './api/ConsumerAPI'
import {mapState, mapAction} from 'vuex'
export default {
  name: 'apiConsumerModify',
  data () {
    return {
      modal: false,
      queryShow: true,
      pageShow: false,
      tableLoading: false,
      total: 0,
      paramValidate: {
        unityCode: [{required: true, type: 'string', message: '统一编码必填', trigger: 'blur'}],
        name: [{required: true, type: 'string', message: '消费者名称必填', trigger: 'blur'}],
        linkman: [{required: true, type: 'string', message: '消费者联系人必填', trigger: 'blur'}],
        linkmanEmail: [{required: true, type: 'string', message: '消费者联系人邮箱必填', trigger: 'blur'}],
        linkmanMobile: [{required: true, type: 'string', message: '消费者联系人电话必填', trigger: 'blur'}]
      },
      formData: {
        id: this.$route.query.id,
        unityCode: '',
        name: '',
        linkman: '',
        linkmanEmail: '',
        linkmanMobile: '',
        ipWhiteList: '',
        ipBlackList: '',
        pageNo: 1,
        pageSize: 10
      },
      tableData: []
    }
  },
  computed: {
    tableColumns () {
      let tableCol = [
        {title: '编号', key: 'number', width: 80, align: 'center'},
        {title: '统一编码', key: 'groupCode', align: 'center'},
        {title: '角色名称', key: 'groupName', align: 'center'},
        {title: '状态', key: 'state', align: 'center'},
        {title: '操作',
          key: 'operate',
          align: 'left',
          render: (h, params) => {
            const row = params.row
            return h('div', [
              h('tooltip', {
                props: {
                  content: '撤销权限',
                  placement: 'top'
                }
              },
              [h('span', {
                on: {
                  click: () => {
                    this.revoke(row.id)
                  }
                }
              }, [h('icon', {
                props: {
                  type: 'trash-a',
                  size: 20,
                  color: 'red'
                }
              }, '撤销权限')])]
              )])
          }
        }
      ]
      return tableCol
    }
  },
  methods: {
    handleSubmit (name) {
      this.modify()
    },
    handleReset (name) {
      this.$refs[name].resetFields()
    },
    modify () {
      let consumer = {
        id: this.$route.query.id,
        unityCode: this.formData.unityCode,
        name: this.formData.name,
        linkman: this.formData.linkman,
        linkmanEmail: this.formData.linkmanEmail,
        linkmanMobile: this.formData.linkmanMobile,
        ipWhiteList: this.formData.ipWhiteList,
        ipBlackList: this.formData.ipBlackList
      }
      ConsumerAPI.modify(consumer).then((res) => {
        if (res.data.state == 1) {
          this.$Notice.success({
            title: '操作成功',
            desc: '修改消费者成功'
          })
          this.close('apiConsumerModify')
        } else {
          this.$Notice.error({
            title: '操作失败',
            desc: res.data.msg
          })
        }
      })
    },
    view () {
      ConsumerAPI.view(this.$route.query.id).then((res) => {
        this.formData = res.data.data
      })
    },
    showPermissions () {
      this.tableLoading = !this.tableLoading
      let query = {
        'pageNo': this.formData.pageNo,
        'pageNumber': this.formData.pageSize,
        'consumerId': this.formData.id
      }
      ConsumerAPI.queryPermissions(query).then((res) => {
        this.tableData = []
        if (res.data.state == 1) {
          let dataList = res.data.data.list
          if (dataList != null) {
            this.pageShow = true
            this.total = res.data.data.total
          }
          for (let i = 0; i < dataList.length; i++) {
            let permission = dataList[i]
            this.tableData.push({
              id: permission.id,
              number: (i + 1),
              groupCode: permission.group.unityCode,
              groupName: permission.group.name,
              state: permission.state
            })
          }
        }
        this.tableLoading = !this.tableLoading
      })
    },
    revoke (permissionId) {
      let permission = {
        'id': permissionId
      }
      ConsumerAPI.revoke(this.formData.id, permission).then((res) => {
        if (res.data.state == 1) {
          this.$Notice.success({
            title: '操作成功',
            desc: '分组成功'
          })
          this.showPermissions()
        } else {
          this.$Notice.error({
            title: '操作失败',
            desc: res.data.msg
          })
        }
      })
    },
    close (router) {
      Event.$emit('closePage', router)
      Event.$once('refreshConsumerList', null)
    }
  },
  mounted () {
    this.view()
    this.showPermissions()
  },
  components: {}
}
</script>



