<template >
  <div>
    <template v-for="menu in menuList">
      <fdcmenuitem v-if="!menu.subMenu && menu.module==defaultModule " :key="menu.path" :name="menu.router">
        <Icon type="ios-paper"></Icon>
        <span>{{menu.title}}</span>
      </fdcmenuitem>
      <submenu v-if="menu.subMenu && menu.subMenu.length > 0 && menu.module==defaultModule" :name="menu.router" :key="menu.path">
        <template slot="title">
          <Icon type="ios-paper"></Icon>
          <span>{{menu.title}}</span>
        </template>
        <side-menu-item v-if="menu.subMenu && menu.subMenu.length > 0 && menu.module==defaultModule" :menuList="menu.subMenu"></side-menu-item>
      </submenu>
    </template>
  </div>

</template>

<script>
import {mapState} from 'vuex'
export default {
  name: 'SideMenuItem',
  data () {
    return {
    }
  },
  props: {
    menuList: {
      type: Array
    }
  },
  computed: {
    ...mapState({
      menuMap: state => state.User.menuMap,
      defaultModule: state => state.defaultModule
    })
  },
  watch: {
    $route () {
      this.checkMenu()
    }
  },
  methods: {
    checkMenu () {
      let routerName = this.$route.name;
      let path = this.menuMap[routerName]
      if(path===null || path ===undefined || path===''){
          return ;
      }
      let menupath = path.split('_');
      if (menupath) {
        if(routerName!='page404' && routerName != 'page500' && routerName != 'demo'){
          this.$store.commit('changeModule', parseInt(menupath[0]));
          this.$emit('menuCollapsed', menupath.slice(1));
        }else{
          this.$store.commit('changeModule', this.defaultModule);
        }
      }
    }
  }

}
</script>



