<template>
  <div>
    <div class="page-header-box">
      <div class="header-btn-box">
        <fdcbutton v-show="queryShow" @click="hideForm" icon="eye-disabled">隐藏</fdcbutton>
        <fdcbutton v-show="!queryShow" @click="showForm" icon="eye">显示</fdcbutton>
        <fdcbutton icon="android-add" @click="toAdd">新增</fdcbutton>
      </div>
    </div>

    <div class="form-box" v-show="queryShow">
      <fdcform ref="form" inline :model="formData" :label-width="80">
        <formitem label="编码" prop="code">
          <fdcinput v-model="formData.code" placeholder="请输入编码"
                    class="search-input"></fdcinput>
        </formitem>
        <formitem label="名称" prop="name">
          <fdcinput v-model="formData.name" placeholder="请输入名称"
                    class="search-input"></fdcinput>
          <fdcbutton type="primary" @click="handleSubmit('form')" class="search-submit">查询</fdcbutton>
          <fdcbutton type="ghost" @click="handleReset('form')" class="search-reset">重置</fdcbutton>
        </formitem>
      </fdcform>
    </div>

    <div class="table-box">
      <fdctable border size="small" :loading="tableLoading" :data="tableData"
                :columns="tableColumns"
                stripe></fdctable>
      <div class="page-box">
        <Page v-show="pageShow" placement="top" :page-size="formData.pageSize" :total="total"
              :current.sync="formData.pageNo" @on-change="changePageNo" @on-page-size-change="changePageSize" show-total
              show-elevator show-sizer></Page>
      </div>
    </div>

    <!--新增修改页面弹窗-->
    <Modal :width="900" v-model="showEditModal">
      <div class="form-box-content">
        <div class="module-title-wrap">用户组信息</div>
        <fdcform ref="formValidate" :model="formValidate" :rules="ruleValidate" :label-width="80" inline>
          <row span="24">
            <fdccol span="8" offset="4">
              <formitem label="编码" prop="code">
                <fdcinput v-model="formValidate.code" placeholder="Enter code" style="width:400px">
                </fdcinput>
              </formitem>
            </fdccol>
          </row>
          <row span="24">
            <fdccol span="8" offset="4">
              <formitem label="名称" prop="name">
                <fdcinput v-model="formValidate.name" placeholder="Enter name" style="width:400px">
                </fdcinput>
              </formitem>
            </fdccol>
          </row>
        </fdcform>
      </div>
      <!--自定义页脚和按钮-->
      <div slot="footer">
        <fdcbutton type="primary" @click="add('formValidate')" v-show="addSaveBtnShow">提交</fdcbutton>
        <fdcbutton type="primary" @click="modify('formValidate')" v-show="editSaveBtnShow">提交</fdcbutton>
        <fdcbutton type="ghost" @click="cancel" style="margin-left: 8px">取消</fdcbutton>
      </div>
    </Modal>

  </div>
</template>

<script>
  import SysGroupAPI from './api/SysGroupAPI'
  import {mapState, mapAction} from 'vuex'

  export default {
    name: 'sysGroup',
    data() {
      return {
        modal: false,
        queryShow: true,
        pageShow: false,
        tableLoading: false,
        i18nCodes: [],
        total: 0,
        formData: {
          code: '',
          name: '',
          pageNo: 1,
          pageSize: 20
        },
        tableData: [],
        ruleValidate: {
          code: [{
            required: true,
            type: 'string',
            message: '编码必填',
            trigger: 'blur'
          }],
          name: [{
            required: true,
            type: 'string',
            message: '名称必填',
            trigger: 'blur'
          }]
        },
        formValidate: {
          id: '',
          name: '',
          code: ''
        },
        showEditModal: false,
        addSaveBtnShow: false,
        editSaveBtnShow: false
      }
    },
    computed: {
      // ...mapState({
      //   i18nMap : state => state.I18n.i18nMap
      // }),
      tableColumns() {
        let tableCol = [{
          title: ' ',
          key: 'number',
          width: 60,
          align: 'center'
        }, {
          title: '编码',
          key: 'code',
          align: 'center'
        }, {
          title: '名称',
          key: 'name',
          align: 'center'
        }, {
          title: '操作',
          key: 'action',
          width: 100,
          align: 'center',
          render: (h, params) => {
            return h('div', [
              h('tooltip', {
                props: {
                  content: '修改',
                  placement: 'top'
                }
              }, [h('span', {
                  on: {
                    click: () => {
                      this.toModify(params.row);
                    }
                  }
                }, [h('icon', {
                  props: {
                    type: 'edit',
                    size: 20,
                    color: '#0d96ff'
                  },
                  style: {
                    marginRight: '8px'
                  }
                }, '修改')])]
              ),
              h('tooltip', {
                props: {
                  content: '删除',
                  placement: 'top'
                }
              }, [h('span', {
                on: {
                  click: () => {
                    this.$Modal.confirm({
                      title: '删除',
                      content: '<p>确定删除？</p>',
                      onOk: () => {
                        this.remove(params.row.id)
                      },
                      onCancel: () => {
                        // this.$Message.info('Clicked cancel');
                      }
                    })
                  }
                }
              }, [h('icon', {
                props: {
                  type: 'trash-a',
                  size: 20,
                  color: 'red'
                }
              }, '删除')])]
              )
            ])
          }
        }
        ]
        return tableCol
      }
    },
    mounted() {
    },
    methods: {
      hideForm() {
        this.queryShow = !this.queryShow
      },
      showForm() {
        this.queryShow = !this.queryShow
      },
      // 查询
      handleSubmit(name) {
        this.query()
      },
      handleReset(name) {
        this.$refs[name].resetFields()
      },
      query() {
        this.isLoading = !this.isLoading
        let queryCondition = {
          'pageNo': this.formData.pageNo,
          'pageNumber': this.formData.pageSize,
          'code': this.formData.code,
          'name': this.formData.name
        }
        SysGroupAPI.query(queryCondition).then((res) => {
          this.tableData = []
          let state = res.data.state
          if (state === 1) {
            let dataList = res.data.data.list
            if (dataList !== null) {
              this.pageShow = true
              this.total = res.data.data.total
            }
            for (let i = 0; i < dataList.length; i++) {
              let data = dataList[i]
              this.tableData.push({
                number: ((this.formData.pageNo - 1) * this.formData.pageSize + i + 1),
                id: data.id,
                code: data.code,
                name: data.name
              })
            }
          }
          this.isLoading = !this.isLoading
        })
      },
      changePageNo(value) {
        this.formData.pageNo = value
        this.query()
      },
      changePageSize(value) {
        this.formData.pageNo = 1
        this.formData.pageSize = value
        this.query()
      },
      toAdd() {
        this.showEditModal = true;
        this.addSaveBtnShow = true;
        this.editSaveBtnShow = false;
        this.formValidate.id = '';
        this.formValidate.code = '';
        this.formValidate.name = '';
      },
      add(name) {
        this.$refs[name].validate((valid) => {
          if (valid) {
            SysGroupAPI.insert(this.formValidate).then((res) => {
              if (res.data.state === 1) {
                this.$Notice.success({
                  title: '新增用户组成功',
                });
                this.showEditModal = false;
                this.query();
              } else {
                this.$Notice.error({
                  title: '操作失败',
                  desc: res.data.msg
                });
              }
            });
          }
        });
      },
      toModify(role) {
        this.showEditModal = true;
        this.addSaveBtnShow = false;
        this.editSaveBtnShow = true;
        this.formValidate.id = role.id;
        this.formValidate.code = role.code;
        this.formValidate.name = role.name;
      },
      modify(name) {
        this.$refs[name].validate((valid) => {
          if (valid) {
            SysGroupAPI.modify(this.formValidate).then((res) => {
              if (res.data.state === 1) {
                this.$Notice.success({
                  title: '修改用户组成功',
                });
                this.showEditModal = false;
                this.query();
              } else {
                this.$Notice.error({
                  title: '操作失败',
                  desc: res.data.msg
                });
              }
            });
          }
        })
      },
      cancel() {
        this.showEditModal = false;
      },
      // showDetail (groupId) {
      //   Event.$emit('addPage', {
      //     title: '用户组明细',
      //     router: 'sysGroupDetail',
      //     checked: true,
      //     groupId: groupId
      //   })
      // },
      remove(groupId) {
        // 这里需要弹窗
        let condition = {
          'groupId': groupId
        }
        SysGroupAPI.delete(condition).then((res) => {
          if (res.data.state == 1) {
            this.$Notice.success({
              title: '操作成功',
              desc: '删除成功'
            })
            this.query()
          } else {
            this.$Notice.error({
              title: '操作失败',
              desc: res.data.msg
            })
          }
        })
      },
      // 测试数据
      mockTableData() {
        let data = []
        for (let i = 0; i < 20; i++) {
          data.push({
            number: i + 1,
            name: 'UerGroup' + i,
            code: 'UG' + i
          })
        }
        return data
      }
    },
    components: {}
  }
</script>



