<style scoped>
</style>
<template>
  <div>
    <div class="form-box-content">
      <div class="module-title-wrap">基本信息</div>
      <fdcform   :model="formValidate" :label-width="120">
        <row type="flex" justify="space-around">
          <fdccol span="10">
            <formitem label="代码">
              <fdcinput v-model="formValidate.code"  placeholder="" x></fdcinput>
            </formitem>
          </fdccol>
          <fdccol span="10"></fdccol>
        </row>
        <div v-for="item in formValidate.details">
          <row type="flex" justify="space-around">
            <fdccol span="10">
              <formitem :label="item.name">
                <fdcinput v-model="item.id"  v-show="false" placeholder="" readonly>
                </fdcinput>
                <fdcinput v-model="item.language"  v-show="false" placeholder="" readonly>
                </fdcinput>
                <fdcinput v-model="item.value" type="textarea" placeholder="" readonly>
                </fdcinput>
              </formitem>
            </fdccol>
            <fdccol span="10">
              <formitem label="备注" prop="remark">
                <fdcinput  v-model="item.remark" type="textarea" placeholder="Enter value" readonly>
                </fdcinput>
              </formitem>
            </fdccol>
          </row>
        </div>
        <row type="flex" justify="space-around">
          <fdccol span = '6'>
            <formitem>
              <fdcbutton type="ghost" @click="cancel('showI18n')" class="buttonClass">关闭</fdcbutton>
            </formitem>
          </fdccol>
        </row>
      </fdcform>
    </div>
  </div>
</template>
<script>
import I18nAPI from './api/I18nAPI'
import Event from '@/utility/Event.js'
export default {
  data: function () {
    return {
      formValidate: {
        code: '',
        changeCode: false,
        details: []
      }
    }
  },
  methods: {
    generateForm: function () {
      I18nAPI.getByCode(this.$route.query.param).then((res) => {
        if (res.data) {
          this.formValidate.code = res.data.data.code
          this.getI18nLanguageList(res.data.data.details)
        } else {
          this.$Notice.error({
            title: '操作失败',
            desc: res.data.msg
          })
        }
      })
    },
    getI18nLanguageList: function (details) {
      I18nAPI.getI18nLanguageList().then((res) => {
        if (res.data) {
          for (let i = 0; i < res.data.length; i++) {
            let name = res.data[i].name
            let code = res.data[i].code
            var formData
            var flag = false
            for (let j = 0; j < details.length; j++) {
              if (details[j].language === code) {
                flag = true
                formData = {
                  id: details[j].id,
                  language: code,
                  value: details[j].value,
                  name: name,
                  remark: details[j].remark
                }
                break
              }
            }
            if (flag === false) {
              formData = {
                language: code,
                value: '',
                name: name,
                remark: ''
              }
            }
            this.formValidate.details.push(formData)
          }
        }
      })
    },
    cancel: function (router) {
      Event.$emit('closePage', router)
    }
  },
  components: {},
  mounted: function () {
    this.generateForm()
  }
}
</script>



