import HttpUtil from '@/common/HttpUtil'
const CustomerAPI = {
  insert (params) {
    return HttpUtil.postJSON('/api/basic/customer/insert', params)
  },
  getById (id) {
    return HttpUtil.post('/api/basic/customer/getById', {id: id})
  },
  modify (params) {
    return HttpUtil.post('/api/basic/customer/modify', params)
  },
  query (params) {
    return HttpUtil.post('/api/basic/customer/query', params)
  },
  delete (param) {
    return HttpUtil.post('/api/basic/customer/del', {customerId: param.id})
  }

}
export default CustomerAPI



