import HttpUtil from '@/common/HttpUtil';

const SysRoleAPI = {
  query(params) {
    return HttpUtil.post('/api/system/role/query', params, false);
  },
  insert(params, callback) {
    return HttpUtil.post('/api/system/role/insert', params, false, callback);
  },
  getById(id){
    return HttpUtil.post('/api/system/role/get',{roleId:id})
  },
  modify(params, callback) {
    return HttpUtil.post('/api/system/role/update', params, false, callback);
  },
  delete(params, callback) {
    return HttpUtil.post('/api/system/role/del', params, false, callback);
  },
  queryRolePermission(params){
    return HttpUtil.post('/api/system/role/queryRolePermission', params);
  },
  updateRolePermission(params, callback){
    return HttpUtil.post('/api/system/role/updateRolePermission', params, false, callback);
  },
  queryBindUsers(params){
    return HttpUtil.post('/api/system/role/queryBindUsers', params);
  },
  queryUnBindUsers(params){
    return HttpUtil.post('/api/system/role/queryUnBindUsers', params);
  },
  bindUsers(params, callback) {
    return HttpUtil.post('/api/system/role/bindUsers', params, false, callback);
  },
  unBindUsers(params, callback) {
    return HttpUtil.post('/api/system/role/unBindUsers', params, false, callback);
  }
}

export default SysRoleAPI



