import HttpUtil from '@/common/HttpUtil';

const SysGroupAPI = {
  query(params) {
    return HttpUtil.post('/api/system/group/query', params, false);
  },
  insert(params, callback) {
    return HttpUtil.post('/api/system/group/insert', params, false, callback);
  },
  getById(id){
    return HttpUtil.post('/api/system/group/get',{groupId:id})
  },
  modify(params, callback) {
    return HttpUtil.post('/api/system/group/update', params, false, callback);
  },
  delete(params, callback) {
    return HttpUtil.post('/api/system/group/del', params, false, callback);
  },
  queryBindRoles(params){
    return HttpUtil.post('/api/system/group/queryBindRoles', params, false);
  },
  queryUnBindRoles(params){
    return HttpUtil.post('/api/system/group/queryUnBindRoles', params, false);
  },
  bindRole(params, callback) {
    return HttpUtil.post('/api/system/group/bindRole', params, false, callback);
  },
  unBindRole(params, callback) {
    return HttpUtil.post('/api/system/group/unBindRole', params, false, callback);
  }
}

export default SysGroupAPI



