<style scoped>
  .table-box {
    position: relative;
    box-sizing: border-box ;
    height:100%
  }
</style>
<template>
  <div>
    <div class="page-header-box">
      <div class="header-msg-box">
        <p>公司管理</p>
      </div>
      <div class="header-btn-box">
        <fdcbutton v-show="queryShow" @click="hideForm" icon="eye-disabled">{{i18nMap['system_hiddenQuery']}}</fdcbutton>
        <fdcbutton v-show="!queryShow" @click="showForm" icon="eye">{{i18nMap['system_showQuery']}}</fdcbutton>
        <fdcbutton icon="android-add" @click="add">{{i18nMap['system_companyList_button_add']}}</fdcbutton>
      </div>
    </div>

    <div class="form-box" v-show="queryShow" >
      <fdcform ref="formData" inline :model="formData" :label-width="80" class="search-form" @submit.native.prevent
               @keyup.enter.native="handleSubmit('formData')">
        <formitem :label="i18nMap['system_companyList_label_name']" prop="name">
          <fdcinput v-model="formData.name" :placeholder="i18nMap['system_companyList_query_name']" class="search-input"></fdcinput>
          <fdcbutton type="primary" @click="handleSubmit('formData')" class="search-submit">{{i18nMap['system_companyList_button_submit']}}</fdcbutton>
          <fdcbutton type="ghost" @click="handleReset('formData')" class="search-reset">{{i18nMap['system_button_reset']}}</fdcbutton>
        </formitem>
      </fdcform>
    </div>

    <div class="table-box">
      <fdctable border size="small" :loading="tableLoading" :data="tableData" :columns="tableColumns" stripe></fdctable>
      <div class="page-box">
        <Page v-show="pageShow" placement="top" :page-size="formData.pageNumber" :total="total" :current.sync="formData.pageNo"  @on-change="changePageNo" @on-page-size-change="changePageSize" show-total show-elevator show-sizer></Page>
      </div>
    </div>
  </div>
</template>

<script>
import Event from '@/utility/Event.js'
import CompanyAPI from './api/CompanyAPI'
import {mapState, mapActions} from 'vuex'

export default {
  name: 'company',
  data () {
    return {
      queryShow: true,
      pageShow: false,
      tableLoading: false,
      i18nCodes: ['system_companyList_title', 'system_showQuery',
        'system_hiddenQuery', 'system_companyList_label_name',
        'system_companyList_query_name', 'system_companyList_button_add',
        'system_companyList_button_submit', 'system_button_reset',
        'system_companyList_companyName_message', 'system_companyList_column_code',
        'system_companyList_column_name', 'system_companyList_column_state',
        'system_companyList_column_contact', 'system_companyList_column_email',
        'system_companyList_column_telephone', 'system_companyList_column_address',
        'system_companyList_column_remark', 'system_column_operate'],
      total: 0,
      formData: {
        name: '',
        pageNo: 1,
        pageNumber: 10
      },
      tableData: []
    }
  },
  computed: {
    ...mapState({
      i18nMap: state => state.I18n.i18nMap,
      sysEnumList: state => state.Enum.enumList
    }),
    tableColumns () {
      let tableCol = [{
        title: ' ',
        type: 'index2',
        width: 60,
        align: 'center',
        render: (h, params) => {
          return h('span', params.index + (this.formData.pageNo - 1) * this.formData.pageNumber + 1)
        }
      }, {
        title: this.i18nMap['system_companyList_column_code'],
        key: 'code',
        align: 'center'
      }, {
        title: this.i18nMap['system_companyList_column_name'],
        key: 'name',
        align: 'center'
      }, {
        title: this.i18nMap['system_companyList_column_state'],
        key: 'isDeleted',
        align: 'center',
        render: (h, params) => {
          return h('span', {}, this.sysEnumList['YES_NO_' + params.row.isDeleted])
        }
      }, {
        title: this.i18nMap['system_companyList_column_contact'],
        key: 'contact',
        align: 'center'
      }, {
        title: this.i18nMap['system_companyList_column_email'],
        key: 'email',
        align: 'center'
      }, {
        title: this.i18nMap['system_companyList_column_telephone'],
        key: 'telephone',
        align: 'center'
      }, {
        title: this.i18nMap['system_companyList_column_address'],
        key: 'address',
        align: 'center'
      }, {
        title: this.i18nMap['system_companyList_column_remark'],
        key: 'remark',
        align: 'center'
      }, {
        title: this.i18nMap['system_column_operate'],
        key: 'operate',
        align: 'left',
        render: (h, params) => {
          const row = params.row
          const type = row.isDeleted === 0 ? 'locked' : 'unlocked'
          const color = row.isDeleted === 0 ? '#8a8a8a' : '#0d96ff'
          const content = row.isDeleted === 0 ? '禁用' : '启用'
          return h('div', [
            h('span', {
              attrs : {
                title: '修改'
              },
              on: {
                click: () => {
                  if (row.isDeleted === 0) {
                    this.modify(row.id)
                  }else{
                    this.$Notice.error({
                      title: '公司禁用不允许修改'
                    })
                  }
                }
              }
            }, [h('icon', {
              props: {
                type: 'edit',
                size: 20,
                color: '#0d96ff'
              },
              style: {
                marginRight: '5px'
              }
            }, '修改')])
            ,
            h('tooltip', {
              props: {
                content: content,
                placement: 'top'
              }
            }, [
              h('span', {
                on: {
                  click: () => {
                    if (row.isDeleted === 0) {
                      this.$Modal.confirm({
                        title: '禁用',
                        content: '<p>确定禁用？</p>',
                        onOk: () => {
                          this.delete(row.id)
                        },
                        onCancel: () => {}
                      })
                    } else {
                      this.active(row.id)
                    }
                  }
                }
              }, [h('icon', {
                props: {
                  type: type,
                  size: 20,
                  color: color
                }
              }, '删除')])
            ])
          ])
        }
      }
      ]
      return tableCol
    }
  },

  mounted () {
    Event.$emit('addEnum', {typeList:['YES_NO']})
    this.fetchI18n(this.i18nCodes)
    Event.$on('refreshCompanyList', obj => this.query())
  },
  methods: {
    ...mapActions([
      'fetchI18n'
    ]),
    hideForm () {
      this.queryShow = !this.queryShow
    },
    showForm () {
      this.queryShow = !this.queryShow
    },
    collapsedSider () {
      this.isCollapsed = !this.isCollapsed
    },
    handleSubmit (name) {
      this.query()
    },
    handleReset (name) {
      this.$refs[name].resetFields()
    },
    query () {
      this.tableLoading = !this.tableLoading;
      CompanyAPI.query(this.formData).then((res) => {
        this.tableData = []
        let state = res.data.state
        if (state === 1) {
          let dataList = res.data.data.list
          if (dataList != null) {
            this.pageShow = true
            this.total = res.data.data.total
          }
          this.tableData.push(...dataList)
        }
        this.tableLoading = !this.tableLoading
      })
    },
    changePageNo (value) {
      this.formData.pageNo = value
      this.query()
    },
    changePageSize (value) {
      this.formData.pageNo = 1
      this.formData.pageNumber = value
      this.query()
    },
    showModal () {
      this.modal = !this.modal
    },
    add () {
      Event.$emit('addPage', {
        title: '新增公司',
        router: 'addCompany',
        check: true
      })
    },
    modify (params) {
      Event.$emit('addPage', {
        title: '修改公司',
        router: 'modifyCompany',
        checked: true,
        paramId: params
      })
    },
    viewDetail (value) {

    },
    delete (companyId) {
      CompanyAPI.delete({companyId: companyId}, this.query)
    },
    active (companyId) {
      CompanyAPI.active({companyId: companyId}, this.query)
    }
  },
  components: {
  }
}
</script>



