import HttpUtil from '@/common/HttpUtil'

const LoginAPI = {
  getRSAPublicKey (params) {
    let postUrl = '/api/system/sso/loginPass'
    return HttpUtil.post(postUrl, params,false)
  },
  login (params) {
    let postUrl = '/api/system/sso/login'
    return HttpUtil.post(postUrl, params,false)
  },
  logout(params){
    let postUrl = '/api/system/sso/logout'
    return HttpUtil.post(postUrl, params,false)
  }
}
export default LoginAPI



