<style scoped>
  .table-box {
    position: relative;
    box-sizing: border-box;
    height: 100%
  }
</style>
<template>
  <div>
    <div class="page-header-box">
      <div class="header-msg-box">
        <p>角色绑定用户</p>
      </div>
      <div class="header-btn-box">
        <fdcbutton icon="plus-round" @click="toBindUsers">绑定用户</fdcbutton>
        <fdcbutton icon="close-round" @click="toUnBindUsers">批量解绑</fdcbutton>
        <fdcbutton icon="ios-undo" @click="close">关闭</fdcbutton>
      </div>
    </div>
    <div class="form-box">
      <fdcform ref="form" inline :model="formData" :label-width="80" @keyup.enter.native="handleSubmit('form')">
        <formitem label="姓名" prop="userName">
          <fdcinput v-model="formData.userName" placeholder="请输入姓名"
                    class="search-input" ></fdcinput>
        </formitem>
        <formitem label="登录名" prop="loginName">
          <fdcinput v-model="formData.loginName" placeholder="请输入登录名"
                    class="search-input"></fdcinput>
          <fdcbutton type="primary" @click="handleSubmit('form')" class="search-submit">查询</fdcbutton>
          <fdcbutton type="ghost" @click="handleReset('form')" class="search-reset">重置</fdcbutton>
        </formitem>
      </fdcform>
    </div>
    <div class="table-box">
      <fdctable ref="table" border size="small" :loading="isLoading" :data="tableData"
                :columns="tableColumns" stripe @on-selection-change="selectChange"></fdctable>
      <div class="page-box">
        <Page v-show="pageShow" placement="top" :page-size="formData.pageNumber" :total="total" :current.sync="formData.pageNo"
              @on-change="changePageNo" @on-page-size-change="changePageSize" show-total show-elevator
              show-sizer></Page>
      </div>
    </div>

    <modal v-model="bindUsersShow" :width="1000" title="请绑定用户" @on-ok="bindUsers" @on-cancel="cancel">
      <div class="form-box">
        <fdcform ref="form2" inline :model="formData2" :label-width="80" @keyup.enter.native="handleSubmit2('form2')">
          <formitem label="姓名" prop="userName">
            <fdcinput v-model="formData2.userName"  placeholder="请输入姓名"
                      class="search-input"></fdcinput>
          </formitem>
          <formitem label="登录名" prop="loginName">
            <fdcinput v-model="formData2.loginName" placeholder="请输入登录名"
                      class="search-input"></fdcinput>
            <fdcbutton type="primary" @click="handleSubmit2('form2')" class="search-submit">查询</fdcbutton>
            <fdcbutton type="ghost" @click="handleReset('form2')" class="search-reset">重置</fdcbutton>
          </formitem>
        </fdcform>
      </div>
      <div class="table-box">
        <fdctable ref="table2" border size="small" :loading="isLoading" :data="tableData2"
                  :columns="tableColumns2" stripe @on-selection-change="selectChange"></fdctable>
        <div class="page-box">
          <Page v-show="pageShow2" placement="top" :page-size="formData2.pageNumber" :total="total2" :current.sync="formData2.pageNo"
                @on-change="changePageNo2" @on-page-size-change="changePageSize2" show-total show-elevator
                show-sizer></Page>
        </div>
      </div>
    </modal>
  </div>
</template>

<script>
  import SysRoleAPI from './api/SysRoleAPI'
  import Event from '@/utility/Event.js'
  import {mapActions, mapState} from 'vuex'

  export default {
    name: 'sysRoleUser',
    data() {
      return {
        total: 0,
        total2: 0,
        formData: {
          userName: '',
          loginName: '',
          pageNumber: 10,
          pageNo: 1
        },
        formData2: {
          userName: '',
          loginName: '',
          pageNumber: 10,
          pageNo: 1
        },
        isLoading: false,
        pageShow: false,
        pageShow2: false,
        tableData: [],
        tableData2: [],
        bindUsersShow: false,
        selectDataStore: [], //分页批量选择，建一个数据仓库
        dataStoreTo: [],   //建立一个中间数据仓库
        roleId:this.$route.query.roleId
      }
    },
    mounted() {
      this.queryBindUsers();
    },
    watch: {
      roleIds() {
        if(this.$route.query.roleId) {
          this.roleId = this.$route.query.roleId
        }
        this.queryBindUsers();
      }
    },
    computed: {
      roleIds() {
        return this.$route.query.roleId
      },
      tableColumns() {
        let tableCol = [
          {
            type: 'selection',
            width: 60,
            align: 'center'
          },
          {
            title: ' ',
            key: 'number',
            width: 60,
            align: 'center',
            render: (h, params) => {
              return h('span', params.index + (this.formData.pageNo - 1) * this.formData.pageNumber + 1)
            }
          },
          {
            title: '登录名',
            key: 'loginName',
            align: 'center',
            ellipsis: true
          },
          {
            title: '姓名',
            key: 'name',
            align: 'center',
            ellipsis: true
          },
          {
            title: '操作',
            key: 'action',
            align: 'left',
            render: (h, params) => {
              return h('div', [
                h('tooltip', {
                  props: {
                    content: '解绑',
                    placement: 'top'
                  }
                }, [h('span', {
                  on: {
                    click: () => {
                      this.$Modal.confirm({
                        title: '解绑',
                        content: '<p>确定解绑？</p>',
                        onOk: () => {
                          this.unBind(params.row.id);
                        },
                        onCancel: () => {
                        }
                      });
                    }
                  }
                }, [h('icon', {
                  props: {
                    type: 'close-round',
                    size: 20
                  },
                  style: {
                    marginRight: '5px'
                  }
                }, '解绑')])
                ])
              ])
            }
          }];
        return tableCol;
      },
      tableColumns2() {
        let tableCol = [
          {
            type: 'selection',
            width: 60,
            align: 'center'
          },
          {
            title: ' ',
            key: 'number',
            width: 60,
            align: 'center',
            render: (h, params) => {
              return h('span', params.index + (this.formData2.pageNo - 1) * this.formData2.pageNumber + 1)
            }
          },
          {
            title: '登录名',
            key: 'loginName',
            align: 'center',
            ellipsis: true
          },
          {
            title: '姓名',
            key: 'name',
            align: 'center',
            ellipsis: true
          }
        ];
        return tableCol;
      },
    },
    methods: {
      ...mapActions([
          'generateEnumList'
        ]),
      // 绑定用户按钮
      toBindUsers() {
        this.bindUsersShow = true;
        this.queryUnBindUsers();

        //已选择数据要清空
        this.selectDataStore = [];
        this.dataStoreTo = [];
      },
      // 查询绑定的用户
      handleSubmit(name) {
        this.$refs[name].validate((valid) => {
          if (valid) {
            this.queryBindUsers();
          }
        })
      },
      //查询未绑定的用户
      handleSubmit2(name) {
        this.$refs[name].validate((valid) => {
          if (valid) {
            this.queryUnBindUsers();
          }
        })
      },
      queryBindUsers() {
        this.isLoading = !this.isLoading;
        let condition = {
          'roleId': this.roleId,
          ...this.formData
        };
        SysRoleAPI.queryBindUsers(condition).then((res) => {
          this.tableData = [];
          let state = res.data.state;
          if (state === 1) {
            let dataList = res.data.data.list;
            if (dataList !== null && dataList.length > 0) {
              this.pageShow = true;
              this.total = res.data.data.total;
              this.tableData.push(...dataList)
            }
          }
          this.isLoading = !this.isLoading
        })
      },
      queryUnBindUsers() {
        this.isLoading = !this.isLoading;
        let condition = {
          'roleId': this.roleId,
          ...this.formData2
        };
        SysRoleAPI.queryUnBindUsers(condition).then((res) => {
          this.tableData2 = [];
          let state = res.data.state;
          if (state === 1) {
            let dataList = res.data.data.list;
            if (dataList !== null && dataList.length > 0) {
              this.pageShow2 = true;
              this.total2 = res.data.data.total
              this.tableData2.push(...dataList)
            }
          }
          this.isLoading = !this.isLoading
        })
      },
      //绑定用户
      bindUsers() {
        this.getSelectedData();
        if (this.selectDataStore.length > 0) {
          let userIdList = [];
          for (let i = 0; i < this.selectDataStore.length; i++) {
            userIdList.push(this.selectDataStore[i].id);
          }
          let condition = {
            'userIdList': userIdList,
            'roleId': this.roleId
          };
          SysRoleAPI.bindUsers(condition).then((res) => {
            if (res.data.state == 1) {
              this.$Notice.success({
                title: '绑定成功',
              });
              this.queryBindUsers();
            } else {
              this.$Notice.error({
                title: '操作失败',
                desc: res.data.msg
              });
            }
          });

          this.bindUsersShow = false;
          //已选择数据要清空
          this.selectDataStore = [];
          this.dataStoreTo = [];
        }
      },
      toUnBindUsers(){
        this.getSelectedData();
        if (this.selectDataStore.length > 0) {
          this.$Modal.confirm({
            title: '解绑',
            content: '<p>确定解绑？</p>',
            onOk: () => {
              this.unBindUsers();
            }
          });
        }
      },
      //解绑用户
      unBindUsers() {
        if (this.selectDataStore.length > 0) {
          let userIdList = [];
          for (let i = 0; i < this.selectDataStore.length; i++) {
            userIdList.push(this.selectDataStore[i].id);
          }
          let condition = {
            'userIdList': userIdList,
            'roleId': this.roleId
          };
          if(this.total === userIdList.length){
            this.$Notice.warning({
              title: '解绑所有用户将会失去所有权限',
            });
            return;
          }
          SysRoleAPI.unBindUsers(condition).then((res) => {
            if (res.data.state == 1) {
              this.$Notice.success({
                title: '解绑成功',
              });
              this.queryBindUsers();
            } else {
              this.$Notice.error({
                title: '操作失败',
                desc: res.data.msg
              });
            }
          });

          this.bindUsersShow = false;
          //已选择数据要清空
          this.selectDataStore = [];
          this.dataStoreTo = [];
          this.queryBindUsers();
        }
      },
      unBind(userId) {
        let userIdList = [];
        userIdList.push(userId);
        let condition = {
          'userIdList': userIdList,
          'roleId': this.roleId
        };
        if(this.total === 1){
          this.$Notice.warning({
            title: '解绑所有用户将会失去所有权限',
          });
          return;
        }
        SysRoleAPI.unBindUsers(condition).then((res) => {
          if (res.data.state == 1) {
            this.$Notice.success({
              title: '解绑成功',
            });
            this.queryBindUsers();
          } else {
            this.$Notice.error({
              title: '操作失败',
              desc: res.data.msg
            });
          }
        });
      },
      cancel() {
        this.bindUsersShow = false;
        //已选择数据要清空
        this.selectDataStore = [];
        this.dataStoreTo = [];
      }
      ,
      handleReset(name) {
        this.$refs[name].resetFields()
      }
      ,
      changePageNo(value) {
        this.formData.pageNo = value
        this.queryBindUsers();
      }
      ,
      changePageSize(value) {
        this.formData.pageNo = 1;
        this.formData.pageNumber = value;
        this.queryBindUsers();
      },
      changePageNo2(value) {
        this.formData2.pageNo = value;
        this.queryUnBindUsers();
      }
      ,
      changePageSize2(value) {
        this.formData2.pageNo = 1;
        this.formData2.pageNumber = value;
        this.queryUnBindUsers();
      },
      //获取选择的数据
      selectChange(selection) {
        let pageDataStore = {
          page: this.formData.pageNo,   //当前操作页数
          dataArr: selection   //当前操作页用户选择的数据
        };
        this.dataStoreTo[this.formData.pageNo - 1] = pageDataStore    //丢给中间数据仓库(记录下位置，用以解决数据覆盖问题)
      },
      //获取所有选择的数据
      getSelectedData() {
        this.selectDataStore = [];
        this.dataStoreTo.forEach((item, index) => {
          this.selectDataStore = this.selectDataStore.concat(this.dataStoreTo[index].dataArr); //拿到用户所选全部数据this.selectDataStore
        })
      },
      //关闭角色绑定用户页面
      close() {
        Event.$emit('closePage', 'sysRoleUser');
      }
    }
    ,
    components: {}
  }
</script>



