<template>
  <div>
    <div class="form-box-content">
      <div class="module-title-wrap">角色信息</div>
      <fdcform ref="formValidate" :model="formValidate" :label-width="80" inline>
        <row span="24">
          <fdccol span="8" offset="2">
            <formitem label="编码" prop="code">
              <fdcinput v-model="formValidate.code" disabled style="width:300px">
              </fdcinput>
            </formitem>
          </fdccol>
          <fdccol span="8" offset="2">
            <formitem label="名称" prop="name">
              <fdcinput v-model="formValidate.name" disabled style="width:300px">
              </fdcinput>
            </formitem>
          </fdccol>
        </row>

      </fdcform>
      <div class="module-title-wrap">权限编辑</div>
      <div style="padding: 0px 0px 20px 50px;">
        <tree :data="menuData" show-checkbox multiple ref="tree"></tree>
      </div>
      <div style="padding: 0px 0px 20px 50px;">
        <fdcbutton type="primary" @click="save">提交</fdcbutton>
        <fdcbutton type="ghost" @click="cancel" style="margin-left: 8px">关闭</fdcbutton>
      </div>
    </div>
  </div>
</template>

<script>
  import Event from '@/utility/Event.js';
  import SysRoleAPI from "./api/SysRoleAPI";
  import {mapState} from 'vuex'

  export default {
    name: "sysRolePermission",
    data() {
      return {
        formValidate: {
          id: this.$route.query.roleId,
          name: '',
          code: '',
          level: ''
        },
        menuData: [],
        treeNodeList: [],
        moduleMap: new Map(),
        nodeList1: [],
        nodeList2: []
      }
    },
    methods: {
      //填充修改页面的数据
      fillModelData() {
        SysRoleAPI.getById(this.formValidate.id).then((res) => {
          if (res.data.state === 1) {
            this.formValidate = res.data.data;
            this.queryMenuData();
          }
        });
      },
      //保存权限分配
      save() {
        let checkedNodeList = this.$refs.tree.getCheckedNodes();
        let permissionIdList = [];
        for(let i = 0; i < checkedNodeList.length; i++){
          permissionIdList.push(checkedNodeList[i].id);
        }
        let params = {
          'roleId': this.formValidate.id,
          'permissionIdList': permissionIdList
        };
        SysRoleAPI.updateRolePermission(params).then((res)=>{
          if (res.data.state == 1) {
            //关闭页面
            this.cancel();
            this.$Notice.success({
              title: '操作成功',
              desc: '权限分配成功'
            });
          } else {
            this.$Notice.error({
              title: '操作失败',
              desc: res.data.msg
            });
          }
        });
      },
      cancel() {
        Event.$emit('closePage', 'sysRolePermission');
      },
      //查询菜单树
      queryMenuData() {
        SysRoleAPI.queryRolePermission({roleId: this.formValidate.id, roleLevel: this.formValidate.level}).then((res) => {
          if (res.data.state = 1) {
            this.treeNodeList = res.data.data;
            if (this.treeNodeList.length > 0) {
              this.getDataByModule();
            }
          }
        });
      },
      //按模块区分数据并生成树
      getDataByModule(){
        for (let i = 0; i < this.enumSelect['MODULE_TYPE'].length; i++) {
          let nodeList = [];
          for (let j = 0; j < this.treeNodeList.length; j++) {
            if(this.enumSelect['MODULE_TYPE'][i].enumCode === this.treeNodeList[j].moduleType){
              nodeList.push(this.treeNodeList[j]);
            }
          }
          if(nodeList.length > 0){
            this.moduleMap.set(this.enumSelect['MODULE_TYPE'][i].enumValue, nodeList);
          }
        }
        this.mackTree();
      },
      //生成树
      mackTree() {
        this.moduleMap.forEach((value, key) => {
          let node = {
            id: 0,
            title: key,
            expand: true,
            checked: false,
            selected: false,
            disabled: false,
            children: this.getChildrenNode(0, value)
          };
          this.menuData.push(node);
        });
      },
      //获取子节点
      getChildrenNode(pId, nodeList) {
        let childrenNode = [];
        for (let i = 0; i < nodeList.length; i++) {
          let item = nodeList[i];
          if (item.parentId === pId) {
            let node = {
              id: item.id,
              title: item.name,
              expand: true,
              checked: false,
              disabled: false,
              children: this.getChildrenNode(item.id, nodeList)
            };
            if(node.children.length > 0){
              node.checked = false;
            }else {
              node.checked = item.checked;
            }
            childrenNode.push(node);
          }
        }
        return childrenNode;
      }
    },
    mounted() {
      Event.$emit('addEnum', {typeList:['MODULE_TYPE']});
      this.fillModelData();
    },
    watch: {
      roleIds() {
        if(this.$route.query.roleId) {
          this.formValidate.id = this.$route.query.roleId;
        }
        this.menuData = [];
        this.fillModelData();
      }
    },
    computed: {
      ...mapState({
        enumSelect: state => state.Enum.enumSelect
      }),
      roleIds() {
        return this.$route.query.roleId
      },
    },
    components: {
    }
  }
</script>





