<template>
  <div>
    <div class="page-header-box">
      <div class="header-msg-box">
        <p>菜单管理</p>
      </div>
      <div class="header-btn-box">
        <fdcbutton v-show="formshow" @click="hideForm" icon="eye-disabled">隐藏</fdcbutton>
        <fdcbutton v-show="!formshow" @click="showForm" icon="eye">显示</fdcbutton>
        <fdcbutton icon="android-add" @click="addPermission">新增</fdcbutton>
      </div>
    </div>
    <div class="form-box" v-show="formshow">
      <fdcform ref="formData"  :model="formData"  :label-width="80" class="search-form" inline>
            <formitem label="菜单名" prop="name">
              <fdcinput v-model="formData.name" @enter="handleSubmit('formData')" placeholder="" class="search-input">
              </fdcinput>
            </formitem>
            <formitem label="父菜单" prop="parentName">
              <fdcinput v-model="formData.parentName" @enter="handleSubmit('formData')" placeholder="" class="search-input">
              </fdcinput>
              <fdcbutton type="primary" class = "search-submit" @click="handleSubmit('formData')">查询</fdcbutton>
              <fdcbutton type="ghost"  class = "search-reset" @click="handleReset('formData')">重置</fdcbutton>
            </formitem>
      </fdcform>
    </div>
    <div class="table-box">
      <fdctable border size="small" :loading="isLoading" :data="tableData"
                :columns="tableColumns" @on-current-change="selectData" stripe></fdctable>
      <div class="page-box">
        <Page v-show="pageShow" placement="top" :total="total" :page-size="formData.pageNumber" :current.sync="formData.pageNo" @on-change="changePageNo" @on-page-size-change="changePageSize" show-total show-elevator show-sizer></Page>
      </div>
    </div>
  </div>
</template>

<script>

import Event from '@/utility/Event.js'
import PermissionAPI from './api/PermissionAPI'
import { mapActions, mapState } from 'vuex'
export default {
  name: 'permission',
  data: function () {
    return {
      formshow: true,
      isLoading: false,
      pageShow: false,
      total: 0,
      formData: {
        parentName: '',
        name: '',
        pageNo: 1,
        pageNumber: 10
      },
      tableData: [],
      tableColumns: [
        {
          title: ' ',
          key: 'number',
          type: 'index2',
          align: 'center',
          width: '5%',
          render: (h, params) => {
            return h('span', params.index + (this.formData.pageNo - 1) * this.formData.pageNumber + 1)
          }
        },
        {
          title: '国际化编码',
          key: 'i18nCode',
          align: 'center',
          /* render: (h, params) => {
            return h('span', {}, "<a href='javascript:void(0)' onclick='showDetail(" + params.row.i18nCode + ")'/>")
          } */
          render: (h, params) => {
            return h('a', {
              on: {
                click: () => {
                  this.clickHandler(params.row.i18nCode)
                }
              }
            }, params.row.i18nCode)
          }

        },
        {
          title: '菜单名',
          key: 'localName',
          align: 'center'
        },
        {
          title: '权限码',
          key: 'code',
          align: 'center'
        },
        {
          title: '类型',
          key: 'type',
          align: 'center',
          render: (h, params) => {
            return h('span', {}, this.sysEnumList['MENU_TYPE_' + params.row.type])
          }
        },
        {
          title: '父节点名称',
          key: 'parentName',
          align: 'center'
        },
        {
          title: '模块',
          key: 'module',
          align: 'center',
          render: (h, params) => {
            return h('span', {}, this.sysEnumList['MODULE_TYPE_' + params.row.module])
          }
        },
        {
          title: '角色权重',
          key: 'roleLevel',
          align: 'center',
          render: (h, params) => {
            return h('span', {}, this.sysEnumList['ROLE_LEVEL_' + params.row.roleLevel])
          }
        },
        {
          title: '层级',
          key: 'level',
          align: 'center'
        },
        {
          title: '层内排序',
          key: 'levelOrder',
          align: 'center'
        },
        {
          title: 'URL',
          key: 'url',
          align: 'center'
        },
        {
          title: '操作',
          key: 'action',
          width: 120,
          align: 'left',
          render: (h, params) => {
            return h('div', [
              h('tooltip', {
                props: {
                  content: '修改',
                  placement: 'top'
                }
              }, [h('span', {
                on: {
                  click: () => {
                    this.modifyPermission(params.row.id)
                  }
                }
              }, [h('icon', {
                props: {
                  type: 'edit',
                  size: 20,
                  color: '#0d96ff'
                },
                style: {
                  marginRight: '8px'
                }
              }, '修改')])]),
              h('tooltip', {
                props: {
                  content: '删除',
                  placement: 'top'
                }
              }, [h('span', {
                on: {
                  click: () => {
                    this.$Modal.confirm({
                      title: '删除',
                      content: '<p>确定删除？</p>',
                      onOk: () => {
                        this.removePermission(params.row.id)
                      },
                      onCancel: () => {
                      }
                    })
                  }
                }
              }, [h('icon', {
                props: {
                  type: 'trash-a',
                  size: 20,
                  color: '#f11a09'
                },
                style: {
                  marginRight: '8px'
                }
              }, '删除')])])
            ])
          }
        }
      ]
    }
  },
  mounted () {
    Event.$emit('addEnum', {typeList:['MENU_TYPE', 'MODULE_TYPE', 'ROLE_LEVEL']})
    Event.$once('refreshPermissionList', obj => this.query())
  },
  computed: {
    ...mapState({
      sysEnumList: state => state.Enum.enumList
    })
  },
  methods: {
    ...mapActions([
      'generateEnumList'
    ]),
    clickHandler: function (code) {
      Event.$emit('addPage', {
        title: '修改国际化配置',
        router: 'modifyI18n',
        param: code,
        checked: true
      })
    },
    hideForm () {
      this.formshow = !this.formshow
    },
    showForm () {
      this.formshow = !this.formshow
    },
    query () {
      this.isLoading = !this.isLoading
      PermissionAPI.query(this.formData).then((res) => {
        this.tableData = []
        let state = res.data.state
        if (state === 1) {
          let dataList = res.data.data.list
          if (dataList !== null) {
            this.pageShow = true
            this.total = res.data.data.total
          }
          this.tableData.push(...dataList)
        }
        this.isLoading = !this.isLoading
      })
    },
    handleSubmit (name) {
      this.$refs[name].validate((valid) => {
        if (valid) {
          this.query()
        }
      })
    },
    handleReset (name) {
      this.$refs[name].resetFields()
    },
    changePageNo (value) {
      this.formData.pageNo = value
      this.query()
    },
    changePageSize (value) {
      this.formData.pageNo = 1
      this.formData.pageNumber = value
      this.query()
    },
    selectData () {

    },
    addPermission () {
      Event.$emit('addPage', {
        title: '新增菜单',
        router: 'addPermission',
        checked: true
      })
    },
    modifyPermission (params) {
      Event.$emit('addPage', {
        title: '修改菜单',
        router: 'modifyPermission',
        checked: true,
        paramId: params
      })
    },
    removePermission (params) {
      PermissionAPI.delete({id: params}, this.query())
    }
  },
  components: {
  }
}
</script>



