<style scoped>
</style>
<template>
  <div>
      <div class="form-box-content">
        <div class="module-title-wrap">基本信息</div>
        <fdcform ref="formValidate"  :model="formValidate" :rules="ruleValidate" :label-width="120">
          <row type="flex" justify="space-around">
            <fdccol span="10">
              <formitem label="代码" prop="code"
                        :rules="{required: true,type: 'string',message: '枚举类型必填',trigger: 'blur'}">
                  <fdcinput v-model="formValidate.code" placeholder="" ></fdcinput>
              </formitem>
            </fdccol>
            <fdccol span="10"></fdccol>
          </row>
          <div v-for="(item, index) in formValidate.details">
            <row type="flex" justify="space-around">
              <fdccol span="10">
                <fdcinput v-model="item.language"  v-show="false" placeholder="">
                </fdcinput>
                <formitem :label="item.name" :key="index" :prop="'details.'+index+'.value'"
                          :rules="[{required: true, type:'string', message:'国际化值必填', trigger: 'blur'}]">
                  <fdcinput v-model="item.value" type="textarea" placeholder="" >
                  </fdcinput>
                </formitem>
              </fdccol>
              <fdccol span="10">
                <formitem label="备注" prop="remark">
                  <fdcinput  v-model="item.remark" type="textarea" placeholder="Enter value">
                  </fdcinput>
                </formitem>
              </fdccol>
            </row>
          </div>
          <row type="flex" justify="space-around">
            <fdccol span = '6'>
              <formitem>
                <fdcbutton type="primary" @click="handleSubmit('formValidate')">提交</fdcbutton>
                <fdcbutton type="ghost" @click="cancel('addI18n')" class="buttonClass">取消</fdcbutton>
              </formitem>
            </fdccol>
          </row>
        </fdcform>
      </div>
  </div>
</template>
<script>
import I18nAPI from './api/I18nAPI'
import Event from '@/utility/Event.js'
export default {
  name:'addI18n',
  data: function () {
    return {
      ruleValidate: {
      },
      formValidate: {
        code: '',
        details: [
        ]
      }
    }
  },
  computed: {

  },
  components: {},
  methods: {
    generateForm: function () {
      I18nAPI.getI18nLanguageList().then((res) => {
        if (!res.data) {
          this.$Notice.error({
            title: '操作失败',
            desc: res.data.msg
          })
        } else {
          for (let i = 0; i < res.data.length; i++) {
            const language = {
              language: res.data[i].code,
              name: res.data[i].name,
              value: '',
              remark: ''
            }
            this.formValidate.details.push(language)
          }
        }
      })
    },
    handleSubmit (name) {
      this.$refs[name].validate((valid) => {
        if (valid) {
          this.insert()
        }
      })
    },
    insert: function () {
      const values = this.formValidate.details
      this.formValidate.details = []
      for (let i = 0; i < values.length; i++) {
        const detail = {
          language: values[i].language,
          value: values[i].value,
          remark: values[i].remark
        }
        this.formValidate.details.push(detail)
      }
      I18nAPI.insert(this.formValidate).then((res) => {
        if (res.data.state === 1) {
          this.$Notice.success({
            title: '操作成功',
            desc: '新增国际化配置成功'
          })
          /* Event.$emit('closeTag', {router: 'addI18n'}) */
          Event.$emit('closePage', 'addI18n')
          Event.$emit('refreshI18nList', null)
        } else {
          this.$Notice.error({
            title: '操作失败',
            desc: res.data.msg
          })
        }
      })
    },
    cancel: function (router) {
      Event.$emit('closePage', router)
    }
  },
  mounted: function () {
    this.generateForm()
  }
}
</script>



