<template>
  <div>
    <div class="page-header-box">
      <div class="header-msg-box">
        <p>运单管理</p>
      </div>
      <div class="header-btn-box">
        <fdcbutton v-show="queryShow" @click="hideForm" icon="eye-disabled">隐藏</fdcbutton>
        <fdcbutton v-show="!queryShow" @click="showForm" icon="eye">显示</fdcbutton>
      </div>
    </div>
    <div class="form-box" v-show="queryShow">
      <fdcform ref="form" inline :model="formData" :label-width="80" class="search-form" @keyup.enter.native="handleSubmit('form')" >
        <formitem label="运单号" prop="code">
          <fdcinput v-model="formData.code" placeholder="请输入运单号" class="search-input"></fdcinput>
        </formitem>
        <formitem label="商家订单号" prop="customerCode">
          <fdcinput v-model="formData.customerCode" placeholder="请输入商家订单号" class="search-input"></fdcinput>
          <fdcbutton type="primary" @click="handleSubmit('form')" class="search-submit">查询</fdcbutton>
          <fdcbutton type="ghost" @click="handleReset('form')" class="search-reset">重置</fdcbutton>
        </formitem>
      </fdcform>
    </div>
    <div class="table-box">
      <fdctable ref="table" border size="small" :loading="isLoading" :data="tableData" :columns="tableColumns" stripe></fdctable>
      <div class="page-box">
        <Page placement="top" :page-size="formData.pageNumber" :total="total" :current.sync="formData.pageNo"  @on-change="changePageNo" @on-page-size-change="changePageSize" show-total show-elevator show-sizer></Page>
      </div>
    </div>


  </div>
</template>

<script>
  import DoOrderAPI from './api/DoOrderAPI'
  import Event from '@/utility/Event.js'
  import { mapState } from 'vuex'

  export default {
    name: 'doOrder',
    data () {
      return {
        total: 0,
        queryShow: true,
        showModal: false,
        formData: {
          name: '',
          pageNumber: 10,
          pageNo: 1
        },
        isLoading: false,
        pageShow: false,
        tableColumns: [
          {
            title: ' ',
            type: 'index2',
            width: 60,
            align: 'center',
            render: (h, params) => {
              return h('span', params.index + (this.formData.pageNo - 1) * this.formData.pageNumber + 1)
            }
          },
          {
            title: '运单号',
            key: 'code',
            align: 'center',
            width: 140
          },
          {
            title: '客户单号',
            key: 'customerCode',
            align: 'center',
            width: 140
          },
          {
            title: '3PL运单号',
            key: 'tplCode',
            align: 'center',
            width: 140
          },
          {
            title: '订单类型',
            key: 'type',
            align: 'center',
            width: 120,
            render: (h, params) => {
              return h('span', {}, this.sysEnumList['ORDER_TYPE_' + params.row.type])
            }
          },
          {
            title: '订单状态',
            key: 'state',
            align: 'center',
            width: 120,
            render: (h, params) => {
              return h('span', {}, this.sysEnumList['ORDER_STATE_' + params.row.type])
            }
          },
          {
            title: '下单时间',
            key: 'orderTime',
            align: 'center',
            width: 120
          },
          {
            title: '送货时间',
            key: 'deliveryTime',
            align: 'center',
            width: 120
          },
          {
            title: '回单时间',
            key: 'returnTime',
            align: 'center',
            width: 120
          },
          {
            title: '配送方式',
            key: 'deliveryMode',
            align: 'center',
            render: (h, params) => {
              return h('span', {}, this.sysEnumList['DELIVERY_MODE_' + params.row.deliveryMode])
            },
            width: 120
          },
          {
            title: '配送商',
            key: 'carrierName',
            align: 'center',
            width: 100
          },
          {
            title: '委托方',
            key: 'customerName',
            align: 'center',
            width: 100
          },
          {
            title: '收货人',
            key: 'receiver',
            align: 'center',
            width: 100
          },
          {
            title: '收货人电话',
            key: 'receiverTelephone',
            align: 'center',
            width: 100
          },
          {
            title: '收货省',
            key: 'toProvince',
            align: 'center',
            width: 80
          },
          {
            title: '收货市',
            key: 'toCity',
            align: 'center',
            width: 80
          },
          {
            title: '收货区',
            key: 'toDistrict',
            align: 'center',
            width: 100
          },
          {
            title: '收货人地址',
            key: 'receiverAddress',
            align: 'center',
            width: 280
          },
          {
            title: '付款方式',
            key: 'payMode',
            render: (h, params) => {
              return h('span', {}, this.sysEnumList['PAY_MODE_' + params.row.payMode])
            },
            align: 'center',
            width: 100
          },
          {
            title: '应收金额',
            key: 'amountReceivable',
            align: 'center',
            width: 100
          },
          {
            title: '网点',
            key: 'siteName',
            align: 'center',
            width: 100
          },
          {
            title: '操作',
            key: 'action',
            align: 'left',
            width: 100,
            render: (h, params) => {
              const row = params.row;
              return h('div', [h('tooltip', {
                props: {
                  content: '详情',
                  placement: 'top'
                }
              }, [h('span', {
                on: {
                  click: () => {
                    console.log(params.row.id)
                    this.showDetail(params.row.id)
                  }
                }
              }, [h('icon', {
                props: {
                  type: 'clipboard',
                  size: 20,
                  color: '#ffb80d'
                },
                style: {
                  marginRight: '5px'
                }
              }, '查看')])])
              ])
            }
          }
        ],
        tableData: []
      }
    },
    mounted () {
      Event.$emit('addEnum', {typeList:['ORDER_STATE','ORDER_TYPE','DELIVERY_MODE','PAY_MODE']})
      Event.$on('refreshList', obj => this.query())
    },
    computed: {
      ...mapState({
        sysEnumList: state => state.Enum.enumList
      })
    },
    methods: {
      hideForm() {
        this.queryShow = !this.queryShow
      },
      showForm() {
        this.queryShow = !this.queryShow
      },
      // 查询
      handleSubmit(name) {
        this.$refs[name].validate((valid) => {
          if (valid) {
            this.query();
          }
        })
      },
      query() {
        this.isLoading = !this.isLoading
        DoOrderAPI.query(this.formData).then((res) => {
          this.tableData = []
          let state = res.data.state
          if (state === 1 && res.data.data) {
            let dataList = res.data.data.list
            if (dataList !== null) {
              this.total = res.data.data.total
            }
            this.tableData.push(...dataList)
          }
          this.isLoading = !this.isLoading
        })
      },
      handleReset(name) {
        this.$refs[name].resetFields()
      },
      changePageNo(value) {
        this.formData.pageNo = value
        this.query()
      },
      changePageSize(value) {
        this.formData.pageNo = 1
        this.formData.pageNumber = value
        this.query()
      },
      showDetail(params) {
        Event.$emit('addPage', {
          title: '运单详情',
          router: 'doOrderDetail',
          checked: true,
          paramId: params
        })
      }
    },
    components: {
    }
  }
</script>



