<template>
  <div>
    <div class="form-box-content">
      <div class="module-title-wrap">基本信息</div>
      <fdcform ref="formValidate" :model="formValidate" :rules="ruleValidate" :label-width="100" >
        <row type="flex" justify="space-around">
          <fdccol span="12">
            <formitem label="所属省份" prop="parentId">
              <fdcselect v-model="formValidate.parentId">
                <fdcoption v-for="item in provinceList" :value="item.id" :key="item.id">
                  {{ item.name }}
                </fdcoption>
              </fdcselect>
            </formitem>
          </fdccol>
        </row>
        <row type="flex" justify="space-around">
          <fdccol span="12">
            <formitem label="编码" prop="code">
              <fdcinput v-model="formValidate.code" placeholder=""></fdcinput>
            </formitem>
          </fdccol>
        </row>
        <row type="flex" justify="space-around">
          <fdccol span="12">
            <formitem label="名称" prop="name">
              <fdcinput v-model="formValidate.name" placeholder=""></fdcinput>
            </formitem>
          </fdccol>
        </row>
        <row type="flex" justify="space-around">
          <fdccol span="12">
            <formitem>
              <fdcbutton type="primary" @click="handlerSubmit('formValidate')">提交</fdcbutton>
              <fdcbutton type="ghost" @click="cancel('addCity')" class="buttonClass">取消</fdcbutton>
            </formitem>
          </fdccol>
        </row>
      </fdcform>
    </div>
  </div>
</template>
<script>
import GeoAPI from './api/GEOAPI'
import {mapState} from 'vuex'
import Event from '@/utility/Event.js'
export default {
  name: 'addCity',
  data: function () {
    return {
      ruleValidate: {
        code: [{
          required: true,
          type: 'string',
          message: '请输入城市编码',
          trigger: 'blur'
        }],
        parentId: [{
          required: true,
          type: 'string',
          message: '请选择所属省份',
          trigger: 'blur'
        }],
        name: [{
          required: true,
          type: 'string',
          message: '请输入城市名称',
          trigger: 'blur'
        }]
      },
      formValidate: {
        type: 2,
        name: '',
        code: '',
        parentId: ''

      },
      provinceList: ''
    }
  },
  computed: {
    ...mapState({
      enumSelect: state => state.Enum.enumSelect
    })
  },
  methods: {
    getProvinceList: function () {
      GeoAPI.getProvinceList().then((res) => {
        if (res.data) {
          this.provinceList = res.data
          console.log(this.provinceList)
        }
      })
    },
    insert: function () {
      GeoAPI.insert(this.formValidate).then((res) => {
        if (res.data.state == 1) {
          this.$Notice.success({
            title: '操作成功',
            desc: '新增城市成功'
          })
          Event.$emit('closePage', 'addCity')
          Event.$emit('refreshCityList', null)
        } else {
          this.$Notice.error({
            title: '操作失败',
            desc: res.data.msg
          })
        }
      })
    },
    handlerSubmit: function (name) {
      this.$refs[name].validate((valid) => {
        if (valid) {
          this.insert()
        }
      })
    },
    cancel: function (router) {
      Event.$emit('closePage', router)
    }
  },
  mounted: function () {
    this.getProvinceList()
  }
}
</script>



