/**
 *  getValue(list,'a.b');
 * @param obj 需要取值的对象
 * @param exp  取值表达式
 */
export function getValue(obj, exp) {
  if (isEmpty(obj)) {
    return undefined;
  }
  if (isEmpty(exp)) {
    return undefined;
  }
  if (type(exp) !== 'string') {
    return undefined;
  }
  let exps = exp.split('.');
  let passedObj = {};
  let len = exps.length;
  let passed = exps.every(function (t, num) {
    if (num === 0) {
      passedObj = obj[t];
      if (isEmpty(passedObj)) {
        return false;
      }
    } else if (num < len) {
      passedObj = passedObj[t];
      if (isEmpty(passedObj)) {
        return false;
      }
    }
    return true;
  });
  if (passed) {
    let returnObj = {};
    exps.forEach(function (t, num) {
      if (num === 0) {
        returnObj = obj[t];
      } else if (num < len) {
        returnObj = returnObj[t];
      }
    });
    return returnObj;
  } else {
    return undefined;
  }
}
export function generateMenuMap(menuList,menuMap){
  for(let i=0;i<menuList.length;i++){
    let menu = menuList[i];
    if(menu.subMenu && menu.subMenu.length>0){
      generateMenuMap(menu.subMenu,menuMap);
    }else{
      menuMap[menu.router] = menu.path;
    }
  }
}


/**
 * 通过meta.permissionCode判断是否与当前用户权限匹配
 * @param permissionCodes 用户权限编码
 * @param route
 */
function filterPermission(permissionCodes, router) {
  if (router.meta && router.meta.permissionCode) {
    return permissionCodes.indexOf(router.meta.permissionCode) >= 0;
  } else {
    //路由中无permissionCode属性的视为公共页面
    return true;
  }
}

/**
 * 递归过滤异步路由表，返回符合用户权限的路由表
 * @param asyncRouterList
 * @param permissionCodes
 */
export function filterAsyncRouter(asyncRouterList, permissionCodes) {
  const accessedRouters = asyncRouterList.filter(router => {
    return filterPermission(permissionCodes, router);
  });
  return accessedRouters
}



