<style scoped>
  .header-menu-box-collapsed {
    padding-left: 78px;
  }
  .rotate-icon {
    transform: rotate(-90deg);
  }
  .vertical-center-modal {
    display: flex;
    align-items: center;
    justify-content: center;
  }
  .search-header-menu-box {
    position: absolute;
    width: 300px;
    right:750px;
    top:14px

  }
</style>
<template>
  <div class="header-menu-box" :class="headerClass">
    <div class="top-header-box">
      <div class="navicon-toggle">
        <icon type="navicon" size="32" @click.native="collapsedSider" :class="rotateIcon"></icon>
      </div>
      <module></module>
      <div class="header-avator-con">
        <headerLocation></headerLocation>
        <fullScreen></fullScreen>
        <headerMessage></headerMessage>
        <div class="user-dropdown-menu-con">
          <Row type="flex" justify="end" align="middle" class="user-dropdown-innercon">
            <dropdown transfer placement="bottom-end" @on-click="handleClickUserDropdown">
              <a href="javascript:void(0)">
                <span class="main-user-name">{{ userName }}</span>
                <icon type="arrow-down-b"></icon>
              </a>
              <dropdownmenu slot="list">
                <dropdownitem name="ownSpace"><Icon type="person" size="13" color="green"></Icon>&nbsp;<a href="#">个人中心</a></dropdownitem>
                <dropdownitem name="changeLocation" v-show="typeof(locationMsg) !== 'undefined' && locationMsg !== 'undefined'"><Icon type="location" size="15" color="#faad14"></Icon>&nbsp;<a href="#">切换作业点</a></dropdownitem>
                <dropdownitem name="loginout" divided><Icon type="log-out" size="13" color="red"></Icon>&nbsp;&nbsp;退出登录</dropdownitem>
              </dropdownmenu>
            </dropdown>
            <avatar src="https://i.loli.net/2017/08/21/599a521472424.jpg" style="background: #619fe7;margin-left: 10px;"></avatar>
          </Row>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapState, mapActions } from 'vuex'
import Event from '@/utility/Event.js'
import module from './module.vue'
import fullScreen from './fullScreen'
import headerLocation from './headerLocation'
import headerMessage from './headerMessage'

export default {
  name: 'headerMenu',
  data () {
    return {
    }
  },
  components: {module,fullScreen,headerLocation,headerMessage},
  computed: {
    ...mapState({
      isCollapsed: state => state.isCollapsed,
      userName: state => state.User.userName,
      token: state => state.User.token,
      tagList: state => state.tagList,
      locationMsg: state => state.User.location
    }),
    rotateIcon () {
      return [
        this.isCollapsed ? 'rotate-icon' : ''
      ]
    },
    headerClass () {
      return [
        this.isCollapsed ? 'header-menu-box-collapsed' : ''
      ]
    }
  },
  methods: {
    ...mapActions([
      'LogOut',
      'initAll'
    ]),
    // 收缩菜单
    collapsedSider () {
      this.$store.commit('changeCollapsed', !this.isCollapsed)
    },
    logout () {
      let that = this
      this.LogOut(this.token).then(res => {
        that.$router.push({ path: '/login' })
        this.initAll()
      })
    },
    handleClickUserDropdown (name) {
      if (name === 'ownSpace') {
        // 个人中心
        Event.$emit('addPage', {
          title: '个人中心',
          router: 'userInfo',
          checked: true
        })
      }else if (name === 'changeLocation') {
        // 切换作业点
        this.$store.commit('changeLocation', true)
      } else if (name === 'loginout') {
        // 退出登录
        let that = this
        this.LogOut(this.token).then(res => {
          that.$router.push({ path: '/login' })
          this.initAll()
        })
      }
    }
  },
  mounted: function () {
  }
}
</script>



