<template>
  <div>
    <div class="form-box-content">
      <div class="module-title-wrap">{{i18nMap['company_add_label_base']}}</div>
      <fdcform ref="formValidate" :model="formValidate" :rules="ruleValidate" :label-width="80">
        <row type="flex" justify="space-around">
          <fdccol span="7">
            <formitem :label="i18nMap['company_add_label_code']" prop="code">
              <fdcinput v-model="formValidate.code" placeholder="" ></fdcinput>
            </formitem>
          </fdccol>
          <fdccol span="7">
            <formitem label="公司名称" prop="name">
              <fdcinput v-model="formValidate.name" placeholder="" ></fdcinput>
            </formitem>
          </fdccol>
          <fdccol span="7">
            <formitem label="简称" prop="abbr">
              <fdcinput v-model="formValidate.abbr" placeholder=""></fdcinput>
            </formitem>
          </fdccol>
        </row>
        <row type="flex" justify="space-around">
          <fdccol span="7">
            <formitem label="联系人" prop="contact">
              <fdcinput v-model="formValidate.contact" placeholder="">
              </fdcinput>
            </formitem>
          </fdccol>
          <fdccol span="7">
            <formitem label="联系电话" prop="telephone">
              <fdcinput v-model="formValidate.telephone" placeholder="">
              </fdcinput>
            </formitem>
          </fdccol>
          <fdccol span="7">
            <formitem label="邮箱" prop="email">
              <fdcinput v-model="formValidate.email" placeholder="">
              </fdcinput>
            </formitem>
          </fdccol>
        </row>
        <row type="flex" justify="space-around">
          <fdccol span="15" >
            <formitem label="地址" prop="address">
              <fdcinput v-model="formValidate.address"  placeholder="">
              </fdcinput>
            </formitem>
          </fdccol>
          <fdccol span="7"></fdccol>
        </row>
        <row type="flex" justify="space-around">
          <fdccol span="15">
          <formitem label="备注" prop="remark">
            <fdcinput v-model="formValidate.remark" type="textarea" placeholder=""></fdcinput>
          </formitem>
          </fdccol>
          <fdccol span="7"></fdccol>
        </row>
        <div class="module-title-wrap">公司管理员信息</div>
        <row type="flex" justify="space-around">
          <fdccol span="7">
            <formitem label="登录账号" prop="loginName">
              <fdcinput v-model="formValidate.loginName" placeholder="">
              </fdcinput>
            </formitem>
          </fdccol>
          <fdccol span="7">
            <formitem label="用户姓名" prop="userName">
              <fdcinput v-model="formValidate.userName" placeholder="">
              </fdcinput>
            </formitem>
          </fdccol>
          <fdccol span="7">
          </fdccol>
        </row>
        <div class="module-title-wrap">公司角色信息</div>
        <row type="flex" justify="space-around">
          <fdccol span="7">
            <formitem label="角色名称" prop="roleName">
              <fdcinput v-model="formValidate.roleName" placeholder="">
              </fdcinput>
            </formitem>
          </fdccol>
          <fdccol span="7">
            <formitem label="角色代码" prop="roleCode">
              <fdcinput v-model="formValidate.roleCode" placeholder="">
              </fdcinput>
            </formitem>
          </fdccol>
          <fdccol span="7">
          </fdccol>
        </row>
        <row type="flex" justify="space-around">
          <fdccol span = '7'>
            <formitem>
              <fdcbutton type="primary" @click="handlerSubmit('formValidate')">提交</fdcbutton>
              <fdcbutton type="ghost" @click="cancel('addCompany')" class="buttonClass">取消</fdcbutton>
            </formitem>
          </fdccol>
        </row>
      </fdcform>
    </div>
  </div>
</template>

<script>
import Event from '@/utility/Event.js'
import CompanyAPI from './api/CompanyAPI'
import {mapState, mapActions} from 'vuex'

export default {
  name: 'addCompany',
  data () {
    return {
      i18nCodes: ['company_add_message_code_required', 'company_add_message_name_required',
        'company_add_message_email_required', 'company_add_message_email_format',
        'company_add_label_code', 'company_add_label_name', 'company_add_label_email',
        'company_add_label_telephone', 'company_add_label_abbr', 'company_add_label_contact',
        'company_add_label_address', 'company_add_label_remark', 'company_add_label_base',
        'company_add_label_detail', 'company_add_button_commit', 'company_add_label_cancel'],
      formValidate: {
        code: '',
        name: '',
        email: '',
        telephone: '',
        abbr: '',
        contact: '',
        address: '',
        remark: '',
        loginName: '',
        userName: '',
        roleName: '',
        roleCode: ''
      }
    }
  },
  computed: {
    ...mapState({
      i18nMap: state => state.I18n.i18nMap
    }),
    ruleValidate () {
      return {
        code: [{
          required: true,
          message: '公司编码不允许为空',
          trigger: 'blur'
        },{
          len:3,
          message: '请输入3位字符的公司编码',
          trigger: 'blur'
        },{
          pattern: /^[a-z,A-Z,0-9]+$/,
          message: '公司编码格式不正确',
          trigger: 'blur'
        }],
        name: [{
          required: true,
          message: '公司名称不允许为空',
          trigger: 'blur'
        },{
          max:30,
          message: '公司名称超长',
          trigger: 'blur'
        }],
        abbr: [
          {
            max:6,
            message: '简称超长',
            trigger: 'blur'
          }
        ],
        contact: [
          {
            max:16,
            message: '联系人超长',
            trigger: 'blur'
          }
        ],
        telephone :[
          {
            max:50,
            message: '联系电话超长',
            trigger: 'blur'
          },
          {
            pattern: /^[0-9,'\-']+$/,
            message: '联系电话格式不正确',
            trigger: 'blur'
          }
        ],
        address: [
          {
            max:66,
            message: '地址超长',
            trigger: 'blur'
          },
        ],
        remark: [
          {
            max:66,
            message: '备注超长',
            trigger: 'blur'
          },
        ],
        loginName: [{
          required: true,
          message: '登录名不允许为空',
          trigger: 'blur'
        },{
          max:20,
          message: '登录名超长',
          trigger: 'blur'
        },{
          pattern: /^[a-z,A-Z,0-9]+$/,
          message: '登录名格式不正确',
          trigger: 'blur'
        }],
        userName: [{
          required: true,
          message: '用户姓名不允许为空',
          trigger: 'blur'
        },{
          max:16,
          message: '用户姓名超长',
          trigger: 'blur'
        }],
        email: [
          {
            type: 'email',
            message: '邮箱格式不正确',
            trigger: 'blur'
          },{
            max:50,
            message: '邮箱格式超长',
            trigger: 'blur'
          }
        ],
        roleName: [{
          required: true,
          message: '角色名称不允许为空',
          trigger: 'blur'
        },{
          max:16,
          message: '角色名称超长',
          trigger: 'blur'
        }],
        roleCode: [{
          required: true,
          message: '角色代码不允许为空',
          trigger: 'blur'
        },{
          pattern: /^[a-z,A-Z,0-9]+$/,
          message: '角色代码格式不正确',
          trigger: 'blur'
        },{
          max:20,
          message: '角色代码超长',
          trigger: 'blur'
        }],
      };
    }
  },
  components: {
  },
  methods: {
    ...mapActions([
      'fetchI18n'
    ]),
    handlerSubmit (name) {
      this.$refs[name].validate((valid) => {
        if (valid) {
          CompanyAPI.insert(this.formValidate).then((res) => {
            if (res.data.state === 1) {
              Event.$emit('closePage', 'addCompany')
              Event.$emit('refreshCompanyList', null)
            }
          })
        }
      })
    },
    cancel (router) {
      Event.$emit('closePage', router)
    }
  },
  mounted () {
    this.fetchI18n(this.i18nCodes)
  }
}
</script>



