import Vue from 'vue'
import Vuex from 'vuex'
import I18n from './modules/i18n/index'
import User from './modules/user/index'
import Enum from './modules/enum/index'

Vue.use(Vuex);

const vueInstance = new Vuex.Store({
  strict: process.env.NODE_ENV !== 'production',
  state: {
    isCollapsed: false,
    defaultModule: 1,
    lastPageObj: [
      {
        title: '首页',
        router: 'index'
      }
    ],
    tagList: [
      {
        title: '首页',
        router: 'index'
      }
    ],
    showChangeLocation: false
  },
  getters: {

  },
  actions: {
    changeTag (context, tagList) {
      context.commit('changeTag', tagList)
    },
    changeCollapsed (context, collapsed) {
      context.commit('changeCollapsed', collapsed)
    },
    changeModule (context, defaultModule) {
      context.commit('changeModule', defaultModule)
    },
    changeLocation (context, showChangeLocation) {
      context.commit('changeLocation', showChangeLocation)
    },
    initAll (context) {
      context.commit('initAll')
    }
  },
  mutations: {
    changeTag (state, tagList) {
      state.tagList = tagList;
      sessionStorage.setItem('tagList', JSON.stringify(tagList));
    },
    changeCollapsed (state, collapsed) {
      state.isCollapsed = collapsed;
    },
    changeModule (state, defaultModule) {
      state.defaultModule = defaultModule;
    },
    removeTag (state, name) {
      state.tagList.map((item, index) => {
        if (item.router === name) {
          state.tagList.splice(index, 1);
        }
      });
      sessionStorage.setItem('tagList', JSON.stringify(state.tagList))
    },
    handleStorage (state) {
      sessionStorage.tagList = JSON.stringify(state.tagList)
    },
    setOpenedList (state) {
      state.tagList = sessionStorage.tagList ? JSON.parse(sessionStorage.tagList) : state.tagList;
    },
    setLastPageObj (state, lastPageObj) {
      state.lastPageObj = lastPageObj
    },
    changeLocation (state, showChangeLocation) {
      state.showChangeLocation = showChangeLocation
    },
    initAll (state) {
      state.isCollapsed = false
      state.defaultModule = 1
      state.lastPageObj = [{title: '首页', router: 'index'}]
      state.tagList = [{title: '首页', router: 'index'}]
      state.showChangeLocation = false
    }
  },
  modules: {
    I18n,
    User,
    Enum
  }
});

export default vueInstance;



