import HttpUtil from '@/common/HttpUtil'
const GroupAPI = {
  query (params) {
    return HttpUtil.post('/api/api/group/display/list/page', params, false)
  },
  queryPermissions (params) {
    return HttpUtil.post('/api/api/group/display/list/permission', params, false)
  },
  queryUngrantService (params) {
    return HttpUtil.post('/api/api/group/display/list/ungrant/service', params, false)
  },
  add (params) {
    return HttpUtil.post('/api/api/group/add', params)
  },
  view (params) {
    return HttpUtil.post('/api/api/group/display/view/' + params)
  },
  modify (params) {
    return HttpUtil.post('/api/api/group/modify', params)
  },
  normal (params) {
    return HttpUtil.post('/api/api/group/normal/' + params)
  },
  remove (params) {
    return HttpUtil.post('/api/api/group/remove/' + params)
  },
  grant (groupId, permissions) {
    return HttpUtil.post('/api/api/group/grant/permissions/to/' + groupId, permissions, false)
  },
  revoke (groupId, permissions) {
    return HttpUtil.post('/api/api/group/revoke/permissions/from/' + groupId, permissions)
  }
}
export default GroupAPI



