<style scoped>
  .uiBase-table .demo-table-info-row td {
    background-color: #2db7f5;
    color: #fff;
  }
</style>
<template>
  <div>
    <div class="form-box">
      <fdcform ref="queryData" :model="queryData"  :label-width="80" inline class="search-form">
        <row span="24">
          <fdccol span="24">
            <formitem label="城市名称" prop="name">
              <fdcinput v-model="queryData.name" placeholder="请输入城市名称" style="width:200px">
              </fdcinput>
              <fdcbutton type="primary" class="search-submit" @click="handleSubmit('queryData')">查询</fdcbutton>
              <fdcbutton type="ghost" class="search-reset" @click="handleReset('queryData')">重置</fdcbutton>
            </formitem>
          </fdccol>
        </row>
      </fdcform>
    </div>
    <div>
      <fdctable border highlight-row ref="cityTable" size="small" height="400" :data="tableData" :columns="tableColumns"
                @on-current-change="select"></fdctable>
      <div class="page-box">
        <Page v-show="pageShow" placement="top" :total="total" :page-size="queryData.pageNumber"
              :current.sync="queryData.pageNo" @on-change="changeNo" @on-page-size-change="changeSize"
               show-total show-elevator show-sizer></Page>
      </div>
    </div>
  </div>
</template>
<script>
import GEOAPI from '../../page/basic/basicData/geoInfo/api/GEOAPI'
import Event from '@/utility/Event.js'

export default {
  name: 'selectCity',
  data () {
    return {
      pageShow: false,
      total: 0,
      rowData: {},
      queryData: {
        name: '',
        type: 2,
        pageNumber: 10,
        pageNo: 1
      },
      tableData: [],
      tableColumns: [
        {
          title: ' ',
          type: 'index2',
          width: 60,
          align: 'center',
          render: (h, params) => {
            return h('span', params.index + (this.queryData.pageNo - 1) * this.queryData.pageNumber + 1)
          }
        },
        {
          title: '城市代码',
          key: 'code',
          align: 'center'
        },
        {
          title: '城市名称',
          key: 'name',
          align: 'center'
        },
        {
          title: '所属省份',
          key: 'parentName',
          align: 'center'
        }
      ]
    }
  },
  methods: {
    select (currentRow, oldCurrentRow) {
      this.rowData = currentRow
      Event.$emit('selectCity', this.rowData)
    },
    // 查询
    handleSubmit (name) {
      this.$refs[name].validate((valid) => {
        if (valid) {
          this.queryCity();
        }
      })
    },
    handleReset (name) {
      this.$refs[name].resetFields();
    },
    queryCity() {
      GEOAPI.query(this.queryData).then((res) => {
        this.tableData = []
        let state = res.data.state
        if (state === 1) {
          let dataList = res.data.data.list
          if (dataList !== null) {
            this.pageShow = true
            this.total = res.data.data.total
          }
          this.tableData.push(...dataList)
        }
        this.isLoading = !this.isLoading
      })
    },
    changeNo (value) {
      this.queryData.pageNo = value
      this.queryCity()
    },
    changeSize (value) {
      this.queryData.pageNo = 1
      this.queryData.pageNumber = value
      this.queryCity()
    },
    handleClearCurrentRow (cityTable) {
      cityTable.clearCurrentRow()
    }
  },
  mounted () {
    this.queryCity();
    let cityTable = this.$refs['cityTable']
    Event.$on('clearSelect', () => this.handleClearCurrentRow(cityTable))
    Event.$on('queryCity', () => this.queryCity())
  }
}

</script>



