<template>
  <div>
    <div class="form-box-content">
      <div class="module-title-wrap">基本信息</div>
      <fdcform ref="formData" :model="formData" :label-width="100">
        <row type="flex" justify="space-around">
          <fdccol span="11">
            <formitem :label="'统一编码：'" prop="untiyCode">
              <fdcinput v-model="formData.unityCode" placeholder="消费者统一编码" readonly>
              </fdcinput>
            </formitem>
          </fdccol>
          <fdccol span="11">
            <formitem :label="'名称：'" prop="name" style="padding-left: 5px;">
              <fdcinput v-model="formData.name" placeholder="消费者名称" readonly>
              </fdcinput>
            </formitem>
          </fdccol>
        </row>
        <row type="flex" justify="space-around">
          <fdccol span="11">
            <formitem :label="'联系人：'" prop="linkman">
              <fdcinput v-model="formData.linkman" placeholder="消费者联系人" readonly>
              </fdcinput>
            </formitem>
          </fdccol>
          <fdccol span="11">
            <formitem :label="'联系人邮箱：'" prop="linkmanEmail">
              <fdcinput v-model="formData.linkmanEmail" placeholder="消费者联系人" readonly>
              </fdcinput>
            </formitem>
          </fdccol>
        </row>
        <row type="flex" justify="space-around">
          <fdccol span="11">
            <formitem :label="'联系人电话：'" prop="linkmanMobile">
              <fdcinput v-model="formData.linkmanMobile" placeholder="消费者联系人电话" readonly>
              </fdcinput>
            </formitem>
          </fdccol>
          <fdccol span="11"></fdccol>
        </row>
      </fdcform>
      <div class="table-box">
        <div class="module-title-wrap">接口列表</div>
        <fdctable border :loading="tableLoading" width="200px" :data="tableData" :columns="tableColumns" stripe></fdctable>
        <div class="page-box">
          <Page v-show="pageShow" placement="top" :page-size="formData.pageSize" :total="total"
                :current.sync="formData.pageNo" @on-change="changePageNo" @on-page-size-change="changePageSize" show-total show-elevator show-sizer></Page>
        </div>
      </div>
      <div style="padding-left: 30px;padding-bottom: 10px">
        <fdcbutton type="ghost" @click="close('apiConsumerView')" style="margin-left: 8px">关闭</fdcbutton>
      </div>
    </div>
  </div>
</template>

<script>
import Event from '@/utility/Event.js'
import ConsumerAPI from './api/ConsumerAPI'
import {mapState, mapAction} from 'vuex'
export default {
  name: 'apiConsumerView',
  data () {
    return {
      modal: false,
      queryShow: true,
      pageShow: false,
      tableLoading: false,
      total: 0,
      formData: {
        unityCode: '',
        name: '',
        consumerId: this.$route.query.id,
        linkman: '',
        pageNo: 1,
        pageSize: 10
      },
      tableData: []
    }
  },
  computed: {
    tableColumns () {
      let tableCol = [
        {title: '编号', key: 'number', width: 80, align: 'center'},
        {title: '统一编码', key: 'groupCode', align: 'center'},
        {title: '角色名称', key: 'groupName', align: 'center'},
        {title: '状态', key: 'state', align: 'center'}
      ]
      return tableCol
    }
  },
  methods: {
    changePageNo (value) {
      this.formData.pageNo = value
      this.query()
    },
    changePageSize (value) {
      this.formData.pageNo = 1
      this.formData.pageSize = value
      this.showPermissions()
    },
    view () {
      ConsumerAPI.view(this.$route.query.id).then((res) => {
        if (res.data.state == 1) {
          this.formData = res.data.data
        }
      })
    },
    showPermissions () {
      this.tableLoading = !this.tableLoading
      let query = {
        'pageNo': this.formData.pageNo,
        'pageNumber': this.formData.pageSize,
        'consumerId': this.formData.consumerId
      }
      ConsumerAPI.queryPermissions(query).then((res) => {
        this.tableData = []
        if (res.data.state == 1) {
          let dataList = res.data.data.list
          if (dataList != null) {
            this.pageShow = true
            this.total = res.data.data.total
          }
          for (let i = 0; i < dataList.length; i++) {
            let permission = dataList[i]
            this.tableData.push({
              id: permission.id,
              number: (i + 1),
              groupCode: permission.group.unityCode,
              groupName: permission.group.name,
              state: permission.state
            })
          }
        }
        this.tableLoading = !this.tableLoading
      })
    },
    close (router) {
      Event.$emit('closePage', router)
      Event.$once('refreshConsumerList', null)
    }
  },
  mounted () {
    this.view()
    this.showPermissions()
  },
  components: {}
}
</script>



