<!-- Add "scoped" attribute to limit CSS to this component only -->

<template>
  <div>

    <div class="page-header-box">
      <div class="header-btn-box">
        <fdcbutton v-show="formshow" @click="hideForm" icon="eye-disabled">隐藏</fdcbutton>
        <fdcbutton v-show="!formshow" @click="showForm" icon="eye">显示</fdcbutton>
        <fdcbutton icon="android-add">新增</fdcbutton>
        <fdcbutton class="commonColor" icon="android-add" @click="print">打印</fdcbutton>
        <fdcselect v-model="lang" placeholder="fdcselect your lang" style="width:100px" @on-change="changeLang">
          <fdcoption value="zh-CN">简体中文</fdcoption>
          <fdcoption value="en-US">英文</fdcoption>
          <fdcoption value="id-ID">印尼</fdcoption>
        </fdcselect>
      </div>
    </div>

    <form id="form1">
      <table border="1" width="300" id="tb01" bgcolor="#CCFFCC" style="border:solid 1px black;border-collapse:collapse">
        <tr>
          <td width="133" id="mtb001">
            <font face="黑体" color="#FF0000" size="3"><u>&nbsp;《表单一》&nbsp;</u></font></td>
        </tr>
      </table>
      <table border="1" width="300" height="106" cellspacing="0" bgcolor="#CCFFFF"
             style="border-collapse:collapse;table-layout:fixed;border:solid 1px black;">
        <tr>
          <td width="66" height="16" style="border:solid 1px black"><font color="#0000FF">A</font><font color="#0000FF">等</font>
          </td>
          <td width="51" height="16" style="border:solid 1px black"><font color="#0000FF">B</font><font color="#0000FF">等</font>
          </td>
          <td width="51" height="16" style="border:solid 1px black"><font color="#0000FF">C</font><font color="#0000FF">等</font>
          </td>
        </tr>
        <tr>
          <td width="66" height="16" style="border:solid 1px black">A<sub>01</sub></td>
          <td width="80" height="12" style="border:solid 1px black">中-001</td>
          <td width="51" height="12" style="border:solid 1px black">C1<sup>x</sup></td>
        </tr>
        <tr>
          <td width="66" height="16" style="border:solid 1px black">A<sub>02</sub>Φ</td>
          <td width="80" height="16" style="border:solid 1px black">日-スの</td>
          <td width="51" height="16" style="border:solid 1px black"><font face='Vernada'>7&#13221</font></td>
        </tr>
        <tr>
          <td width="66" height="16" style="border:solid 1px black;overflow:hidden">A<sub>03</sub>
            <nobr>over隐藏后面的：1234567890</nobr>
          </td>
          <td width="80" height="16" style="border:solid 1px black;overflow:hidden">韩-안녕</td>
          <td width="51" height="16">C3<sup>x</sup>
          </td>
        </tr>
        <tr>
          <td width="66" height="16" style="border:solid 1px black;overflow:hidden">
            <nobr>枚举下拉框组件示例:</nobr>
          </td>
          <td width="80" height="16" style="border:solid 1px black;overflow:hidden">{{$t('message.sys_login')}}</td>
          <td width="200" height="16"><enumSelect enum-type="USER_TYPE" enum-code="1" size="large" clearable  @onChange="testEnumSelect"></enumSelect></td>
        </tr>
      </table>
    </form>

    <div class="form-box" v-show="formshow">
      <div v-show="has('base')" icon="ios-search"><fdcbutton slot="append" @click="test(111)" icon="ios-search">权限测试按钮</fdcbutton></div>
      <fdcform ref="formValidate" :model="formValidate" :rules="ruleValidate" :label-width="80">
        <formitem label="Name" prop="name">
          <fdcinput v-model="formValidate.name" placeholder="Enter your name" style="width:300px">
            <fdcbutton slot="append" @click="test(111)" icon="ios-search"></fdcbutton>
          </fdcinput>
        </formitem>
        <formitem label="Age" prop="age">
          <inputnumber  v-model="formValidate.age" placeholder="Enter your age" style="width:300px">
          </inputnumber>
        </formitem>

        <formitem label="E-mail" prop="mail">
          <fdcinput v-model="formValidate.mail" placeholder="Enter your e-mail" style="width:300px"></fdcinput>
        </formitem>
        <formitem label="City" prop="city">
          <fdcselect v-model="formValidate.city" placeholder="fdcselect your city" style="width:300px">
            <fdcoption v-for="(item,index) in city" :value="item" :key="'city'+index">{{item}}</fdcoption>
          </fdcselect>
        </formitem>
        <formitem label="Date">
          <row>
            <fdccol span="5">
              <formitem prop="date">
                <datepicker type="date" placeholder="fdcselect date" v-model="formValidate.date"></datepicker>
              </formitem>
            </fdccol>
            <fdccol span="2" style="text-align: center">-</fdccol>
            <fdccol span="5">
              <formitem prop="time">
                <timepicker type="time" placeholder="fdcselect time" v-model="formValidate.time"></timepicker>
              </formitem>
            </fdccol>
          </row>
        </formitem>
        <formitem label="Gender" prop="gender">
          <radiogroup v-model="formValidate.gender">
            <radio label="male">Male</radio>
            <radio label="female">Female</radio>
          </radiogroup>
        </formitem>
        <formitem label="Hobby" prop="interest">
          <checkboxgroup v-model="formValidate.interest">
            <checkbox label="Eat"></checkbox>
            <checkbox label="Sleep"></checkbox>
            <checkbox label="Run"></checkbox>
            <checkbox label="Movie"></checkbox>
          </checkboxgroup>
        </formitem>
        <formitem label="Desc" prop="desc">
          <fdcinput v-model="formValidate.desc" type="textarea" :autosize="{minRows: 2,maxRows: 5}"
                    placeholder="Enter something..." style="width:300px"></fdcinput>
        </formitem>
        <formitem>
          <fdcbutton type="primary" @click="handleSubmit('formValidate')">Submit</fdcbutton>
          <fdcbutton type="ghost" @click="handleReset('formValidate')" style="margin-left: 8px">Reset</fdcbutton>
        </formitem>
      </fdcform>
    </div>
    <div class="table-box">
      <fdctable border stripe :columns="columns" :data="data"></fdctable>
    </div>
    <div class="table-box">
      <fdctable border height="522" :data="tableData1" :columns="tableColumns1" stripe></fdctable>
      <div class="page-box">
        <Page :total="100" :current="1" @on-change="changePage" show-total show-elevator show-sizer></Page>
      </div>
    </div>
    <div style="height: 300px;"></div>
    <modal v-model="modal1" title="选择公司" @on-ok="ok" @on-cancel="cancel">
      <div class="add-new-modal">
        <span>关键字：</span>
        <fdcinput placeholder="Enter something..." clearable style="width: 200px"></fdcinput>
        <fdcbutton icon="ios-search">搜索</fdcbutton>
      </div>
      <div>
        <tree :data="data2" show-checkbox></tree>
      </div>
    </modal>
  </div>
</template>

<script>
  import tags from '../../components/tags.vue'
  import UT from '@/utility/utility.js'
  import {getLodop} from '@/utility/LodopFuncs.js'
  import {mapState,mapActions} from 'vuex'
  import EnumSelect from '@/components/enumSelect.vue'

  export default {
    name: 'demo',
    data() {
      return {
//        tagList:this.$store.getters.getTag,
        lang:"zh-CN",
        modal1: false,
        data2: [{
          title: 'parent 1',
          expand: true,
          children: [{
            title: 'parent 1-1',
            expand: true,
            children: [{
              title: 'leaf 1-1-1'
            },
              {
                title: 'leaf 1-1-2'
              }
            ]
          },
            {
              title: 'parent 1-2',
              expand: true,
              children: [{
                title: 'leaf 1-2-1'
              },
                {
                  title: 'leaf 1-2-1'
                }
              ]
            }
          ]
        }],
        formshow: true,
        city: ["北京", "上海", "武汉"],
        formValidate: {
          name: '',
          age: 0,
          mail: '',
          city: '',
          gender: '',
          interest: [],
          date: '',
          time: '',
          desc: ''
        },
        ruleValidate: {
          name: [{
            required: true,
            message: 'The name cannot be empty',
            trigger: 'blur'
          },{
            type: 'number',
            message: 'Incorrect number format',
            trigger: 'blur'
          }],
          /* age: [{
           required: true,
           message: 'The age cannot be empty',
           trigger: 'blur'
           }],*/
          mail: [{
            required: true,
            message: 'Mailbox cannot be empty',
            trigger: 'blur'
          },
            {
              type: 'email',
              message: 'Incorrect email format',
              trigger: 'blur'
            }
          ],
          city: [{
            required: true,
            message: 'Please select the city',
            trigger: 'change'
          }],
          gender: [{
            required: true,
            message: 'Please select gender',
            trigger: 'change'
          }],
          interest: [{
            required: true,
            type: 'array',
            min: 1,
            message: 'Choose at least one hobby',
            trigger: 'change'
          },
            {
              type: 'array',
              max: 2,
              message: 'Choose two hobbies at best',
              trigger: 'change'
            }
          ],
          date: [{
            required: true,
            type: 'date',
            message: 'Please select the date',
            trigger: 'change'
          }],
          time: [{
            required: true,
            type: 'string',
            message: 'Please select time',
            trigger: 'change'
          }],
          desc: [{
            required: true,
            message: 'Please enter a personal introduction',
            trigger: 'blur'
          },
            {
              type: 'string',
              min: 20,
              message: 'Introduce no less than 20 words',
              trigger: 'blur'
            }
          ]
        },
        columns: [{
          type: 'selection',
          width: 60,
          align: 'center'
        },
          {
            title: 'Name',
            key: 'name'
          },
          {
            title: 'Age',
            key: 'age'
          },
          {
            title: 'Address',
            key: 'address'
          },
          {
            title: 'Action',
            key: 'action',
            width: 150,
            align: 'center',
            render: (h, params) => {
              return h('div', [
                h('fdcbutton', {
                  props: {
                    type: 'primary',
                    size: 'small'
                  },
                  style: {
                    marginRight: '5px'
                  },
                  on: {
                    click: () => {
                      this.show(params.index)
                    }
                  }
                }, 'View'),
                h('fdcbutton', {
                  props: {
                    type: 'error',
                    size: 'small'
                  },
                  on: {
                    click: () => {
                      this.remove(params.index)
                    }
                  }
                }, 'Delete')
              ]);
            }
          }
        ],
        data: [{
          name: 'John Brown',
          age: 18,
          address: 'New York No. 1 Lake Park',
          date: '2016-10-03'
        },
          {
            name: 'Jim Green',
            age: 24,
            address: 'London No. 1 Lake Park',
            date: '2016-10-01'
          },
          {
            name: 'Joe Black',
            age: 30,
            address: 'Sydney No. 1 Lake Park',
            date: '2016-10-02'
          },
          {
            name: 'Jon Snow',
            age: 26,
            address: 'Ottawa No. 2 Lake Park',
            date: '2016-10-04'
          }
        ],
        tableData1: this.mockTableData1(),
        tableColumns1: [{
          title: 'Number',
          key: 'number',
          align: 'center'
        }, {
          title: 'Name',
          key: 'name'
        },
          {
            title: 'Status',
            key: 'status',
            render: (h, params) => {
              const row = params.row;
              const color = row.status === 1 ? 'blue' : row.status === 2 ? 'green' : 'red';
              const text = row.status === 1 ? 'Working' : row.status === 2 ? 'Success' : 'Fail';

              return h('Tag', {
                props: {
                  type: 'dot',
                  color: color
                }
              }, text);
            }
          },
          {
            title: 'Portrayal',
            key: 'portrayal',
            render: (h, params) => {
              return h('Poptip', {
                props: {
                  trigger: 'hover',
                  title: params.row.portrayal.length + 'portrayals',
                  placement: 'bottom'
                }
              }, [
                h('Tag', params.row.portrayal.length),
                h('div', {
                  slot: 'content'
                }, [
                  h('ul', this.tableData1[params.index].portrayal.map(item => {
                    return h('li', {
                      style: {
                        textAlign: 'center',
                        padding: '4px'
                      }
                    }, item)
                  }))
                ])
              ]);
            }
          },
          {
            title: 'People',
            key: 'people',
            render: (h, params) => {
              return h('Poptip', {
                props: {
                  trigger: 'hover',
                  title: params.row.people.length + 'customers',
                  placement: 'bottom'
                }
              }, [
                h('Tag', params.row.people.length),
                h('div', {
                  slot: 'content'
                }, [
                  h('ul', this.tableData1[params.index].people.map(item => {
                    return h('li', {
                      style: {
                        textAlign: 'center',
                        padding: '4px'
                      }
                    }, item.n + '：' + item.c + 'People')
                  }))
                ])
              ]);
            }
          },
          {
            title: 'Sampling Time',
            key: 'time',
            render: (h, params) => {
              return h('div', 'Almost' + params.row.time + 'days');
            }
          },
          {
            title: 'Updated Time',
            key: 'update',
            render: (h, params) => {
              return h('div', this.formatDate(this.tableData1[params.index].update));
            }
          },
          {
            title: 'Action',
            key: 'action',
            render: (h, params) => {
              return h('div', [
                h('fdcbutton', {
                  props: {
                    type: 'primary',
                    size: 'small'
                  },
                  style: {
                    marginRight: '5px'
                  },
                  on: {
                    click: () => {
                      //this.show(params.index)
                    }
                  }
                }, 'View'),
                h('fdcbutton', {
                  props: {
                    type: 'error',
                    size: 'small'
                  },
                  on: {
                    click: () => {
                      //this.remove(params.index)
                    }
                  }
                }, 'Delete')
              ]);
            }
          }
        ]
      }
    },
    computed: {
      ...mapState({
        tagList: state => state.tagList
      }),
    },
    methods: {
      ...mapActions([
         'hasPermission'
        ]),
      testEnumSelect(temp){
          //alert(temp);
      },
      hideForm() {
        this.formshow = !this.formshow;
      },
      showForm() {
        this.formshow = !this.formshow;
      },
      collapsedSider() {
        this.isCollapsed = !this.isCollapsed;
      },
      handleSubmit(name) {
        this.$refs[name].validate((valid) => {
          if (valid) {
            this.$Message.success('Success!');
          } else {
            this.$Message.error('Fail!');
          }
        })
      },
      handleReset(name) {
        this.$refs[name].resetFields();
      },
      show(index) {
        this.$Modal.info({
          title: 'User Info',
          content: `Name：${this.data[index].name}<br>Age：${this.data[index].age}<br>Address：${this.data[index].address}`
        })
      },
      remove(index) {
        this.data.splice(index, 1);
      },
      mockTableData1() {
        let data = [];
        for (let i = 0; i < 10; i++) {
          data.push({
            number: i + 1,
            name: 'Business' + Math.floor(Math.random() * 100 + 1),
            status: Math.floor(Math.random() * 3 + 1),
            portrayal: ['City', 'People', 'Cost', 'Life', 'Entertainment'],
            people: [{
              n: 'People' + Math.floor(Math.random() * 100 + 1),
              c: Math.floor(Math.random() * 1000000 + 100000)
            },
              {
                n: 'People' + Math.floor(Math.random() * 100 + 1),
                c: Math.floor(Math.random() * 1000000 + 100000)
              },
              {
                n: 'People' + Math.floor(Math.random() * 100 + 1),
                c: Math.floor(Math.random() * 1000000 + 100000)
              }
            ],
            time: Math.floor(Math.random() * 7 + 1),
            update: new Date()
          })
        }
        return data;
      },
      formatDate(date) {
        const y = date.getFullYear();
        let m = date.getMonth() + 1;
        m = m < 10 ? '0' + m : m;
        let d = date.getDate();
        d = d < 10 ? ('0' + d) : d;
        return y + '-' + m + '-' + d;
      },
      changePage() {
        // The simulated data is changed directly here, and the actual usage scenario should fetch the data from the server
        this.tableData1 = this.mockTableData1();
      },
      ok() {

      },
      cancel() {

      },
      print() {
        let LODOP = getLodop();
        LODOP.PRINT_INIT("打印控件功能演示_Lodop功能_表单一");
        LODOP.SET_PRINT_STYLE("FontSize", 18);
        LODOP.SET_PRINT_STYLE("Bold", 1);
        LODOP.ADD_PRINT_TEXT(50, 231, 260, 39, "打印页面部分内容");
        LODOP.ADD_PRINT_HTM(88, 200, 350, 600, document.getElementById("form1").innerHTML);
        LODOP.PREVIEW();
        // window.location.reload()
      },
      changeLang(value){
        UT.switchLang(value);
      }
    },
    mounted() {
    },
    components: {
      tags: tags,
      enumSelect:EnumSelect
    }
  }
</script>




