<template>
  <div>
    <div class="form-box-content" style="padding-top: 15px;padding-bottom: 15px">
      <fdcform ref="form" :model="formData" :rules="paramValidate" :label-width="120">
        <row type="flex" justify="space-around">
          <fdccol span="12">
            <formitem :label="'统一编码：'" prop="unityCode">
              <fdcinput v-model="formData.unityCode" placeholder="消费者统一编码">
              </fdcinput>
            </formitem>
          </fdccol>
        </row>
        <row type="flex" justify="space-around">
          <fdccol span="12">
            <formitem :label="'名称：'" prop="name">
              <fdcinput v-model="formData.name" placeholder="消费者名称">
              </fdcinput>
            </formitem>
          </fdccol>
        </row>
        <row type="flex" justify="space-around">
          <fdccol span="12">
            <formitem :label="'IP白名单：'" prop="ipWhiteList">
              <fdcinput v-model="formData.ipWhiteList" placeholder="IP白名单">
              </fdcinput>
            </formitem>
          </fdccol>
        </row>
        <row type="flex" justify="space-around">
          <fdccol span="12" >
            <formitem :label="'IP黑名单：'" prop="ipBlackList">
              <fdcinput v-model="formData.ipBlackList" placeholder="IP黑名单">
              </fdcinput>
            </formitem>
          </fdccol>
        </row>
        <row type="flex" justify="space-around">
          <fdccol span="12" >
            <formitem :label="'联系人：'" prop="linkman">
              <fdcinput v-model="formData.linkman" placeholder="消费者联系人">
              </fdcinput>
            </formitem>
          </fdccol>
        </row>
        <row type="flex" justify="space-around">
          <fdccol span="12" >
            <formitem :label="'联系人邮箱：'" prop="linkmanEmail" >
              <fdcinput v-model="formData.linkmanEmail" placeholder="消费者联系人">
              </fdcinput>
            </formitem>
          </fdccol>
        </row>
        <row type="flex" justify="space-around">
          <fdccol span="12">
            <formitem :label="'联系人电话：'" prop="linkmanMobile">
              <fdcinput v-model="formData.linkmanMobile" placeholder="消费者联系人电话">
              </fdcinput>
            </formitem>
          </fdccol>
        </row>
        <row type="flex" justify="space-around">
          <fdccol span="12" >
            <formitem>
            <fdcbutton type="primary" @click="handleSubmit('form')">提交</fdcbutton>
            <fdcbutton type="ghost" @click="handleReset('form')" class="buttonClass">重置</fdcbutton>
            <fdcbutton type="ghost" @click="close('apiConsumerAdd')" class="buttonClass">取消</fdcbutton>
            </formitem>
          </fdccol>
        </row>
      </fdcform>
    </div>
  </div>
</template>

<script>
import Event from '@/utility/Event.js'
import ConsumerAPI from './api/ConsumerAPI'
import {mapState, mapAction} from 'vuex'
export default {
  name: 'apiConsumerAdd',
  data () {
    return {
      paramValidate: {
        unityCode: [{required: true, type: 'string', message: '统一编码必填', trigger: 'blur'}],
        name: [{required: true, type: 'string', message: '消费者名称必填', trigger: 'blur'}],
        linkman: [{required: true, type: 'string', message: '消费者联系人必填', trigger: 'blur'}],
        linkmanEmail: [{required: true, type: 'string', message: '消费者联系人邮箱必填', trigger: 'blur'}],
        linkmanMobile: [{required: true, type: 'string', message: '消费者联系人电话必填', trigger: 'blur'}]
      },
      formData: {
        unityCode: '',
        name: '',
        linkman: '',
        linkmanEmail: '',
        linkmanMobile: '',
        ipWhiteList: '',
        ipBlackList: ''
      }
    }
  },
  methods: {
    handleSubmit (name) {
      this.add()
    },
    handleReset (name) {
      this.$refs[name].resetFields()
    },
    add () {
      ConsumerAPI.add(this.formData).then((res) => {
        if (res.data.state == 1) {
          this.$Notice.success({
            title: '操作成功',
            desc: '新增消费者成功'
          })
          this.close('apiConsumerAdd')
        } else {
          this.$Notice.error({
            title: '操作失败',
            desc: res.data.msg
          })
        }
      })
    },
    close (router) {
      Event.$emit('closePage', router)
      Event.$once('refreshConsumerList', null)
    }
  },
  components: {}
}
</script>



