<style scoped>
</style>
<template>
  <div>
    <div class="page-header-box">
      <div class="header-msg-box">
        <p>分组管理</p>
      </div>
      <div class="header-btn-box">
        <fdcbutton v-show="queryShow" @click="hideForm" icon="eye-disabled">隐藏</fdcbutton>
        <fdcbutton v-show="!queryShow" @click="showForm" icon="eye">显示</fdcbutton>
        <fdcbutton icon="android-add" @click="add">新增</fdcbutton>
      </div>
    </div>
    <div class="form-box" v-show="queryShow">
      <fdcform ref="form" inline :model="formData" :label-width="80" class="search-form" @submit.native.prevent>
            <formitem :label="'统一编码'" prop="unityCode">
              <fdcinput v-model="formData.unityCode" placeholder="分组统一编码" class="search-input"></fdcinput>
            </formitem>
            <formitem :label="'名称：'" prop="developer">
              <fdcinput v-model="formData.name" placeholder="分组名称" class="search-input"></fdcinput>
              <fdcbutton type="primary" @click="handleSubmit('form')" style="margin-left: 20px" class="search-submit">查询</fdcbutton>
              <fdcbutton type="ghost" @click="handleReset('form')" style="margin-left: 8px" class="search-reset">重置</fdcbutton>
            </formitem>
      </fdcform>
    </div>

    <div class="table-box">
      <fdctable border size="small" :loading="tableLoading" width="200px" :data="tableData" :columns="tableColumns" stripe></fdctable>
      <div class="page-box">
        <Page v-show="pageShow" placement="top" :page-size="formData.pageSize" :total="total"
              :current.sync="formData.pageNo" @on-change="changePageNo" @on-page-size-change="changePageSize" show-total show-elevator show-sizer></Page>
      </div>
    </div>
    <modal v-model="grantShow" :width="1200" title="给API分组分配服务" @on-ok="grant" @on-cancel="closeGrant">
      <div class="form-box">
        <fdcform ref="grantForm" inline :model="grantFormData" :label-width="100">
          <row span="24">
            <fdccol span="12">
              <formitem :label="'接口全路径：'" prop="fullName">
                <fdcinput v-model="grantFormData.fullName" placeholder="请输入服务接口全路径" style="width:380px" class="search-input"></fdcinput>
              </formitem>
            </fdccol>
            <fdccol span="6">
              <formitem :label="'开发者名称：'" prop="developer">
                <fdcinput v-model="grantFormData.developer" placeholder="请输入开发者名称" style="width:100px" class="search-input"></fdcinput>
              </formitem>
            </fdccol>
            <fdccol span="6">
              <fdcbutton type="primary" @click="handleSubmit('grantForm')" style="margin-left: 20px" class="search-submit">查询</fdcbutton>
              <fdcbutton type="ghost" @click="handleReset('grantForm')" style="margin-left: 8px" class="search-reset">重置</fdcbutton>
            </fdccol>
          </row>
        </fdcform>
      </div>
      <div class="table-box">
        <fdctable border :loading="grantLoading" width="200px" height="500px" :data="grantData" :columns="grantDataColumns"
                  stripe @on-selection-change="selectChange"></fdctable>
        <div class="page-box">
          <Page v-show="grantPageShow" placement="top" :page-size="grantFormData.pageSize" :total="grantTotal"
                :current.sync="grantFormData.pageNo" @on-change="grantChangePageNo" @on-page-size-change="grantChangePageSize" show-total show-elevator show-sizer></Page>
        </div>
      </div>
    </modal>
  </div>
</template>

<script>
import Event from '@/utility/Event.js'
import GroupAPI from './api/GroupAPI'
import {mapState, mapAction} from 'vuex'
export default {
  name: 'apiGroupManage',
  data () {
    return {
      queryShow: true,
      pageShow: false,
      tableLoading: false,
      grantShow: false,
      grantLoading: false,
      grantPageShow: false,
      total: 0,
      grantTotal: 0,
      formData: {
        unityCode: '',
        name: '',
        pageNo: 1,
        pageSize: 10
      },
      tableData: [],
      grantFormData: {
        id: '',
        groupId: '',
        fullName: '',
        developer: '',
        pageNo: 1,
        pageSize: 10
      },
      grantData: [],
      selectedData: [],
      toGrantData: []
    }
  },
  computed: {
    tableColumns () {
      let tabCol = [
        {title: '编号', key: 'number', width: 80, align: 'center'},
        {title: '统一编码', key: 'unityCode', align: 'center'},
        {title: '名称', key: 'name', align: 'center'},
        {title: '状态', key: 'state', align: 'center'},
        {title: '创建时间', key: 'createdTime', align: 'center'},
        {title: '创建者', key: 'creator', align: 'center'},
        {title: '操作',
          key: 'operate',
          align: 'center',
          render: (h, params) => {
            const row = params.row
            return h('div', [
              h('tooltip', {
                props: {
                  content: '查看',
                  placement: 'top'
                }
              },
              [h('span', {
                on: {
                  click: () => {
                    this.view(row.id)
                  }
                }
              }, [h('icon', {
                props: {
                  type: 'clipboard',
                  size: 20,
                  color: '#ffb80d'
                },
                style: {
                  marginRight: '5px'
                }
              }, '查看')])]),
              h('tooltip', {
                props: {
                  content: '修改',
                  placement: 'top'
                }
              },
              [h('span', {
                on: {
                  click: () => {
                    this.modify(row.id)
                  }
                }
              }, [h('icon', {
                props: {
                  type: 'edit',
                  size: 20,
                  color: '#0d96ff'
                },
                style: {
                  marginRight: '5px'
                }
              }, '修改')])]),
              h('tooltip', {
                props: {
                  content: '给API分组分配服务',
                  placement: 'top'
                }
              }, [h('span', {
                on: {
                  click: () => {
                    this.toGrant(row.id)
                  }
                }
              },
              [h('icon', {
                props: { type: 'person-add', size: 20, color: '#0d96ff' },
                style: { marginRight: '5px' }
              }, '给API分组分配服务')])]),
              h('tooltip', {
                props: {
                  content: row.state == 1 ? '禁用' : '启用',
                  placement: 'top'
                }
              },
              [h('span', {
                on: {
                  click: () => {
                    if (row.state == 1) {
                      this.remove(row.id)
                    } else {
                      this.normal(row.id)
                    }
                  }
                }
              }, [h('icon', {
                props: {
                  type: row.state == 1 ? 'locked' : 'unlocked',
                  size: 20,
                  color: row.state == 1 ? '#8a8a8a' : '#0d96ff'
                }
              }, row.state == 1 ? '禁用' : '启用')])])
            ])
          }
        }
      ]
      return tabCol
    },
    grantDataColumns () {
      let tableCol = [
        {type: 'selection', width: 60, align: 'center'},
        {title: '接口名称', key: 'fullName', width: 300, align: 'center'},
        {title: '开发者名称', key: 'developer', align: 'center'},
        {title: '开发者邮箱', key: 'developerEmail', align: 'center'},
        {title: '开发者电话', key: 'developerMobile', align: 'center'},
        {title: '状态', key: 'state', align: 'center'}
      ]
      return tableCol
    }
  },
  methods: {
    query () {
      this.tableLoading = !this.tableLoading
      let query = { 'pageNo': this.formData.pageNo,
        'pageNumber': this.formData.pageSize,
        'fullName': this.formData.fullName,
        'developer': this.formData.developer,
        'developerEmail': this.formData.developerEmail
      }
      GroupAPI.query(query).then((res) => {
        this.tableData = []
        if (res.data.state == 1) {
          let dataList = res.data.data.list
          if (dataList != null) {
            this.pageShow = true
            this.total = res.data.data.total
          }
          for (let i = 0; i < dataList.length; i++) {
            let group = dataList[i]
            this.tableData.push({
              id: group.id,
              number: (i + 1),
              unityCode: group.unityCode,
              name: group.name,
              state: group.state,
              creator: group.creator,
              createdTime: group.createdTime
            })
          }
        }
        this.tableLoading = !this.tableLoading
      })
    },
    queryUngrant () {
      this.grantLoading = !this.grantLoading
      let query = { 'pageNo': this.grantFormData.pageNo,
        'pageNumber': this.grantFormData.pageSize,
        'groupId': this.grantFormData.groupId,
        'serviceFullName': this.grantFormData.fullName,
        'serviceDeveloper': this.grantFormData.developer
      }
      GroupAPI.queryUngrantService(query).then((res) => {
        this.grantData = []
        if (res.data.state == 1) {
          let dataList = res.data.data.list
          if (dataList != null) {
            this.grantPageShow = true
            this.grantTotal = res.data.data.total
          }
          for (let i = 0; i < dataList.length; i++) {
            let service = dataList[i]
            this.grantData.push({
              id: service.id,
              number: (i + 1),
              fullName: service.fullName,
              developer: service.developer,
              developerEmail: service.developerEmail,
              developerMobile: service.developerMobile,
              state: service.state,
              creator: service.creator,
              createdTime: service.createdTime
            })
          }
        }
        this.grantLoading = !this.grantLoading
      })
    },
    add () {
      Event.$emit('addPage', {
        title: '添加分组',
        router: 'apiGroupAdd',
        check: true
      })
    },
    view (groupId) {
      Event.$emit('addPage', {
        title: '分组明细',
        router: 'apiGroupView',
        checked: true,
        groupId: groupId
      })
    },
    modify (groupId) {
      Event.$emit('addPage', {
        title: '更新分组',
        router: 'apiGroupModify',
        checked: true,
        groupId: groupId
      })
    },
    toGrant (groupId) {
      this.grantShow = true
      this.selectedData = []
      this.toGrantData = []
      this.grantFormData.groupId = groupId
      this.queryUngrant()
    },
    selectChange (selection) {
      let pageSelectData = {
        page: this.grantFormData.pageNo,
        grantData: selection
      }
      this.toGrantData[this.grantFormData.pageNo - 1] = pageSelectData
    },
    getGrantData () {
      this.selectedData = []
      this.toGrantData.forEach((togrant) => {
        this.selectedData = this.selectedData.concat(togrant.grantData)
      })
    },
    grant () {
      this.getGrantData()
      if (this.selectedData.length > 0) {
        let serviceIdlist = []
        this.selectedData.forEach((data) => {
          serviceIdlist.push(data.id)
        })
        let permissions = {
          'id': this.grantFormData.groupId,
          'serviceIdList': serviceIdlist
        }
        GroupAPI.grant(this.grantFormData.groupId, permissions).then((res) => {
          if (res.data.state == 1) {
            this.$Notice.success({
              title: '操作成功',
              desc: '服务分配成功'
            })
            this.query()
          } else {
            this.$Notice.error({
              title: '操作失败',
              desc: res.data.msg
            })
          }
        })
      }
    },
    normal (groupId) {
      GroupAPI.normal(groupId).then((res) => {
        if (res.data.state == 1) {
          this.$Notice.success({
            title: '操作成功',
            desc: '启用成功'
          })
          this.query()
        } else {
          this.$Notice.error({
            title: '操作失败',
            desc: res.data.msg
          })
        }
      })
    },
    remove (groupId) {
      GroupAPI.remove(groupId).then((res) => {
        if (res.data.state == 1) {
          this.$Notice.success({
            title: '操作成功',
            desc: '禁用成功'
          })
          this.query()
        } else {
          this.$Notice.error({
            title: '操作失败',
            desc: res.data.msg
          })
        }
      })
    },
    hideForm () {
      this.queryShow = !this.queryShow
    },
    showForm () {
      this.queryShow = !this.queryShow
    },
    // 查询
    handleSubmit (name) {
      if (name == 'form') {
        this.query()
      } else {
        this.queryUngrant()
      }
    },
    closeGrant () {
      this.grantShow = false
      this.selectedData = []
      this.toGrantData = []
    },
    handleReset (name) {
      this.$refs[name].resetFields()
    },
    changePageNo (value) {
      this.formData.pageNo = value
      this.query()
    },
    changePageSize (value) {
      this.formData.pageNo = 1
      this.formData.pageSize = value
      this.query()
    },
    grantChangePageNo (value) {
      this.grantFormData.pageNo = value
      this.queryUngrant()
    },
    grantChangePageSize (value) {
      this.grantFormData.pageNo = 1
      this.grantFormData.pageSize = value
    }
  },
  mounted () {
    this.query()
    Event.$once('refreshGroupList', obj => this.query())
    this.tableHeight = window.innerHeight - 310
  },
  components: {}
}
</script>



