<style scoped>
  .table-box {
    position: relative;
    box-sizing: border-box ;
    height:100%
  }
</style>
<template>
  <div>
    <div class="page-header-box">
      <div class="header-msg-box">
        <p>服务管理</p>
      </div>
      <div class="header-btn-box">
        <fdcbutton v-show="queryShow" @click="hideForm" icon="eye-disabled">隐藏</fdcbutton>
        <fdcbutton v-show="!queryShow" @click="showForm" icon="eye">显示</fdcbutton>
        <fdcbutton icon="android-add" @click="add">新增</fdcbutton>
      </div>
    </div>

    <div class="form-box" v-show="queryShow">
      <fdcform ref="form" inline :model="formData" :label-width="100" class="search-form" @submit.native.prevent>
        <formitem :label="'接口全路径：'" prop="fullName">
          <fdcinput v-model="formData.fullName" placeholder="请输入服务接口全路径" class="search-input"></fdcinput>
        </formitem>
        <formitem :label="'开发者名称：'" prop="developer">
          <fdcinput v-model="formData.developer" placeholder="请输入开发者名称" class="search-input"></fdcinput>
        </formitem>
        <formitem :label="'开发者邮箱：'" prop="developerEmail">
          <fdcinput v-model="formData.developerEmail" placeholder="请输入开发者邮箱" class="search-input"></fdcinput>
          <fdcbutton type="primary" @click="handleSubmit('form')" class="search-submit">查询</fdcbutton>
          <fdcbutton type="ghost" @click="handleReset('form')" class="search-reset">重置</fdcbutton>
        </formitem>
      </fdcform>
    </div>

    <div class="table-box">
      <fdctable border size="small" :loading="tableLoading" width="200px" :data="tableData" :columns="tableColumns" stripe></fdctable>
      <div class="page-box">
        <Page v-show="pageShow" placement="top" :page-size="formData.pageSize" :total="total"
              :current.sync="formData.pageNo" @on-change="changePageNo" @on-page-size-change="changePageSize" show-total show-elevator show-sizer></Page>
      </div>
    </div>
  </div>
</template>

<script>
import Event from '@/utility/Event.js'
import ServiceAPI from './api/ServiceAPI'
import {mapState, mapAction} from 'vuex'
export default {
  name: 'serviceManage',
  data () {
    return {
      modal: false,
      queryShow: true,
      pageShow: false,
      tableLoading: false,
      total: 0,
      formData: {
        fullName: '',
        developer: '',
        developerEmail: '',
        pageNo: 1,
        pageSize: 10
      },
      tableData: []
    }
  },
  computed: {
    tableColumns () {
      let tableCol = [
        {title: '编号', key: 'number', width: 80, align: 'center'},
        {title: '接口名称', key: 'fullName', width: 500, align: 'center'},
        {title: '开发者名称', key: 'developer', align: 'center'},
        {title: '开发者邮箱', key: 'developerEmail', align: 'center'},
        {title: '开发者电话', key: 'developerMobile', align: 'center'},
        {title: '状态', key: 'state', align: 'center'},
        {title: '创建时间', key: 'createdTime', align: 'center'},
        {title: '操作',
          key: 'operate',
          align: 'left',
          render: (h, params) => {
            const row = params.row
            return h('div', [
              h('tooltip', {
                props: {
                  content: '查看',
                  placement: 'top'
                }
              },
              [h('span', {
                on: {
                  click: () => {
                    this.view(row.id)
                  }
                }
              }, [h('icon', {
                props: {
                  type: 'clipboard',
                  size: 20,
                  color: '#ffb80d'
                },
                style: {
                  marginRight: '5px'
                }
              }, '查看')])]),
              h('tooltip', {
                props: {
                  content: '修改',
                  placement: 'top'
                }
              },
              [h('span', {
                on: {
                  click: () => {
                    this.modify(row.id)
                  }
                }
              }, [h('icon', {
                props: {
                  type: 'edit',
                  size: 20,
                  color: '#0d96ff'
                },
                style: {
                  marginRight: '5px'
                }
              }, '修改')])]),
              h('tooltip', {
                props: {
                  content: row.state == 1 ? '禁用' : '启用',
                  placement: 'top'
                }
              },
              [h('span', {
                on: {
                  click: () => {
                    if (row.state == 1) {
                      this.remove(row.id)
                    } else {
                      this.normal(row.id)
                    }
                  }
                }
              }, [h('icon', {
                props: {
                  type: row.state == 1 ? 'locked' : 'unlocked',
                  size: 20,
                  color: row.state == 1 ? '#8a8a8a' : '#0d96ff'
                }
              }, row.state == 1 ? '禁用' : '启用')])])
            ])
          }
        }
      ]
      return tableCol
    }
  },
  mounted () {
    this.query()
    Event.$once('refreshServiceList', obj => this.query())
    this.tableHeight = window.innerHeight - 310
  },
  methods: {
    hideForm () {
      this.queryShow = !this.queryShow
    },
    showForm () {
      this.queryShow = !this.queryShow
    },
    // 查询
    handleSubmit (name) {
      this.query()
    },
    handleReset (name) {
      this.$refs[name].resetFields()
    },
    query () {
      this.tableLoading = !this.tableLoading
      let query = { 'pageNo': this.formData.pageNo,
        'pageNumber': this.formData.pageSize,
        'fullName': this.formData.fullName,
        'developer': this.formData.developer,
        'developerEmail': this.formData.developerEmail
      }
      ServiceAPI.query(query).then((res) => {
        this.tableData = []
        if (res.data.state == 1) {
          let dataList = res.data.data.list
          if (dataList != null) {
            this.pageShow = true
            this.total = res.data.data.total
          }
          for (let i = 0; i < dataList.length; i++) {
            let apiService = dataList[i]
            this.tableData.push(
              { id: apiService.id,
                number: (i + 1),
                fullName: apiService.fullName,
                developer: apiService.developer,
                developerEmail: apiService.developerEmail,
                developerMobile: apiService.developerMobile,
                state: apiService.state,
                createdTime: apiService.createdTime,
                creator: apiService.creator
              }
            )
          }
        }
        this.tableLoading = !this.tableLoading
      })
    },
    changePageNo (value) {
      this.formData.pageNo = value
      this.query()
    },
    changePageSize (value) {
      this.formData.pageNo = 1
      this.formData.pageSize = value
      this.query()
    },
    add () {
      Event.$emit('addPage', {
        title: '添加服务',
        router: 'serviceAdd',
        check: true
      })
    },
    view (id) {
      Event.$emit('addPage', {
        title: '服务明细',
        router: 'serviceView',
        checked: true,
        id: id
      })
    },
    modify (id) {
      Event.$emit('addPage', {
        title: '修改服务',
        router: 'serviceModify',
        checked: true,
        id: id
      })
    },
    normal (id) {
      ServiceAPI.normal(id).then((res) => {
        if (res.data.state == 1) {
          this.$Notice.success({
            title: '操作成功',
            desc: '启用成功'
          })
          this.query()
        } else {
          this.$Notice.error({
            title: '操作失败',
            desc: res.data.msg
          })
        }
      })
    },
    remove (id) {
      ServiceAPI.remove(id).then((res) => {
        if (res.data.state == 1) {
          this.$Notice.success({
            title: '操作成功',
            desc: '禁用成功'
          })
          this.query()
        } else {
          this.$Notice.error({
            title: '操作失败',
            desc: res.data.msg
          })
        }
      })
    }
  },
  components: {}
}
</script>



