<template>
  <div class="full-screen">
    <tooltip content="全屏" v-show="!fullScreen">
      <span @click="fullscreen"><icon type="arrow-expand" size="22"></icon></span>
    </tooltip>
    <tooltip content="退出全屏" v-show="fullScreen">
      <span @click="exitFullscreen"><icon type="arrow-shrink" size="22"></icon></span>
    </tooltip>
  </div>
</template>

<script>
    export default {
      name: "fullScreen",
      data () {
        return {
          fullScreen: false,
        }
      },
      methods: {
        // 全屏
        fullscreen() {
          var docElm = document.documentElement
          if (docElm.requestFullscreen) {
            docElm.requestFullscreen()
          } else if (docElm.mozRequestFullScreen) {
            docElm.mozRequestFullScreen()
          } else if (docElm.webkitRequestFullScreen) {
            docElm.webkitRequestFullScreen()
          } else if (docElm.msRequestFullscreen) {
            docElm.msRequestFullscreen()
          }
        },
        // 退出全屏
        exitFullscreen() {
          if (document.exitFullscreen) {
            document.exitFullscreen()
          } else if (document.mozCancelFullScreen) {
            document.mozCancelFullScreen()
          } else if (document.webkitCancelFullScreen) {
            document.webkitCancelFullScreen()
          } else if (document.msExitFullscreen) {
            document.msExitFullscreen()
          }
        }
      },
      created () {
        let isFullscreen = document.fullscreenElement || document.mozFullScreenElement || document.webkitFullscreenElement || document.fullScreen || document.mozFullScreen || document.webkitIsFullScreen;
        isFullscreen = !!isFullscreen;
        document.addEventListener('fullscreenchange', () => {
          this.fullScreen = !this.fullScreen
        });
        document.addEventListener('mozfullscreenchange', () => {
          this.fullScreen = !this.fullScreen
        });
        document.addEventListener('webkitfullscreenchange', () => {
          this.fullScreen = !this.fullScreen
        });
        document.addEventListener('msfullscreenchange', () => {
          this.fullScreen = !this.fullScreen
        });
        this.fullScreen = isFullscreen
      }
    }
</script>

<style scoped>

</style>



