import HttpUtil from '@/common/HttpUtil'
const CacheAPI = {
  query (cacheKey) {
    return HttpUtil.post('/api/system/sysCache/query', {cacheKey:cacheKey});
  },
  delete(cacheKey){
    return HttpUtil.post('/api/system/sysCache/del', {cacheKey:cacheKey});
  },
  update(params){
    return HttpUtil.post('/api/system/sysCache/update', params);
  }
}
export default CacheAPI



