<template>
  <div>
    <div class="page-header-box">
      <div class="header-msg-box">
        <p>枚举管理</p>
      </div>
      <div class="header-btn-box">
        <fdcbutton v-show="formshow" @click="hideForm" icon="eye-disabled">隐藏</fdcbutton>
        <fdcbutton v-show="!formshow" @click="showForm" icon="eye">显示</fdcbutton>
        <fdcbutton icon="android-add"  @click="addEnum">新增</fdcbutton>
      </div>
    </div>
    <div class="form-box" v-show="formshow">
      <fdcform ref="formData" :model="formData" :label-width="80" class="search-form" inline
               @submit.native.prevent @keyup.enter.native="handleSubmit('formData')" >
        <formitem label="枚举类型" prop="enumType">
          <fdcinput v-model="formData.enumType" placeholder="请输入枚举类型" class="search-input"></fdcinput>
          <fdcbutton type="primary" @click="handleSubmit('formData')" class="search-submit">查询</fdcbutton>
          <fdcbutton type="ghost" @click="handleReset('formData')" class="search-reset">重置</fdcbutton>
        </formitem>
      </fdcform>
    </div>
    <div class="table-box">
      <fdctable border size="small" :loading="isLoading" :data="tableData"
            :columns="tableColumns" stripe></fdctable>
      <div class="page-box">
        <Page v-show="pageShow" placement="top" :total="total" :page-size="formData.pageNumber" :current.sync="formData.pageNo" @on-change="changePageNo" @on-page-size-change="changePageSize" show-total show-elevator show-sizer></Page>
      </div>
    </div>
  </div>
</template>

<script>
import Event from '@/utility/Event.js'
import EnumAPI from './api/EnumAPI'
import {mapState} from 'vuex'

export default {
  name: 'enumerate',
  data () {
    return {
      formshow: true,
      isLoading: false,
      pageShow: false,
      total: 0,
      formData: {
        enumType: '',
        pageNo: 1,
        pageNumber: 10
      },
      tableData: [],
      tableColumns: [{
        title: ' ',
        type: 'index2',
        width: 60,
        align: 'center',
        render: (h, params) => {
          return h('span', params.index + (this.formData.pageNo - 1) * this.formData.pageNumber + 1)
        }
      },
      {
        title: '枚举类型',
        key: 'enumType',
        align: 'center'
      },
      {
        title: '操作',
        key: 'action',
        align: 'left',
        render: (h, params) => {
          const row = params.row
          return h('div', [
            h('tooltip', {
              props: {
                content: '修改',
                placement: 'top'
              }
            }, [h('span', {
              on: {
                click: () => {
                  this.modify(row.enumType)
                }
              }
            }, [h('icon', {
              props: {
                type: 'edit',
                size: 20,
                color: '#0d96ff'
              },
              style: {
                marginRight: '5px'
              }
            }, '修改')])]),
            h('tooltip', {
              props: {
                content: '删除',
                placement: 'top'
              }
            }, [h('span', {
              on: {
                click: () => {
                  this.$Modal.confirm({
                    title: '删除',
                    content: '<p>确定删除？</p>',
                    onOk: () => {
                      this.remove(row.enumType)
                    },
                    onCancel: () => {}
                  })
                }
              }
            }, [h('icon', {
              props: {
                type: 'trash-a',
                size: 20,
                color: '#f11a09'
              }
            }, '删除')])])
          ])
        }
      }
      ]
    }
  },
  computed: {
    ...mapState({
      tagList: state => state.tagList
    })
  },
  methods: {
    hideForm () {
      this.formshow = !this.formshow;
    },
    showForm () {
      this.formshow = !this.formshow;
    },
    // 查询
    handleSubmit (name) {
      this.$refs[name].validate((valid) => {
        if (valid) {
          this.query();
        }
      })
    },
    query () {
      this.isLoading = !this.isLoading;
      EnumAPI.query(this.formData).then((res)=>{
        this.tableData = [];
        let state = res.data.state;
        if (state === 1) {
          let dataList = res.data.data.list;
          if (dataList !== null) {
            this.pageShow = true;
            this.total = res.data.data.total;
          }
          this.tableData.push(...dataList)
        }
        this.isLoading = !this.isLoading;
      });
    },
    handleReset (name) {
      this.$refs[name].resetFields();
    },
    remove (enumType) {
      EnumAPI.delete(enumType, this.query)
    },
    modify (params) {
      Event.$emit('addPage', {
        title: '修改枚举',
        router: 'modifyEnum',
        checked: true,
        paramId: params
      })
    },
    changePageNo(value) {
      this.formData.pageNo = value;
      this.query();
    },
    changePageSize(value) {
      this.formData.pageNo=1;
      this.formData.pageNumber = value;
      this.query();
    },
    addEnum () {
      // this.modal1 =
      Event.$emit('addPage', {
        title: '新增枚举',
        router: 'addEnum',
        checked: true
      })
    }
  },
  mounted () {
    Event.$on('refreshList', obj => this.query())
  },
  components: {
  }
}
</script>



