<style scoped>
  .sider-menu-box {
    height: 100%;
    position: fixed;
    top: 0;
    left: 0;
    z-index: 21;
    transition: width .3s;
    width: 200px;
    overflow: auto;
    background: #495060;
  }

  .sider-menu-box-collapsed {
    width: 78px;
  }
  .min-menu .uiBase-icon {
    font-size: 20px;
  }

  .logo-box {
    padding: 15px 10px;
    text-align: center;
  }

</style>
<template>
  <div class="sider-menu-box" :class="silderClass">
  <div class="all-menu" v-show="!isCollapsed">
    <div class="logo-box">
      <img class="big-logo" src="../assets/logo/jd-logo.png">
    </div>
    <div class="search-menu-box">
      <Autocomplete clearable transfer :data="searchResult"  @on-select="selectSearch" @on-search="handleSearch"  placeholder="搜索菜单" icon="ios-search" style="width:150px">
      </Autocomplete>
    </div>
    <fdcmenu ref="sideMenu" theme="dark" style="width: auto" @on-select="selectMenu" :active-name="this.$route.name"  :open-names="openedSubmenu">
      <side-menu-item :menuList="menuList" @menuCollapsed="menuCollapsed"></side-menu-item>
    </fdcmenu>
  </div>

  <div class="min-menu" v-show="isCollapsed">
    <div class="logo-box">
      <img class="small-logo" width="30" src="../assets/logo/jd-logo.png">
    </div>
    <fdcmenu ref="sideMenu" theme="dark" accordion style="width: 78px" @on-select="selectMenu" :active-name="this.$route.name"  :open-names="openedSubmenu">
      <side-menu-item :menuList="menuList" @menuCollapsed="menuCollapsed"></side-menu-item>
    </fdcmenu>
    <fdcmenu theme="dark" >
      <fdcmenuitem name="0-1">
        <Icon type="ios-paper"></Icon>
      </fdcmenuitem>
      <fdcmenuitem name="0-2">
        <Icon type="ios-paper"></Icon>
      </fdcmenuitem>
      <fdcmenuitem name="1">
        <Icon type="ios-paper"></Icon>
      </fdcmenuitem>
      <fdcmenuitem name="2">
        <Icon type="ios-people"></Icon>
      </fdcmenuitem>
      <fdcmenuitem name="3">
        <Icon type="stats-bars"></Icon>
      </fdcmenuitem>
    </fdcmenu>
  </div>
  </div>

</template>

<script>
import UT from '@/utility/utility.js'
import { mapState,mapActions } from 'vuex'
import SideMenuItem from './sideMenuItem'
import Event from '@/utility/Event.js'
import router from '@/router/index'

export default {
  data() {
    return {
      searchResult:[],
      openedSubmenu:[]
    }
  },
  components: { SideMenuItem },
  computed: {
    ...mapState({
        menuList : state => state.User.menuList,
        isCollapsed:state=> state.isCollapsed,
        permissionRouters : state => state.User.permissionRouters
      }
    ),
    silderClass() {
      return [
        this.isCollapsed ? 'sider-menu-box-collapsed' : ''
      ]
    },
  },
  methods:{
    selectMenu(routerName) {
      if (this.$route.name === routerName) {
        return false
      }
      let title = '';
      this.permissionRouters.map((item) => {
        if (item.name === routerName) {
          title = item.meta.title;
        }
      })
      Event.$emit('addPage', {
        title: title,
        router: routerName
      });
    },
    //选择菜单
    selectSearch(title){
        if(this.permissionRouters){
            for(let i=0;i<this.permissionRouters.length;i++){
              let item = this.permissionRouters[i];
              if(!item.meta || !item.meta.title || !item.meta.isMenu) continue;
              if(item.meta.title == title){
                Event.$emit('addPage', {
                  title: title,
                  router: item.name
                });
              }
            }
        }
    },

    //搜索菜单
    handleSearch(query){
      query = UT.trim(query);
      this.searchResult = [];
      var result = [];
      if(this.permissionRouters){
        for(let i=0;i<this.permissionRouters.length;i++){
          let item = this.permissionRouters[i];
          if(!item.meta || !item.meta.title || !item.meta.isMenu) continue;
          if(item.meta.title.indexOf(query)>-1 && item.meta.isMenu==true){
            result.push(item.meta.title);
          }
        }
      }
      this.searchResult = result;
    },

    menuCollapsed(temp){
        this.openedSubmenu = temp;
    }
  },
  updated() {
    //用于更新菜单展开/收起
    this.$nextTick(() => {
      if (this.$refs.sideMenu) {
        this.$refs.sideMenu.updateOpened();
        this.$refs.sideMenu.updateActiveName();
      }
    });
  }

}
</script>



