<style scoped>
  .uiBase-table .demo-table-info-row td {
    background-color: #2db7f5;
    color: #fff;
  }
</style>
<template>
  <div>
    <div class="form-box">
      <fdcform ref="queryData" :model="queryData"  :label-width="80" inline class="search-form">
        <row span="24">
          <fdccol span="24">
            <formitem label="作业点名称" prop="name">
              <fdcinput v-model="queryData.name" placeholder="作业点名称" class="search-input">
              </fdcinput>
              <fdcbutton type="primary" class="search-submit" @click="handleSubmit('queryData')">查询</fdcbutton>
              <fdcbutton type="ghost" class="search-reset" @click="handleReset('queryData')">重置</fdcbutton>
            </formitem>
          </fdccol>
        </row>
      </fdcform>
    </div>
    <div>
      <fdctable border highlight-row ref="table" size="small" height="400" :data="tableData" :columns="tableColumns"
                @on-current-change="select"></fdctable>
      <div class="page-box">
        <Page v-show="pageShow" placement="top" :total="total" :page-size="queryData.pageNumber"
              :current.sync="queryData.pageNo" @on-change="changeNo" @on-page-size-change="changeSize"
              show-total show-elevator show-sizer></Page>
      </div>
    </div>
  </div>
</template>
<script>
  import LocationAPI from '../../page/basic/basicData/location/api/LocationAPI'
  import Event from '@/utility/Event.js'

  export default {
    name: 'locationModal',
    data () {
      return {
        pageShow: false,
        total: 0,
        rowData: {},
        queryData: {
          name: '',
          pageNumber: 10,
          pageNo: 1,
          isDeleted: 0
        },
        tableData: [],
        tableColumns: [
          {
            title: ' ',
            type: 'index2',
            width: 60,
            align: 'center',
            render: (h, params) => {
              return h('span', params.index + (this.queryData.pageNo - 1) * this.queryData.pageNumber + 1)
            }
          },
          {
            title: '公司代码',
            key: 'code',
            align: 'center'
          },
          {
            title: '公司名称',
            key: 'name',
            align: 'center'
          }
        ]
      }
    },
    methods: {
      select (currentRow, oldCurrentRow) {
        this.rowData = currentRow
        Event.$emit('selectLocation', this.rowData)
      },
      // 查询
      handleSubmit (name) {
        this.$refs[name].validate((valid) => {
          if (valid) {
            this.query();
          }
        })
      },
      handleReset (name) {
        this.$refs[name].resetFields();
      },
      query () {
        LocationAPI.query(this.queryData).then((res) => {
          this.tableData = []
          let state = res.data.state
          if (state === 1) {
            let dataList = res.data.data.list
            if (dataList !== null) {
              this.pageShow = true
              this.total = res.data.data.total
            }
            this.tableData.push(...dataList)
          }
          this.isLoading = !this.isLoading
        })
      },
      changeNo (value) {
        this.queryData.pageNo = value
        this.query()
      },
      changeSize (value) {
        this.queryData.pageNo = 1
        this.queryData.pageNumber = value
        this.query()
      },
      handleClearCurrentRow (table) {
        table.clearCurrentRow()
      }
    },
    mounted () {
      this.query()
      let table = this.$refs['table']
      Event.$on('clearSelect', () => this.handleClearCurrentRow(table))
      Event.$on('queryLocation', () => this.query())
    }
  }

</script>



