import HttpUtil from '@/common/HttpUtil'
const SysParameterAPI = {
  query (params) {
    return HttpUtil.post('/api/system/sysParameter/query', params);
  },
  insert(params) {
    return HttpUtil.post('/api/system/sysParameter/insert', params,false);
  },
  delete(params){
    return HttpUtil.post('/api/system/sysParameter/del', params);
  },
  getById(id){
    return HttpUtil.post('/api/system/sysParameter/getById',{id:id})
  },
  modify(params){
    return HttpUtil.post('/api/system/sysParameter/modify', params);
  }
}
export default SysParameterAPI



