<template>
  <div>
    <div class="form-box-content">
      <div class="module-title-wrap">基本信息</div>
      <fdcform ref="formValidate" :model="formValidate" :rules="ruleValidate" :label-width="100" >
       <!-- <fdcinput v-model="formValidate.parentId" v-show='false'></fdcinput>-->
        <row type="flex" justify="space-around">
          <fdccol span="7">
        <formitem label="所属模块" prop="module" >
          <fdcselect v-model="formValidate.module" @on-change="generateMenuTree()">
            <fdcoption v-for="item in enumSelect['MODULE_TYPE']" :value="item.enumCode" :key="item.enumCode">
              {{ item.enumValue }}
            </fdcoption>
          </fdcselect>
        </formitem>
          </fdccol>
          <fdccol span="7">
        <formitem label="上级菜单" prop="parentName">
          <fdcinput v-model="formValidate.parentName" placeholder="" >
            <fdcbutton slot="append" icon="ios-search" @click="selectParentMenu()"></fdcbutton>
          </fdcinput>
        </formitem>
          </fdccol>
          <fdccol span="7">
        <formitem label="菜单类型" prop="type">
          <fdcselect v-model="formValidate.type"  >
            <fdcoption v-for="item in enumSelect['MENU_TYPE']" :value="item.enumCode" :key="item.enumCode">
              {{ item.enumValue }}
            </fdcoption>
          </fdcselect>
        </formitem>
          </fdccol>
        </row>
            <row type="flex" justify="space-around">
              <fdccol span="7">
        <formitem label="权限码" prop="code">
          <fdcinput v-model="formValidate.code" placeholder="" ></fdcinput>
        </formitem>
          </fdccol>
          <fdccol span="7">
        <formitem label="菜单名称" prop="name">
          <fdcinput v-model="formValidate.name" placeholder=""></fdcinput>
        </formitem>
          </fdccol>
          <fdccol span="7">
        <formitem label="角色权重" prop="roleLevel">
          <fdcselect v-model="formValidate.roleLevel"  >
            <fdcoption v-for="item in enumSelect['ROLE_LEVEL']" :value="item.enumCode" :key="item.enumCode">
              {{ item.enumValue }}
            </fdcoption>
          </fdcselect>
        </formitem>
          </fdccol>
            </row>
        <row type="flex" justify="space-around">
          <fdccol span="7">
            <formitem label="层级" prop="level" v-show="false">
              <fdcinput v-model="formValidate.level" placeholder="">
              </fdcinput>
            </formitem>
            <formitem label="排序" >
              <fdcinput v-model="formValidate.levelOrder"  placeholder=""></fdcinput>
            </formitem>
          </fdccol>
          <fdccol span="15">
            <formitem label="链接（Href）" >
              <fdcinput v-model="formValidate.url"  placeholder="">
              </fdcinput>
            </formitem>
          </fdccol>
        </row>
        <row type="flex" justify="space-around">
          <fdccol span="7">
            <formitem>
              <fdcbutton type="primary" @click="handlerSubmit('formValidate')">提交</fdcbutton>
              <fdcbutton type="ghost" @click="cancel('modifyPermission')" class="buttonClass">取消</fdcbutton>
            </formitem>
          </fdccol>
        </row>
      </fdcform>

      <modal v-model="showModal" @close="showModal = false" @on-ok="getParent">
        <div style="padding: 0px 0px 0px 20px; ">
          <div class="module-title-wrap">选择父菜单</div>
          <div style="height: 600px;overflow-y:auto">
            <tree :data="menuData"  ref="tree"  node-key="id"></tree>
          </div>
        </div>
      </modal>
    </div>
  </div>
</template>
<script>
import PermissionAPI from './api/PermissionAPI'
import Event from '@/utility/Event.js'
import {mapActions, mapState} from 'vuex'
import EnumSelect from '@/components/enumSelect.vue'
export default {
  name: 'modifyPermission',
  data: function () {
    return {
      ruleValidate: {
        name: [{
          required: true,
          type: 'string',
          message: '请输入菜单名',
          trigger: 'blur'
        }],
        type: [{
          required: true,
          type: 'number',
          message: '请选择类型',
          trigger: 'change'
        }],
        code: [{
          required: true,
          message: '请输入权限码',
          trigger: 'blur'
        }],
        roleLevel: [{
          required: true,
          type: 'number',
          message: '请选择角色权限',
          trigger: 'change'
        }],
        module: [{
          required: true,
          type: 'number',
          message: '请选择所属模块',
          trigger: 'change'
        }]
      },
      formValidate: {
        id: '',
        parentName: '',
        parentId: '',
        type: '',
        name: '',
        roleLevel: '',
        module: '',
        level: '',
        url: '',
        levelOrder: ''
      },
      showModal: false,
      menuData: [],
      treeNodeList: []
    }
  },
  watch: {
    'formValidate.module': function (newValue, oldValue) {
      if (oldValue == '') {
        return
      }
      this.formValidate.parentName = ''
    }
  },
  computed: {
    ...mapState({
      sysEnumList: state => state.Enum.enumList,
      enumSelect: state => state.Enum.enumSelect
    })
  },
  methods: {
    generateForm: function () {
      PermissionAPI.getById(this.$route.query.paramId).then((res) => {
        if (res.data) {
          this.formValidate = res.data.data
        } else {
          this.$Notice.error({
            title: '操作失败',
            desc: res.data.msg
          })
        }
      })
    },
    selectParentMenu: function () {
      if (this.formValidate.module == '') {
        alert('请先选择模块')
      } else {
        this.showModal = true
      }
    },
    generateMenuTree: function () {
      // 清空上级菜单
      /* this.formValidate.parentName = '' */
      PermissionAPI.findListByModule(this.formValidate.module).then((res) => {
        if (res.data.state = 1) {
          this.treeNodeList = res.data.data
          if (this.treeNodeList.length > 0) {
            this.generateTreeNode()
          }
        }
      })
    },
    generateTreeNode: function () {
      // 将上次生成的菜单树清空
      this.menuData = []
      let node = {
        id: 0,
        title: '菜单管理',
        expand: true,
        checked: false,
        children: []
      }
      node.children = this.getChildrenNode(0)
      this.menuData.push(node)
    },
    getChildrenNode (pId) {
      let childrenNode = []
      for (let i = 0; i < this.treeNodeList.length; i++) {
        let item = this.treeNodeList[i]
        if (item.parentId === pId) {
          let node = {
            id: '',
            title: '',
            expand: true,
            children: []
          }
          node.id = item.id
          node.title = item.name
          node.expand = true
          node.children = this.getChildrenNode(item.id)
          childrenNode.push(node)
        }
      }
      return childrenNode
    },
    getParent: function () {
      let selectedNodes = this.$refs.tree.getSelectedNodes()
      if (selectedNodes.length > 0) {
        this.formValidate.parentId = selectedNodes[0].id
        this.formValidate.parentName = selectedNodes[0].title
        this.showModal = false
      } else {
        alert('请选择父菜单')
        this.showModal = true
      }
    },
    selectRoleLevel: function (value) { this.formValidate.roleLevel = value },
    selectMenuType: function (value) { this.formValidate.type = value },
    selectModuleType: function (value) { this.formValidate.module = value },
    handlerSubmit: function (name) {
      this.$refs[name].validate((valid) => {
        if (valid) {
          this.updatePermission()
        }
      })
    },
    updatePermission: function () {
      PermissionAPI.modify(this.formValidate).then((res) => {
        if (res.data.state == 1) {
          this.$Notice.success({
            title: '操作成功',
            desc: '修改菜单成功'
          })
          Event.$emit('closePage', 'modifyPermission')
          Event.$emit('refreshPermissionList', null)
        } else {
          this.$Notice.error({
            title: '操作失败',
            desc: res.data.msg
          })
        }
      })
    },
    cancel: function (router) {
      Event.$emit('closePage', router)
    }
  },
  components: {
    enumSelect: EnumSelect
  },
  mounted: function () {
    const condition = ['MENU_TYPE', 'MODULE_TYPE', 'ROLE_LEVEL']
    Event.$emit('addEnum', {typeList: condition})
    this.generateForm()
  }
}
</script>



